import { Observable, of, map } from 'rxjs';
import { TreeNode } from 'primeng/api';
import {
  DataGridParams,
  TreeTableAdapter,
  TreeTableResponse,
} from '../../../../shared/models';
import {
  isDefined,
  isEmpty,
  isNullOrUndefined,
} from '../../../../shared/utils';
import { IdName } from '../../../../api';

// Common interface for hierarchical entities
export interface HierarchicalEntity {
  id: string;
  name?: string | null;
  parent?: IdName;
  owner?: { id: string };
  [key: string]: unknown;
}

export abstract class BaseTreeAdapter<
  TList extends HierarchicalEntity,
  TListResponse extends { items: TList[]; count: number },
  TDetail = TList,
> implements TreeTableAdapter<TDetail, DataGridParams, TreeTableResponse<TList>>
{
  /**
   * Fetches entities from the server based on the provided parameters.
   * This method is responsible for making the API call to retrieve the list of entities.
   * It should be implemented in derived classes to provide the specific API call.
   *
   * @param owner - The owner ID to filter entities by
   * @param offset - The offset for pagination
   * @param limit - The limit for pagination
   * @param sortField - The field to sort the entities by
   * @param filters - The filters to apply to the entity list
   * @return Observable containing the list of entities and their count
   * @throws Error if the API call fails
   */
  protected abstract fetchEntities(
    owner: string,
    offset: number | undefined,
    limit: number | undefined,
    sortField: string | undefined,
    filters: string | undefined,
  ): Observable<TListResponse>;

  /**
   * Fetches a single entity by its ID.
   * This method should be implemented in derived classes to provide the specific API call.
   *
   * @param id - The ID of the entity to fetch
   * @return Observable containing the entity data
   * @throws Error if the API call fails
   */
  abstract getById(id: string): Observable<TDetail>;

  /**
   * Fetches a list of entities based on the provided parameters.
   * This method transforms the flat list of entities into a hierarchical structure.
   *
   * @param params - The parameters for fetching the entity list
   * @return Observable containing the hierarchical TreeNode structure and other metadata
   */
  getList(params: DataGridParams): Observable<TreeTableResponse<TList>> {
    const { owner, limit, offset, sortField, filters } = params;

    if (isNullOrUndefined(owner)) {
      return of({ nodes: [], items: [], count: 0, limit: 0 });
    }

    return this.fetchEntities(
      owner,
      offset,
      limit,
      sortField as string,
      filters,
    ).pipe(
      map((response) => {
        // Transform the flat list into a hierarchical structure
        const nodes = this.buildEntityTree(response.items);

        return {
          nodes,
          items: response.items,
          count: response.count,
          limit: limit || 0,
        };
      }),
    );
  }

  /**
   * Converts flat entity data into hierarchical TreeNode structure
   * This method builds a tree structure from a flat list of entities.
   * It handles circular references, missing parents, and owner relationships.
   *
   * @param items - Flat list of entities to be converted into a tree structure
   * @returns Hierarchical TreeNode structure for TreeTable
   */
  protected buildEntityTree(items: TList[]): TreeNode[] {
    if (!items?.length) return [];

    // Map of id -> node
    const nodeMap = new Map<string, TreeNode>();
    // Map of ownerId -> nodeId (for root/owner nodes)
    const ownerRootMap = new Map<string, string>();
    // Map for missing parent nodes
    const missingParentMap = new Map<string, TreeNode>();
    // Set to track circular/self references
    const circularRefs = new Set<string>();

    // Identify root nodes and circular references
    items.forEach((item) => {
      if (isDefined(item.owner?.id) && item.id === item.owner.id) {
        ownerRootMap.set(item.owner.id, item.id);
      }
      if (item.parent?.id === item.id) {
        circularRefs.add(item.id);
        console.warn(
          `Self-reference detected for entity ${item.id} (${item.name})`,
        );
      }
    });

    // Create all nodes
    items.forEach((item) => {
      nodeMap.set(item.id, {
        key: item.id,
        data: item,
        children: [],
        leaf: true,
      });
    });

    const rootNodes: TreeNode[] = [];
    const attached = new Set<string>();

    // Build the tree structure
    items.forEach((item) =>
      this.processTreeNode(
        item,
        nodeMap,
        ownerRootMap,
        missingParentMap,
        circularRefs,
        rootNodes,
        attached,
      ),
    );

    this.sortNodesByName(rootNodes);
    return rootNodes;
  }

  /**
   * Processes each tree node and attaches it to its parent or root node
   * Handles circular references and missing parents.
   *
   * @param item - The entity item to process
   * @param nodeMap - Map of all nodes by ID
   * @param ownerRootMap - Map of owner IDs to root node IDs
   * @param missingParentMap - Map for missing parent nodes
   * @param circularRefs - Set of circular references
   * @param rootNodes - Array of root nodes
   * @param attached - Set of already attached node keys
   * @returns void
   */
  private processTreeNode(
    item: TList,
    nodeMap: Map<string, TreeNode>,
    ownerRootMap: Map<string, string>,
    missingParentMap: Map<string, TreeNode>,
    circularRefs: Set<string>,
    rootNodes: TreeNode[],
    attached: Set<string>,
  ): void {
    const currentNode = nodeMap.get(item.id);
    if (!currentNode) return;

    // Handle circular references
    if (circularRefs.has(item.id)) {
      this.attachToOwnerOrRoot(
        item,
        currentNode,
        ownerRootMap,
        nodeMap,
        rootNodes,
        attached,
      );
      return;
    }

    // If this is a root/owner node
    if (item.owner?.id === item.id && !attached.has(item.id)) {
      rootNodes.push(currentNode);
      attached.add(item.id);
      return;
    }

    // Attach to parent if possible
    if (isDefined(item.parent?.id) && !circularRefs.has(item.id)) {
      this.attachToParent(
        item,
        currentNode,
        nodeMap,
        missingParentMap,
        ownerRootMap,
        rootNodes,
        attached,
      );
      return;
    }

    // If not attached yet, attach to owner or root
    this.attachToOwnerOrRoot(
      item,
      currentNode,
      ownerRootMap,
      nodeMap,
      rootNodes,
      attached,
    );
  }

  /**
   * Attaches the current node to its parent node
   * If the parent is missing, it creates a placeholder node for it.
   * @param item - The entity item to process
   * @param currentNode - The current node being processed
   * @param nodeMap - Map of all nodes by ID
   * @param missingParentMap - Map for missing parent nodes
   * @param ownerRootMap - Map of owner IDs to root node IDs
   * @param rootNodes - Array of root nodes
   * @param attached - Set of already attached node keys
   * @returns void
   */
  private attachToParent(
    item: TList,
    currentNode: TreeNode,
    nodeMap: Map<string, TreeNode>,
    missingParentMap: Map<string, TreeNode>,
    ownerRootMap: Map<string, string>,
    rootNodes: TreeNode[],
    attached: Set<string>,
  ): void {
    let parentNode = isDefined(item.parent)
      ? nodeMap.get(item.parent.id)
      : undefined;

    // If parent is missing, create placeholder
    parentNode ??= this.createMissingParentNode(
      item,
      missingParentMap,
      ownerRootMap,
      nodeMap,
      rootNodes,
      attached,
    );

    // Attach current node to parent
    if (!attached.has(item.id) && isDefined(parentNode.children)) {
      parentNode.children.push(currentNode);
      parentNode.leaf = false;
      attached.add(item.id);
    }
  }

  /**
   * Creates a placeholder node for a missing parent entity
   * This method is called when a parent node is not found in the map.
   * It creates a new node with a unique key and default data.
   *
   * @param item - The entity item for which the parent is missing
   * @param missingParentMap - Map for missing parent nodes
   * @param ownerRootMap - Map of owner IDs to root node IDs
   * @param nodeMap - Map of all nodes by ID
   * @param rootNodes - Array of root nodes
   * @param attached - Set of already attached node keys
   * @returns The created placeholder node for the missing parent
   */
  private createMissingParentNode(
    item: TList,
    missingParentMap: Map<string, TreeNode>,
    ownerRootMap: Map<string, string>,
    nodeMap: Map<string, TreeNode>,
    rootNodes: TreeNode[],
    attached: Set<string>,
  ): TreeNode {
    let parentNode = isDefined(item.parent)
      ? missingParentMap.get(item.parent.id)
      : undefined;

    if (!parentNode) {
      const parentId = item?.parent?.id;
      const parentKey = `missing-${parentId}`;

      // Use the configurable method to create entity data
      const entityData = this.createMissingEntityData(
        parentId,
        item?.parent?.name,
        item.owner,
      );

      parentNode = {
        key: parentKey,
        data: entityData,
        children: [],
        leaf: false,
        styleClass: 'missing-parent-node',
      };
      missingParentMap.set(parentId ?? '', parentNode);

      // Use shared attachment logic
      this.attachNodeToOwnerOrRoot(
        parentKey,
        parentNode,
        item.owner?.id,
        ownerRootMap,
        nodeMap,
        rootNodes,
        attached,
      );
    }

    return parentNode;
  }

  /**
   * Attaches the current node to its owner or to root nodes
   * This method is called when the node cannot be attached to a parent.
   * It checks if the node has an owner and tries to attach it to the owner's root node.
   * If not, it adds the node to the root nodes array.
   * @param item - The entity item to process
   * @param currentNode - The current node being processed
   * @param ownerRootMap - Map of owner IDs to root node IDs
   * @param nodeMap - Map of all nodes by ID
   * @param rootNodes - Array of root nodes
   * @param attached - Set of already attached node keys
   * @returns void
   * */
  private attachToOwnerOrRoot(
    item: TList,
    currentNode: TreeNode,
    ownerRootMap: Map<string, string>,
    nodeMap: Map<string, TreeNode>,
    rootNodes: TreeNode[],
    attached: Set<string>,
  ): void {
    this.attachNodeToOwnerOrRoot(
      item.id,
      currentNode,
      item.owner?.id,
      ownerRootMap,
      nodeMap,
      rootNodes,
      attached,
    );
  }

  /**
   * Shared utility method to attach a node to its owner or to root nodes
   * @param nodeKey - Unique identifier of the node
   * @param node - The node to attach
   * @param ownerId - ID of the owner (if any)
   * @param ownerRootMap - Map of owner IDs to root node IDs
   * @param nodeMap - Map of all nodes by ID
   * @param rootNodes - Array of root nodes
   * @param attached - Set of already attached node keys
   * @returns true if node was attached, false otherwise
   */
  private attachNodeToOwnerOrRoot(
    nodeKey: string,
    node: TreeNode,
    ownerId: string | undefined,
    ownerRootMap: Map<string, string>,
    nodeMap: Map<string, TreeNode>,
    rootNodes: TreeNode[],
    attached: Set<string>,
  ): boolean {
    // Skip if already attached
    if (attached.has(nodeKey)) {
      return false;
    }

    // Try to attach to owner
    if (
      isDefined(ownerId) &&
      ownerRootMap.has(ownerId) &&
      nodeKey !== ownerId // Prevent self-attachment
    ) {
      const rootNodeId = ownerRootMap.get(ownerId);
      const ownerNode = isDefined(rootNodeId) ? nodeMap.get(rootNodeId) : null;

      if (isDefined(ownerNode?.children)) {
        ownerNode.children.push(node);
        ownerNode.leaf = false;
        attached.add(nodeKey);
        return true;
      }
    }

    // Add to root if not attached to owner
    rootNodes.push(node);
    attached.add(nodeKey);
    return true;
  }

  /**
   * Creates data for a missing parent entity
   * Override this method in derived classes to customize the missing parent structure
   * @param parentId - ID of the missing parent
   * @param parentName - Name of the missing parent, if available
   * @param ownerInfo - Owner information to inherit
   * @returns Partial entity data for the missing parent
   */
  protected createMissingEntityData(
    parentId: string | undefined,
    parentName: string | null | undefined,
    ownerInfo: { id: string } | undefined,
  ) {
    return {
      id: parentId,
      name: parentName ?? 'Unknown',
      owner: ownerInfo,
      parent: null,
    };
  }

  /**
   * Sorts the nodes by their name property in a case-insensitive manner
   * This method is called recursively for all child nodes.
   * @param nodes - Array of TreeNode objects to be sorted
   * @return void
   */
  protected sortNodesByName(nodes: TreeNode[]): void {
    nodes.sort((a, b) => {
      const nameA = a.data?.name?.toLowerCase() ?? '';
      const nameB = b.data?.name?.toLowerCase() ?? '';
      return nameA.localeCompare(nameB);
    });

    nodes.forEach((node) => {
      if (isDefined(node.children) && !isEmpty(node.children)) {
        this.sortNodesByName(node.children);
      }
    });
  }
}
