import {
  Component,
  computed,
  TemplateRef,
  ViewChild,
  OnInit,
} from '@angular/core';
import {
  ThemeSettingsSectionBase,
  ThemeFieldConfig,
} from '../../base/theme-settings-section-base';
import { ThemeCreateRequest, ThemeResponse } from '../../../../../api';
import { FormFieldConfig } from '../../../../../shared/models';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { CommonModule } from '@angular/common';
import { Observable } from 'rxjs';
import { isDefined } from '../../../../../shared/utils';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { ThemeValueDisplayComponent } from '../theme-value-display/theme-value-display.component';

// Constants for theme property paths
const HEADER_THEME_PATHS = {
  FONT_FAMILY: ['header', 'font', 'family'],
  FONT_SIZE: ['header', 'font', 'size'],
  FONT_WEIGHT: ['header', 'font', 'weight'],
  FONT_COLOR: ['header', 'font', 'color'],
  FONT_TRANSFORM: ['header', 'font', 'transform'],
  BACKGROUND_COLOR: ['header', 'background', 'color'],
  BORDER_COLOR: ['header', 'border', 'color'],
  BORDER_WIDTH: ['header', 'border', 'width'],
} as const;

@Component({
  selector: 'app-theme-settings-header-section',
  imports: [
    DynamicFormComponent,
    FontAwesomeModule,
    CommonModule,
    GridComponent,
    ThemeValueDisplayComponent,
  ],
  templateUrl: './theme-settings-header-section.component.html',
  styleUrl: './theme-settings-header-section.component.scss',
})
export class ThemeSettingsHeaderSectionComponent
  extends ThemeSettingsSectionBase
  implements OnInit
{
  @ViewChild('valueTemplate', { static: true })
  valueTemplate!: TemplateRef<unknown>;
  readonly themeDetails = computed(() => {
    const themeDetails = this.entity();
    if (!isDefined(themeDetails)) return [];

    const themeSettings = this.themeEngine.themeSettingsMergedWithDefault(
      themeDetails?.settings ?? {},
    );

    const detailConfigs = this.createThemeDetailsFromConfig(
      this.getFieldConfigurations(),
    );
    return detailConfigs.map((config) => ({
      key: config.key,
      value: this.formatThemeValue(themeSettings, config),
    }));
  });

  constructor() {
    super('header');
  }

  ngOnInit(): void {
    this.initializeColumns();
  }

  private initializeColumns(): void {
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      {
        field: 'value',
        header: '',
        cellTemplate: this.valueTemplate,
      },
    ];
  }

  protected getFieldConfigurations(): ThemeFieldConfig[] {
    return [
      {
        id: 'font.family',
        label: 'Font Family',
        path: HEADER_THEME_PATHS.FONT_FAMILY,
        type: 'font-family',
        presets: ['main', 'content'],
      },
      {
        id: 'font.size',
        label: 'Font Size',
        path: HEADER_THEME_PATHS.FONT_SIZE,
        type: 'size',
      },
      {
        id: 'font.weight',
        label: 'Font Weight',
        path: HEADER_THEME_PATHS.FONT_WEIGHT,
        type: 'font-weight',
        presets: ['main', 'content'],
      },
      {
        id: 'font.color',
        label: 'Font Color',
        path: HEADER_THEME_PATHS.FONT_COLOR,
        type: 'color',
        group: 'header',
        presets: ['main', 'content', 'header'],
      },
      {
        id: 'font.transform',
        label: 'Font Case',
        path: HEADER_THEME_PATHS.FONT_TRANSFORM,
        type: 'font-transform',
      },
      {
        id: 'background.color',
        label: 'Background Color',
        path: HEADER_THEME_PATHS.BACKGROUND_COLOR,
        type: 'color',
        group: 'header',
        presets: ['main', 'content'],
      },
      {
        id: 'border.color',
        label: 'Border Color',
        path: HEADER_THEME_PATHS.BORDER_COLOR,
        type: 'color',
        group: 'header',
        presets: ['main', 'content'],
      },
      {
        id: 'border.width',
        label: 'Border Width',
        path: HEADER_THEME_PATHS.BORDER_WIDTH,
        type: 'size',
        props: { unit: 'px' },
      },
    ];
  }

  getFields(entity: ThemeResponse): FormFieldConfig[] {
    return this.createFormFieldsFromConfig(
      entity,
      this.getFieldConfigurations(),
    );
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<ThemeResponse>,
  ) => Observable<ThemeResponse> {
    return (
      this.operationRegistry.getOperation<
        ThemeResponse,
        ThemeCreateRequest,
        ThemeResponse,
        'update'
      >('theme', 'update') ??
      ((id) =>
        this.entitiesService.updateTheme(id, {
          settings: {
            header: this.formChanges,
          },
        }))
    );
  }
}
