import { Component, computed, effect, inject, input } from '@angular/core';
import { Validators } from '@angular/forms';

import {
  AssetCategoryCreateRequest,
  EntitiesService,
  AssetCategoryResponse,
} from '../../../../../api';
import {
  AssetGroupingItem,
  createDropdownField,
  createTextField,
} from '../../../../../shared/models';
import { EntitySectionBaseComponent } from '../../base';
import { isDefined } from '../../../../../shared/utils';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { CommonModule } from '@angular/common';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { Observable, tap } from 'rxjs';
import { faSitemap } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { AssetGroupingService } from '../../../../../shared/services/asset-grouping.service';
import { RequestCacheService } from '../../../../../shared/services/request-cache.service';

type AssetCategoryEntity = AssetCategoryResponse | AssetCategoryCreateRequest;

@Component({
  selector: 'app-asset-category-details-section',
  imports: [
    CommonModule,
    GridComponent,
    DynamicFormComponent,
    FontAwesomeModule,
  ],
  templateUrl: './asset-category-details-section.component.html',
  styleUrl: './asset-category-details-section.component.scss',
})
export class AssetCategoryDetailsSectionComponent extends EntitySectionBaseComponent<
  AssetCategoryEntity | Partial<AssetCategoryCreateRequest>
> {
  readonly ownerId = input<string | null>(null);
  readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly assetGroupingService = inject(AssetGroupingService);
  private readonly cacheService = inject(RequestCacheService);

  readonly assetCategoryDetails = computed(() => {
    const assetCategoryDetails = this.entity();
    if (!isDefined(assetCategoryDetails)) return [];

    return [{ key: 'Name', value: assetCategoryDetails.name }];
  });

  faSitemap = faSitemap;
  assetCategoryGroups: AssetGroupingItem[] = [];

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    effect(() => {
      const ownerId = this.entity()?.owner?.id ?? this.ownerId();
      const entityId = this.entity()?.id;
      if (isDefined(ownerId)) {
        this.fetchAssetCategories(ownerId)
          .then((groups) => {
            const root = AssetGroupingService.createRootNode(ownerId);
            this.assetCategoryGroups = [
              root,
              ...groups.filter((group) => group.id !== entityId),
            ];
            this.updateFormFields();
          })
          .catch(() => {});
      }
    });
  }

  private fetchAssetCategories(ownerId: string): Promise<AssetGroupingItem[]> {
    return this.assetGroupingService.getAssetCategories(ownerId);
  }

  protected override updateFormFields(): void {
    const assetCategory = this.entity();
    const parentOptions = this.assetCategoryGroups.map((group) => ({
      label: group.name,
      value: group.id,
    }));
    this.formFields = [
      createTextField('name', 'Name', assetCategory?.name ?? '', {
        name: 'name',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Name is required',
          },
        ],
      }),
    ];

    if (this.createMode()) {
      this.formFields.unshift(
        createDropdownField(
          'parent',
          'Parent',
          parentOptions,
          assetCategory?.parent?.id ?? '',
          {
            placeholder: 'Select Parent Group',
            validations: [
              {
                name: 'required',
                validator: Validators.required,
                message: 'Parent group is required',
              },
            ],
            props: {
              appendTo: 'body',
            },
          },
        ),
      );
    }
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<AssetCategoryEntity>,
  ) => Observable<AssetCategoryResponse> {
    return (
      this.operationRegistry.getOperation<
        AssetCategoryEntity,
        AssetCategoryCreateRequest,
        AssetCategoryResponse,
        'update'
      >('asset-category', 'update') ??
      ((id, data) =>
        this.entitiesService
          .updateAssetCategory(id, this.transformFormData(data))
          .pipe(
            tap(() => {
              this.cacheService.clearCacheByPattern(`asset-categories-`);
            }),
          ))
    );
  }

  protected override onFormValueChange(
    formData: Record<string, string | number | object | null>,
  ): void {
    const transformedData = this.transformFormData(formData);

    this.formValueChange.emit(transformedData);
  }

  protected override transformFormData(
    formData: Record<string, string | number | object | null>,
  ) {
    const transformedData = { ...formData };
    if (isDefined(transformedData['parent'])) {
      const parentId = transformedData['parent'] as string;
      const parent = this.assetCategoryGroups?.find(
        (option) => option.id === parentId,
      );
      if (parent) {
        transformedData['parent'] = {
          id: parent.id,
          name: parent.name,
        };
      } else if (parentId === 'root') {
        transformedData['parent'] = null; // Set to null for root
      }
    }
    return transformedData;
  }
}
