import { Injectable, inject } from '@angular/core';
import {
  FormFieldConfig,
  createColorPickerField,
  createNumberField,
  createDropdownField,
  FormFieldProps,
} from '../../../shared/models';
import {
  AVAILABLE_FONTS,
  FONT_WEIGHTS,
  FONT_TRANSFORMS,
  DROP_SHADOW_OPTIONS,
} from '../models';
import { ThemeColorService } from '../services/theme/theme-color.service';
import { ThemeUtilityService } from '../services/theme/theme-utility.service';

@Injectable({ providedIn: 'root' })
export class ThemeFormFieldFactory {
  private readonly colorService = inject(ThemeColorService);
  private readonly themeUtilityService = inject(ThemeUtilityService);

  createFontFamilyField(
    id: string,
    value: string,
    presets?: string[],
  ): FormFieldConfig {
    const fontPresets =
      Object.entries({
        ...this.themeUtilityService.createPresets(
          'font-family',
          presets ?? [],
          (variable) => variable,
        ),
      }).map(([key, val]) => ({
        label: key,
        value: val,
      })) ?? [];
    return this.createDropdownWithGrouping({
      id,
      label: 'Font Family',
      options: [
        ...fontPresets,
        ...AVAILABLE_FONTS.map((font) => ({ label: font, value: font })),
      ],
      value,
      props: { appendTo: 'body', filter: true },
    });
  }

  createFontWeightField(id: string, value?: string): FormFieldConfig {
    return this.createDropdownWithGrouping({
      id,
      label: 'Font Weight',
      options: FONT_WEIGHTS.map((weight) => ({ label: weight, value: weight })),
      value,
      props: { appendTo: 'body', filter: true },
    });
  }

  createFontTransformField(id: string, value: string): FormFieldConfig {
    return this.createDropdownWithGrouping({
      id,
      label: 'Font Transform',
      options: FONT_TRANSFORMS,
      value,
      props: { appendTo: 'body' },
    });
  }

  createFontColorField(
    id: string,
    value: string,
    group: string,
    presets?: string[],
    label = 'Font Color',
    customColors?: Record<string, string>,
  ): FormFieldConfig {
    const colorPresets = this.colorService.getFontColorPresets(group, presets);
    return this.createColorComboField(
      id,
      value,
      { ...colorPresets, ...customColors },
      label,
    );
  }

  createBackgroundColorField(
    id: string,
    value: string,
    presets?: string[],
    label = 'Background Color',
    customColors?: Record<string, string>,
  ): FormFieldConfig {
    const colorPresets = this.colorService.getBackgroundColorPresets(
      presets,
      customColors,
    );
    return this.createColorComboField(id, value, colorPresets, label);
  }

  createBorderColorField(
    id: string,
    value: string,
    group: string,
    presets?: string[],
    backgrounds?: string[],
    customColors?: Record<string, string>,
  ): FormFieldConfig {
    const colorPresets = this.colorService.getBorderColorPresets(
      group,
      presets,
      backgrounds,
      customColors,
    );
    return this.createColorComboField(id, value, colorPresets, 'Border Color');
  }

  createDropShadowField(
    id: string,
    value: string,
    label: string,
  ): FormFieldConfig {
    return this.createDropdownWithGrouping({
      id,
      label,
      options: DROP_SHADOW_OPTIONS,
      value,
      props: { appendTo: 'body' },
    });
  }

  createColorComboField(
    id: string,
    value: string,
    colors: Record<string, string>,
    label: string,
  ): FormFieldConfig {
    const presets = Object.entries(colors).map(([key, val]) => ({
      label: key,
      value: val,
    }));

    return this.createFieldWithGrouping(id, (fieldId) =>
      createColorPickerField(
        fieldId,
        label,
        this.colorService.computeColor(value),
        {
          name: fieldId,
          props: {
            appendTo: 'body',
            colorOptions: presets,
          },
        },
      ),
    );
  }

  createSizeNumberField(
    id: string,
    value: number,
    label: string,
    unit = 'rem',
  ): FormFieldConfig {
    return this.createFieldWithGrouping(id, (fieldId) =>
      createNumberField(fieldId, label, value, {
        name: fieldId,
        props: {
          appendTo: 'body',
          suffix: unit,
        },
      }),
    );
  }

  createDropdownWithGrouping(config: DropdownConfig): FormFieldConfig {
    return this.createFieldWithGrouping(config.id, (fieldId) =>
      createDropdownField(fieldId, config.label, config.options, config.value, {
        name: fieldId,
        validations: [],
        props: config.props,
      }),
    );
  }

  private createFieldWithGrouping(
    id: string,
    fieldFactory: (id: string) => FormFieldConfig,
  ): FormFieldConfig {
    if (id.includes('.')) {
      const [groupKey, fieldKey] = id.split('.', 2);
      return {
        key: groupKey,
        name: groupKey,
        type: 'group',
        fields: [fieldFactory(fieldKey)],
      };
    }
    return fieldFactory(id);
  }
}

interface DropdownConfig {
  id: string;
  label: string;
  options: { label: string; value: string }[];
  value?: string;
  props?: FormFieldProps;
}
