import { Injectable, inject } from '@angular/core';
import { RequestCacheService } from './request-cache.service';
import { AssetGroupingService } from './asset-grouping.service';
import { BroadcastEventMessageService } from '../../features/administration/services/broadcast-event-messages.service';

export interface EntityUpdateEvent {
  entityType: string;
  operation?: 'create' | 'update' | 'delete';
  entityId?: string;
  clientId?: string;
}

@Injectable({
  providedIn: 'root',
})
export class CacheInvalidationService {
  private readonly requestCache = inject(RequestCacheService);
  private readonly assetGroupingService = inject(AssetGroupingService);
  private readonly broadcastService = inject(BroadcastEventMessageService);

  constructor() {
    // Listen for entity updates and clear relevant caches
    this.broadcastService.on('entity-updated').subscribe((event) => {
      this.handleEntityUpdate(event);
    });
  }

  private handleEntityUpdate(event: EntityUpdateEvent): void {
    const { entityType, operation } = event;

    switch (entityType) {
      case 'assetCategory':
        this.clearAssetCategoryCaches();
        break;
      case 'assetGroup':
        this.clearAssetGroupCaches();
        break;
      case 'costCentre':
        this.clearCostCentreCaches();
        break;
      case 'asset':
        // When assets are created/updated, categories might be affected
        if (operation === 'create' || operation === 'update') {
          this.clearAssetCategoryCaches();
        }
        break;
    }
  }

  /**
   * Clear asset category caches
   */
  clearAssetCategoryCaches(): void {
    this.requestCache.clearCacheByPattern('assetCategories-');
    this.assetGroupingService.clear();
  }

  /**
   * Clear asset group caches
   */
  clearAssetGroupCaches(): void {
    this.requestCache.clearCacheByPattern('assetGroups-');
    this.assetGroupingService.clear();
  }

  /**
   * Clear cost centre caches
   */
  clearCostCentreCaches(): void {
    this.requestCache.clearCacheByPattern('costCentres-');
    this.assetGroupingService.clear();
  }

  /**
   * Clear all caches for a specific client
   */
  clearClientCaches(clientId: string): void {
    this.requestCache.clearClientCache(clientId);
    this.assetGroupingService.clear();
  }

  /**
   * Clear all caches (useful when user changes context)
   */
  clearAllCaches(): void {
    this.requestCache.clearCache();
    this.assetGroupingService.clear();
  }

  /**
   * Force refresh of asset categories for a specific client
   */
  refreshAssetCategories(clientId: string): void {
    this.requestCache.clearCacheByPattern(`assetCategories-${clientId}`);
  }
}
