import { CommonModule } from '@angular/common';
import { Component, computed, inject, input, OnInit } from '@angular/core';

import { ButtonModule } from 'primeng/button';
import { Observable } from 'rxjs';

import {
  EntitiesService,
  DeviceResponse,
  DeviceCreateRequest,
  DeviceTypeAccessory,
} from '../../../../../api';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import {
  createDropdownField,
  createTextField,
  FormFieldConfig,
} from '../../../../../shared/models';
import { isDefined } from '../../../../../shared/utils';
import { DeviceEntity } from '../../../models';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { Validators } from '@angular/forms';
import { EntitySectionBaseComponent } from '../../base';

type AccessorySettingValue = string | boolean | number | null | undefined;
export interface FormBuilderField {
  id: string;
  title: string;
  type: string;
  value?: string | number;
  defaultValue?: string | number;
  required?: boolean;
  description?: string;
  min?: number;
  max?: number;
  width?: number;
  writeonce?: boolean;
  values?: {
    key: string;
    value: string;
    fields?: FormBuilderField[];
  }[];
}

@Component({
  selector: 'app-device-accessory-section',
  imports: [CommonModule, ButtonModule, GridComponent, DynamicFormComponent],
  templateUrl: './device-accessory-section.component.html',
  styleUrl: './device-accessory-section.component.scss',
})
export class DeviceAccessorySectionComponent
  extends EntitySectionBaseComponent<DeviceEntity>
  implements OnInit
{
  readonly sectionHeader = input<string>('Device Accessory Configuration');
  readonly deviceTypeAccessory = input.required<DeviceTypeAccessory>();
  readonly accessoryId = input.required<string>();
  readonly ownerId = input<string | null>(null);
  readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);

  readonly accessoriesDisplay = computed(() => {
    const accessories = this.entity()?.accessories;
    const accessory = accessories?.[this.accessoryId()];
    const subfields = this.parsedSettingsDefinition();

    if (!accessory) {
      return [{ key: 'Enabled', value: 'No' }];
    }

    return this.mapSettingsToKeyValuePairs(
      (accessory.settings as unknown as Record<
        string,
        AccessorySettingValue
      >) ?? {},
      subfields,
    );
  });

  ngOnInit(): void {
    this.updateFormFields();
    this.columns = [
      {
        field: 'key',
        header: '',
        transform: 'uppercase',
      },
      { field: 'value', header: '' },
    ];
  }

  private mapSettingsToKeyValuePairs(
    settings: Record<string, AccessorySettingValue>,
    subfields: FormBuilderField[],
  ): { key: string; value: string }[] {
    return Object.entries(settings).map(([key, value]) => {
      const field = subfields.find((field) => field.id === key);
      return {
        key: field?.title ?? key,
        value: field
          ? this.formatFieldValue(value, field)
          : String(value ?? 'N/A'),
      };
    });
  }

  private formatFieldValue(
    value: AccessorySettingValue,
    field: FormBuilderField,
  ): string {
    if (value === 'true') return 'Yes';
    if (value === 'false') return 'No';
    if (field.type === 'combo' && field.values) {
      return field.values.find((v) => v.key === String(value))?.value ?? 'N/A';
    }
    return String(value ?? 'N/A');
  }

  private readonly parsedSettingsDefinition = computed(
    (): FormBuilderField[] => {
      const deviceTypeAccessory = this.deviceTypeAccessory();
      return isDefined(deviceTypeAccessory?.settingsDefinition)
        ? (structuredClone(JSON.parse(deviceTypeAccessory.settingsDefinition))
            ?.fields ?? [])
        : [];
    },
  );

  protected override updateFormFields(): void {
    const device = this.entity();
    const deviceTypeAccessory = this.deviceTypeAccessory();
    const subfields: FormBuilderField[] = isDefined(
      deviceTypeAccessory?.settingsDefinition,
    )
      ? structuredClone(JSON.parse(deviceTypeAccessory.settingsDefinition))
          ?.fields
      : [];
    const customFields: FormFieldConfig[] = subfields.map((field) => {
      if (field.type === 'combo') {
        return createDropdownField(
          field.id,
          field.title,
          (field.values ?? []).map((value) => ({
            label: value.value,
            value: value.key,
          })),
          (device?.accessories?.[this.accessoryId()]?.settings?.[field.id] ??
            '') as string,
          {
            name: field.id,
            validations:
              (field.required ?? false)
                ? [
                    {
                      name: 'required',
                      validator: Validators.required,
                      message: `${field.title} is required`,
                    },
                  ]
                : [],
            props: {
              appendTo: 'body',
            },
          },
        );
      }

      return createTextField(
        field.id,
        field.title,
        (device?.accessories?.[this.accessoryId()]?.settings?.[field.id] ??
          '') as string,
        {
          name: field.id,
          validations:
            (field.required ?? false)
              ? [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: `${field.title} is required`,
                  },
                ]
              : [],
        },
      );
    });

    this.formFields = [
      createDropdownField(
        'enabled',
        'Enabled',
        [
          { label: 'Yes', value: true },
          { label: 'No', value: false },
        ],
        true,
        {
          name: 'enabled',
          props: {
            appendTo: 'body',
          },
        },
      ),
      ...customFields,
    ];
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<DeviceEntity>,
  ) => Observable<DeviceResponse> {
    return (
      this.operationRegistry.getOperation<
        DeviceEntity,
        DeviceCreateRequest,
        DeviceResponse,
        'update'
      >('device', 'update') ??
      ((id, data) => this.entitiesService.updateDevice(id, data))
    );
  }
}
