import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import {
  UserRoleListItem,
  UserRoleResponse,
  AccountsService,
} from '../../../../api';
import {
  DataGridParams,
  EntityDataAdapter,
  GridResponse,
} from '../../../../shared/models';
import { ConfirmationService } from 'primeng/api';
import { ActionIcon, EntityConfigAdapter } from '../../models';
import { BroadcastEventMessageService } from '../broadcast-event-messages.service';

@Injectable({
  providedIn: 'root',
})
export class UserRolesDataAdapter
  implements
    EntityDataAdapter<UserRoleListItem, UserRoleResponse>,
    EntityConfigAdapter
{
  private readonly accountsService = inject(AccountsService);
  private readonly confirmationService = inject(ConfirmationService);
  private readonly broadcastService = inject(BroadcastEventMessageService);

  getList(params: DataGridParams): Observable<GridResponse<UserRoleListItem>> {
    const { owner, limit, offset, sortField, filters } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.accountsService.listUserRoles(
      owner,
      offset,
      limit,
      sortField as string,
      filters,
    );
  }

  getById(id: string): Observable<UserRoleResponse> {
    return this.accountsService.getUserRole(id);
  }

  delete(id: string): Observable<unknown> {
    return this.accountsService.deleteUserRole(id);
  }

  getActionIcons(entityId: string): ActionIcon[] {
    return [
      {
        id: 'delete',
        icon: 'pi pi-trash',
        tooltip: 'Delete User Role',
        visible: true,
        action: () => {
          this.confirmDelete(entityId);
        },
      },
    ];
  }

  private confirmDelete(entityId: string): void {
    const message = `Are you sure you want to delete this user role?`;

    this.confirmationService.confirm({
      message,
      header: 'Confirm Delete',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        this.delete(entityId).subscribe({
          next: (result) => {
            this.broadcastService.broadcast('entity-deleted', {
              entityType: 'user-role',
              operation: 'delete',
              entityId,
              data: result,
            });
          },
          error: (error) => {
            console.error('Delete failed', error);
          },
        });
      },
      acceptButtonProps: { label: 'Delete' },
      rejectButtonProps: { label: 'Cancel', severity: 'secondary' },
    });
  }
}
