import {
  ChangeDetectorRef,
  Component,
  computed,
  effect,
  inject,
  input,
  OnInit,
  signal,
  untracked,
} from '@angular/core';
import { Validators } from '@angular/forms';

import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';

import {
  EmailProviderListItem,
  EmailProviderCreateRequest,
  EntitiesService,
  EmailProviderResponse,
  EmailProviderTypeListItem,
} from '../../../../../api';
import {
  createDropdownField,
  createTextField,
} from '../../../../../shared/models';
import { EntitySectionBaseComponent } from '../../base';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { CommonModule } from '@angular/common';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { Observable } from 'rxjs';
import { faAt } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

type EmailProviderEntity =
  | EmailProviderListItem
  | Partial<EmailProviderCreateRequest>;

@Component({
  selector: 'app-email-provider-details-section',
  imports: [
    CommonModule,
    ButtonModule,
    InputTextModule,
    GridComponent,
    DynamicFormComponent,
    FontAwesomeModule,
  ],
  templateUrl: './email-provider-details-section.component.html',
  styleUrl: './email-provider-details-section.component.scss',
})
export class EmailProviderDetailsSectionComponent
  extends EntitySectionBaseComponent<EmailProviderEntity>
  implements OnInit
{
  readonly ownerId = input<string | null>(null);
  readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);

  private readonly cdr = inject(ChangeDetectorRef);
  protected readonly selectOptionsLoaded = signal<boolean>(false);

  readonly emailProviderDetails = computed(() => {
    const emailProviderDetails = this.entity();
    if (!isDefined(emailProviderDetails)) return [];

    return [
      {
        key: 'Type',
        value: emailProviderDetails.type?.name ?? 'None',
      },
      { key: 'Name', value: emailProviderDetails.name },
    ];
  });
  selectOptions: Record<
    string,
    { label: string; value: string; data: unknown }[]
  > = {};
  faAt = faAt;

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    effect(() => {
      const selectOptionsLoaded = this.selectOptionsLoaded();
      if (selectOptionsLoaded) {
        untracked(() => {
          this.updateFormFields();
          this.cdr.detectChanges();
        });
      }
    });

    effect(() => {
      const entity = this.entity();
      if (isDefined(entity) && !this.createMode()) {
        untracked(() => {
          this.updateFormFields();
        });
      }
    });
  }

  ngOnInit(): void {
    this.loadSelectOptions();
  }

  private loadSelectOptions(): void {
    const emailProvider = this.entity();
    const ownerIdValue = this.ownerId();

    const effectiveOwnerId = emailProvider?.owner?.id ?? ownerIdValue;

    if (isNullOrUndefined(effectiveOwnerId)) {
      this.selectOptions = {};
      this.selectOptionsLoaded.set(true);
      this.updateFormFields();
    }

    this.entitiesService
      .listEmailProviderTypes(effectiveOwnerId)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((result) => {
        this.selectOptions = {
          emailProviderTypes: result.items
            .map((item) => ({
              label: item?.name,
              value: item.id,
              data: item,
            }))
            .sort((a, b) => a.label.localeCompare(b.label)),
        };

        this.selectOptionsLoaded.set(true);
      });
  }

  protected override updateFormFields(): void {
    const emailProvider = this.entity();
    this.formFields = [
      createDropdownField(
        'type',
        'Type',
        this.selectOptions?.['emailProviderTypes'],
        emailProvider?.type?.id,
        {
          name: 'type',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Type is required',
            },
          ],
          disabled: !this.createMode(),
          props: {
            appendTo: 'body',
          },
        },
      ),
      createTextField('name', 'Name', emailProvider?.name, {
        name: 'name',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Name is required',
          },
        ],
      }),
    ];
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<EmailProviderEntity>,
  ) => Observable<EmailProviderResponse> {
    return (
      this.operationRegistry.getOperation<
        EmailProviderEntity,
        EmailProviderCreateRequest,
        EmailProviderResponse,
        'update'
      >('email-provider', 'update') ??
      ((id, data) => this.entitiesService.updateEmailProvider(id, data))
    );
  }

  protected override onFormValueChange(
    formData: Record<string, string | number | object>,
  ): void {
    const transformedData = this.transformFormData(formData);
    this.formValueChange.emit(transformedData);
  }

  protected override transformFormData(
    formData: Record<string, string | number | object>,
  ): Record<string, string | number | object> {
    const transformedData = { ...formData };
    if (isDefined(transformedData['type'])) {
      const emailProviderTypeId = transformedData['type'] as string;
      const emailProviderType = this.selectOptions?.[
        'emailProviderTypes'
      ]?.find((option) => option.value === emailProviderTypeId);
      if (emailProviderType) {
        transformedData['type'] = {
          id: emailProviderTypeId,
          name: (emailProviderType.data as EmailProviderTypeListItem)?.name,
        };
      }
    }

    return transformedData;
  }
}
