import { CommonModule } from '@angular/common';
import { Component, computed, effect, inject, input } from '@angular/core';

import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';
import { Observable } from 'rxjs';

import {
  SmsGatewayProviderCreateRequest,
  SmsGatewayProviderResponse,
  EntitiesService,
  SmsGatewayProviderTypeResponse,
} from '../../../../../api';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import {
  createDropdownField,
  createNumberField,
  createPasswordField,
  createTextAreaField,
  createTextField,
  FormFieldConfig,
} from '../../../../../shared/models';
import { isNullOrUndefined } from '../../../../../shared/utils';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { EntitySectionBaseComponent } from '../../base';
import { Validators } from '@angular/forms';

type SmsGatewayProviderEntity =
  | SmsGatewayProviderResponse
  | Partial<SmsGatewayProviderCreateRequest>;

interface FormField {
  id: string;
  title: string;
  type: string;
  value?: string | number;
  defaultValue?: string | number;
  required?: boolean;
  description?: string;
  min?: number;
  max?: number;
  width?: number;
  writeonce?: boolean;
  values?: {
    key: string;
    value: string;
    fields?: FormField[];
  }[];
}

@Component({
  selector: 'app-sms-gateway-provider-parameters-section',
  imports: [
    CommonModule,
    ButtonModule,
    InputTextModule,
    GridComponent,
    DynamicFormComponent,
  ],
  templateUrl: './sms-gateway-provider-parameters-section.component.html',
  styleUrl: './sms-gateway-provider-parameters-section.component.scss',
})
export class SmsGatewayProviderParametersSectionComponent extends EntitySectionBaseComponent<SmsGatewayProviderEntity> {
  readonly ownerId = input<string | null>(null);
  readonly smsGatewayProviderType =
    input<SmsGatewayProviderTypeResponse | null>(null);
  readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);

  readonly smsGatewayProviderDeviceParameters = computed(() => {
    const smsGatewayProviderDeviceParameters = this.entity()?.parameters;
    const smsGatewayProviderType = this.smsGatewayProviderType();

    if (
      isNullOrUndefined(smsGatewayProviderDeviceParameters) ||
      isNullOrUndefined(smsGatewayProviderType)
    )
      return [];

    const parameterDefinition =
      smsGatewayProviderType.parameterDefinition ?? '{"groups":[]}';
    const params: { groups: { name: string; fields: FormField[] }[] } =
      JSON.parse(parameterDefinition);
    const values: { key: string; value: string }[] = [];

    params.groups.forEach((group) => {
      group.fields.forEach((field: FormField) => {
        // Add the primary field
        let fieldValue = 'None';
        if (field.type === 'password') {
          fieldValue = '****';
        } else {
          const paramValue = smsGatewayProviderDeviceParameters?.[field.id];
          if (typeof paramValue === 'object') {
            fieldValue = JSON.stringify(paramValue);
          } else if (paramValue !== undefined) {
            fieldValue = paramValue as unknown as string;
          }
        }

        values.push({
          key: field.title,
          value: fieldValue,
        });

        // Handle combo fields with dependent fields
        if (field.type === 'combo' && field.values) {
          const selectedValue = smsGatewayProviderDeviceParameters?.[
            field.id
          ] as unknown as string;
          const selectedOption = field.values.find(
            (opt) => opt.key === selectedValue,
          );

          if (selectedOption?.fields) {
            selectedOption.fields.forEach((subField) => {
              if (
                smsGatewayProviderDeviceParameters?.[subField.id] !== undefined
              ) {
                let fieldValue = 'None';
                const paramValue =
                  smsGatewayProviderDeviceParameters?.[subField.id];

                if (subField.type === 'password') {
                  fieldValue = '****';
                } else if (typeof paramValue === 'object') {
                  fieldValue = JSON.stringify(paramValue);
                } else if (paramValue !== undefined) {
                  fieldValue = paramValue as unknown as string;
                }

                values.push({
                  key: `${subField.title}`,
                  value: fieldValue,
                });
              }
            });
          }
        }
      });
    });
    return values;
  });

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    effect(() => {
      const smsGatewayProviderType = this.smsGatewayProviderType();
      if (smsGatewayProviderType) {
        this.updateFormFields();
      }
    });
  }

  protected override updateFormFields(): void {
    const smsGatewayProvider = this.entity();
    const smsGatewayProviderType = this.smsGatewayProviderType();

    if (!smsGatewayProviderType) {
      return;
    }
    const parameterDefinition =
      smsGatewayProviderType.parameterDefinition ?? '{"groups":[]}';
    const params: { groups: { name: string; fields: FormField[] }[] } =
      JSON.parse(parameterDefinition);

    const fields: FormFieldConfig[] = params.groups.reduce(
      (acc: FormFieldConfig[], group) => {
        const groupFields = group.fields.flatMap((field: FormField) => {
          // Create base field
          const baseField = this.createFormFieldConfig(
            field,
            smsGatewayProvider,
          );

          return [baseField];
        });

        return [...acc, ...groupFields];
      },
      [],
    );

    this.formFields = fields;
  }

  private createFormFieldConfig(
    field: FormField,
    smsGatewayProvider: SmsGatewayProviderEntity | null,
  ): FormFieldConfig {
    if (field.type === 'memo') {
      return createTextAreaField(
        field.id,
        field.title,
        smsGatewayProvider?.parameters?.[field.id] as unknown as string,
        {
          name: field.id,
          validations:
            (field.required ?? false)
              ? [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: `${field.title} is required`,
                  },
                ]
              : [],
          hint: field.description,
        },
      );
    }
    if (field.type === 'number') {
      return createNumberField(
        field.id,
        field.title,
        smsGatewayProvider?.parameters?.[field.id] as unknown as number,
        {
          name: field.id,
          validations:
            (field.required ?? false)
              ? [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: `${field.title} is required`,
                  },
                ]
              : [],
          hint: field.description,
        },
      );
    }
    if (field.type === 'combo' && field.values) {
      // Handle dropdown field creation
      const options = field.values.map((value) => ({
        label: value.value,
        value: value.key,
      }));

      return createDropdownField(
        field.id,
        field.title,
        options,
        (smsGatewayProvider?.parameters?.[field.id] as unknown as string) ||
          (field.defaultValue as string),
        {
          name: field.id,
          validations:
            (field.required ?? false)
              ? [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: `${field.title} is required`,
                  },
                ]
              : [],
          hint: field.description,
          props: {
            appendTo: 'body',
          },
          conditional: true,
          conditions: options.reduce(
            (acc, option) => ({
              ...acc,
              [option.value]: this.createConditionFields(
                field?.values?.find((opt) => opt.key === option.value)
                  ?.fields ?? [],
              ),
            }),
            {},
          ),
        },
      );
    }
    if (field.type === 'password') {
      return createPasswordField(field.id, field.title, {
        name: field.id,
        validations:
          (field.required ?? false)
            ? [
                {
                  name: 'required',
                  validator: Validators.required,
                  message: `${field.title} is required`,
                },
              ]
            : [],
        hint: field.description,
        props: {
          type: 'password',
        },
      });
    }

    // Default text field
    return createTextField(
      field.id,
      field.title,
      smsGatewayProvider?.parameters?.[field.id] as unknown as string,
      {
        name: field.id,
        validations:
          (field.required ?? false)
            ? [
                {
                  name: 'required',
                  validator: Validators.required,
                  message: `${field.title} is required`,
                },
              ]
            : [],
        hint: field.description,
      },
    );
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<SmsGatewayProviderEntity>,
  ) => Observable<SmsGatewayProviderResponse> {
    return (
      this.operationRegistry.getOperation<
        SmsGatewayProviderEntity,
        SmsGatewayProviderCreateRequest,
        SmsGatewayProviderResponse,
        'update'
      >('sms-gateway-providers', 'update') ??
      ((id, data) =>
        this.entitiesService.updateSmsGatewayProvider(id, {
          parameters: data,
        }))
    );
  }

  createConditionFields(fields: FormField[]): FormFieldConfig[] {
    return fields.map((field) => {
      return this.createFormFieldConfig(field, this.entity());
    });
  }
}
