import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import {
  IoTypeListItem,
  EntitiesService,
  IoTypeResponse,
} from '../../../../api';
import {
  DataGridParams,
  GridDataService,
  GridResponse,
} from '../../../../shared/models';
import { ActionIcon, EntityConfigAdapter, StateOption } from '../../models';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';

@Injectable({
  providedIn: 'root',
})
export class IoTypesDataAdapter
  implements GridDataService<IoTypeListItem>, EntityConfigAdapter
{
  private readonly entitiesService = inject(EntitiesService);
  private readonly entityStateService = inject(EntityDetailsStateService);

  getList(params: DataGridParams): Observable<GridResponse<IoTypeListItem>> {
    const { owner, limit, offset, sortField, filters, recurse } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.entitiesService.listIoTypes(
      owner,
      recurse,
      offset,
      limit,
      sortField as string,
      filters,
    );
  }

  getById(id: string): Observable<IoTypeResponse> {
    return this.entitiesService.getIoType(id);
  }

  getStateOptions(): StateOption[] {
    return [
      { label: 'Active', value: IoTypeResponse.StateEnum.Active },
      { label: 'Deleted', value: IoTypeResponse.StateEnum.Deleted },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.entitiesService.updateIoType(id, {
      state: state as IoTypeResponse.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    return [
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change IO Type State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
