import {
  Component,
  computed,
  effect,
  inject,
  input,
  OnInit,
} from '@angular/core';
import { tap } from 'rxjs';
import { Validators } from '@angular/forms';
import { ConfirmationService } from 'primeng/api';
import { DeviceTypeResponse, DeviceTypesService } from '../../../../../../api';
import { DynamicFormComponent } from '../../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { ModalListFormComponent } from '../../../../../../shared/components/forms/modal-list-form/modal-list-form.component';
import { GridComponent } from '../../../../../../shared/components/grid/grid.component';
import {
  ListFieldModalConfig,
  FormFieldConfig,
  createTextField,
  createModalListField,
} from '../../../../../../shared/models';
import { EntityOperationRegistryService } from '../../../../services/entity/entity-operation-registry.service';
import { EntitySectionBaseComponent } from '../../../base';
import { isDefined } from '../../../../../../shared/utils';

interface CachedFieldItem {
  name: string;
}

@Component({
  selector: 'app-device-types-cached-field',
  imports: [DynamicFormComponent, GridComponent],
  templateUrl: './device-types-cached-field.component.html',
  styleUrl: './device-types-cached-field.component.scss',
})
export class DeviceTypesCachedFieldComponent
  extends EntitySectionBaseComponent<DeviceTypeResponse>
  implements OnInit
{
  constructor() {
    super();

    effect(() => {
      const entity = this.entity();
      if (entity && !this.createMode()) {
        this.updateFormFields();
      }
    });
  }

  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly deviceTypesService = inject(DeviceTypesService);
  private readonly confirmationService = inject(ConfirmationService);

  override entity = input<DeviceTypeResponse | null>(null);

  cachedFieldModalConfig: ListFieldModalConfig<CachedFieldItem> | undefined;

  // Get cached fields from ioCapabilities.cachedFields
  readonly cachedFieldItems = computed(() => {
    const deviceType = this.entity();
    const ioCapabilities = deviceType?.ioCapabilities;
    const cachedFields = ioCapabilities?.['cached_fields'];

    if (!isDefined(cachedFields) || !Array.isArray(cachedFields)) {
      return [];
    }

    return cachedFields.map((field) => ({
      name: typeof field === 'string' ? field : String(field),
    }));
  });

  readonly hasData = computed(() => {
    return this.cachedFieldItems().length > 0;
  });

  override readonly columns = [
    { field: 'key', header: 'Property' },
    { field: 'value', header: 'Value' },
  ];

  // Display cached fields as a comma-separated list
  readonly cachedFieldsDisplay = computed(() => {
    const items = this.cachedFieldItems();
    if (items.length === 0) {
      return [{ key: 'Cached Fields', value: 'No cached fields configured' }];
    }

    const fieldNames = items.map((item) => item.name).join(', ');

    return [
      {
        key: 'Cached Fields',
        value: fieldNames || 'No cached fields configured',
      },
    ];
  });

  protected override updateFormFields(): void {
    this.cachedFieldModalConfig = {
      modalComponent: ModalListFormComponent,
      modalTitle: 'Add Cached Field',
      defaultItemProvider: () => ({ name: '' }),
      listItemFormatter: (item: CachedFieldItem) => ({
        id: item.name,
        title: item.name,
        display: item.name,
        icon: 'pi pi-database',
      }),
      modalFields: (item: CachedFieldItem | undefined): FormFieldConfig[] => {
        return [
          createTextField('name', 'Field Name', item?.name ?? '', {
            placeholder: 'Enter field name...',
            validations: [
              {
                name: 'required',
                validator: Validators.required,
                message: 'Field name is required',
              },
            ],
          }),
        ];
      },
      onSubmit: async (item: CachedFieldItem) => {
        // Validate the item
        if (!item.name?.trim()) {
          return false;
        }

        return true; // Close modal
      },
      onDeleteItem: async (item: CachedFieldItem) => {
        return new Promise<boolean>((resolve) => {
          this.confirmationService.confirm({
            header: 'Confirmation',
            message: `Are you sure you want to delete "${item.name}"?`,
            closable: true,
            closeOnEscape: true,
            icon: 'pi pi-exclamation-triangle',
            rejectButtonProps: {
              label: 'Cancel',
              severity: 'secondary',
              outlined: true,
            },
            acceptButtonProps: {
              label: 'Delete',
              severity: 'danger',
            },
            accept: async () => {
              resolve(true); // Allow default deletion
            },
            reject: () => {
              resolve(false); // Cancel deletion
            },
          });
        });
      },
      subHeader: '',
      showEditButton: false,
    };

    this.formFields = [
      createModalListField<CachedFieldItem>(
        'cached_fields',
        'Cached Fields',
        this.cachedFieldItems(),
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          name: 'cached_fields',
          props: {
            modalConfig: this.cachedFieldModalConfig,
          },
        },
      ) as FormFieldConfig<unknown>,
    ];
  }

  protected override transformFormData(
    formData: Record<string, unknown>,
  ): Partial<DeviceTypeResponse> {
    // Transform cachedFields array to simple string array within ioCapabilities
    if (
      isDefined(formData['cached_fields']) &&
      Array.isArray(formData['cached_fields'])
    ) {
      const cachedFieldsArray = formData['cached_fields'] as CachedFieldItem[];
      const entity = this.entity();

      // Start with existing ioCapabilities to preserve other I/O types
      const ioCapabilities: Record<string, unknown> = isDefined(
        entity?.ioCapabilities,
      )
        ? { ...entity.ioCapabilities }
        : {};

      // Transform to simple string array
      const cachedFields = cachedFieldsArray
        .filter((item) => isDefined(item.name) && item.name.trim() !== '')
        .map((item) => item.name);

      // Update cachedFields within ioCapabilities
      ioCapabilities['cached_fields'] = cachedFields;

      return {
        ioCapabilities: ioCapabilities as DeviceTypeResponse['ioCapabilities'],
      };
    }

    return formData as Partial<DeviceTypeResponse>;
  }

  protected override getUpdateMethod() {
    return (
      this.operationRegistry.getOperation('device-types', 'update') ??
      ((id, data) =>
        this.deviceTypesService.updateDeviceType(id, data).pipe(
          tap((response) => {
            this.entityChange.emit(response);
            this.broadcastService.broadcast('entity-updated', {
              entityType: 'device-types',
              operation: 'update',
              entityId: response.id,
              data: response,
            });
          }),
        ))
    );
  }

  ngOnInit(): void {
    this.updateFormFields();
  }
}
