import { CommonModule } from '@angular/common';
import { Component, computed, effect, inject, input } from '@angular/core';
import { Validators } from '@angular/forms';

import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';

import {
  EntitiesService,
  ZoneCreateRequest,
  ZoneListItem,
  ZoneResponse,
  ZoneUpdateRequest,
} from '../../../../../api';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import {
  AssetGroupingTreeItem,
  createDropdownField,
  createNumberField,
  createTextField,
  createTreeSelectField,
  MeasurementUnitType,
  TreeSelectNode,
} from '../../../../../shared/models';
import {
  enumToSelectOptionValues,
  isDefined,
} from '../../../../../shared/utils';
import { TypedEntitySectionBaseComponent } from '../../base';
import { AssetGroupingService } from '../../../../../shared/services/asset-grouping.service';
import { MeasurementUnitsService } from '../../../../../shared/services/measurement-units/measurement-units.service';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { Observable } from 'rxjs';
import { UtilityService } from '../../../../../core/services/utility.service';
import { FormToRequestMapper } from '../../../utils/form-to-request-mapper';

type FormData = Record<string, string | number | object>;

@Component({
  selector: 'app-geofence-details-section',
  imports: [
    CommonModule,
    ButtonModule,
    InputTextModule,
    GridComponent,
    DynamicFormComponent,
  ],
  templateUrl: './geofence-details-section.component.html',
  styleUrl: './geofence-details-section.component.scss',
})
export class GeofenceDetailsSectionComponent extends TypedEntitySectionBaseComponent<
  ZoneListItem,
  ZoneUpdateRequest,
  Partial<ZoneCreateRequest>,
  ZoneResponse
> {
  readonly ownerId = input<string | null>(null);
  private readonly entitiesService = inject(EntitiesService);
  private readonly assetGroupService = inject(AssetGroupingService);
  private readonly measurementUnitsService = inject(MeasurementUnitsService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly utilityService = inject(UtilityService);

  readonly geofenceDetails = computed(() => {
    const geofenceDetails = this.entity();
    if (!isDefined(geofenceDetails)) return [];

    return [
      { key: 'Name', value: geofenceDetails.name },
      {
        key: 'Type',
        value: this.utilityService.toTitleCase(geofenceDetails.zoneType ?? ''),
      },
      { key: 'Cost Centre', value: geofenceDetails.costCentre?.name ?? 'None' },
      { key: 'Group', value: geofenceDetails.group?.name ?? 'None' },
      {
        key: 'Speed Limit',
        value:
          isDefined(geofenceDetails.speed) && geofenceDetails.speed > 0
            ? this.measurementUnitsService.format(
                geofenceDetails.speed,
                MeasurementUnitType.SPEED,
              ).format
            : 'None',
      },
      {
        key: 'Proximity Warning',
        value:
          isDefined(geofenceDetails.radius) && geofenceDetails.radius > 0
            ? this.measurementUnitsService.format(
                geofenceDetails.radius,
                MeasurementUnitType.DISTANCE,
              ).format
            : 'None',
      },
    ];
  });
  zoneGroups: AssetGroupingTreeItem[] = [];
  costCenters: AssetGroupingTreeItem[] = [];
  private requestMapper?: FormToRequestMapper<ZoneUpdateRequest>;

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    effect(() => {
      const geofence = this.entity();
      if (isDefined(geofence)) {
        // Track completion of both async operations
        const dataLoadPromises: Promise<void>[] = [];

        // Add cost centers loading promise
        const costCentresPromise = this.loadCostCentres(
          geofence['ownerId'],
        ).catch(() => {
          console.error('Failed to load cost centres');
        });
        dataLoadPromises.push(costCentresPromise);

        // Add zone groups loading promise
        const zoneGroupsPromise = this.assetGroupService
          .getZoneGroupsAsTree(geofence['ownerId'])
          .then((res) => {
            this.zoneGroups = res;
          })
          .catch(() => {
            console.error('Failed to load zone groups');
          });
        dataLoadPromises.push(zoneGroupsPromise);

        // Wait for all data to load before updating form fields
        Promise.all(dataLoadPromises)
          .then(() => {
            // Initialize the request mapper now that we have all the data
            this.initializeRequestMapper();
            this.updateFormFields();
          })
          .catch(() => {});
      }
    });
  }

  private async loadCostCentres(ownerId: string): Promise<void> {
    const costCentreTree =
      await this.assetGroupService.getCostCentresAsTree(ownerId);
    this.costCenters = costCentreTree;
  }

  private initializeRequestMapper(): void {
    this.requestMapper = new FormToRequestMapper<ZoneUpdateRequest>({
      name: {
        requestProperty: 'name',
        validate: (value) => typeof value === 'string' && value.length > 0,
      },
      zoneType: {
        requestProperty: 'zoneType',
        validate: (value) => typeof value === 'string' && value.length > 0,
      },
      costCentre: {
        requestProperty: 'costCentre',
        transform: (value) => {
          const item = this.assetGroupService.findInTree(
            this.costCenters,
            `${(value as TreeSelectNode).id}`,
          );
          return item ? { id: item.id, name: item.name } : undefined;
        },
      },
      group: {
        requestProperty: 'group',
        transform: (value) => {
          const item = this.assetGroupService.findInTree(
            this.zoneGroups,
            (value as TreeSelectNode).id,
          );
          return item ? { id: item.id, name: item.name } : undefined;
        },
      },
      speed: {
        requestProperty: 'speed',
        transform: (value) => {
          if (typeof value !== 'number') return undefined;
          return this.measurementUnitsService.toBackend(
            MeasurementUnitType.SPEED,
            value,
          );
        },
      },
      radius: {
        requestProperty: 'radius',
        transform: (value) => {
          if (typeof value !== 'number') return undefined;
          return this.measurementUnitsService.toBackend(
            MeasurementUnitType.DISTANCE,
            value,
          );
        },
      },
    });
  }

  protected override updateFormFields(): void {
    const geofence = this.entity();
    this.formFields = [
      createTextField('name', 'Name', geofence?.name, {
        name: 'name',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Name is required',
          },
        ],
      }),
      createDropdownField(
        'zoneType',
        'Type',
        enumToSelectOptionValues(ZoneListItem.ZoneTypeEnum).map((item) => {
          if (item.label === 'Nogo') {
            item.label = 'No Go';
          }
          if (item.label === 'Keepin') {
            item.label = 'Keep In';
          }
          return item;
        }),
        geofence?.zoneType,
        {
          name: 'zoneType',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Type is required',
            },
          ],
          props: {
            appendTo: 'body',
          },
        },
      ),
      createTreeSelectField(
        'costCentre',
        'Cost Centre',
        geofence?.costCentre
          ? {
              id: geofence.costCentre.id,
              label: geofence.costCentre.name,
              data: geofence.costCentre,
            }
          : null,
        this.costCenters,
        {
          name: 'costCentre',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Cost centre is required',
            },
          ],
          props: {
            appendTo: 'body',
            placeholder: 'Select Cost Centre',
            multiple: false,
            showClear: true,
            selectionMode: 'single',
            filter: true,
          },
        },
      ),
      createTreeSelectField(
        'group',
        'Group',
        geofence?.group
          ? {
              id: geofence.group.id,
              label: geofence.group.name,
              data: geofence.group,
            }
          : null,
        this.zoneGroups,
        {
          name: 'group',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Group is required',
            },
          ],
          props: {
            appendTo: 'body',
            placeholder: 'Select Group',
            multiple: false,
            showClear: true,
            selectionMode: 'single',
            filter: true,
          },
        },
      ),
      createNumberField(
        'speed',
        'Speed Limit',
        this.measurementUnitsService.fromBackend(
          MeasurementUnitType.SPEED,
          geofence?.speed ?? 0,
        ),
        {
          name: 'speed',
          props: {
            appendTo: 'body',
            placeholder: 'Enter Speed Limit',
            showClear: true,
            suffix: this.measurementUnitsService.unitSymbol(
              MeasurementUnitType.SPEED,
            ),
          },
        },
      ),
      createNumberField(
        'radius',
        'Proximity Warning',
        this.measurementUnitsService.fromBackend(
          MeasurementUnitType.DISTANCE,
          geofence?.radius ?? 0,
        ),
        {
          name: 'radius',
          props: {
            appendTo: 'body',
            placeholder: 'Enter Proximity Warning',
            showClear: true,
            suffix: this.measurementUnitsService.unitSymbol(
              MeasurementUnitType.DISTANCE,
            ),
          },
        },
      ),
    ];
  }

  override getUpdateMethod(): (
    id: string,
    data: ZoneUpdateRequest,
  ) => Observable<ZoneResponse> {
    return (
      this.operationRegistry.getOperation<
        ZoneListItem,
        ZoneUpdateRequest,
        ZoneResponse,
        'update'
      >('zone', 'update') ??
      ((id, data) => this.entitiesService.updateZone(id, data))
    );
  }

  protected override onFormValueChange(formData: FormData): void {
    try {
      const transformedData = this.transformFormData(formData);
      this.formValueChange.emit(transformedData);
    } catch (error) {
      console.warn('Form data transformation failed:', error);
      // Emit raw data as fallback
      this.formValueChange.emit(formData);
    }
  }

  protected override transformFormData(
    formData: Record<string, unknown> | Record<string, string | number>[],
  ): ZoneUpdateRequest {
    if (!this.requestMapper) {
      return {};
    }

    try {
      // Handle both possible input types
      const normalizedFormData = Array.isArray(formData)
        ? formData.reduce((acc, item) => ({ ...acc, ...item }), {})
        : formData;

      const partialRequest = this.requestMapper.transform(normalizedFormData);

      // Build update request - only include properties that were actually provided
      const updateRequest: Partial<ZoneUpdateRequest> = {};

      // Only include properties that exist in the partial request (even if they're falsy)
      if ('name' in partialRequest) {
        updateRequest.name = partialRequest.name;
      }

      if ('zoneType' in partialRequest) {
        updateRequest.zoneType = partialRequest.zoneType;
      }

      if ('costCentre' in partialRequest) {
        updateRequest.costCentre = partialRequest.costCentre;
      }

      if ('group' in partialRequest) {
        updateRequest.group = partialRequest.group;
      }

      if ('speed' in partialRequest) {
        updateRequest.speed = partialRequest.speed;
      }

      if ('radius' in partialRequest) {
        updateRequest.radius = partialRequest.radius;
      }

      return updateRequest;
    } catch (error) {
      console.error('Form data transformation failed:', error);
      throw error;
    }
  }
}
