import {
  Component,
  computed,
  effect,
  inject,
  input,
  signal,
} from '@angular/core';

import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';

import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';
import {
  EmailProviderCreateRequest,
  EmailProviderListItem,
  EmailProviderResponse,
  EntitiesService,
  EmailProviderTypeResponse,
  IdName,
} from '../../../../../api';
import { EmailProviderSectionConfigFactory } from '../../../factories';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { firstValueFrom } from 'rxjs';
import { RequestCacheService } from '../../../../../shared/services/request-cache.service';
import { isDefined } from '../../../../../shared/utils';
import { ApiSchemaService } from '../../../../../core/services/api-schema.service';

@Component({
  selector: 'app-email-provider-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './email-provider-details-container.component.html',
  styleUrl: './email-provider-details-container.component.scss',
})
export class EmailProviderDetailsContainerComponent {
  readonly entity = input<EmailProviderListItem | null>(null);

  private readonly emailProviderSectionFactory = inject(
    EmailProviderSectionConfigFactory,
  );
  readonly entityDetailsState = inject(EntityDetailsStateService);
  private readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly cacheService = inject(RequestCacheService);
  private readonly apiSchemaService = inject(ApiSchemaService);
  private readonly entityName = 'EmailProvider';

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly isSoftRefreshing = computed(() =>
    this.entityDetailsState.isSoftRefreshing(),
  );
  readonly isInitialLoading = computed(
    () =>
      this.isLoading() && !this.isSoftRefreshing() && !this.fullEmailProvider(),
  );
  readonly fullEmailProvider = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();

    if (!entityDetails) {
      return null;
    }

    return entityDetails as EmailProviderResponse;
  });
  readonly sections = computed(() => {
    const emailProvider = this.fullEmailProvider();
    const emailProviderType = this.emailProviderType();
    if (!(emailProvider || isDefined(emailProviderType))) return [];
    return this.emailProviderSectionFactory.createAllEmailProviderSections(
      emailProvider,
      emailProviderType,
    );
  });
  readonly error = signal<string | null>(null);
  readonly emailProviderType = signal<EmailProviderTypeResponse | null>(null);

  constructor() {
    this.operationRegistry.registerEntityOperations('email-provider', {
      update: (id, data) => this.entitiesService.updateEmailProvider(id, data),
      create: (data: EmailProviderCreateRequest) =>
        this.entitiesService.createEmailProvider(data),
      get: (id) => this.entitiesService.getEmailProvider(id),
    });

    effect(() => {
      const emailProvider = this.fullEmailProvider();
      let typeIdToLoad: string | undefined | null = null;
      if (emailProvider) {
        typeIdToLoad = this.apiSchemaService.getEntityType<
          Record<string, unknown>,
          IdName
        >(emailProvider as Record<string, unknown>, this.entityName)?.id;
      }
      if (isDefined(typeIdToLoad) && typeIdToLoad !== '') {
        this.loadEmailProviderType(typeIdToLoad).catch((error) => {
          this.error.set(error.message);
        });
      } else {
        this.emailProviderType.set(null);
      }
    });
  }

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as EmailProviderResponse);
    this.error.set(null);
  }

  async loadEmailProviderType(emailProviderTypeId: string): Promise<void> {
    if (!emailProviderTypeId) {
      this.emailProviderType.set(null);
      return;
    }
    await this.cacheService
      .getOrFetchAsync(
        `emailProviderType-${emailProviderTypeId}`,
        () =>
          firstValueFrom(
            this.entitiesService.getEmailProviderType(emailProviderTypeId),
          ),
        300000,
      )
      .then((response) => {
        this.emailProviderType.set(response);
      });
  }
}
