import {
  Component,
  computed,
  TemplateRef,
  ViewChild,
  OnInit,
} from '@angular/core';
import {
  ThemeSettingsSectionBase,
  ThemeFieldConfig,
} from '../../base/theme-settings-section-base';
import { ThemeCreateRequest, ThemeResponse } from '../../../../../api';
import { FormFieldConfig } from '../../../../../shared/models';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { CommonModule } from '@angular/common';
import { Observable } from 'rxjs';
import { isDefined } from '../../../../../shared/utils';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { ThemeValueDisplayComponent } from '../theme-value-display/theme-value-display.component';

// Constants for theme property paths
const TAB_THEME_PATHS = {
  FONT_FAMILY: ['tab', 'font', 'family'],
  FONT_SIZE: ['tab', 'font', 'size'],
  FONT_WEIGHT: ['tab', 'font', 'weight'],
  FONT_TRANSFORM: ['tab', 'font', 'transform'],
  FONT_COLOR: ['tab', 'font', 'color'],
  BACKGROUND_COLOR: ['tab', 'background', 'color'],
  BORDER_COLOR: ['tab', 'border', 'color'],
  ACTIVE_FONT_COLOR: ['tab', 'active', 'font', 'color'],
  ACTIVE_BACKGROUND_COLOR: ['tab', 'active', 'background', 'color'],
  ACTIVE_INDICATOR_STYLE: ['tab', 'active', 'style'],
  ACTIVE_INDICATOR_COLOR: ['tab', 'active', 'indicator', 'color'],
} as const;

@Component({
  selector: 'app-theme-settings-tab-section',
  imports: [
    DynamicFormComponent,
    FontAwesomeModule,
    CommonModule,
    GridComponent,
    ThemeValueDisplayComponent,
  ],
  templateUrl: './theme-settings-tab-section.component.html',
  styleUrl: './theme-settings-tab-section.component.scss',
})
export class ThemeSettingsTabSectionComponent
  extends ThemeSettingsSectionBase
  implements OnInit
{
  @ViewChild('valueTemplate', { static: true })
  valueTemplate!: TemplateRef<unknown>;
  readonly themeDetails = computed(() => {
    const themeDetails = this.entity();
    if (!isDefined(themeDetails)) return [];

    const themeSettings = this.themeEngine.themeSettingsMergedWithDefault(
      themeDetails?.settings ?? {},
    );

    const detailConfigs = this.createThemeDetailsFromConfig(
      this.getFieldConfigurations(),
    );
    return detailConfigs.map((config) => ({
      key: config.key,
      value: this.formatThemeValue(themeSettings, config),
    }));
  });

  constructor() {
    super('tab');
  }

  ngOnInit(): void {
    this.initializeColumns();
  }

  private initializeColumns(): void {
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      {
        field: 'value',
        header: '',
        cellTemplate: this.valueTemplate,
      },
    ];
  }

  protected getFieldConfigurations(): ThemeFieldConfig[] {
    return [
      {
        id: 'font.family',
        label: 'Font Family',
        path: TAB_THEME_PATHS.FONT_FAMILY,
        type: 'font-family',
        presets: ['main', 'content'],
      },
      {
        id: 'font.size',
        label: 'Font Size',
        path: TAB_THEME_PATHS.FONT_SIZE,
        type: 'size',
      },
      {
        id: 'font.weight',
        label: 'Font Weight',
        path: TAB_THEME_PATHS.FONT_WEIGHT,
        type: 'font-weight',
        presets: ['main', 'content'],
      },
      {
        id: 'font.transform',
        label: 'Font Case',
        path: TAB_THEME_PATHS.FONT_TRANSFORM,
        type: 'font-transform',
      },
      {
        id: 'font.color',
        label: 'Font Color',
        path: TAB_THEME_PATHS.FONT_COLOR,
        type: 'color',
        group: 'tab',
        presets: ['main', 'content', 'header'],
      },
      {
        id: 'background.color',
        label: 'Background Color',
        path: TAB_THEME_PATHS.BACKGROUND_COLOR,
        type: 'color',
        group: 'tab',
        presets: ['content', 'header'],
      },
      {
        id: 'border.color',
        label: 'Border Color',
        path: TAB_THEME_PATHS.BORDER_COLOR,
        type: 'color',
        group: 'tab',
        presets: ['tab', 'header', 'main', 'content'],
      },
      {
        id: 'active.font.color',
        label: 'Active Font',
        path: TAB_THEME_PATHS.ACTIVE_FONT_COLOR,
        type: 'color',
        group: 'tab',
        presets: ['main', 'content', 'header'],
      },
      {
        id: 'active.background.color',
        label: 'Active Background',
        path: TAB_THEME_PATHS.ACTIVE_BACKGROUND_COLOR,
        type: 'color',
        group: 'tab',
        presets: ['tab', 'content'],
      },
      {
        id: 'active.style',
        label: 'Active Style',
        path: TAB_THEME_PATHS.ACTIVE_INDICATOR_STYLE,
        type: 'dropdown',
        options: [
          { value: 'none', label: 'None' },
          { value: 'bottom', label: 'Bottom' },
        ],
      },
      {
        id: 'active.indicator.color',
        label: 'Active Indicator Color',
        path: TAB_THEME_PATHS.ACTIVE_INDICATOR_COLOR,
        type: 'color',
        group: 'tab',
        presets: ['tab', 'header'],
      },
    ];
  }

  getFields(entity: ThemeResponse): FormFieldConfig[] {
    return this.createFormFieldsFromConfig(
      entity,
      this.getFieldConfigurations(),
    );
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<ThemeResponse>,
  ) => Observable<ThemeResponse> {
    return (
      this.operationRegistry.getOperation<
        ThemeResponse,
        ThemeCreateRequest,
        ThemeResponse,
        'update'
      >('theme', 'update') ??
      ((id) =>
        this.entitiesService.updateTheme(id, {
          settings: {
            tab: this.formChanges,
          },
        }))
    );
  }
}
