import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import {
  SimCardListItem,
  EntitiesService,
  SimCardResponse,
} from '../../../../api';
import {
  DataGridParams,
  EntityDataAdapter,
  GridResponse,
} from '../../../../shared/models';
import { EntityConfigAdapter } from '../../models/entity-config-adapter.interface';
import { ActionIcon, StateOption } from '../../models';
import { AuthDataService } from '../../../../core/services/auth-data.service';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';

@Injectable({
  providedIn: 'root',
})
export class SimCardsDataAdapter
  implements
    EntityDataAdapter<SimCardListItem, SimCardResponse>,
    EntityConfigAdapter
{
  private readonly entitiesService = inject(EntitiesService);
  private readonly authDataService = inject(AuthDataService);
  private readonly entityStateService = inject(EntityDetailsStateService);

  getList(params: DataGridParams): Observable<GridResponse<SimCardListItem>> {
    const { owner, limit, offset, sortField, filters } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.entitiesService.listSimCards(
      owner,
      offset,
      limit,
      sortField as string,
      filters,
    );
  }

  getById(id: string): Observable<SimCardResponse> {
    return this.entitiesService.getSimCard(id);
  }

  getStateOptions(): StateOption[] {
    return [
      { label: 'Active', value: SimCardResponse.StateEnum.Active },
      { label: 'Inactive', value: SimCardResponse.StateEnum.Inactive },
      { label: 'Suspended', value: SimCardResponse.StateEnum.Suspended },
      { label: 'Deleted', value: SimCardResponse.StateEnum.Deleted },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.entitiesService.updateSimCard(id, {
      state: state as SimCardResponse.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    if (!this.authDataService.hasPermission('simcards:create')) {
      return [];
    }

    return [
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change SIM Card State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
