import { Injectable } from '@angular/core';
import { FormFieldConfig } from '../../../shared/models';

@Injectable({
  providedIn: 'root',
})
export class FormFieldService {
  merge(
    existingFields: FormFieldConfig[],
    newFields: FormFieldConfig[],
  ): FormFieldConfig[] {
    if (!existingFields?.length) {
      return this.consolidateByKey(newFields);
    }

    const consolidatedExisting = this.consolidateByKey(existingFields);
    const consolidatedNew = this.consolidateByKey(newFields);

    return this.mergeConsolidatedFields(consolidatedExisting, consolidatedNew);
  }

  private consolidateByKey(fields: FormFieldConfig[]): FormFieldConfig[] {
    const fieldMap = new Map<string, FormFieldConfig>();
    const fieldsWithoutKeys: FormFieldConfig[] = [];

    for (const field of fields) {
      const key = field.key || field.name;
      if (!key) {
        fieldsWithoutKeys.push(field);
        continue;
      }

      if (fieldMap.has(key)) {
        const existing = fieldMap.get(key) as FormFieldConfig;
        fieldMap.set(key, this.mergeFields(existing, field));
      } else {
        fieldMap.set(key, field);
      }
    }

    return [...fieldsWithoutKeys, ...Array.from(fieldMap.values())];
  }

  private mergeFields(
    existing: FormFieldConfig,
    incoming: FormFieldConfig,
  ): FormFieldConfig {
    if (this.isGroupField(existing) && this.isGroupField(incoming)) {
      // Combine all fields from both groups
      const mergedFields = [
        ...(existing.fields ?? []),
        ...(incoming.fields ?? []),
      ];
      return {
        ...existing,
        ...incoming,
        fields: this.consolidateByKey(mergedFields),
      };
    }
    return incoming; // New field takes precedence
  }

  private isGroupField(field: FormFieldConfig): boolean {
    return field.type === 'group' && Array.isArray(field.fields);
  }

  private mergeConsolidatedFields(
    existingFields: FormFieldConfig[],
    newFields: FormFieldConfig[],
  ): FormFieldConfig[] {
    const existingMap = new Map(
      existingFields.map((field) => [field.key || field.name, field]),
    );

    return newFields.map((newField) => {
      const key = newField.key || newField.name;
      const existing = key ? existingMap.get(key) : undefined;

      return existing ? this.mergeFields(existing, newField) : newField;
    });
  }
}
