import {
  ChangeDetectorRef,
  Component,
  computed,
  effect,
  inject,
  input,
  OnInit,
  signal,
  untracked,
} from '@angular/core';
import { Validators } from '@angular/forms';

import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';

import {
  DeviceProviderListItem,
  DeviceProviderCreateRequest,
  EntitiesService,
  DeviceProviderResponse,
  DeviceProviderTypeListItem,
} from '../../../../../api';
import {
  createDropdownField,
  createTextField,
} from '../../../../../shared/models';
import { EntitySectionBaseComponent } from '../../base';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { CommonModule } from '@angular/common';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { Observable } from 'rxjs';
import { faPlug } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

type DeviceProviderEntity =
  | DeviceProviderListItem
  | Partial<DeviceProviderCreateRequest>;

@Component({
  selector: 'app-device-provider-details-section',
  imports: [
    CommonModule,
    ButtonModule,
    InputTextModule,
    GridComponent,
    DynamicFormComponent,
    FontAwesomeModule,
  ],
  templateUrl: './device-provider-details-section.component.html',
  styleUrl: './device-provider-details-section.component.scss',
})
export class DeviceProviderDetailsSectionComponent
  extends EntitySectionBaseComponent<DeviceProviderEntity>
  implements OnInit
{
  readonly ownerId = input<string | null>(null);
  readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);

  private readonly cdr = inject(ChangeDetectorRef);
  protected readonly selectOptionsLoaded = signal<boolean>(false);

  readonly deviceProviderDetails = computed(() => {
    const deviceProviderDetails = this.entity();
    if (!isDefined(deviceProviderDetails)) return [];

    return [
      {
        key: 'Type',
        value: deviceProviderDetails.deviceProviderType?.name ?? 'None',
      },
      { key: 'Name', value: deviceProviderDetails.name },
    ];
  });
  selectOptions: Record<
    string,
    { label: string; value: string; data: unknown }[]
  > = {};
  faPlug = faPlug;

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    effect(() => {
      const selectOptionsLoaded = this.selectOptionsLoaded();
      if (selectOptionsLoaded) {
        untracked(() => {
          this.updateFormFields();
          this.cdr.detectChanges();
        });
      }
    });
  }

  ngOnInit(): void {
    this.loadSelectOptions();
  }

  private loadSelectOptions(): void {
    const deviceProvider = this.entity();
    const ownerIdValue = this.ownerId();

    const effectiveOwnerId = deviceProvider?.owner?.id ?? ownerIdValue;

    if (isNullOrUndefined(effectiveOwnerId)) {
      this.selectOptions = {};
      this.selectOptionsLoaded.set(true);
      this.updateFormFields();
    }

    this.entitiesService
      .listDeviceProviderTypes(effectiveOwnerId)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((result) => {
        this.selectOptions = {
          deviceProviderTypes: result.items
            .map((item) => ({
              label: item?.name,
              value: item.id,
              data: item,
            }))
            .sort((a, b) => a.label.localeCompare(b.label)),
        };

        this.selectOptionsLoaded.set(true);
      });
  }

  protected override updateFormFields(): void {
    const deviceProvider = this.entity();
    this.formFields = [
      createDropdownField(
        'deviceProviderType',
        'Type',
        this.selectOptions?.['deviceProviderTypes'],
        deviceProvider?.deviceProviderType?.id,
        {
          name: 'deviceProviderType',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Type is required',
            },
          ],
          disabled: !this.createMode(),
          props: {
            appendTo: 'body',
          },
        },
      ),
      createTextField('name', 'Name', deviceProvider?.name, {
        name: 'name',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Name is required',
          },
        ],
      }),
    ];
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<DeviceProviderEntity>,
  ) => Observable<DeviceProviderResponse> {
    return (
      this.operationRegistry.getOperation<
        DeviceProviderEntity,
        DeviceProviderCreateRequest,
        DeviceProviderResponse,
        'update'
      >('device-provider', 'update') ??
      ((id, data) => this.entitiesService.updateDeviceProvider(id, data))
    );
  }

  protected override onFormValueChange(
    formData: Record<string, string | number | object>,
  ): void {
    const transformedData = { ...formData };
    if (isDefined(transformedData['deviceProviderType'])) {
      const deviceProviderTypeId = transformedData[
        'deviceProviderType'
      ] as string;
      const deviceProviderType = this.selectOptions?.[
        'deviceProviderTypes'
      ]?.find((option) => option.value === deviceProviderTypeId);
      if (deviceProviderType) {
        transformedData['deviceProviderType'] = {
          id: deviceProviderTypeId,
          name: (deviceProviderType.data as DeviceProviderTypeListItem)?.name,
        };
      }
    }

    this.formValueChange.emit(transformedData);
  }
}
