import { Injectable } from '@angular/core';

import {
  DeviceConfigProfileResponse,
  DeviceTypeAccessory,
  DeviceTypeResponse,
  IoTypeListItem,
} from '../../../api';
import { AdminDetailSection } from '../models';
import { DeviceConfigProfileDetailsSectionComponent } from '../components/device-config-profiles/device-config-profile-details-section/device-config-profile-details-section.component';
import { DeviceParametersSectionComponent } from '../components/device/device-parameters-section/device-parameters-section.component';
import { DeviceIoWhitelistSectionComponent } from '../components/device/device-io-whitelist-section/device-io-whitelist-section.component';
import { DeviceBitmapSectionComponent } from '../components/device/device-bitmap-section/device-bitmap-section.component';
import { DeviceIOSectionComponent } from '../components/device/device-io-section/device-io-section.component';
import { DeviceAccessorySectionComponent } from '../components/device/device-accessory-section/device-accessory-section.component';

@Injectable({ providedIn: 'root' })
export class DeviceConfigProfileSectionConfigFactory {
  /**
   * Create sections for device config profile information
   * @param deviceConfigProfile The device config profile object to display in the section
   * @returns An array of AdminDetailSection objects representing the device config profile information sections
   */
  createDeviceConfigProfileInformationSection(
    deviceConfigProfile: DeviceConfigProfileResponse | null,
    deviceType: DeviceTypeResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'device-config-profile-details',
        title: 'Device Config Details',
        component: DeviceConfigProfileDetailsSectionComponent,
        componentInputs: { entity: deviceConfigProfile },
        category: 'information',
        order: 10,
      },
      {
        id: 'device-config-profile-parameters',
        title: 'Device Config Parameters',
        component: DeviceParametersSectionComponent,
        componentInputs: {
          entity: deviceConfigProfile,
          entityType: 'device-config-profile',
          dataPath: 'parameters',
          injectDataAtPath: true,
          deviceType,
        },
        category: 'information',
        order: 20,
      },
    ];
  }

  /**
   * Create sections for device config profile IO
   * @param deviceConfigProfile The device config profile object to display in the section
   * @returns An array of AdminDetailSection objects representing the device config profile IO sections
   */
  createDeviceConfigProfileIOSection(
    deviceConfigProfile: DeviceConfigProfileResponse | null,
    deviceType: DeviceTypeResponse | null,
    ioTypes: IoTypeListItem[],
  ): AdminDetailSection[] {
    return [
      {
        id: 'device-config-profile-io-selection',
        title: 'IO Selection',
        component: DeviceIoWhitelistSectionComponent,
        componentInputs: {
          entity: deviceConfigProfile,
          entityType: 'device-config-profile',
          dataPath: 'parameters',
          injectDataAtPath: true,
          deviceType,
        },
        category: 'io',
        order: 10,
      },
      {
        id: 'device-config-profile-bitmapped-io',
        title: 'Bitmapped IO',
        component: DeviceBitmapSectionComponent,
        componentInputs: {
          entity: deviceConfigProfile,
          entityType: 'device-config-profile',
          dataPath: 'parameters.io',
          injectDataAtPath: true,
        },
        category: 'io',
        order: 20,
      },
      {
        id: 'device-config-profile-analog-inputs',
        title: 'Analog Inputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: deviceConfigProfile,
          entityType: 'device-config-profile',
          dataPath: 'parameters.io',
          injectDataAtPath: true,
          ioType: 'analog_input',
          iconType: 'svg',
          iconName: 'gauge-simple-high-solid',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 30,
      },
      {
        id: 'device-config-profile-camera-inputs',
        title: 'Camera Inputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: deviceConfigProfile,
          entityType: 'device-config-profile',
          dataPath: 'parameters.io',
          injectDataAtPath: true,
          ioType: 'camera_input',
          iconType: 'primeng',
          iconName: 'pi pi-camera',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 40,
      },
      {
        id: 'device-config-profile-can-bus-inputs',
        title: 'CAN Bus Inputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: deviceConfigProfile,
          entityType: 'device-config-profile',
          dataPath: 'parameters.io',
          injectDataAtPath: true,
          ioType: 'can_input',
          iconType: 'svg',
          iconName: 'oil-can-solid',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 50,
      },
      {
        id: 'device-config-profile-counter-inputs',
        title: 'Counter Inputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: deviceConfigProfile,
          entityType: 'device-config-profile',
          dataPath: 'parameters.io',
          injectDataAtPath: true,
          ioType: 'counter_input',
          iconType: 'primeng',
          iconName: 'pi pi-clock',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 60,
      },
      {
        id: 'device-config-profile-digital-inputs',
        title: 'Digital Inputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: deviceConfigProfile,
          entityType: 'device-config-profile',
          dataPath: 'parameters.io',
          injectDataAtPath: true,
          ioType: 'digital_input',
          iconType: 'primeng',
          iconName: 'pi pi-arrow-left',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 70,
      },
      {
        id: 'device-config-profile-digital-outputs',
        title: 'Digital Outputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: deviceConfigProfile,
          entityType: 'device-config-profile',
          dataPath: 'parameters.io',
          injectDataAtPath: true,
          ioType: 'digital_output',
          iconType: 'primeng',
          iconName: 'pi pi-arrow-right',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 80,
      },
      {
        id: 'device-config-profile-temperature-inputs',
        title: 'Temperature Inputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: deviceConfigProfile,
          entityType: 'device-config-profile',
          dataPath: 'parameters.io',
          injectDataAtPath: true,
          ioType: 'temperature_input',
          iconType: 'svg',
          iconName: 'temperature-half-solid',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 90,
      },
      {
        id: 'device-config-profile-value-inputs',
        title: 'Value Inputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: deviceConfigProfile,
          entityType: 'device-config-profile',
          dataPath: 'parameters.io',
          injectDataAtPath: true,
          ioType: 'value_input',
          iconType: 'primeng',
          iconName: 'pi pi-list-check',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 100,
      },
    ];
  }

  createDeviceConfigProfileAccessoriesSection(
    deviceConfigProfile: DeviceConfigProfileResponse | null,
    deviceTypeAccessories: Record<string, DeviceTypeAccessory> | undefined,
  ): AdminDetailSection[] {
    if (!deviceConfigProfile) return [];

    const sections: AdminDetailSection[] = [];
    Object.entries(deviceConfigProfile?.accessories ?? {}).forEach(([key]) => {
      sections.push({
        id: `device-config-profile-accessory-${key}`,
        title: deviceTypeAccessories?.[key]?.name ?? key,
        component: DeviceAccessorySectionComponent,
        componentInputs: {
          entity: deviceConfigProfile,
          entityType: 'device-config-profile',
          dataPath: `accessories.${key}`,
          injectDataAtPath: true,
          deviceTypeAccessory: deviceTypeAccessories?.[key],
          accessoryId: key,
          sectionHeader: deviceTypeAccessories?.[key]?.name ?? key,
        },
        category: 'accessories',
        order: 10 + Number(key),
      });
    });

    return sections;
  }

  /**
   * Create all deviceConfigProfile sections in one call
   * @param deviceConfigProfile The deviceConfigProfile object to display in the sections
   * @returns An array of AdminDetailSection objects representing all deviceConfigProfile sections
   */
  createAllDeviceConfigProfileSections(
    deviceConfigProfile: DeviceConfigProfileResponse | null,
    deviceTypeAccessories: Record<string, DeviceTypeAccessory> | undefined,
    deviceType: DeviceTypeResponse | null,
    ioTypes: IoTypeListItem[],
  ): AdminDetailSection[] {
    return [
      ...this.createDeviceConfigProfileInformationSection(
        deviceConfigProfile,
        deviceType,
      ),
      ...this.createDeviceConfigProfileIOSection(
        deviceConfigProfile,
        deviceType,
        ioTypes,
      ),
      ...this.createDeviceConfigProfileAccessoriesSection(
        deviceConfigProfile,
        deviceTypeAccessories,
      ),
    ];
  }
}
