import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import {
  DeviceConfigProfileListItem,
  DeviceConfigProfileResponse,
  EntitiesService,
} from '../../../../api';
import {
  DataGridParams,
  EntityDataAdapter,
  GridResponse,
} from '../../../../shared/models';

import { ActionIcon, StateOption } from '../../models';
import { EntityConfigAdapter } from '../../models/entity-config-adapter.interface';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';

@Injectable({
  providedIn: 'root',
})
export class DeviceConfigProfilesDataAdapter
  implements
    EntityDataAdapter<DeviceConfigProfileListItem, DeviceConfigProfileResponse>,
    EntityConfigAdapter
{
  private readonly entitiesService = inject(EntitiesService);
  private readonly entityStateService = inject(EntityDetailsStateService);

  getList(
    params: DataGridParams,
  ): Observable<GridResponse<DeviceConfigProfileListItem>> {
    const { owner, limit, offset, sortField, filters, recurse } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.entitiesService.listDeviceConfigProfiles(
      owner,
      recurse,
      offset,
      limit,
      sortField as string,
      filters,
    );
  }

  getById(id: string): Observable<DeviceConfigProfileResponse> {
    return this.entitiesService.getDeviceConfigProfile(id);
  }

  getStateOptions(): StateOption[] {
    return [
      { label: 'Active', value: DeviceConfigProfileResponse.StateEnum.Active },
      {
        label: 'Deleted',
        value: DeviceConfigProfileResponse.StateEnum.Deleted,
      },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.entitiesService.updateDeviceConfigProfile(id, {
      state: state as DeviceConfigProfileResponse.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    return [
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change Device Config Profile State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
