import {
  AfterViewInit,
  Component,
  computed,
  effect,
  inject,
  TemplateRef,
  untracked,
  ViewChild,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';

import { InputTextModule } from 'primeng/inputtext';
import { ButtonModule } from 'primeng/button';
import { DataViewModule } from 'primeng/dataview';
import { PanelModule } from 'primeng/panel';
import { DividerModule } from 'primeng/divider';

import {
  DeviceBitmapConfiguration,
  DeviceCreateRequest,
  DeviceResponse,
  EntitiesService,
} from '../../../../../api';
import { UtilityService } from '../../../../../core/services/utility.service';
import {
  createTextField,
  createDropdownField,
  FormAction,
  createNumberField,
} from '../../../../../shared/models';
import {
  enumToSelectOptionValues,
  isDefined,
  isNullOrUndefined,
} from '../../../../../shared/utils';
import { DynamicFormModalComponent } from '../../../../../shared/components/forms/dynamic-form-modal/dynamic-form-modal.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { Observable } from 'rxjs';
import { DeviceEntity } from '../../../models';
import { EntitySectionBaseComponent } from '../../base';

@Component({
  selector: 'app-device-bitmap-section',
  imports: [
    CommonModule,
    ReactiveFormsModule,
    ButtonModule,
    InputTextModule,
    DynamicFormModalComponent,
    DataViewModule,
    PanelModule,
    DividerModule,
    GridComponent,
  ],
  templateUrl: './device-bitmap-section.component.html',
  styleUrl: './device-bitmap-section.component.scss',
})
export class DeviceBitmapSectionComponent
  extends EntitySectionBaseComponent<DeviceEntity>
  implements AfterViewInit
{
  @ViewChild('iconTemplate', { static: true })
  iconTemplate: TemplateRef<unknown> | undefined;
  readonly utilityService = inject(UtilityService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);
  readonly entitiesService = inject(EntitiesService);

  readonly deviceBitmaps = computed(() => {
    const deviceBitmaps = this.entity()?.parameters?.bitmaps;
    if (!isDefined(deviceBitmaps)) return [];

    return [
      {
        key: 'Inputs',
        value:
          deviceBitmaps?.length > 0
            ? deviceBitmaps.map((bitmap) => bitmap.target_input).join(', ')
            : 'None',
      },
    ];
  });
  override readonly actions: FormAction[] = [
    {
      label: 'Cancel',
      action: () => {
        this.showModal = false;
      },
      severity: 'secondary',
    },
    {
      label: 'Ok',
      action: (formGroup: FormGroup) => {
        if (formGroup.valid) {
          // Convert form values to a bitmap configuration
          const newBitmap: DeviceBitmapConfiguration = {
            target_input: formGroup.get('target_input')?.value,
            target_type: formGroup.get('target_type')?.value,
            source_input: formGroup.get('source_input')?.value,
            bitshift: Number(formGroup.get('bitshift')?.value ?? 0),
            bitmask: parseInt(
              ((formGroup.get('bitmask')?.value as string) ?? '').startsWith(
                '0x',
              )
                ? formGroup.get('bitmask')?.value.slice(2)
                : formGroup.get('bitmask')?.value,
              16,
            ),
          };

          // Add to current bitmaps list
          this.bitmaps = [...this.bitmaps, newBitmap];

          // Close the modal
          this.showModal = false;
        }
      },
      severity: 'primary',
      disabled: (form?: FormGroup) => {
        if (isNullOrUndefined(form)) return true;
        return form.pristine || form.invalid;
      },
    },
  ];
  bitmaps: DeviceBitmapConfiguration[] = [];
  showModal = false;
  bitmapTypes = [
    'owner',
    'emergency',
    'administrative',
    'technical',
    'operational',
    'management',
  ];

  constructor() {
    super();

    // React to entity changes
    effect(() => {
      const deviceBitmaps = this.entity()?.parameters?.bitmaps;
      if (deviceBitmaps) {
        this.bitmaps = [...deviceBitmaps];
        untracked(() => {
          this.updateFormFields();
        });
      }
    });
  }

  override ngAfterViewInit(): void {
    super.ngAfterViewInit();
    this.columns = [
      {
        field: 'key',
        header: '',
        transform: 'uppercase',
        cellTemplate: this.iconTemplate,
      },
      { field: 'value', header: '' },
    ];
  }

  protected override updateFormFields(): void {
    this.formFields = [
      createTextField('target_input', 'Target Input', '', {
        name: 'target_input',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Target Input is required',
          },
        ],
      }),
      createDropdownField(
        'target_type',
        'Target Type',
        enumToSelectOptionValues(DeviceBitmapConfiguration.TargetTypeEnum),
        '',
        {
          name: 'target_type',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Target Type is required',
            },
          ],
          props: {
            appendTo: 'body',
          },
        },
      ),
      createTextField('source_input', 'Source Input', '', {
        name: 'source_input',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Source Input is required',
          },
        ],
      }),
      createNumberField('bitshift', 'Bitshift', 0, {
        name: 'bitshift',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Bitshift is required',
          },
        ],
        hint: 'Number of bits the source input value will be shifted to the right',
      }),
      createTextField('bitmask', 'Bitmask', '0x1', {
        name: 'bitmask',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Bitmask is required',
          },
        ],
        hint: 'Bitmask to be applied to the value after shifting, in hex format (i.e. 0x01)',
      }),
    ];
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<DeviceEntity>,
  ) => Observable<DeviceResponse> {
    return (
      this.operationRegistry.getOperation<
        DeviceEntity,
        DeviceCreateRequest,
        DeviceResponse,
        'update'
      >('device', 'update') ??
      ((id, data) => this.entitiesService.updateDevice(id, data))
    );
  }

  onClose() {
    this.showModal = false;
  }

  getBitMapDescription(bitmap: DeviceBitmapConfiguration): string {
    return `(${bitmap.source_input} >> ${(bitmap.bitshift || 0).toString()}) & 0x${(bitmap.bitmask || 1).toString(16)}`;
  }

  addBitmap(): void {
    this.updateFormFields();
    this.showModal = true;
  }

  removeBitmap(index: number): void {
    this.bitmaps = this.bitmaps.filter((_, i) => i !== index);
  }
}
