import {
  Component,
  computed,
  effect,
  inject,
  input,
  OnInit,
} from '@angular/core';
import { DeviceTypeResponse, DeviceTypesService } from '../../../../../../api';
import { EntitySectionBaseComponent } from '../../../base';
import { isNullOrUndefined, isDefined } from '../../../../../../shared/utils';
import { EntityOperationRegistryService } from '../../../../services/entity/entity-operation-registry.service';
import { tap } from 'rxjs';
import { Validators } from '@angular/forms';
import {
  createTextField,
  createToggleSwitchField,
} from '../../../../../../shared/models';
import { GridComponent } from '../../../../../../shared/components/grid/grid.component';
import { DynamicFormComponent } from '../../../../../../shared/components/forms/dynamic-form/dynamic-form.component';

@Component({
  selector: 'app-device-types-setting',
  imports: [GridComponent, DynamicFormComponent],
  templateUrl: './device-types-setting.component.html',
  styleUrl: './device-types-setting.component.scss',
})
export class DeviceTypesSettingComponent
  extends EntitySectionBaseComponent<DeviceTypeResponse>
  implements OnInit
{
  constructor() {
    super();

    // Grid columns for view mode
    this.columns = [
      { field: 'key', header: 'Property' },
      { field: 'value', header: 'Value' },
    ];

    effect(() => {
      const entity = this.entity();
      if (entity && !this.createMode()) {
        this.updateFormFields();
      }
    });
  }

  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly deviceTypesService = inject(DeviceTypesService);

  override entity = input<DeviceTypeResponse | null>(null);

  // Configuration inputs
  fieldId = input.required<string>(); // The field id (e.g., 'loggingTime')
  displayTitle = input.required<string>(); // Display name (e.g., 'Logging Time')

  // Get the parsed settings definition
  readonly parsedDefinition = computed(() => {
    const entity = this.entity();
    const definition = entity?.settingsDefinition;
    if (isNullOrUndefined(definition) || definition.trim() === '') return null;

    try {
      const parsed = JSON.parse(definition);
      return parsed;
    } catch {
      return null;
    }
  });

  // Get the specific field data
  readonly fieldData = computed(() => {
    const parsed = this.parsedDefinition();
    const fieldId = this.fieldId();

    if (isNullOrUndefined(parsed) || !fieldId) return null;

    // Navigate through the structure to find the field
    if (isDefined(parsed['groups']) && Array.isArray(parsed['groups'])) {
      for (const group of parsed['groups']) {
        if (isDefined(group['fields']) && Array.isArray(group['fields'])) {
          const field = group['fields'].find(
            (f: Record<string, unknown>) => f['id'] === fieldId,
          );
          if (isDefined(field)) return field;
        }
      }
    }

    return null;
  });

  // Display field details in a simple grid format
  readonly fieldDetails = computed(() => {
    const field = this.fieldData();
    if (isNullOrUndefined(field)) return [];

    const details = [
      { key: 'ID', value: field['id'] ?? 'Unknown' },
      { key: 'Title', value: field['title'] ?? 'Untitled Field' },
      { key: 'Type', value: field['type'] ?? 'text' },
    ];

    if (
      isDefined(field['unit']) &&
      typeof field['unit'] === 'string' &&
      field['unit'].trim() !== ''
    ) {
      details.push({ key: 'Unit', value: field['unit'] });
    }

    if (isDefined(field['min'])) {
      details.push({ key: 'Minimum Value', value: field['min'].toString() });
    }

    if (isDefined(field['max'])) {
      details.push({ key: 'Maximum Value', value: field['max'].toString() });
    }

    if (isDefined(field['width'])) {
      details.push({ key: 'Width', value: field['width'].toString() });
    }

    if (isDefined(field['defaultValue'])) {
      details.push({
        key: 'Default Value',
        value: field['defaultValue'].toString(),
      });
    }

    if (
      isDefined(field['placeholder']) &&
      typeof field['placeholder'] === 'string' &&
      field['placeholder'].trim() !== ''
    ) {
      details.push({ key: 'Placeholder', value: field['placeholder'] });
    }

    if (
      isDefined(field['description']) &&
      typeof field['description'] === 'string' &&
      field['description'].trim() !== ''
    ) {
      details.push({ key: 'Description', value: field['description'] });
    }

    if (isDefined(field['required'])) {
      details.push({
        key: 'Required',
        value: field['required'] === true ? 'Yes' : 'No',
      });
    }

    return details;
  });

  readonly hasData = computed(() => {
    return this.fieldData() !== null;
  });

  protected override updateFormFields(): void {
    const field = this.fieldData();
    if (isNullOrUndefined(field)) {
      this.formFields = [];
      return;
    }

    // Dynamically create form fields based on what's displayed in the grid
    this.formFields = [];

    if (isDefined(field['title'])) {
      this.formFields.push(
        createTextField('title', 'Title', field['title']?.toString() ?? '', {
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Title is required',
            },
          ],
        }),
      );
    }

    if (isDefined(field['type'])) {
      this.formFields.push(
        createTextField('type', 'Type', field['type']?.toString() ?? '', {
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Type is required',
            },
          ],
        }),
      );
    }

    if (isDefined(field['unit'])) {
      this.formFields.push(
        createTextField('unit', 'Unit', field['unit']?.toString() ?? ''),
      );
    }

    if (isDefined(field['min'])) {
      this.formFields.push(
        createTextField(
          'min',
          'Minimum Value',
          field['min']?.toString() ?? '',
          {
            validations: [
              {
                name: 'pattern',
                validator: Validators.pattern(/^-?\d+(\.\d+)?$/),
                message: 'Minimum value must be a valid number',
              },
            ],
          },
        ),
      );
    }

    if (isDefined(field['max'])) {
      this.formFields.push(
        createTextField(
          'max',
          'Maximum Value',
          field['max']?.toString() ?? '',
          {
            validations: [
              {
                name: 'pattern',
                validator: Validators.pattern(/^-?\d+(\.\d+)?$/),
                message: 'Maximum value must be a valid number',
              },
            ],
          },
        ),
      );
    }

    if (isDefined(field['width'])) {
      this.formFields.push(
        createTextField('width', 'Width', field['width']?.toString() ?? '', {
          validations: [
            {
              name: 'pattern',
              validator: Validators.pattern(/^\d+$/),
              message: 'Width must be a positive integer',
            },
          ],
        }),
      );
    }

    if (isDefined(field['defaultValue'])) {
      this.formFields.push(
        createTextField(
          'defaultValue',
          'Default Value',
          field['defaultValue']?.toString() ?? '',
        ),
      );
    }

    if (isDefined(field['placeholder'])) {
      this.formFields.push(
        createTextField(
          'placeholder',
          'Placeholder',
          field['placeholder']?.toString() ?? '',
        ),
      );
    }

    if (isDefined(field['description'])) {
      this.formFields.push(
        createTextField(
          'description',
          'Description',
          field['description']?.toString() ?? '',
        ),
      );
    }

    if (isDefined(field['required'])) {
      this.formFields.push(
        createToggleSwitchField(
          'required',
          'Required',
          field['required'] === true,
        ),
      );
    }
  }

  protected override transformFormData(
    formData: Record<string, unknown>,
  ): Partial<DeviceTypeResponse> {
    const entity = this.entity();
    const fieldId = this.fieldId();

    if (isNullOrUndefined(entity)) {
      return {};
    }
    const parsedDefinition = this.parseSettingsDefinition(entity);
    if (isNullOrUndefined(parsedDefinition)) {
      return {};
    }

    this.updateFieldInDefinition(parsedDefinition, fieldId, formData);

    return {
      settingsDefinition: JSON.stringify(this.parseSettingsDefinition(entity)),
    };
  }

  private parseSettingsDefinition(
    entity: DeviceTypeResponse,
  ): Record<string, unknown> | null {
    try {
      const settingsDefinition = entity.settingsDefinition;
      if (
        isNullOrUndefined(settingsDefinition) ||
        settingsDefinition.trim() === ''
      ) {
        return {};
      }
      return JSON.parse(settingsDefinition);
    } catch {
      console.error('Failed to parse settings definition');
      return null;
    }
  }

  private updateFieldInDefinition(
    parsedDefinition: Record<string, unknown>,
    fieldId: string,
    formData: Record<string, unknown>,
  ): boolean {
    if (!this.hasValidGroups(parsedDefinition)) {
      return false;
    }

    const groups = parsedDefinition['groups'] as Record<string, unknown>[];

    for (const group of groups) {
      if (this.updateFieldInGroup(group, fieldId, formData)) {
        return true;
      }
    }

    return false;
  }

  private hasValidGroups(parsedDefinition: Record<string, unknown>): boolean {
    return (
      isDefined(parsedDefinition['groups']) &&
      Array.isArray(parsedDefinition['groups'])
    );
  }

  private updateFieldInGroup(
    group: Record<string, unknown>,
    fieldId: string,
    formData: Record<string, unknown>,
  ): boolean {
    if (!this.isValidGroup(group)) {
      return false;
    }

    const fields = group['fields'] as Record<string, unknown>[];
    const fieldIndex = fields.findIndex(
      (f: Record<string, unknown>) => f['id'] === fieldId,
    );

    if (fieldIndex === -1) {
      return false;
    }

    const updatedField = this.applyFormDataToField(
      fields[fieldIndex],
      formData,
    );
    fields[fieldIndex] = updatedField;
    return true;
  }

  private isValidGroup(group: Record<string, unknown>): boolean {
    return (
      isDefined(group) &&
      typeof group === 'object' &&
      isDefined(group['fields']) &&
      Array.isArray(group['fields'])
    );
  }

  private applyFormDataToField(
    originalField: Record<string, unknown>,
    formData: Record<string, unknown>,
  ): Record<string, unknown> {
    const updatedField = { ...originalField };

    for (const [key, value] of Object.entries(formData)) {
      if (key !== 'id') {
        updatedField[key] = this.convertFormValue(key, value);
      }
    }

    return updatedField;
  }

  private convertFormValue(key: string, value: unknown): unknown {
    if (this.isNumericField(key)) {
      const numValue = Number(value);
      return Number.isNaN(numValue) ? value : numValue;
    }

    if (key === 'required') {
      return value === 'true' || value === true;
    }

    return value;
  }

  private isNumericField(key: string): boolean {
    return ['min', 'max', 'width', 'defaultValue'].includes(key);
  }

  protected override getUpdateMethod() {
    return (
      this.operationRegistry.getOperation('device-types', 'update') ??
      ((id, data) =>
        this.deviceTypesService.updateDeviceType(id, data).pipe(
          tap((response) => {
            this.entityChange.emit(response);
            this.broadcastService.broadcast('entity-updated', {
              entityType: 'device-types',
              operation: 'update',
              entityId: response.id,
              data: response,
            });
          }),
        ))
    );
  }

  ngOnInit(): void {
    this.updateFormFields();
  }
}
