import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import {
  RoadProfileListItem,
  RoadProfileResponse,
  EntitiesService,
} from '../../../../api';
import {
  DataGridParams,
  EntityDataAdapter,
  GridResponse,
} from '../../../../shared/models';

@Injectable({
  providedIn: 'root',
})
export class RoadProfilesDataAdapter
  implements EntityDataAdapter<RoadProfileListItem, RoadProfileResponse>
{
  private readonly entitiesService = inject(EntitiesService);

  getList(
    params: DataGridParams,
  ): Observable<GridResponse<RoadProfileListItem>> {
    const { owner, limit, offset, sortField, filters, recurse } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.entitiesService.listRoadProfiles(
      owner,
      recurse,
      offset,
      limit,
      sortField as string,
      filters,
    );
  }

  getById(id: string): Observable<RoadProfileResponse> {
    return this.entitiesService.getRoadProfile(id);
  }
}
