import { CommonModule } from '@angular/common';
import {
  Component,
  computed,
  effect,
  inject,
  input,
  OnInit,
  signal,
  untracked,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

import { Validators } from '@angular/forms';
import { ButtonModule } from 'primeng/button';
import { MessageModule } from 'primeng/message';
import { v4 as uuidv4 } from 'uuid';

import {
  GeoLockProfileListItem,
  GeoLockProfileCreateRequest,
  EntitiesService,
  GeoLockTimeItem,
} from '../../../../../api';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { ModalListFormComponent } from '../../../../../shared/components/forms/modal-list-form/modal-list-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import {
  ListFieldModalConfig,
  FormFieldConfig,
  createTextField,
  createNumberField,
  createModalListField,
  MeasurementUnitType,
  createDateField,
  createDropdownField,
  createListboxField,
} from '../../../../../shared/models';
import {
  isDefined,
  isEmpty,
  isNullOrUndefined,
} from '../../../../../shared/utils';
import { EntitySectionBaseComponent } from '../../base';
import { MeasurementUnitsService } from '../../../../../shared/services/measurement-units/measurement-units.service';
import { HttpClient } from '@angular/common/http';
import { AuthDataService } from '../../../../../core/services/auth-data.service';
import { WEEKDAY_OPTIONS } from '../../../models';

type FormDataType = Record<string, string | number | object | null | undefined>;

@Component({
  selector: 'app-geo-lock-profile-details-section',
  imports: [
    CommonModule,
    ButtonModule,
    GridComponent,
    DynamicFormComponent,
    MessageModule,
  ],
  templateUrl: './geo-lock-profile-details-section.component.html',
  styleUrl: './geo-lock-profile-details-section.component.scss',
})
export class GeoLockProfileDetailsSectionComponent
  extends EntitySectionBaseComponent<
    GeoLockProfileListItem | Partial<GeoLockProfileCreateRequest>
  >
  implements OnInit
{
  readonly ownerId = input<string | null>(null);
  private readonly entitiesService = inject(EntitiesService);
  private readonly measurementUnitsService = inject(MeasurementUnitsService);
  private readonly http = inject(HttpClient);
  private readonly authDataService = inject(AuthDataService);

  readonly geoLockProfileDetails = computed(() => {
    const geoLockProfileDetails = this.entity();
    if (!isDefined(geoLockProfileDetails)) return [];

    return [
      { key: 'Name', value: geoLockProfileDetails.name },
      {
        key: 'Radius',
        value: isDefined(geoLockProfileDetails.radiusKm)
          ? `${this.measurementUnitsService.fromBackend(
              MeasurementUnitType.DISTANCE,
              geoLockProfileDetails.radiusKm,
              2,
            )} ${this.measurementUnitsService.getUnitConfig(
              MeasurementUnitType.DISTANCE,
            )}`
          : 'None',
      },
      {
        key: 'Times',
        value: isEmpty(geoLockProfileDetails.times)
          ? 'None'
          : (geoLockProfileDetails.times ?? [])
              .map((time) => {
                const listItem = this.stateItemToListItem(time);
                return `${listItem?.title} - (${listItem?.display})`;
              })
              .join('\n'),
      },
    ];
  });
  geoLockProfileItemModalConfig:
    | ListFieldModalConfig<GeoLockTimeItem>
    | undefined;
  protected readonly warning = signal<string | null>(null);
  timezones = signal<{ label: string; value: string }[]>([]);

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '', transform: 'whitespace-pre-line' },
    ];

    effect(() => {
      const geoLockProfileDetails = this.entity();
      if (
        isNullOrUndefined(geoLockProfileDetails?.times) ||
        geoLockProfileDetails.times.length < 1
      ) {
        this.warning.set(
          'At least one time entry is required for the geo lock profile to be useful.',
        );
      } else {
        this.warning.set(null);
      }
    });

    effect(() => {
      const entity = this.entity();
      this.timezones();
      if (isDefined(entity) && !this.createMode()) {
        untracked(() => {
          this.updateFormFields();
        });
      }
    });
  }

  ngOnInit(): void {
    this.loadTimezones();
  }

  private loadTimezones(): void {
    this.http
      .get<{ id: string; name: string }[]>('/assets/docs/timezones.json')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((data) => {
        this.timezones.set(
          data
            .map((tz) => ({ label: tz.id, value: tz.id }))
            .sort((a, b) => a.label.localeCompare(b.label)),
        );
      });
  }

  protected override updateFormFields(): void {
    const geoLockProfile = this.entity();

    this.geoLockProfileItemModalConfig = {
      modalComponent: ModalListFormComponent,
      modalTitle: 'Geo lock Times',
      defaultItemProvider: () =>
        ({
          id: uuidv4(),
          from: '00:00',
          to: '23:59',
          timeZoneId: this.authDataService.getUserProfile()?.timeZoneId ?? '',
          days: ['0', '1', '2', '3', '4', '5', '6'],
        }) as unknown as GeoLockTimeItem,
      listItemFormatter: (item: GeoLockTimeItem) =>
        this.stateItemToListItem(item),
      modalFields: (item: GeoLockTimeItem | undefined): FormFieldConfig[] => {
        return [
          createTextField('id', 'ID', item?.id, {
            name: 'id',
            hidden: true,
          }),
          createDateField('from', 'From', item?.from ?? '00:00', {
            name: 'from',
            placeholder: 'Select time',
            props: {
              timeOnly: true,
              showSeconds: true,
            },
            style: { width: '100%' },
          }),
          createDateField('to', 'To', item?.to ?? '23:59', {
            name: 'to',
            placeholder: 'Select time',
            props: {
              timeOnly: true,
              showSeconds: true,
            },
            style: { width: '100%' },
          }),
          createDropdownField('timeZoneId', 'Time Zone', this.timezones(), '', {
            layout: 'horizontal',
            labelWidth: '1/3',
            placeholder: 'Select timezone',
            name: 'timeZone',
            props: {
              appendTo: 'body',
            },
          }),
          createListboxField('days', 'Days', [], WEEKDAY_OPTIONS, {
            name: 'days',
            validations: [
              {
                name: 'required',
                validator: Validators.required,
                message: 'Weekdays is required',
              },
            ],
            props: {
              checkmark: true,
              multiple: true,
            },
          }),
        ];
      },
      subHeader:
        'Configure the days of the week and times in which the lock is active.',
    };
    this.formFields = [
      createTextField('name', 'Name', geoLockProfile?.name, {
        name: 'name',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Name is required',
          },
        ],
      }),
      createNumberField(
        'radiusKm',
        'Radius',
        isDefined(geoLockProfile?.radiusKm)
          ? this.measurementUnitsService.fromBackend(
              MeasurementUnitType.DISTANCE,
              geoLockProfile?.radiusKm ?? 0,
              2,
            )
          : undefined,
        {
          name: 'radiusKm',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Radius is required',
            },
          ],
          props: {
            suffix: this.measurementUnitsService.getUnitConfig(
              MeasurementUnitType.DISTANCE,
            ),
          },
        },
      ),
    ];

    if (!this.createMode()) {
      this.formFields.push(
        createModalListField<GeoLockTimeItem>(
          'times',
          'Geo Lock Times',
          geoLockProfile?.times ?? [],
          {
            layout: 'horizontal',
            labelWidth: '1/3',
            name: 'times',
            props: {
              modalConfig: this.geoLockProfileItemModalConfig,
            },
          },
        ) as FormFieldConfig<unknown>,
      );
    }
  }

  protected override submitChanges(
    formData: Record<string, string | number>,
  ): void {
    const geoLockProfile = this.entity();
    if (isNullOrUndefined(geoLockProfile)) return;
    const transformedData = this.transformFormData(formData);
    this.entitiesService
      .updateGeoLockProfile(geoLockProfile.id, transformedData)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe({
        next: (response) => {
          this.isFormEditable.set(false);
          this.isSubmitting.set(false);
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'geo-lock-profile',
            operation: 'update',
            entityId: geoLockProfile.id,
            data: response,
          });
        },
        error: () => {
          this.isFormEditable.set(true);
          this.isSubmitting.set(false);
        },
      });
  }

  stateItemToListItem(item: GeoLockTimeItem) {
    return {
      id: item.id,
      title: item.from && item.to ? `${item.from} to ${item.to}` : '',
      display: isDefined(item.days)
        ? item.days
            ?.map(
              (day) =>
                WEEKDAY_OPTIONS.find((option) => option.value === String(day))
                  ?.label ?? day,
            )
            .join(', ')
        : '',
      icon: `clock`,
    };
  }

  protected override onFormValueChange(formData: FormDataType): void {
    const transformedData = this.transformFormData(formData);

    this.formValueChange.emit(transformedData);
  }

  protected override transformFormData(formData: FormDataType): FormDataType {
    const transformedData = { ...formData };

    if (Array.isArray(transformedData['times'])) {
      transformedData['times'] = (
        transformedData['times'] as GeoLockTimeItem[]
      ).map((item) => ({
        id: item.id,
        from:
          item.from && typeof item.from === 'string'
            ? item.from.split(':').length === 2
              ? `${item.from}:00`
              : item.from
            : item.from,
        to:
          item.to && typeof item.to === 'string'
            ? item.to.split(':').length === 2
              ? `${item.to}:00`
              : item.to
            : item.to,
        timeZoneId: item.timeZoneId,
        days: Array.isArray(item.days) ? item.days.map(Number) : item.days,
      }));
    }

    if (isDefined(formData['radiusKm']) && formData['radiusKm'] !== '') {
      transformedData['radiusKm'] = this.measurementUnitsService.toBackend(
        MeasurementUnitType.DISTANCE,
        Number(formData['radiusKm']),
      );
    }

    return transformedData;
  }
}
