import {
  AfterViewInit,
  Component,
  computed,
  effect,
  inject,
  signal,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';

import { ButtonModule } from 'primeng/button';
import { DataViewModule } from 'primeng/dataview';

import { AssetResponse, Contact, EntitiesService } from '../../../../../api';
import { UtilityService } from '../../../../../core/services/utility.service';
import {
  createTextField,
  createDropdownField,
  FormAction,
} from '../../../../../shared/models';
import {
  isDefined,
  isNullOrUndefined,
  mobileValidator,
  phoneValidator,
} from '../../../../../shared/utils';
import { EntitySectionBaseComponent } from '../../base';
import { DynamicFormModalComponent } from '../../../../../shared/components/forms/dynamic-form-modal/dynamic-form-modal.component';
import { PanelModule } from 'primeng/panel';
import { DividerModule } from 'primeng/divider';

@Component({
  selector: 'app-asset-contacts-section',
  imports: [
    CommonModule,
    ReactiveFormsModule,
    ButtonModule,
    DynamicFormModalComponent,
    DataViewModule,
    PanelModule,
    DividerModule,
  ],
  templateUrl: './asset-contacts-section.component.html',
  styleUrl: './asset-contacts-section.component.scss',
})
export class AssetContactsSectionComponent
  extends EntitySectionBaseComponent<AssetResponse>
  implements AfterViewInit
{
  @ViewChild('iconTemplate', { static: true })
  iconTemplate: TemplateRef<unknown> | undefined;
  private readonly entitiesService = inject(EntitiesService);
  readonly utilityService = inject(UtilityService);
  protected readonly selectOptionsLoaded = signal<boolean>(false);

  readonly assetContacts = computed(() => {
    const assetContacts = this.entity()?.contacts;
    if (!isDefined(assetContacts)) return [];

    return assetContacts.flatMap((contact) => {
      return [
        { key: contact.type, value: contact.name },
        {
          key: 'mobile',
          value: contact.phoneMobile,
          icon: 'pi pi-mobile',
        },
      ];
    });
  });

  // State management
  contacts = signal<Contact[]>([]);
  private originalContacts: Contact[] = []; // Backup for reverting changes - will be reassigned in onEnterEditMode
  showModal = signal<boolean>(false);
  editingContact = signal<Contact | null>(null);
  editingContactIndex = signal<number>(-1);
  modalHeader = computed(() =>
    this.editingContact() ? 'Edit Contact' : 'Add Contact',
  );

  contactTypes = [
    'owner',
    'emergency',
    'administrative',
    'technical',
    'operational',
    'management',
  ];

  // Modal form actions
  override readonly actions: FormAction[] = [
    {
      label: 'Cancel',
      action: () => {
        this.closeModal();
      },
      severity: 'secondary',
    },
    {
      label: 'OK',
      action: (formData: FormGroup) => {
        this.saveContact(formData);
      },
      severity: 'primary',
      disabled: (form?: FormGroup) => {
        if (isNullOrUndefined(form)) return true;
        return form.invalid;
      },
    },
  ];

  constructor() {
    super();

    // React to entity changes
    effect(() => {
      const assetContacts = this.entity()?.contacts;
      if (assetContacts) {
        this.contacts.set([...assetContacts]);
        this.updateFormFields();
      }
    });
  }

  /**
   * Override onEnterEditMode to backup current contacts
   */
  protected override onEnterEditMode(): void {
    super.onEnterEditMode();
    // Backup contacts when entering edit mode
    this.originalContacts = [...this.contacts()];
  }

  /**
   * Override cancelEdit to restore original contacts
   */
  protected override cancelEdit(): void {
    // Restore original contacts
    this.contacts.set([...this.originalContacts]);
    super.cancelEdit();
  }

  override ngAfterViewInit(): void {
    super.ngAfterViewInit();
    this.columns = [
      {
        field: 'key',
        header: '',
        transform: 'uppercase',
        cellTemplate: this.iconTemplate,
      },
      { field: 'value', header: '' },
    ];
  }

  protected override updateFormFields(): void {
    const editingContact = this.editingContact();

    this.formFields = [
      createTextField('id', 'ID', editingContact?.id ?? '', {
        name: 'id',
        hidden: true,
      }),
      createTextField('name', 'Name', editingContact?.name ?? '', {
        name: 'name',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Name is required',
          },
        ],
      }),
      createDropdownField(
        'type',
        'Type',
        this.contactTypes.map((type) => ({
          label: this.utilityService.toTitleCase(type),
          value: type,
        })),
        editingContact?.type ?? 'owner',
        {
          name: 'type',
          props: {
            appendTo: 'body',
          },
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Type is required',
            },
          ],
        },
      ),
      createTextField(
        'emailAddress',
        'Email',
        editingContact?.emailAddress ?? '',
        {
          name: 'emailAddress',
          validations: [
            {
              name: 'email',
              validator: Validators.email,
              message: 'Invalid email format',
            },
          ],
        },
      ),
      createTextField(
        'phoneMobile',
        'Mobile',
        editingContact?.phoneMobile ?? '',
        {
          name: 'phoneMobile',
          validations: [
            {
              name: 'mobile',
              validator: mobileValidator(),
              message: 'Invalid mobile number (10-15 digits required)',
            },
          ],
        },
      ),
      createTextField(
        'phoneOffice',
        'Phone',
        editingContact?.phoneOffice ?? '',
        {
          name: 'phoneOffice',
          validations: [
            {
              name: 'phone',
              validator: phoneValidator(),
              message: 'Invalid phone number (7-15 digits required)',
            },
          ],
        },
      ),
      createTextField('notes', 'Notes', editingContact?.notes ?? '', {
        name: 'notes',
      }),
    ];
  }

  /**
   * Open modal to add a new contact
   */
  addContact(): void {
    this.editingContact.set(null);
    this.editingContactIndex.set(-1);
    this.updateFormFields();
    this.showModal.set(true);
  }

  /**
   * Open modal to edit an existing contact
   */
  editContact(contact: Contact, index: number): void {
    this.editingContact.set({ ...contact });
    this.editingContactIndex.set(index);
    this.updateFormFields();
    this.showModal.set(true);
  }

  /**
   * Delete a contact
   */
  deleteContact(index: number): void {
    const currentContacts = [...this.contacts()];
    currentContacts.splice(index, 1);
    this.contacts.set(currentContacts);
    this.markFormAsDirty();
  }

  /**
   * Save contact (add or edit)
   */
  saveContact(formGroup: FormGroup): void {
    if (formGroup.invalid) return;

    const formValue = formGroup.value as Contact;
    const currentContacts = [...this.contacts()];
    const index = this.editingContactIndex();

    if (index >= 0) {
      // Edit existing contact
      currentContacts[index] = formValue;
    } else {
      // Add new contact - generate ID if not present
      if (!formValue.id) {
        formValue.id = crypto.randomUUID();
      }
      currentContacts.push(formValue);
    }

    this.contacts.set(currentContacts);
    this.markFormAsDirty();
    this.closeModal();
  }

  /**
   * Close the modal
   */
  closeModal(): void {
    this.showModal.set(false);
    this.editingContact.set(null);
    this.editingContactIndex.set(-1);
  }

  /**
   * Mark the parent form as dirty to enable save button
   */
  private markFormAsDirty(): void {
    this.hasFormChanges = true;
  }

  /**
   * Show success message
   */
  protected override showSuccessMessage(message: string): void {
    this.messageService.add({
      severity: 'success',
      summary: 'Success',
      detail: message,
      life: 3000,
    });
  }

  /**
   * Show error message
   */
  protected override showErrorMessage(error: unknown): void {
    const errorMessage =
      error instanceof Error ? error.message : 'An error occurred';
    this.messageService.add({
      severity: 'error',
      summary: 'Error',
      detail: errorMessage,
      life: 5000,
    });
  }

  protected override submitChanges(
    _formData: Record<string, string | number>,
  ): void {
    const asset = this.entity();
    if (isNullOrUndefined(asset)) return;

    // Prepare the payload with contacts
    const payload = {
      contacts: this.contacts(),
    };

    this.isSubmitting.set(true);
    this.entitiesService
      .updateAsset(asset.id, payload)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe({
        next: () => {
          this.isFormEditable.set(false);
          this.isSubmitting.set(false);
          this.clearUnsavedChanges();

          // Update the original contacts backup after successful save
          this.originalContacts = [...this.contacts()];

          this.showSuccessMessage('Contacts updated successfully');
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'asset',
            entityId: asset.id,
          });
        },
        error: (error) => {
          this.isFormEditable.set(true);
          this.isSubmitting.set(false);
          this.showErrorMessage(error);
        },
      });
  }

  onClose(): void {
    this.closeModal();
  }
}
