import {
  Component,
  effect,
  inject,
  input,
  OnInit,
  ChangeDetectorRef,
} from '@angular/core';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { map, Observable } from 'rxjs';
import {
  EntitiesService,
  UserResponse,
  UserListItem,
  AccountsService,
} from '../../../../../api';
import {
  BaseEntity,
  createDropdownField,
  FormAction,
} from '../../../../../shared/models';
import {
  isDefined,
  isEmpty,
  isNullOrUndefined,
} from '../../../../../shared/utils';
import { EntitySectionBaseComponent } from '../../base';
import { UtilityService } from '../../../../../core/services/utility.service';
import { TableRowSelectEvent } from 'primeng/table';
import { DynamicFormModalComponent } from '../../../../../shared/components/forms/dynamic-form-modal/dynamic-form-modal.component';
import { FormGroup } from '@angular/forms';
import { TooltipModule } from 'primeng/tooltip';
import { DEFAULT_LIST_SIZE } from '../../../../../core/models';
import { RequestCacheService } from '../../../../../shared/services/request-cache.service';

@Component({
  selector: 'app-user-list-section',
  imports: [GridComponent, DynamicFormModalComponent, TooltipModule],
  templateUrl: './user-list-section.component.html',
  styleUrl: './user-list-section.component.scss',
})
export class UserListSectionComponent
  extends EntitySectionBaseComponent<BaseEntity>
  implements OnInit
{
  override readonly entity = input<BaseEntity | null>(null);
  readonly groupId = input<string | null>(null);
  readonly groupingEntityType = input<string>('Group');
  readonly userLinkField = input.required<string>();
  readonly getGroupingEntitiesFn =
    input.required<
      (ownerId: string) => Promise<{ id: string; name: string }[]>
    >();
  readonly getAvailableUsersFn =
    input.required<
      (
        ownerId: string,
        currentEntityId?: string,
      ) => Promise<{ id: string; name: string }[]>
    >();

  readonly entitiesService = inject(EntitiesService);
  readonly accountsService = inject(AccountsService);
  private readonly utilityService = inject(UtilityService);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly cacheService = inject(RequestCacheService);

  private handleFormSubmit(form: FormGroup | undefined): void {
    switch (this.modalType) {
      case 'move':
        this.handleUserMove(form);
        break;
      case 'add':
        this.handleUserAdd(form);
        break;
    }
  }

  private handleUserMove(form: FormGroup | undefined): void {
    if (isNullOrUndefined(this.selectedUser) || isNullOrUndefined(form)) return;
    const currentUserLinkField = this.userLinkField();

    const userId = this.selectedUser.id;
    const selectedGroupId = form.get('group')?.value as string | undefined;

    if (isNullOrUndefined(selectedGroupId) || isEmpty(selectedGroupId)) return;

    const groupObject = this.groupingOptions.find(
      (g) => g.id === selectedGroupId,
    );

    if (groupObject) {
      const payload = { [currentUserLinkField]: groupObject };
      this.updateUser(userId, payload as Partial<UserResponse>);
    } else {
      console.error('Selected group not found in grouping options list.');
    }
  }

  private handleUserAdd(form: FormGroup | undefined): void {
    if (isNullOrUndefined(form)) return;
    const currentUserLinkField = this.userLinkField();

    const userIdToAdd = form.get('user')?.value as string | undefined;
    const currentEntity = this.entity();
    const currentEntityObject = currentEntity
      ? { id: currentEntity.id, name: currentEntity.name }
      : null;

    if (
      !currentEntityObject ||
      isNullOrUndefined(currentEntityObject.id) ||
      isEmpty(currentEntityObject.id) ||
      isNullOrUndefined(userIdToAdd) ||
      isEmpty(userIdToAdd)
    )
      return;

    const payload = { [currentUserLinkField]: currentEntityObject };
    this.updateUser(userIdToAdd, payload as Partial<UserResponse>);
  }

  override readonly actions: FormAction[] = [
    {
      label: 'Cancel',
      action: () => {
        this.onClose();
      },
      severity: 'secondary',
    },
    {
      label: 'Ok',
      action: (form) => {
        this.handleFormSubmit(form);
      },
      severity: 'primary',
      disabled: (form?: FormGroup) => isNullOrUndefined(form) || form.pristine,
    },
  ];
  userList: UserListItem[] = [];
  selectedUser: UserListItem | null = null;
  groupingOptions: { id: string; name: string }[] = [];
  availableUsersForLinking: { id: string; name: string }[] = [];

  modalType: 'move' | 'add' | null = null;
  get isModalVisible(): boolean {
    return this.modalType !== null;
  }
  get modalHeader(): string {
    switch (this.modalType) {
      case 'move':
        return 'Move To Different Cost Centre';
      case 'add':
        return 'Add User';
      default:
        return '';
    }
  }
  constructor() {
    super();

    effect(() => {
      const ownerId = this.entity()?.owner?.id;
      const currentGroupIdRqlPath: string | null = this.groupId();
      const currentEntityId = this.entity()?.id;

      if (
        isNullOrUndefined(ownerId) ||
        isNullOrUndefined(currentGroupIdRqlPath) ||
        isNullOrUndefined(currentEntityId)
      ) {
        this.userList = [];
        this.cdr.markForCheck();
        return;
      }
      const filters = {
        state: {
          value: 'deleted',
          operator: '!=',
        },
        [currentGroupIdRqlPath]: {
          value: currentEntityId,
          operator: '=',
        },
      };
      const rqlFilter = this.utilityService.constructRqlFilter(filters);
      return this.cacheService
        .getOrFetch(
          `users-${ownerId}-${rqlFilter}`,
          () => this.fetchUsers(ownerId, rqlFilter),
          3000,
        )
        .subscribe((users) => {
          this.userList = users;
          this.cdr.markForCheck();
        });
    });
  }

  ngOnInit(): void {
    const owner = this.entity()?.owner?.id;
    const currentEntityId = this.entity()?.id;

    this.columns = [
      {
        field: 'name',
        header: 'Name',
        filter: true,
        filterType: 'text',
        filterMatchMode: 'contains',
        transform: 'text-ellipsis',
      },
      {
        field: this.userLinkField(), // Placeholder, actual field on UserListItem might vary
        header: this.groupingEntityType() ?? 'Group', // Dynamic header
        valueGetter: (rowData: unknown) => {
          const linkField = this.userLinkField();
          const user = rowData as UserListItem;
          // Ensure user is an object and has the linkField property
          if (
            isDefined(user) &&
            typeof user === 'object' &&
            Object.hasOwn(user, linkField)
          ) {
            const linkedEntity = user[linkField] as { name?: string }; // Type assertion
            return linkedEntity?.name ?? '';
          }
          return '';
        },
        filter: true,
        filterType: 'multiselect',
        filterMatchMode: 'in',
        filterConfig: {
          type: 'multiselect',
          options: [], // These options could be dynamically populated based on groupingOptions
        },
      },
    ];

    if (typeof owner === 'string' && owner) {
      // Ensure owner is a non-empty string
      const groupEntitiesFn = this.getGroupingEntitiesFn();
      // groupEntitiesFn is a required input, so it should exist.
      groupEntitiesFn(owner)
        .then((groups) => {
          this.groupingOptions = groups;
          if (this.modalType === 'move') this.updateFormFields();
          this.cdr.markForCheck();
        })
        .catch((error) => {
          console.error(
            `Failed to load ${this.groupingEntityType()} groups:`,
            error,
          );
          this.groupingOptions = [];
          this.cdr.markForCheck();
        });

      const availableUsersFn = this.getAvailableUsersFn();
      // availableUsersFn is a required input, so it should exist.
      availableUsersFn(owner, currentEntityId)
        .then((users) => {
          this.availableUsersForLinking = users;
          if (this.modalType === 'add') this.updateFormFields();
          this.cdr.markForCheck();
        })
        .catch((error) => {
          console.error('Failed to load available users for linking:', error);
          this.availableUsersForLinking = [];
          this.cdr.markForCheck();
        });
    } else {
      console.warn(
        'Owner ID is not available or invalid, cannot fetch grouping entities or available users.',
      );
      this.groupingOptions = [];
      this.availableUsersForLinking = [];
      this.cdr.markForCheck();
    }
  }

  fetchUsers(
    ownerId: string,
    filter: string | undefined,
  ): Observable<UserListItem[]> {
    return this.accountsService
      .listUsers(ownerId, 0, DEFAULT_LIST_SIZE, 'name', filter)
      .pipe(map((response) => response.items));
  }

  protected override updateFormFields(): void {
    const groupDropdownOptions = (this.groupingOptions ?? []).map((g) => ({
      label: g.name,
      value: g.id,
    }));

    const userDropdownOptions = (this.availableUsersForLinking ?? []).map(
      (a) => ({
        label: a.name,
        value: a.id,
      }),
    );

    switch (this.modalType) {
      case 'move':
        this.formFields = [
          createDropdownField(
            'group',
            `${this.groupingEntityType()}`,
            groupDropdownOptions,
            '',
            {
              layout: 'horizontal',
              labelWidth: '1/3',
              name: 'group',
              props: {
                appendTo: 'body',
              },
            },
          ),
        ];
        break;
      case 'add':
        this.formFields = [
          createDropdownField('user', `User`, userDropdownOptions, '', {
            layout: 'horizontal',
            labelWidth: '1/3',
            name: 'user',
            props: {
              appendTo: 'body',
            },
          }),
        ];
        break;
      default:
        this.formFields = [];
        break;
    }
    this.cdr.markForCheck();
  }

  updateUser(id: string, data: Partial<UserResponse>) {
    this.accountsService.updateUser(id, data).subscribe({
      next: () => {
        this.broadcastService.broadcast('entity-updated', {
          id,
          changes: data,
          entityType: 'user',
          parentId: this.entity()?.id,
          groupingType: this.groupingEntityType(),
        });
        this.modalType = null;

        const ownerId = this.entity()?.owner?.id;
        const currentGroupIdRqlPath: string | null = this.groupId();
        const currentEntityId = this.entity()?.id;

        if (
          typeof ownerId === 'string' &&
          ownerId &&
          typeof currentGroupIdRqlPath === 'string' &&
          currentGroupIdRqlPath &&
          typeof currentEntityId === 'string' &&
          currentEntityId
        ) {
          const filters = {
            state: { value: 'deleted', operator: '!=' },
            [currentGroupIdRqlPath]: { value: currentEntityId, operator: '=' },
          };
          const rqlFilter = this.utilityService.constructRqlFilter(filters);
          this.fetchUsers(ownerId, rqlFilter).subscribe((users) => {
            this.userList = users;
            this.cdr.markForCheck();
          });
        }

        const availableUsersFn = this.getAvailableUsersFn();
        if (typeof ownerId === 'string' && ownerId) {
          // Check ownerId is a valid string
          availableUsersFn(ownerId, currentEntityId)
            .then((users) => {
              this.availableUsersForLinking = users;
              this.cdr.markForCheck();
            })
            .catch((error) => {
              console.error(
                'Error refreshing available users after update:',
                error,
              );
            });
        }
        this.cdr.markForCheck();
      },
      error: (err) => {
        console.error('Failed to update user:', err);
        // Potentially show a user-facing error message
      },
    });
  }

  confirmUserMove() {
    this.modalType = 'move';
    this.updateFormFields();
  }

  confirmUserAdd() {
    this.modalType = 'add';
    this.updateFormFields();
  }

  onClose() {
    this.modalType = null;
    this.selectedUser = null;
    this.updateFormFields(); // Reset form fields by rebuilding them
  }

  handleRowSelect(event: TableRowSelectEvent): void {
    if (isEmpty(event) || isEmpty(event.data)) return;

    this.selectedUser = event.data as UserListItem;
  }

  handleRowUnselect(): void {
    this.selectedUser = null;
  }
}
