import { inject, Injectable } from '@angular/core';

import {
  AccountsService,
  AssetCategoryResponse,
  EntitiesService,
} from '../../../api';
import { AdminDetailSection } from '../models';
import { AssetCategoryDetailsSectionComponent } from '../components/asset-categories/asset-category-details-section/asset-category-details-section.component';
import { FeedSectionComponent } from '../components/shared/feed-section/feed-section.component';
import { ContactsSectionComponent } from '../components/shared/contacts-section/contacts-section.component';
import { AssetListSectionComponent } from '../components/shared/asset-list-section/asset-list-section.component';
import { map, firstValueFrom } from 'rxjs';
import { AssetGroupingService } from '../../../shared/services/asset-grouping.service';
import { UtilityService } from '../../../core/services/utility.service';
import { RequestCacheService } from '../../../shared/services/request-cache.service';

@Injectable({ providedIn: 'root' })
export class AssetCategorySectionConfigFactory {
  private readonly assetGroupingService = inject(AssetGroupingService);
  private readonly entitiesService = inject(EntitiesService);
  private readonly accountsService = inject(AccountsService);
  private readonly utilityService = inject(UtilityService);
  private readonly cacheService = inject(RequestCacheService);

  /**
   * Create sections for asset category information
   * @param assetCategory The asset category object to display in the section
   * @returns An array of AdminDetailSection objects representing the asset category information sections
   */
  createAssetCategoryInformationSection(
    assetCategory: AssetCategoryResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'asset-category-details',
        title: 'Asset Category Details',
        component: AssetCategoryDetailsSectionComponent,
        componentInputs: { entity: assetCategory },
        category: 'information',
        order: 10,
      },
      {
        id: 'asset-category-contacts',
        title: 'Contacts',
        component: ContactsSectionComponent,
        componentInputs: {
          entity: assetCategory,
          entityType: 'asset-category',
        },
        category: 'information',
        order: 20,
      },
    ];
  }

  /**
   * Create sections for asset category changes
   * @param assetCategory The asset category object to display in the section
   * @returns An array of AdminDetailSection objects representing the asset category changes sections
   */
  createChangesSection(
    assetCategory: AssetCategoryResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'asset-category-changes',
        title: 'Changes',
        component: FeedSectionComponent,
        componentInputs: {
          entity: assetCategory,
        },
        category: 'feed',
        order: 10,
      },
    ];
  }

  /**
   * Create sections for asset category assets
   * @param assetCategory The asset category object to display in the section
   * @returns An array of AdminDetailSection objects representing the asset category assets sections
   */
  createAssetsSection(
    assetCategory: AssetCategoryResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'asset-category-assets',
        title: 'Assets',
        component: AssetListSectionComponent,
        componentInputs: {
          entity: assetCategory, // The current asset category object
          groupId: 'categories.id', // RQL path to filter assets for this asset category
          groupingEntityType: 'Asset category',
          assetLinkField: 'categories', // The field on AssetResponse linking to AssetCategoryResponse
          index: assetCategory?.index ?? 0, // Use the index from the asset category if available
          getGroupingEntitiesFn: (ownerId: string) =>
            this.assetGroupingService.getAssetCategoriesAsTree(ownerId),
          getAvailableAssetsFn: (ownerId: string, _currentEntityId?: string) =>
            this.getAvailableAssets(ownerId),
        },
        category: 'assets',
        order: 10,
      },
    ];
  }

  /**
   * Get available assets for the given owner
   * @param ownerId The owner ID to fetch assets for
   * @returns Promise of simplified asset objects
   */
  private getAvailableAssets(ownerId: string) {
    const filters = {
      state: {
        value: 'active',
        operator: '=',
      },
    };
    const rqlFilter = this.utilityService.constructRqlFilter(filters);
    return this.cacheService.getOrFetchAsync(
      `assets-list-${ownerId}-${rqlFilter}`,
      () =>
        firstValueFrom(
          this.entitiesService
            .listAssets(ownerId, 0, 100, 'name', rqlFilter)
            .pipe(
              map((res) =>
                res.items.map((asset) => ({
                  id: asset.id,
                  name: asset.name,
                })),
              ),
            ),
        ),
      300000,
    );
  }

  /**
   * Create all assetCategory sections in one call
   * @param assetCategory The assetCategory object to display in the sections
   * @returns An array of AdminDetailSection objects representing all assetCategory sections
   */
  createAllAssetCategorySections(
    assetCategory: AssetCategoryResponse | null,
  ): AdminDetailSection[] {
    return [
      ...this.createAssetCategoryInformationSection(assetCategory),
      ...this.createAssetsSection(assetCategory),
      ...this.createChangesSection(assetCategory),
    ];
  }
}
