import { Injectable, inject } from '@angular/core';
import { Observable } from 'rxjs';
import {
  EmailProviderListItem,
  EmailProvidersService,
  EmailProviderListResponse,
} from '../../../../api';
import { GridDataService } from '../../../../shared/models';
import { EmailProviderResponse } from '../../../../api/model/emailProviderResponse';

@Injectable({
  providedIn: 'root',
})
export class EmailProvidersDataAdapter
  implements GridDataService<EmailProviderListItem>
{
  private readonly service = inject(EmailProvidersService);

  getList(params: {
    owner: string;
    offset: number;
    page: number;
    limit: number;
    sortField?: string;
    sortOrder?: number;
    filters?: string;
    recurse?: boolean;
  }): Observable<EmailProviderListResponse> {
    return this.service.listEmailProviders(
      params.owner,
      params.offset,
      params.limit,
      params.sortField,
      params.filters,
    );
  }

  getById(id: string): Observable<EmailProviderResponse> {
    return this.service.getEmailProvider(id);
  }
}
