import { CommonModule } from '@angular/common';
import { Component, computed, inject, input } from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { Validators } from '@angular/forms';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { faPassport } from '@fortawesome/free-solid-svg-icons';
import { ButtonModule } from 'primeng/button';
import { MessageModule } from 'primeng/message';
import { v4 as uuidv4 } from 'uuid';

import {
  UserRoleListItem,
  UserRoleCreateRequest,
  AccountsService,
  Policy,
} from '../../../../../api';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import {
  createModalListField,
  createTextAreaField,
  createTextField,
  FormFieldConfig,
  ListFieldModalConfig,
} from '../../../../../shared/models';
import {
  isDefined,
  isNullOrUndefined,
  isString,
} from '../../../../../shared/utils';
import { EntitySectionBaseComponent } from '../../base';
import { ModalListFormComponent } from '../../../../../shared/components/forms/modal-list-form/modal-list-form.component';

@Component({
  selector: 'app-user-role-inline-policies-section',
  imports: [
    CommonModule,
    ButtonModule,
    GridComponent,
    DynamicFormComponent,
    MessageModule,
    FontAwesomeModule,
  ],
  templateUrl: './user-role-inline-policies-section.component.html',
  styleUrl: './user-role-inline-policies-section.component.scss',
})
export class UserRoleInlinePoliciesSectionComponent extends EntitySectionBaseComponent<
  UserRoleListItem | UserRoleCreateRequest
> {
  readonly ownerId = input<string | null>(null);
  private readonly accountsService = inject(AccountsService);
  faPassport = faPassport;

  readonly userRoleDetails = computed(() => {
    const userRoleDetails: Policy[] = this.entity()?.inlinePolicies;
    if (!isDefined(userRoleDetails)) return [];

    return [
      {
        key: 'Policies',
        value: userRoleDetails.map((item) => item.name).join(', ') ?? 'None',
      },
    ];
  });
  userRolesModalConfig: ListFieldModalConfig<Policy> | undefined;

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];
  }

  protected override updateFormFields(): void {
    const userRole = this.entity();

    this.userRolesModalConfig = {
      modalComponent: ModalListFormComponent,
      modalTitle: 'Inline Policy',
      defaultItemProvider: () => {
        // Create a properly structured default object
        const defaultItem = {
          id: uuidv4(),
          name: 'New Policy', // Add a default name
          statements: JSON.stringify(
            [
              {
                id: 'statement1',
                effect: 'allow',
                actions: ['entities:SomeMethod'],
                resources: ['*'],
              },
            ],
            null,
            2,
          ),
        };
        return defaultItem as unknown as Policy;
      },
      listItemFormatter: (item: Policy) => {
        // Ensure we have valid data to display
        return {
          id: item.id || '',
          title: item.name || 'Unnamed Policy',
          display: Array.isArray(item.statements)
            ? `${item.statements.length} statement(s)`
            : 'No statements',
        };
      },

      modalFields: (_item: Policy | undefined): FormFieldConfig[] => {
        // Default to empty array JSON string
        let statementsJson = '[]';

        try {
          if (_item?.statements) {
            // It's an object or array, stringify it
            statementsJson = JSON.stringify(_item.statements);
          }
        } catch (e) {
          console.error('Error handling statements:', e);
        }

        return [
          createTextField('id', 'ID', _item?.id, {
            name: 'id',
            hidden: true,
          }),
          createTextField('name', 'Name', _item?.name, {
            name: 'name',
            validations: [
              {
                name: 'required',
                validator: Validators.required,
                message: 'Name is required',
              },
            ],
          }),
          createTextAreaField('statements', 'Statements', statementsJson, {
            name: 'statements',
            validations: [
              {
                name: 'required',
                validator: Validators.required,
                message: 'Statements are required',
              },
              {
                name: 'json',
                validator: (control) => {
                  try {
                    if (isNullOrUndefined(control.value)) return null;
                    JSON.parse(control.value);
                    return null;
                  } catch (e) {
                    console.error('Invalid JSON format:', e);
                    return { json: true };
                  }
                },
                message: 'Invalid JSON format',
              },
            ],
          }),
        ];
      },
      subHeader: '',
    };

    this.formFields = [
      createModalListField(
        'inlinePolicies',
        'Policies',
        userRole?.inlinePolicies ?? [],
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          name: 'inlinePolicies',
          props: {
            modalConfig: this.userRolesModalConfig,
          },
        },
      ) as FormFieldConfig<unknown>,
    ];
  }

  protected override submitChanges(
    formData: Record<string, Record<string, unknown>[]>,
  ): void {
    const userRole = this.entity();
    if (isNullOrUndefined(userRole)) return;

    const transformedData = this.transformFormData
      ? this.transformFormData(formData)
      : formData;
    this.accountsService
      .updateUserRole(userRole.id, transformedData)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe({
        next: (response) => {
          this.isFormEditable.set(false);
          this.isSubmitting.set(false);
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'user-role',
            operation: 'update',
            entityId: userRole.id,
            data: response,
          });
        },
        error: () => {
          this.isFormEditable.set(true);
          this.isSubmitting.set(false);
        },
      });
  }

  protected override transformFormData?(
    formData:
      | Record<string, unknown>
      | Record<string, Record<string, unknown>[]>,
  ) {
    const data = formData as Record<string, Record<string, unknown>[]>;

    if (isDefined(data['inlinePolicies'])) {
      data['inlinePolicies'] = data['inlinePolicies'].map((policy) => {
        // Ensure we have a valid object
        const transformedPolicy = {
          id: policy['id'],
          name: policy['name'],
          statements: isString(policy['statements'])
            ? JSON.parse(policy['statements'])
            : policy['statements'],
        };
        return transformedPolicy;
      });
    }
    return data;
  }
}
