import { Injectable, inject } from '@angular/core';
import { Observable } from 'rxjs';

import { EntitiesService, AssetCategoryResponse } from '../../../../api';
import { BaseTreeAdapter } from './base-tree.adapter.service';

import { ActionIcon, EntityConfigAdapter } from '../../models';
import { ConfirmationService } from 'primeng/api';
import { BroadcastEventMessageService } from '../broadcast-event-messages.service';

@Injectable({
  providedIn: 'root',
})
export class AssetCategoriesDataAdapter
  extends BaseTreeAdapter<
    AssetCategoryResponse,
    { items: AssetCategoryResponse[]; count: number }
  >
  implements EntityConfigAdapter
{
  private readonly entitiesService = inject(EntitiesService);
  private readonly confirmationService = inject(ConfirmationService);
  private readonly broadcastService = inject(BroadcastEventMessageService);

  protected fetchEntities(
    owner: string,
    offset: number | undefined,
    limit: number | undefined,
    sortField: string | undefined,
    filters: string,
  ): Observable<{ items: AssetCategoryResponse[]; count: number }> {
    return this.entitiesService.listAssetCategories(
      owner,
      offset,
      limit,
      sortField,
      filters,
    );
  }

  getById(id: string): Observable<AssetCategoryResponse> {
    return this.entitiesService.getAssetCategory(id);
  }

  delete(id: string): Observable<unknown> {
    return this.entitiesService.deleteAssetCategory(id);
  }

  getActionIcons(entityId: string): ActionIcon[] {
    return [
      {
        id: 'delete',
        icon: 'pi pi-trash',
        tooltip: 'Delete Asset Category',
        visible: true,
        action: () => {
          this.confirmDelete(entityId);
        },
      },
    ];
  }

  private confirmDelete(entityId: string): void {
    const message = `Are you sure you want to delete this asset category?`;

    this.confirmationService.confirm({
      message,
      header: 'Confirm Delete',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        this.delete(entityId).subscribe({
          next: (result) => {
            this.broadcastService.broadcast('entity-deleted', {
              entityType: 'asset-category',
              operation: 'delete',
              entityId,
              data: result,
            });
          },
          error: (error) => {
            console.error('Delete failed', error);
          },
        });
      },
      acceptButtonProps: { label: 'Delete' },
      rejectButtonProps: { label: 'Cancel', severity: 'secondary' },
    });
  }
}
