import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import { UserListItem, UserResponse, AccountsService } from '../../../../api';
import {
  DataGridParams,
  EntityDataAdapter,
  GridResponse,
} from '../../../../shared/models';
import { ActionIcon, EntityConfigAdapter, StateOption } from '../../models';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';
import { AuthDataService } from '../../../../core/services/auth-data.service';

@Injectable({
  providedIn: 'root',
})
export class UsersDataAdapter
  implements EntityDataAdapter<UserListItem, UserResponse>, EntityConfigAdapter
{
  private readonly accountsService = inject(AccountsService);
  private readonly authDataService = inject(AuthDataService);
  private readonly entityStateService = inject(EntityDetailsStateService);

  getList(params: DataGridParams): Observable<GridResponse<UserListItem>> {
    const { owner, limit, offset, sortField, filters } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.accountsService.listUsers(
      owner,
      offset,
      limit,
      sortField as string,
      filters,
    );
  }

  getById(id: string): Observable<UserResponse> {
    return this.accountsService.getUser(id);
  }

  getStateOptions(): StateOption[] {
    return [
      {
        label: 'Active',
        value: UserResponse.StateEnum.Active,
      },
      {
        label: 'Inactive',
        value: UserResponse.StateEnum.Inactive,
      },
      {
        label: 'Suspended',
        value: UserResponse.StateEnum.Suspended,
      },
      {
        label: 'Deleted',
        value: UserResponse.StateEnum.Deleted,
      },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.accountsService.updateUser(id, {
      state: state as UserResponse.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    if (!this.authDataService.hasPermission('users.create')) {
      return [];
    }

    return [
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change User State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
