import { Component, computed, inject, input, signal } from '@angular/core';

import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';

import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';
import {
  AssetGroupCreateRequest,
  AssetGroupResponse,
  EntitiesService,
} from '../../../../../api';
import { AssetGroupSectionConfigFactory } from '../../../factories';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';

@Component({
  selector: 'app-asset-group-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './asset-group-details-container.component.html',
  styleUrl: './asset-group-details-container.component.scss',
})
export class AssetGroupDetailsContainerComponent {
  readonly entity = input<AssetGroupResponse | null>(null);
  private readonly assetGroupSectionFactory = inject(
    AssetGroupSectionConfigFactory,
  );
  readonly entityDetailsState = inject(EntityDetailsStateService);
  private readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly isSoftRefreshing = computed(() =>
    this.entityDetailsState.isSoftRefreshing(),
  );
  readonly isInitialLoading = computed(
    () =>
      this.isLoading() && !this.isSoftRefreshing() && !this.fullAssetGroup(),
  );
  readonly fullAssetGroup = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();

    if (!entityDetails) {
      return null;
    }
    return entityDetails as AssetGroupResponse;
  });
  readonly sections = computed(() => {
    const assetGroup = this.fullAssetGroup();
    if (!assetGroup) return [];
    return this.assetGroupSectionFactory.createAllAssetGroupSections(
      assetGroup,
    );
  });
  readonly error = signal<string | null>(null);

  constructor() {
    this.operationRegistry.registerEntityOperations('asset-group', {
      update: (id, data) => this.entitiesService.updateAssetGroup(id, data),
      create: (data: AssetGroupCreateRequest) =>
        this.entitiesService.createAssetGroup(data),
      get: (id) => this.entitiesService.getAssetGroup(id),
    });
  }

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as AssetGroupResponse);
    this.error.set(null);
  }
}
