import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import {
  AssetRatingProfileListItem,
  AssetRatingProfileResponse,
  EntitiesService,
} from '../../../../api';
import {
  DataGridParams,
  EntityDataAdapter,
  GridResponse,
} from '../../../../shared/models';
import { ActionIcon, EntityConfigAdapter, StateOption } from '../../models';
import { AuthDataService } from '../../../../core/services/auth-data.service';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';

@Injectable({
  providedIn: 'root',
})
export class AssetRatingProfilesDataAdapter
  implements
    EntityDataAdapter<AssetRatingProfileListItem, AssetRatingProfileResponse>,
    EntityConfigAdapter
{
  private readonly entitiesService = inject(EntitiesService);
  private readonly authDataService = inject(AuthDataService);
  private readonly entityStateService = inject(EntityDetailsStateService);

  getList(
    params: DataGridParams,
  ): Observable<GridResponse<AssetRatingProfileListItem>> {
    const { owner, limit, offset, sortField, filters } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.entitiesService.listAssetRatingProfiles(
      owner,
      offset,
      limit,
      sortField as string,
      filters,
    );
  }

  getById(id: string): Observable<AssetRatingProfileResponse> {
    return this.entitiesService.getAssetRatingProfile(id);
  }

  getStateOptions(): StateOption[] {
    return [
      {
        label: 'Active',
        value: AssetRatingProfileResponse.StateEnum.Active,
      },
      {
        label: 'Deleted',
        value: AssetRatingProfileResponse.StateEnum.Deleted,
      },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.entitiesService.updateAssetRatingProfile(id, {
      state: state as AssetRatingProfileResponse.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    if (!this.authDataService.hasPermission('asset-rating-profiles.create')) {
      return [];
    }

    return [
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change Asset Rating Profile State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
