import { Injectable, inject, signal, DestroyRef } from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import {
  debounceTime,
  distinctUntilChanged,
  switchMap,
  catchError,
  of,
  Subject,
} from 'rxjs';
import { AccountsService, ClientListItem } from '../../../../../../api';
import { UtilityService } from '../../../../../../core/services/utility.service';
import {
  hasProperty,
  isDefined,
  isNullOrUndefined,
} from '../../../../../../shared/utils';
import { SEARCH_DEBOUNCE_TIME } from '../../../../../../core/models';

@Injectable({
  providedIn: 'root',
})
export class ClientSearchService {
  private readonly accountsService = inject(AccountsService);
  private readonly utilityService = inject(UtilityService);
  private readonly destroyRef = inject(DestroyRef);

  private readonly searchSubject = new Subject<string>();
  private readonly clientsSearchResults = signal<ClientListItem[] | undefined>(
    undefined,
  );
  private readonly isLoading = signal<boolean>(false);
  private ownerId: string | null = null;

  // Getter for readonly access to results
  get searchResults() {
    return this.clientsSearchResults.asReadonly();
  }

  // Getter for readonly access to loading state
  get isSearchLoading() {
    return this.isLoading.asReadonly();
  }

  /**
   * Initialize the search service with an owner ID and set up the search stream
   */
  initialize(ownerId: string | null): void {
    this.ownerId = ownerId;
    this.setupSearchStream();
  }

  /**
   * Trigger a client search
   */
  search(searchTerm: string): void {
    if (!searchTerm || searchTerm.trim() === '' || searchTerm.length < 3) {
      this.clientsSearchResults.set([]);
      this.isLoading.set(false);
      return;
    }

    this.clientsSearchResults.set(undefined);
    this.isLoading.set(true);
    this.searchSubject.next(searchTerm);
  }

  /**
   * Clear search results
   */
  clearResults(): void {
    this.clientsSearchResults.set(undefined);
  }

  /**
   * Set up the reactive search stream
   */
  private setupSearchStream(): void {
    this.searchSubject
      .pipe(
        debounceTime(SEARCH_DEBOUNCE_TIME),
        distinctUntilChanged(),
        takeUntilDestroyed(this.destroyRef),
        switchMap((searchTerm) => this.performSearch(searchTerm)),
      )
      .subscribe({
        next: (data) => this.handleSearchResults(data),
        error: () => this.handleSearchError(),
      });
  }

  /**
   * Perform the actual search API call
   */
  private performSearch(searchTerm: string) {
    if (this.ownerId === null || this.ownerId === undefined) {
      return of({ items: [] });
    }

    const trimmedSearchTerm = searchTerm.trim();

    if (trimmedSearchTerm.length === 0) {
      return of({ items: [] });
    }

    const filters = {
      state: { value: 'active', operator: '=' },
      name: { value: `*${trimmedSearchTerm}*`, operator: '=' },
    };
    const rqlFilter = this.utilityService.constructRqlFilter(filters);

    return this.accountsService
      .listClients(this.ownerId, 0, 10, 'name', rqlFilter)
      .pipe(catchError(() => of({ items: [] })));
  }

  /**
   * Handle successful search results
   */
  private handleSearchResults(data: unknown): void {
    this.isLoading.set(false);

    if (isDefined(data) && hasProperty(data, 'items')) {
      const results = isNullOrUndefined(data.items)
        ? []
        : (data.items as ClientListItem[]);
      this.clientsSearchResults.set(results);
    }
  }

  /**
   * Handle search errors
   */
  private handleSearchError(): void {
    this.isLoading.set(false);
    this.clientsSearchResults.set([]);
  }

  /**
   * Clean up the service
   */
  destroy(): void {
    this.clientsSearchResults.set(undefined);
    this.ownerId = null;
  }
}
