import { Component, computed, inject, input, signal } from '@angular/core';

import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';

import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';
import {
  CostCentreCreateRequest,
  CostCentreResponse,
  EntitiesService,
} from '../../../../../api';
import { CostCentreSectionConfigFactory } from '../../../factories';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';

@Component({
  selector: 'app-cost-centre-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './cost-centre-details-container.component.html',
  styleUrl: './cost-centre-details-container.component.scss',
})
export class CostCentreDetailsContainerComponent {
  readonly entity = input<CostCentreResponse | null>(null);

  private readonly costCentreSectionFactory = inject(
    CostCentreSectionConfigFactory,
  );
  readonly entityDetailsState = inject(EntityDetailsStateService);
  private readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly isSoftRefreshing = computed(() =>
    this.entityDetailsState.isSoftRefreshing(),
  );
  readonly isInitialLoading = computed(
    () =>
      this.isLoading() && !this.isSoftRefreshing() && !this.fullCostCentre(),
  );
  readonly fullCostCentre = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();

    if (!entityDetails) {
      return null;
    }
    return entityDetails as CostCentreResponse;
  });
  readonly sections = computed(() => {
    const costCentre = this.fullCostCentre();
    if (!costCentre) return [];
    return this.costCentreSectionFactory.createAllCostCentreSections(
      costCentre,
    );
  });
  readonly error = signal<string | null>(null);

  constructor() {
    this.operationRegistry.registerEntityOperations('cost-centre', {
      update: (id, data) => this.entitiesService.updateCostCentre(id, data),
      create: (data: CostCentreCreateRequest) =>
        this.entitiesService.createCostCentre(data),
      get: (id) => this.entitiesService.getCostCentre(id),
    });
  }

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as CostCentreResponse);
    this.error.set(null);
  }
}
