import { Injectable } from '@angular/core';

import { EmailProviderResponse, EmailProviderTypeResponse } from '../../../api';
import { AdminDetailSection } from '../models';
import { EmailProviderDetailsSectionComponent } from '../components/email-providers/email-provider-details-section/email-provider-details-section.component';
import { EmailProviderParametersSectionComponent } from '../components/email-providers/email-provider-parameters-section/email-provider-parameters-section.component';
import { FeedSectionComponent } from '../components/shared/feed-section/feed-section.component';

@Injectable({ providedIn: 'root' })
export class EmailProviderSectionConfigFactory {
  /**
   * Create sections for email provider information
   * @param emailProvider The email provider object to display in the section
   * @returns An array of AdminDetailSection objects representing the email provider information sections
   */
  createEmailProviderInformationSection(
    emailProvider: EmailProviderResponse | null,
    emailProviderType: EmailProviderTypeResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'email-provider-details',
        title: 'Email Config Details',
        component: EmailProviderDetailsSectionComponent,
        componentInputs: { entity: emailProvider },
        category: 'information',
        order: 10,
      },
      {
        id: 'email-provider-parameters',
        title: 'Email Config Parameters',
        component: EmailProviderParametersSectionComponent,
        componentInputs: {
          entity: emailProvider,
          entityType: 'email-provider',
          dataPath: 'parameters',
          injectDataAtPath: true,
          emailProviderType,
        },
        category: 'information',
        order: 20,
      },
    ];
  }

  /**
   * Create sections for email provider changes
   * @param emailProvider The email provider object to display in the section
   * @returns An array of AdminDetailSection objects representing the email provider changes sections
   */
  createChangesSection(
    emailProvider: EmailProviderResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'email-provider-changes',
        title: 'Changes',
        component: FeedSectionComponent,
        componentInputs: {
          entity: emailProvider,
        },
        category: 'feed',
        order: 10,
      },
    ];
  }

  /**
   * Create all emailProvider sections in one call
   * @param emailProvider The emailProvider object to display in the sections
   * @returns An array of AdminDetailSection objects representing all emailProvider sections
   */
  createAllEmailProviderSections(
    emailProvider: EmailProviderResponse | null,
    emailProviderType: EmailProviderTypeResponse | null,
  ): AdminDetailSection[] {
    return [
      ...this.createEmailProviderInformationSection(
        emailProvider,
        emailProviderType,
      ),
      ...this.createChangesSection(emailProvider),
    ];
  }
}
