import {
  Component,
  computed,
  DestroyRef,
  inject,
  input,
  OnInit,
  signal,
} from '@angular/core';
import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';
import {
  DistributorListItem,
  AccountsService,
  DistributorResponse,
  DistributorCreateRequest,
} from '../../../../../api';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';
import { DistributorSectionConfigFactory } from '../../../factories';
import { BroadcastEventMessageService } from '../../../services/broadcast-event-messages.service';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { ToastService } from '../../../../../core/services/toast.service';

interface EntityUpdatedMessage {
  entityType: string;
  data: DistributorResponse;
}
@Component({
  selector: 'app-super-admin-distributors-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './super-admin-distributors-details-container.component.html',
  styleUrl: './super-admin-distributors-details-container.component.scss',
})
export class SuperAdminDistributorsDetailsContainerComponent implements OnInit {
  readonly entity = input<DistributorListItem | null>(null);

  private readonly distributorSectionFactory = inject(
    DistributorSectionConfigFactory,
  );
  readonly entityDetailsState = inject(EntityDetailsStateService);
  private readonly accountsService = inject(AccountsService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly broadcastService = inject(BroadcastEventMessageService);
  private readonly toastService = inject(ToastService);
  private readonly destroyRef = inject(DestroyRef);

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly isSoftRefreshing = computed(() =>
    this.entityDetailsState.isSoftRefreshing(),
  );
  readonly isInitialLoading = computed(
    () =>
      this.isLoading() && !this.isSoftRefreshing() && !this.fullDistributor(),
  );
  readonly fullDistributor = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();
    if (isNullOrUndefined(entityDetails)) {
      return null;
    }
    return entityDetails as DistributorResponse;
  });
  readonly sections = computed(() => {
    const distributor = this.fullDistributor();
    if (isNullOrUndefined(distributor)) return [];

    return this.distributorSectionFactory.createAllDistributorSections(
      distributor,
    );
  });
  readonly error = signal<string | null>(null);
  private isEntityUpdatedMessage(
    message: unknown,
  ): message is EntityUpdatedMessage {
    return (
      isDefined(message) &&
      message !== null &&
      typeof message === 'object' &&
      'entityType' in message &&
      'data' in message &&
      (message as EntityUpdatedMessage).entityType === 'distributor'
    );
  }

  constructor() {
    this.operationRegistry.registerEntityOperations('distributor', {
      update: (id, data) => this.accountsService.updateDistributor(id, data),
      create: (data: DistributorCreateRequest) =>
        this.accountsService.createDistributor(data),
      get: (id) => this.accountsService.getDistributor(id),
    });
  }

  ngOnInit(): void {
    this.broadcastService
      .on('entity-updated')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((message) => {
        if (this.isEntityUpdatedMessage(message)) {
          this.toastService.showSuccess(
            'Success',
            'Distributor updated successfully',
          );
          this.onEntityChange(message.data);

          // Broadcast tree refresh for all distributor state changes
          const operation =
            message.data.state === DistributorResponse.StateEnum.Deleted
              ? 'delete'
              : 'update';

          this.broadcastService.broadcast('tree-refresh-required', {
            entityType: 'distributor',
            operation,
            entityId: message.data.id,
            state: message.data.state,
          });
        }
      });
  }

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as DistributorResponse);
    this.error.set(null);
  }
}
