import { Component, computed, inject, input, signal } from '@angular/core';
import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';

import { RoadProfileListItem, RoadProfileResponse } from '../../../../../api';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';
import { RoadProfileSectionConfigFactory } from '../../../factories';

@Component({
  selector: 'app-road-profile-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './road-profile-details-container.component.html',
  styleUrl: './road-profile-details-container.component.scss',
})
export class RoadProfileDetailsContainerComponent {
  readonly entity = input<RoadProfileListItem | null>(null);

  private readonly roadProfileSectionFactory = inject(
    RoadProfileSectionConfigFactory,
  );
  readonly entityDetailsState = inject(EntityDetailsStateService);

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly isSoftRefreshing = computed(() =>
    this.entityDetailsState.isSoftRefreshing(),
  );
  readonly isInitialLoading = computed(
    () =>
      this.isLoading() && !this.isSoftRefreshing() && !this.fullRoadProfile(),
  );
  readonly fullRoadProfile = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();

    if (!entityDetails) {
      return null;
    }
    return entityDetails as RoadProfileResponse;
  });
  readonly sections = computed(() => {
    const roadProfile = this.fullRoadProfile();
    if (!roadProfile) return [];
    return this.roadProfileSectionFactory.createAllRoadProfileSections(
      roadProfile,
    );
  });
  readonly error = signal<string | null>(null);

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as RoadProfileResponse);
    this.error.set(null);
  }
}
