import { Component, computed, inject, input, signal } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Validators } from '@angular/forms';
import { Observable } from 'rxjs';

// PrimeNG imports
import { ButtonModule } from 'primeng/button';

// Core imports
import { EntitiesService, IdName } from '../../../../../api';
import {
  createNumberField,
  createTextField,
} from '../../../../../shared/models';
import { isNullOrUndefined, isNumber } from '../../../../../shared/utils';
import { EntitySectionBaseComponent } from '../../base';
import { AdminStateService } from '../../../services/state/admin-state.service';

// Lazy loaded components
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';

interface FormField {
  id: string;
  title: string;
  type: 'text' | 'number';
  value?: string | number;
  defaultValue?: string | number;
  required?: boolean;
  description?: string;
  min?: number;
  max?: number;
}

interface FormConfig {
  fields: FormField[];
  name?: string;
}

@Component({
  selector: 'app-form-section',
  imports: [CommonModule, ButtonModule, GridComponent, DynamicFormComponent],
  templateUrl: './form-section.component.html',
  styleUrl: './form-section.component.scss',
})
export class FormSectionComponent<
  T extends {
    [x: string]: unknown;
    id: string;
    assetType?: IdName;
  },
> extends EntitySectionBaseComponent<T> {
  valuesKey = input.required<string>();
  formConfig = input.required<FormConfig>();
  private readonly entitiesService = inject(EntitiesService);
  private readonly adminStateService = inject(AdminStateService);

  readonly entityCustomFields = signal<
    Record<string, string | boolean | number>[]
  >([]);

  readonly entityDetails = computed(() => {
    const entityDetails = this.entity();
    const formConfig = this.formConfig();
    if (isNullOrUndefined(entityDetails) || isNullOrUndefined(formConfig))
      return [];

    const values =
      (entityDetails[this.valuesKey()] as Record<string, unknown>) ?? {};

    return formConfig.fields.map((field) => ({
      key: field.title,
      value: values[field.id] ?? 'None',
    }));
  });

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];
  }

  protected override updateFormFields(): void {
    const entityDetails = this.entity();
    const formConfig = this.formConfig();
    const values: Record<string, unknown> =
      (entityDetails?.[this.valuesKey()] as Record<string, unknown>) ?? {};
    this.formFields = formConfig.fields
      .map((field) => {
        switch (field.type) {
          case 'text':
            return createTextField(
              field.id,
              field.title,
              (values[field.id] as string) ??
                field.value?.toString() ??
                field.defaultValue?.toString(),
              {
                validations:
                  (field.required ?? false)
                    ? [
                        {
                          name: 'required',
                          validator: Validators.required,
                          message: `${field.title} is required`,
                        },
                      ]
                    : [],
                hint: field.description,
              },
            );
          case 'number': {
            const validations = [];
            if (field.required ?? false) {
              validations.push({
                name: 'required',
                validator: Validators.required,
                message: `${field.title} is required`,
              });
            }
            if (isNumber(field.min)) {
              validations.push({
                name: 'min',
                validator: Validators.min(field.min),
                message: `Value must be greater than ${field.min}`,
              });
            }
            if (isNumber(field.max)) {
              validations.push({
                name: 'max',
                validator: Validators.max(field.max),
                message: `Value must be less than ${field.max}`,
              });
            }
            return createNumberField(
              field.id,
              field.title,
              (values[field.id] as number) ?? field.value ?? field.defaultValue,
              {
                validations,
                hint: field.description,
              },
            );
          }
          default:
            return undefined;
        }
      })
      .filter((field) => field !== undefined); // Filter out undefined fields
  }

  override getUpdateMethod(): (
    id: string,
    payload: Partial<T>,
  ) => Observable<unknown> {
    switch (this.entityType()) {
      case 'asset':
        return (id, payload) => this.entitiesService.updateAsset(id, payload);
      case 'simcard':
        return (id, payload) => this.entitiesService.updateSimCard(id, payload);
      case 'device':
        return (id, payload) => this.entitiesService.updateDevice(id, payload);
      case 'assettag':
        return (id, payload) =>
          this.entitiesService.updateAssetTag(id, payload);
      default:
        throw new Error(`Update for '${this.entityType}' not yet supported`);
    }
  }
}
