import { Injectable, inject } from '@angular/core';
import { UtilityService } from '../../../../core/services/utility.service';
import {
  THEME_OFF_WHITE,
  THEME_OFF_BLACK,
} from '../../../../shared/models/theme-defaults';
import { ThemeEngine } from '../../../../shared/models/theme-engine';
import { DEFAULT_SHADE_VALUES } from '../../models';
import { ThemeUtilityService } from './theme-utility.service';

@Injectable({ providedIn: 'root' })
export class ThemeColorService {
  private readonly themeEngine = inject(ThemeEngine);
  private readonly utilityService = inject(UtilityService);
  private readonly themeUtilityService = inject(ThemeUtilityService);

  computeColor(variable: string): string {
    return this.themeEngine.resolveFormula(variable);
  }

  getFontColorPresets(
    group: string,
    presets?: string[],
  ): Record<string, string> {
    return {
      ...this.themeUtilityService.createPresets(
        'font-color',
        [...(presets ?? [])],
        this.computeColor.bind(this),
      ),
      Auto: this.computeColor(
        `var(--${group}-background-color).inverse("${THEME_OFF_WHITE}", "${THEME_OFF_BLACK}")`,
      ),
      'Primary Color': this.computeColor('var(--color-primary-color)'),
      'Primary Contrast': this.computeColor('var(--color-primary-contrast)'),
      'Secondary Color': this.computeColor('var(--color-secondary-color)'),
      'Secondary Contrast': this.computeColor(
        'var(--color-secondary-contrast)',
      ),
      'Off Black': this.computeColor(THEME_OFF_BLACK),
      'Off White': this.computeColor(THEME_OFF_WHITE),
      Black: this.computeColor('#000000'),
      White: this.computeColor('#ffffff'),
    };
  }

  getBackgroundColorPresets(
    presets?: string[],
    customColors?: Record<string, string>,
  ): Record<string, string> {
    const groups = [...(presets ?? [])];
    const backgroundShades = groups.reduce(
      (result, grp) => ({
        ...result,
        ...this.themeUtilityService.createPreset(
          'background-color',
          grp,
          `${this.utilityService.toTitleCase(grp)} Background`,
          this.computeColor.bind(this),
        ),
        ...this.createShadeValues('lighten', grp, 'background'),
        ...this.createShadeValues('darken', grp, 'background'),
      }),
      {},
    );
    return {
      ...customColors,
      ...backgroundShades,
      /*      'Primary Color': this.computeColor('var(--color-primary-color)'),
      'Secondary Color': this.computeColor('var(--color-secondary-color)'),
      White: this.computeColor('#ffffff'),
      Transparent: this.computeColor('transparent'), */
    };
  }

  getBorderColorPresets(
    group: string,
    presets?: string[],
    backgrounds?: string[],
    customColors?: Record<string, string>,
  ): Record<string, string> {
    const groups = [group, ...(backgrounds ?? [])];
    const backgroundShades = groups.reduce(
      (result, grp) => ({
        ...result,
        ...this.themeUtilityService.createPreset(
          'background-color',
          grp,
          `${this.utilityService.toTitleCase(grp)} Background`,
          this.computeColor.bind(this),
        ),
        ...this.createShadeValues('lighten', grp, 'background'),
        ...this.createShadeValues('darken', grp, 'background'),
      }),
      {},
    );

    return {
      ...this.themeUtilityService.createPresets(
        'border-color',
        [...(presets ?? [])],
        this.computeColor.bind(this),
      ),
      ...backgroundShades,
      'Primary Color': this.computeColor('var(--color-primary-color)'),
      'Secondary Color': this.computeColor('var(--color-secondary-color)'),
      ...customColors,
    };
  }

  createShadeValues(
    type: 'lighten' | 'darken',
    group: string,
    variable: 'background' | 'font',
    values = DEFAULT_SHADE_VALUES,
  ): Record<string, string> {
    return values.reduce((result: Record<string, string>, val) => {
      const key = `${this.utilityService.toTitleCase(type)} ${this.utilityService.toTitleCase(group)} ${this.utilityService.toTitleCase(variable)} ${val * 100}%`;
      result[key] = this.computeColor(
        `var(--${group}-${variable}-color).${type}(${val})`,
      );
      return result;
    }, {});
  }

  /**
   * Get comprehensive color presets combining all color types for better identification
   * @param group - The primary group context
   * @param additionalGroups - Additional groups to include in presets
   * @returns Record containing all available color presets
   */
  getAllColorPresets(
    group: string,
    additionalGroups: string[] = [],
    customColors: Record<string, string> = {},
  ): Record<string, string> {
    const allGroups = [group, ...additionalGroups];
    const uniqueGroups = [...new Set(allGroups)];

    // Common theme colors that should always be available
    const commonThemeColors = {
      'Primary Color': this.computeColor('var(--color-primary-color)'),
      'Primary Contrast': this.computeColor('var(--color-primary-contrast)'),
      'Secondary Color': this.computeColor('var(--color-secondary-color)'),
      'Secondary Contrast': this.computeColor(
        'var(--color-secondary-contrast)',
      ),
      'Success Color': this.computeColor('var(--color-success-color)'),
      'Success Contrast': this.computeColor('var(--color-success-contrast)'),
      'Info Color': this.computeColor('var(--color-info-color)'),
      'Info Contrast': this.computeColor('var(--color-info-contrast)'),
      'Warning Color': this.computeColor('var(--color-warning-color)'),
      'Warning Contrast': this.computeColor('var(--color-warning-contrast)'),
      'Danger Color': this.computeColor('var(--color-danger-color)'),
      'Danger Contrast': this.computeColor('var(--color-danger-contrast)'),
      'Notify Color': this.computeColor('var(--color-notify-color)'),
      'Notify Contrast': this.computeColor('var(--color-notify-contrast)'),
      White: this.computeColor('#ffffff'),
      Black: this.computeColor('#000000'),
      'Off White': this.computeColor(THEME_OFF_WHITE),
      'Off Black': this.computeColor(THEME_OFF_BLACK),
      Transparent: this.computeColor('transparent'),
    };

    return {
      // Font color presets
      ...this.getFontColorPresets(group, uniqueGroups),

      // Background color presets
      ...this.getBackgroundColorPresets(uniqueGroups, {
        ...commonThemeColors,
        ...customColors,
      }),

      // Border color presets
      ...this.getBorderColorPresets(group, uniqueGroups, uniqueGroups, {
        ...commonThemeColors,
        ...customColors,
      }),

      // Ensure common theme colors are always available
      ...commonThemeColors,

      // Custom colors provided by caller
      ...customColors,
    };
  }
  /**
   * Determine if a value represents a color based on various detection strategies
   * @param value - The value to check
   * @param key - Optional key name for additional context
   * @returns boolean indicating if the value represents a color
   */
  isColorValue(value: string, key?: string): boolean {
    if (!value) return false;

    const trimmedValue = value.trim();

    // Check by key name if provided
    if (
      key !== undefined &&
      key !== null &&
      key.trim() !== '' &&
      this.isColorByKeyName(key)
    ) {
      return true;
    }

    // Check by value format
    return this.isColorByValueFormat(trimmedValue);
  }
  /**
   * Check if a key name suggests it represents a color
   */
  private isColorByKeyName(key: string): boolean {
    const keyLower = key.toLowerCase();

    // Direct color-related keywords - but border needs special handling
    const directColorKeywords = ['color', 'background', 'contrast'];

    // Check for direct color keywords (excluding border for now)
    if (directColorKeywords.some((keyword) => keyLower.includes(keyword))) {
      return true;
    }

    // Special handling for border - only if it's specifically about color
    if (keyLower.includes('border')) {
      // Only consider it a color if it's specifically about border color
      // NOT border width, border style, border radius, etc.
      return (
        keyLower.includes('color') ||
        keyLower === 'border' ||
        keyLower.endsWith(' border')
      );
    }

    // Check for font color specifically (not just 'font')
    if (keyLower.includes('font') && keyLower.includes('color')) {
      return true;
    }

    // Theme color keywords (only when combined with 'color' or as standalone values)
    const themeColorKeywords = [
      'primary',
      'secondary',
      'success',
      'info',
      'notify',
      'warning',
      'danger',
    ];

    if (
      themeColorKeywords.some((keyword) => {
        const hasKeyword = keyLower.includes(keyword);
        const hasColor = keyLower.includes('color');
        const isStandaloneThemeColor =
          keyLower === keyword || keyLower === `${keyword} color`;
        return hasKeyword && (hasColor || isStandaloneThemeColor);
      })
    ) {
      return true;
    }

    // Context-specific color keywords
    const contextualColorKeywords = ['active', 'subnav', 'logo', 'client'];

    if (
      contextualColorKeywords.some((keyword) => {
        const hasKeyword = keyLower.includes(keyword);
        const hasColorContext =
          keyLower.includes('color') ||
          keyLower.includes('background') ||
          (keyLower.includes('border') && keyLower.includes('color'));
        return hasKeyword && hasColorContext;
      })
    ) {
      return true;
    }

    return false;
  }

  /**
   * Check if a value format represents a color
   */
  private isColorByValueFormat(value: string): boolean {
    const colorPatterns = [
      /^#[0-9a-fA-F]{3,8}$/, // Hex colors (#fff, #ffffff, #ffffffff)
      /^rgb\s*\(/, // RGB colors
      /^rgba\s*\(/, // RGBA colors
      /^hsl\s*\(/, // HSL colors
      /^hsla\s*\(/, // HSLA colors
      /^(red|blue|green|yellow|purple|orange|pink|brown|black|white|gray|grey|transparent)$/i, // Named colors
    ];

    // Check basic color patterns first
    if (colorPatterns.some((pattern) => pattern.test(value))) {
      return true;
    }

    // More specific CSS variable checking
    if (value.startsWith('var(--') && value.endsWith(')')) {
      const variableName = value.slice(6, -1).toLowerCase(); // Extract variable name

      // Only consider it a color if the variable name explicitly contains color-related terms
      const colorVariablePatterns = [
        /.*color.*/, // Contains 'color'
        /.*background.*/, // Contains 'background'
        /.*border.*/, // Contains 'border'
        /.*contrast.*/, // Contains 'contrast'
      ];

      return colorVariablePatterns.some((pattern) =>
        pattern.test(variableName),
      );
    }

    return false;
  }

  /**
   * Get context-aware color presets based on the key name
   * @param key - The key name to analyze for context
   * @param group - The primary group context
   * @param additionalGroups - Additional groups to include
   * @returns Contextual color presets
   */
  getContextualColorPresets(
    key: string,
    group: string,
    additionalGroups: string[] = [],
  ): Record<string, string> {
    const keyLower = key.toLowerCase();
    const allGroups = [group, ...additionalGroups];

    // Determine the primary color type based on key analysis
    if (keyLower.includes('font') || keyLower.includes('text')) {
      return this.getFontColorPresets(group, allGroups);
    }

    if (keyLower.includes('background') || keyLower.includes('bg')) {
      return this.getBackgroundColorPresets(allGroups);
    }

    if (keyLower.includes('border')) {
      return this.getBorderColorPresets(group, allGroups, allGroups);
    }

    // If no specific type detected, return comprehensive presets
    return this.getAllColorPresets(group, additionalGroups);
  }
}
