import {
  createTextField,
  FormFieldConfig,
} from '../../../../shared/models/form-field.model';
import { EntitySectionBaseComponent } from './entity-section-base';
import {
  EntitiesService,
  ThemeResponse,
  ThemeUpdateRequest,
} from '../../../../api';
import { ThemeEngine } from '../../../../shared/models/theme-engine';
import { ThemeSettings } from '../../../../shared/models';
import { inject } from '@angular/core';
import { ThemeFormFieldFactory } from '../../factories/theme-form-field.factory';
import { FormFieldService } from '../../services/form-field.service';
import { EntityOperationRegistryService } from '../../services/entity/entity-operation-registry.service';
import { isDefined, isNullOrUndefined } from '../../../../shared/utils';
import { faFill } from '@fortawesome/free-solid-svg-icons';
import { ThemeColorService } from '../../services/theme/theme-color.service';
import {
  AVAILABLE_FONTS,
  DROP_SHADOW_OPTIONS,
  ICON_PACKS,
} from '../../models/theme-form.constants';

type EditorValues = Record<string, object | string | number | boolean>;

export interface ThemeDetailConfig {
  key: string;
  path: readonly string[];
  transform?: (value: unknown) => string;
  options?: { value: string; label: string }[];
}

/**
 * Unified configuration interface for theme form fields and details display
 */
export interface ThemeFieldConfig {
  /** Unique identifier for the field */
  id: string;
  /** Display label */
  label: string;
  /** Path to the value in theme settings */
  path: readonly string[];
  /** Field type */
  type:
    | 'text'
    | 'number'
    | 'dropdown'
    | 'color'
    | 'font-family'
    | 'font-weight'
    | 'font-transform'
    | 'size';
  /** Options for dropdown fields */
  options?: { value: string; label: string }[];
  /** Group/section for color preset context */
  group?: string;
  /** Preset groups for color/font fields */
  presets?: string[];
  /** Custom colors for color fields */
  customColors?: Record<string, string>;
  /** Transform function for display value */
  transform?: (value: unknown) => string;
  /** Additional properties for form field */
  props?: Record<string, unknown>;
  /** Whether this field should be included in details display */
  includeInDetails?: boolean;
}

/**
 * Base component for theme settings sections.
 * Provides common functionality for displaying and editing theme settings.
 */
export abstract class ThemeSettingsSectionBase extends EntitySectionBaseComponent<ThemeResponse> {
  faFill = faFill;
  title = 'Theme Settings';
  description = 'Customize your theme by tweaking the following settings.';

  protected readonly themeEngine = inject(ThemeEngine);
  protected readonly formFieldFactory = inject(ThemeFormFieldFactory);
  protected readonly formFieldMerger = inject(FormFieldService);
  protected readonly entitiesService = inject(EntitiesService);
  protected readonly operationRegistry = inject(EntityOperationRegistryService);
  protected readonly colorService = inject(ThemeColorService);

  formChanges: Record<string, string | number | object> = {};

  constructor(public group: string) {
    super();
  }

  protected override updateFormFields(): void {
    const entity = this.entity();
    if (!entity) return;

    const newFields = this.getFields(entity);
    this.formFields = this.formFieldMerger.merge(this.formFields, newFields);
  }

  protected override onEntityLoaded(entity: ThemeResponse): void {
    super.onEntityLoaded(entity);
    if (entity?.settings) {
      this.themeEngine.settingsToVariables(entity.settings);
    }
  }

  protected isColorRow(rowData: Record<string, string>): boolean {
    const colorKeys = ['Font Color', 'Background Color', 'Border Color'];
    return colorKeys.includes(rowData['key']);
  }

  protected getColorLabelAndValue(variable: string): {
    label: string;
    value: string;
  } {
    const presets = {
      ...this.colorService.getFontColorPresets(this.group),
      ...this.colorService.getBackgroundColorPresets([]),
      ...this.colorService.getBorderColorPresets(this.group),
    };
    const computedColor = this.colorService.computeColor(variable);
    const presetKey = Object.keys(presets).find(
      (key) => presets[key] === computedColor,
    );
    if (isDefined(presetKey)) {
      return { label: presetKey, value: computedColor };
    }

    return { label: computedColor, value: computedColor };
  }

  protected onFieldValueChange(values: {
    fieldKey: string;
    value: string | number;
    previousValue: string | number;
  }): void {
    // Split the key by dots to create nested structure
    const keys = values.fieldKey.split('.');
    let current = this.formChanges;

    // Navigate/create the nested structure
    for (let i = 0; i < keys.length - 1; i++) {
      if (
        isNullOrUndefined(current[keys[i]]) ||
        typeof current[keys[i]] !== 'object'
      ) {
        current[keys[i]] = {};
      }
      current = current[keys[i]] as Record<string, string | number | object>;
    }

    // Set the final value
    current[keys[keys.length - 1]] = values.value;
  }

  /**
   * Helper method to safely access nested properties using a path array
   */
  protected getThemeValue(
    themeSettings: Record<string, unknown>,
    path: readonly string[],
  ): unknown {
    return path.reduce((obj: unknown, key: string) => {
      if (
        obj !== null &&
        obj !== undefined &&
        typeof obj === 'object' &&
        key in obj
      ) {
        return (obj as Record<string, unknown>)[key];
      }
      return undefined;
    }, themeSettings);
  } /**
   * Format theme value based on configuration
   */
  protected formatThemeValue(
    themeSettings: Record<string, unknown>,
    config: ThemeDetailConfig,
  ): string {
    const value = this.getThemeValue(themeSettings, config.path);

    if (value === null || value === undefined) return 'None';

    if (config.transform) {
      return config.transform(value);
    }

    if (config.options) {
      const option = config.options.find(
        (opt: { value: string; label: string }) => opt.value === value,
      );
      return option?.label ?? 'None';
    }

    // Handle different field types with CSS variable resolution
    if (
      typeof value !== 'string' &&
      typeof value !== 'number' &&
      typeof value !== 'boolean'
    ) {
      return 'None';
    }

    const stringValue = String(value);

    // Check if it's a CSS variable that needs resolution
    if (stringValue.startsWith('var(')) {
      const resolvedValue = this.themeEngine.resolveFormula(stringValue);
      return this.formatResolvedValue(resolvedValue, config.path);
    }

    // Handle non-CSS variable values based on field context
    return this.formatResolvedValue(stringValue, config.path);
  }
  /**
   * Format a resolved value based on the field context
   */
  private formatResolvedValue(value: string, path: readonly string[]): string {
    if (!value) return 'None';

    const pathStr = path.join('.');

    // Font family formatting
    if (pathStr.includes('font.family')) {
      return this.formatFontFamily(value);
    }

    // Font weight formatting
    if (pathStr.includes('font.weight')) {
      return this.formatFontWeight(value);
    }

    // Font transform formatting
    if (pathStr.includes('font.transform')) {
      return this.formatFontTransform(value);
    }

    // Icon pack formatting
    if (pathStr.includes('icon.pack')) {
      return this.formatIconPack(value);
    }

    // Shadow/dropdown formatting
    if (pathStr.includes('shadow') || pathStr.includes('border.shadow')) {
      return this.formatDropShadow(value);
    }

    // Size formatting (add units if needed)
    if (
      pathStr.includes('size') ||
      pathStr.includes('width') ||
      pathStr.includes('radius')
    ) {
      return this.formatSize(value);
    }

    // Default formatting for other types
    return value;
  }
  /**
   * Format font family value to readable name
   */
  private formatFontFamily(value: string): string {
    // Remove quotes if present
    const cleanValue = value.replace(/['"]/g, '');

    // Check if it's in our available fonts list
    const availableFont = AVAILABLE_FONTS.find((font) => font === cleanValue);
    if (availableFont) {
      return availableFont;
    }

    // Return the cleaned value
    return cleanValue || 'Default';
  }

  /**
   * Format font weight value to readable name
   */
  private formatFontWeight(value: string): string {
    const weightMap: Record<string, string> = {
      '300': 'Light',
      '400': 'Normal',
      '500': 'Medium',
      '600': 'Semi Bold',
      '700': 'Bold',
    };

    return weightMap[value] ?? value ?? 'Normal';
  }

  /**
   * Format font transform value to readable name
   */
  private formatFontTransform(value: string): string {
    const transformMap: Record<string, string> = {
      unset: 'None',
      none: 'None',
      capitalize: 'Title Case',
      uppercase: 'Upper Case',
      lowercase: 'Lower Case',
    };

    return transformMap[value] ?? value ?? 'None';
  }

  /**
   * Format icon pack value to readable name
   */
  private formatIconPack(value: string): string {
    const iconPack = ICON_PACKS.find((pack) => pack.value === value);
    return iconPack?.label ?? value ?? 'Default';
  }

  /**
   * Format drop shadow value to readable name
   */
  private formatDropShadow(value: string): string {
    const shadowOption = DROP_SHADOW_OPTIONS.find(
      (option) => option.value === value,
    );
    return shadowOption?.label ?? (value === 'none' ? 'None' : value) ?? 'None';
  }

  /**
   * Format size value with appropriate units
   */
  private formatSize(value: string): string {
    if (!value || value === '0' || value === 'none') return 'None';
    if (value === 'auto') return 'Auto';
    if (value === 'hidden') return 'Hidden';

    // If it already has units, return as is
    if (/\d+(px|rem|em|%|pt)/.test(value)) {
      return value;
    }

    // If it's just a number, assume pixels for most size fields
    if (/^\d+(\.\d+)?$/.test(value)) {
      return `${value}px`;
    }

    return value ?? 'Unknown';
  }

  /**
   * Safely parse a value to float
   */
  protected parseFloatSafely(value: unknown): number {
    if (typeof value === 'number') return value;
    if (typeof value === 'string') {
      const parsed = parseFloat(value);
      return isNaN(parsed) ? 0 : parsed;
    }
    return 0;
  }

  /**
   * Create form fields from field configurations
   */
  protected createFormFieldsFromConfig(
    entity: ThemeResponse,
    fieldConfigs: ThemeFieldConfig[],
  ): FormFieldConfig[] {
    const themeSettings = this.themeEngine.themeSettingsMergedWithDefault(
      entity?.settings ?? {},
    );

    return fieldConfigs.map((config) => {
      const value = this.getThemeValue(themeSettings, config.path);
      return this.createFormFieldFromConfig(config, value);
    });
  }

  /**
   * Create a single form field from configuration
   */
  protected createFormFieldFromConfig(
    config: ThemeFieldConfig,
    value: unknown,
  ): FormFieldConfig {
    switch (config.type) {
      case 'dropdown':
        return this.formFieldFactory.createDropdownWithGrouping({
          id: config.id,
          label: config.label,
          options: config.options ?? [],
          value: value as string,
          props: config.props,
        });

      case 'font-family':
        return this.formFieldFactory.createFontFamilyField(
          config.id,
          value as string,
          config.presets,
        );

      case 'font-weight':
        return this.formFieldFactory.createFontWeightField(
          config.id,
          value as string,
        );

      case 'font-transform':
        return this.formFieldFactory.createFontTransformField(
          config.id,
          value as string,
        );

      case 'color':
        return this.createColorFieldFromConfig(config, value as string);
      case 'size':
      case 'number':
        return this.formFieldFactory.createSizeNumberField(
          config.id,
          this.parseFloatSafely(value),
          config.label,
          (config.props?.['unit'] as string) || 'rem',
        );
      case 'text':
      default:
        // For text fields, we'll use dropdown with a single option or create a custom implementation
        return createTextField(config.id, config.label, value as string, {
          name: config.id,
          props: {
            ...config.props,
          },
        });
    }
  }

  /**
   * Create color field based on configuration
   * This method determines the type of color field to create
   * based on the label or ID, and returns the appropriate field configuration.
   * @param config - The configuration for the color field
   * @param value - The initial value for the color field
   * @return FormFieldConfig for the color field
   */
  protected createColorFieldFromConfig(
    config: ThemeFieldConfig,
    value: string,
  ): FormFieldConfig {
    const group = config.group ?? this.group ?? '';

    // Determine color field type based on label or ID
    const fieldType = this.determineColorFieldType(config.label, config.id);

    switch (fieldType) {
      case 'font':
        return this.formFieldFactory.createFontColorField(
          config.id,
          value,
          group,
          config.presets,
          config.label,
          config.customColors,
        );

      case 'background':
        return this.formFieldFactory.createBackgroundColorField(
          config.id,
          value,
          config.presets,
          config.label,
          config.customColors,
        );
      case 'border':
        return this.formFieldFactory.createBorderColorField(
          config.id,
          value,
          group,
          config.presets,
          undefined,
          config.customColors,
        );
      default: {
        // Generic color field using color combo
        const colorPresets = this.colorService.getContextualColorPresets(
          config.id,
          group ?? 'default',
          ['main', 'content', 'header', 'footer', 'navigation', 'sidebar'],
        );
        return this.formFieldFactory.createColorComboField(
          config.id,
          value,
          colorPresets,
          config.label,
        );
      }
    }
  }

  /**
   * Determine color field type based on label or ID
   */
  protected determineColorFieldType(
    label: string,
    id: string,
  ): 'font' | 'background' | 'border' | 'generic' {
    const labelLower = label.toLowerCase();
    const idLower = id.toLowerCase();

    if (
      labelLower.includes('font') ||
      labelLower.includes('text') ||
      idLower.includes('font') ||
      idLower.includes('text')
    ) {
      return 'font';
    }

    if (labelLower.includes('background') || idLower.includes('background')) {
      return 'background';
    }

    if (labelLower.includes('border') || idLower.includes('border')) {
      return 'border';
    }

    return 'generic';
  }

  /**
   * Create theme detail configurations from field configurations
   */
  protected createThemeDetailsFromConfig(
    fieldConfigs: ThemeFieldConfig[],
  ): ThemeDetailConfig[] {
    return fieldConfigs
      .filter((config) => config.includeInDetails !== false)
      .map((config) => ({
        key: config.label,
        path: config.path,
        transform: config.transform,
        options: config.options,
      }));
  }

  /**
   * Get field configurations for this section (to be implemented by subclasses)
   * @return Array of field configurations
   */
  protected abstract getFieldConfigurations(): ThemeFieldConfig[];

  async mapEntityToEditor(entity: ThemeResponse): Promise<EditorValues> {
    return this.themeEngine.themeSettingsMergedWithDefault(
      entity.settings as ThemeSettings,
    )[this.group];
  }

  async mapEditorToEntity(
    entity: ThemeUpdateRequest,
    values: EditorValues,
  ): Promise<ThemeUpdateRequest> {
    entity.settings = {
      ...entity.settings,
      [this.group]: {
        ...entity.settings?.[this.group],
        ...values,
      },
    };
    return entity;
  }

  abstract getFields(entity: ThemeResponse): FormFieldConfig[];
}
