import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import {
  GeoLockProfileListItem,
  GeoLockProfileResponse,
  EntitiesService,
} from '../../../../api';
import {
  DataGridParams,
  EntityDataAdapter,
  GridResponse,
} from '../../../../shared/models';
import { EntityConfigAdapter, StateOption, ActionIcon } from '../../models';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';

@Injectable({
  providedIn: 'root',
})
export class GeoLockProfilesDataAdapter
  implements
    EntityDataAdapter<GeoLockProfileListItem, GeoLockProfileResponse>,
    EntityConfigAdapter
{
  private readonly entitiesService = inject(EntitiesService);
  private readonly entityStateService = inject(EntityDetailsStateService);

  getList(
    params: DataGridParams,
  ): Observable<GridResponse<GeoLockProfileListItem>> {
    const { owner, limit, offset, sortField, filters, recurse } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.entitiesService.listGeoLockProfiles(
      owner,
      recurse,
      offset,
      limit,
      sortField as string,
      filters,
    );
  }

  getById(id: string): Observable<GeoLockProfileResponse> {
    return this.entitiesService.getGeoLockProfile(id);
  }

  getStateOptions(): StateOption[] {
    return [
      {
        label: 'Active',
        value: GeoLockProfileResponse.StateEnum.Active,
      },
      {
        label: 'Deleted',
        value: GeoLockProfileResponse.StateEnum.Deleted,
      },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.entitiesService.updateGeoLockProfile(id, {
      state: state as GeoLockProfileResponse.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    return [
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change Geo Lock Profile State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
