import {
  ChangeDetectionStrategy,
  Component,
  computed,
  inject,
  input,
  OnInit,
  signal,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { AccordionModule } from 'primeng/accordion';
import { DividerModule } from 'primeng/divider';
import { ButtonModule } from 'primeng/button';
import { TooltipModule } from 'primeng/tooltip';

import { DetailsComponentFactory } from '../../factories/details-config.factory';
import { isNullOrUndefined } from '../../../../shared/utils';
import { EntitySectionConfig } from '../../models/entity-section.interface';
import { ProgressSpinnerModule } from 'primeng/progressspinner';

@Component({
  selector: 'app-generic-details-container',
  imports: [
    CommonModule,
    AccordionModule,
    DividerModule,
    ButtonModule,
    TooltipModule,
    ProgressSpinnerModule,
  ],
  templateUrl: './generic-details-container.component.html',
  styleUrl: './generic-details-container.component.scss',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class GenericDetailsContainerComponent implements OnInit {
  readonly entity = input<Record<string, unknown>>({});
  readonly entityType = input<string>('');

  private readonly detailsFactory = inject(DetailsComponentFactory);

  readonly isLoading = signal<boolean>(false);
  readonly currentMode = signal<'view' | 'edit'>('view');
  activeIndexes: number[] = [0]; // Default to first panel open

  readonly displayedSections = computed(() => {
    const config = this.getEntityConfig();
    if (!config?.sections) return [];

    return config.sections
      .filter((section) => {
        if (typeof section.visible === 'function') {
          return section.visible(this.entity());
        }
        return section.visible !== false;
      })
      .sort((a, b) => (a.order ?? 0) - (b.order ?? 0));
  });

  // Computed properties for sections by category
  readonly informationSections = computed(() =>
    this.displayedSections().filter((s) => s.category === 'information'),
  );

  readonly settingsSections = computed(() =>
    this.displayedSections().filter((s) => s.category === 'settings'),
  );

  readonly statisticsSections = computed(() =>
    this.displayedSections().filter((s) => s.category === 'statistics'),
  );

  readonly customSections = computed(() =>
    this.displayedSections().filter((s) => s.category === 'custom'),
  );

  // Computed properties for category visibility
  readonly hasInformationSections = computed(
    () => this.informationSections().length > 0,
  );

  readonly hasSettingsSections = computed(
    () => this.settingsSections().length > 0,
  );

  readonly hasStatisticsSections = computed(
    () => this.statisticsSections().length > 0,
  );

  readonly hasCustomSections = computed(() => this.customSections().length > 0);

  readonly canToggleMode = computed(() => {
    const config = this.getEntityConfig();
    return config?.allowModeToggle !== false;
  });

  ngOnInit(): void {
    const config = this.getEntityConfig();

    if (config?.defaultMode) {
      this.currentMode.set(config.defaultMode);
    }

    // Load data if needed
    this.loadEntityDetails();
  }

  loadEntityDetails(): void {
    const id = this.entity()?.['id'];
    const type = this.entityType();

    if (isNullOrUndefined(id) || isNullOrUndefined(type)) return;

    // Check if we already have full details
    if (this.hasFullDetails()) return;

    // Load full details if needed
    this.isLoading.set(true);

    setTimeout(() => {
      this.isLoading.set(false);
    }, 500);
  }

  hasFullDetails(): boolean {
    // Check if we have all required fields
    return true;
  }

  getEntityConfig() {
    return this.detailsFactory.getEntityConfig(this.entityType());
  }

  getSectionInputs(section: EntitySectionConfig) {
    return {
      entity: this.entity(),
      mode: this.currentMode(),
      options: {
        title: section.title,
        readonly: section.readonly ?? false,
        expanded: section.expanded,
        category: section.category,
        order: section.order,
      },
    };
  }

  toggleMode(): void {
    this.currentMode.set(this.currentMode() === 'view' ? 'edit' : 'view');
  }
}
