import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import {
  VendorListItem,
  AccountsService,
  VendorResponse,
} from '../../../../api';
import {
  DataGridParams,
  EntityDataAdapter,
  GridResponse,
} from '../../../../shared/models';
import { ActionIcon, EntityConfigAdapter, StateOption } from '../../models';
import { BroadcastEventMessageService } from '../broadcast-event-messages.service';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';

@Injectable({
  providedIn: 'root',
})
export class VendorsDataAdapter
  implements
    EntityDataAdapter<VendorListItem, VendorResponse>,
    EntityConfigAdapter
{
  private readonly accountsService = inject(AccountsService);
  private readonly entityStateService = inject(EntityDetailsStateService);
  private readonly broadcastService = inject(BroadcastEventMessageService);

  getList(params: DataGridParams): Observable<GridResponse<VendorListItem>> {
    const { owner, limit, offset, sortField, filters, counts } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.accountsService.listVendors(
      owner,
      offset,
      limit,
      sortField as string,
      filters,
      counts,
    );
  }

  getById(id: string): Observable<VendorResponse> {
    return this.accountsService.getVendor(id);
  }

  getStateOptions(): StateOption[] {
    return [
      {
        label: 'Active',
        value: VendorResponse.StateEnum.Active,
      },
      {
        label: 'Inactive',
        value: VendorResponse.StateEnum.Inactive,
      },
      {
        label: 'Suspended',
        value: VendorResponse.StateEnum.Suspended,
      },
      {
        label: 'Deleted',
        value: VendorResponse.StateEnum.Deleted,
      },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.accountsService.updateVendor(id, {
      state: state as VendorResponse.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    return [
      {
        id: 'viewInTree',
        icon: 'pi pi-sitemap',
        tooltip: 'View this vendor in the Navigation Tree',
        visible: true,
        action: () => {
          const entity = this.entityStateService.selectedItem();
          if (entity) {
            this.broadcastService.broadcast('view-entity-in-tree', {
              entity,
            });
          }
        },
      },
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change Vendor State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
