import {
  Component,
  computed,
  effect,
  inject,
  input,
  OnInit,
  ViewChild,
  TemplateRef,
  AfterViewInit,
} from '@angular/core';
import {
  DeviceTypeResponse,
  DeviceTypesService,
  DeviceTypeFeature,
} from '../../../../../../api';
import { EntitySectionBaseComponent } from '../../../base';
import {
  isNullOrUndefined,
  isDefined,
  isEmpty,
} from '../../../../../../shared/utils';
import { EntityOperationRegistryService } from '../../../../services/entity/entity-operation-registry.service';
import { tap } from 'rxjs';
import { Validators } from '@angular/forms';
import {
  createModalListField,
  createTextField,
  createDropdownField,
  GridColumn,
  FormFieldConfig,
  ListFieldModalConfig,
} from '../../../../../../shared/models';
import { GridComponent } from '../../../../../../shared/components/grid/grid.component';
import { DynamicFormComponent } from '../../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { ModalListFormComponent } from '../../../../../../shared/components/forms/modal-list-form/modal-list-form.component';
import { AccordionModule } from 'primeng/accordion';
import { ConfirmationService } from 'primeng/api';

interface SettingItem {
  key: string;
  value: string;
}

interface WebhookItem {
  key: string;
  value: string;
}

@Component({
  selector: 'app-device-types-feature',
  imports: [GridComponent, DynamicFormComponent, AccordionModule],
  templateUrl: './device-types-feature.component.html',
  styleUrl: './device-types-feature.component.scss',
})
export class DeviceTypesFeatureComponent
  extends EntitySectionBaseComponent<DeviceTypeResponse>
  implements OnInit, AfterViewInit
{
  constructor() {
    super();

    // Grid columns for view mode
    this.columns = [
      { field: 'key', header: 'Property' },
      { field: 'value', header: 'Value' },
    ];

    // Column definitions for settings and webhook grids
    this.settingsColumns = [
      { field: 'setting', header: 'Setting' },
      { field: 'value', header: 'Value' },
    ];

    this.webhookColumns = [
      { field: 'setting', header: 'Setting' },
      { field: 'value', header: 'Value' },
    ];

    effect(() => {
      const entity = this.entity();
      if (entity && !this.createMode()) {
        this.updateFormFields();
      }
    });
  }

  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly deviceTypesService = inject(DeviceTypesService);
  private readonly confirmationService = inject(ConfirmationService);

  @ViewChild('actionsTemplate', { static: true })
  actionsTemplate!: TemplateRef<unknown>;

  override entity = input<DeviceTypeResponse | null>(null);

  // Column definitions for grids
  settingsColumns: GridColumn[] = [];
  webhookColumns: GridColumn[] = [];

  // Modal configurations
  settingsModalConfig: ListFieldModalConfig<SettingItem> | undefined;
  webhookModalConfig: ListFieldModalConfig<WebhookItem> | undefined;

  // Configuration inputs
  featureKey = input.required<string>(); // The feature key (e.g., 'voice_dialback', 'gps')
  displayTitle = input.required<string>(); // Display name (e.g., 'Voice Dialback', 'GPS')

  // Get the specific feature data
  readonly featureData = computed(() => {
    const deviceType = this.entity();
    const features = deviceType?.features;
    const key = this.featureKey();

    if (isNullOrUndefined(features) || !key) return null;

    return features[key] ?? null;
  });

  // Display feature overview details in a simple grid format
  readonly featureDetails = computed(() => {
    const feature = this.featureData();
    if (isNullOrUndefined(feature)) return [];

    const details = [
      {
        key: 'Feature',
        value: this.displayTitle(),
      },
    ];

    // Settings summary
    if (isDefined(feature.settings)) {
      const settingCount = Object.keys(feature.settings).length;
      details.push({
        key: 'Settings',
        value:
          settingCount > 0 ? `${settingCount} configured` : 'None configured',
      });
    } else {
      details.push({ key: 'Settings', value: 'None configured' });
    }

    // Webhook summary
    if (isDefined(feature.webhook) && feature.webhook !== null) {
      const webhookCount = Object.keys(feature.webhook).length;
      details.push({
        key: 'Webhook',
        value:
          webhookCount > 0
            ? `${webhookCount} properties configured`
            : 'None configured',
      });
    } else {
      details.push({ key: 'Webhook', value: 'None configured' });
    }

    return details;
  });

  readonly hasData = computed(() => {
    return this.featureData() !== null;
  });

  // Settings grid data
  readonly settingsGridData = computed(() => {
    const feature = this.featureData();
    if (isNullOrUndefined(feature?.settings)) return [];

    return Object.entries(feature.settings).map(([key, value]) => ({
      setting: key,
      value:
        typeof value === 'object' && value !== null
          ? JSON.stringify(value)
          : String(value),
    }));
  });

  readonly hasSettings = computed(() => {
    return this.settingsGridData().length > 0;
  });

  // Webhook grid data
  readonly webhookGridData = computed(() => {
    const feature = this.featureData();
    if (isNullOrUndefined(feature?.webhook) || feature.webhook === null)
      return [];

    return Object.entries(feature.webhook).map(([key, value]) => ({
      setting: key,
      value:
        typeof value === 'object' && value !== null
          ? JSON.stringify(value)
          : String(value),
    }));
  });

  readonly hasWebhook = computed(() => {
    return this.webhookGridData().length > 0;
  });

  // Settings items for modal list
  readonly settingsItems = computed(() => {
    const feature = this.featureData();
    if (isNullOrUndefined(feature?.settings)) return [];

    return Object.entries(feature.settings).map(([key, value]) => ({
      key,
      value:
        typeof value === 'object' && value !== null
          ? JSON.stringify(value)
          : String(value),
    }));
  });

  // Webhook items for modal list
  readonly webhookItems = computed(() => {
    const feature = this.featureData();
    if (isNullOrUndefined(feature?.webhook) || feature.webhook === null)
      return [];

    return Object.entries(feature.webhook).map(([key, value]) => ({
      key,
      value:
        typeof value === 'object' && value !== null
          ? JSON.stringify(value)
          : String(value),
    }));
  });

  protected override updateFormFields(): void {
    // Create settings modal configuration
    this.settingsModalConfig = {
      modalComponent: ModalListFormComponent,
      modalTitle: 'Manage Settings',
      defaultItemProvider: () => ({ key: '', value: '' }),
      listItemFormatter: (item: SettingItem) => ({
        id: item.key,
        title: item.key,
        display: `${item.key}: ${item.value}`,
        icon: 'pi pi-cog',
      }),
      modalFields: (item: SettingItem | undefined): FormFieldConfig[] => [
        createTextField('key', 'Setting Key', item?.key ?? '', {
          placeholder: 'Enter setting key...',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Setting key is required',
            },
          ],
        }),
        createDropdownField(
          'value',
          'Setting Value',
          [
            { label: 'True', value: 'true' },
            { label: 'False', value: 'false' },
          ],
          item?.value ?? 'true',
          {
            placeholder: 'Select a value...',
            validations: [
              {
                name: 'required',
                validator: Validators.required,
                message: 'Setting value is required',
              },
            ],
          },
        ),
      ],
      onSubmit: async (item: SettingItem) => {
        if (!item.key?.trim() || !item.value?.trim()) {
          return false;
        }
        return true;
      },
      onDeleteItem: async (item: SettingItem) => {
        return new Promise<boolean>((resolve) => {
          this.confirmationService.confirm({
            header: 'Confirmation',
            message: `Are you sure you want to delete setting "${item.key}"?`,
            closable: true,
            closeOnEscape: true,
            icon: 'pi pi-exclamation-triangle',
            rejectButtonProps: {
              label: 'Cancel',
              severity: 'secondary',
              outlined: true,
            },
            acceptButtonProps: {
              label: 'Delete',
              severity: 'danger',
            },
            accept: () => resolve(true),
            reject: () => resolve(false),
          });
        });
      },
      subHeader: 'Configure feature settings',
      showEditButton: true,
    };

    // Create webhook modal configuration
    this.webhookModalConfig = {
      modalComponent: ModalListFormComponent,
      modalTitle: 'Manage Webhooks',
      defaultItemProvider: () => ({ key: '', value: '' }),
      listItemFormatter: (item: WebhookItem) => ({
        id: item.key,
        title: item.key,
        display: `${item.key}: ${item.value}`,
        icon: 'pi pi-send',
      }),
      modalFields: (item: WebhookItem | undefined): FormFieldConfig[] => [
        createTextField('key', 'Webhook Key', item?.key ?? '', {
          placeholder: 'Enter webhook key...',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Webhook key is required',
            },
          ],
        }),
        createTextField('value', 'Webhook Value', item?.value ?? '', {
          placeholder: 'Enter webhook value...',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Webhook value is required',
            },
          ],
        }),
      ],
      onSubmit: async (item: WebhookItem) => {
        if (!item.key?.trim() || !item.value?.trim()) {
          return false;
        }
        return true;
      },
      onDeleteItem: async (item: WebhookItem) => {
        return new Promise<boolean>((resolve) => {
          this.confirmationService.confirm({
            header: 'Confirmation',
            message: `Are you sure you want to delete webhook "${item.key}"?`,
            closable: true,
            closeOnEscape: true,
            icon: 'pi pi-exclamation-triangle',
            rejectButtonProps: {
              label: 'Cancel',
              severity: 'secondary',
              outlined: true,
            },
            acceptButtonProps: {
              label: 'Delete',
              severity: 'danger',
            },
            accept: () => resolve(true),
            reject: () => resolve(false),
          });
        });
      },
      subHeader: 'Configure feature webhooks',
      showEditButton: true,
    };

    // Create form fields
    this.formFields = [];

    this.formFields.push(
      createModalListField<SettingItem>(
        'settings',
        'Settings',
        this.settingsItems(),
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          name: 'settings',
          props: {
            modalConfig: this.settingsModalConfig,
          },
        },
      ) as FormFieldConfig<unknown>,
      createModalListField<WebhookItem>(
        'webhooks',
        'Webhooks',
        this.webhookItems(),
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          name: 'webhooks',
          props: {
            modalConfig: this.webhookModalConfig,
          },
        },
      ) as FormFieldConfig<unknown>,
    );
  }

  protected override transformFormData(
    formData: Record<string, unknown>,
  ): Partial<DeviceTypeResponse> {
    const entity = this.entity();
    const key = this.featureKey();
    const existingFeature =
      isDefined(entity) && isDefined(entity.features)
        ? entity.features[key]
        : undefined;

    // Start with all existing features to preserve them
    const features: Record<string, DeviceTypeFeature> = entity?.features
      ? { ...entity.features }
      : {};

    // Transform settings from array to object
    const settings: Record<string, unknown> = this.formatSettings(formData);

    // Transform webhooks from array to object
    const webhook: Record<string, unknown> | null =
      this.formatWebhooks(formData);

    // Update only the specific feature being edited
    features[key] = {
      settings: isEmpty(settings)
        ? (existingFeature?.settings ?? {})
        : (settings as DeviceTypeFeature['settings']),
      webhook: isDefined(webhook)
        ? (webhook as DeviceTypeFeature['webhook'])
        : (existingFeature?.webhook ?? undefined),
    };

    return {
      features: features,
    };
  }

  private formatSettings(
    formData: Record<string, unknown>,
  ): Record<string, unknown> {
    const settings: Record<string, unknown> = {};
    if (
      isDefined(formData['settings']) &&
      Array.isArray(formData['settings'])
    ) {
      const settingsArray = formData['settings'] as SettingItem[];
      for (const setting of settingsArray) {
        if (isDefined(setting.key)) {
          // Parse the value as it might be a stringified object or primitive
          let parsedValue: unknown;
          try {
            parsedValue = JSON.parse(setting.value);
          } catch {
            parsedValue = setting.value;
          }
          settings[setting.key] = parsedValue;
        }
      }
    }
    return settings;
  }

  private formatWebhooks(
    formData: Record<string, unknown>,
  ): Record<string, unknown> | null {
    const webhooks: Record<string, unknown> = {};
    if (
      isDefined(formData['webhooks']) &&
      Array.isArray(formData['webhooks'])
    ) {
      const webhooksArray = formData['webhooks'] as WebhookItem[];
      if (webhooksArray.length === 0) {
        return null;
      }
      for (const webhook of webhooksArray) {
        if (isDefined(webhook.key)) {
          // Parse the value as it might be a stringified object or primitive
          let parsedValue: unknown;
          try {
            parsedValue = JSON.parse(webhook.value);
          } catch {
            parsedValue = webhook.value;
          }
          webhooks[webhook.key] = parsedValue;
        }
      }
    }
    return Object.keys(webhooks).length > 0 ? webhooks : null;
  }

  protected override getUpdateMethod() {
    return (
      this.operationRegistry.getOperation('device-types', 'update') ??
      ((id, data) =>
        this.deviceTypesService.updateDeviceType(id, data).pipe(
          tap((response) => {
            this.entityChange.emit(response);
            this.broadcastService.broadcast('entity-updated', {
              entityType: 'device-types',
              operation: 'update',
              entityId: response.id,
              data: response,
            });
          }),
        ))
    );
  }

  ngOnInit(): void {
    this.updateFormFields();
  }

  onCancel(): void {
    this.cancelEdit();
  }

  onSave(): void {
    const formGroup = this.getFormGroup();
    if (formGroup) {
      this.onSubmit(formGroup);
    }
  }

  readonly getActiveValues = computed(() => {
    const activeValues: string[] = [];

    if (this.hasSettings()) {
      activeValues.push('settings');
    }

    if (this.hasWebhook()) {
      activeValues.push('webhooks');
    }

    return activeValues;
  });
}
