import { Injectable } from '@angular/core';

import {
  DeviceProviderResponse,
  DeviceProviderTypeResponse,
} from '../../../api';
import { AdminDetailSection } from '../models';
import { DeviceProviderDetailsSectionComponent } from '../components/device-providers/device-provider-details-section/device-provider-details-section.component';
import { DeviceProviderParametersSectionComponent } from '../components/device-providers/device-provider-parameters-section/device-provider-parameters-section.component';
import { FeedSectionComponent } from '../components/shared/feed-section/feed-section.component';

@Injectable({ providedIn: 'root' })
export class DeviceProviderSectionConfigFactory {
  /**
   * Create sections for device provider information
   * @param deviceProvider The device provider object to display in the section
   * @returns An array of AdminDetailSection objects representing the device provider information sections
   */
  createDeviceProviderInformationSection(
    deviceProvider: DeviceProviderResponse | null,
    deviceProviderType: DeviceProviderTypeResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'device-provider-details',
        title: 'Device Config Details',
        component: DeviceProviderDetailsSectionComponent,
        componentInputs: { entity: deviceProvider },
        category: 'information',
        order: 10,
      },
      {
        id: 'device-provider-parameters',
        title: 'Device Config Parameters',
        component: DeviceProviderParametersSectionComponent,
        componentInputs: {
          entity: deviceProvider,
          entityType: 'device-provider',
          dataPath: 'parameters',
          injectDataAtPath: true,
          deviceProviderType,
        },
        category: 'information',
        order: 20,
      },
    ];
  }

  /**
   * Create sections for device provider changes
   * @param deviceProvider The device provider object to display in the section
   * @returns An array of AdminDetailSection objects representing the device provider changes sections
   */
  createChangesSection(
    deviceProvider: DeviceProviderResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'device-provider-changes',
        title: 'Changes',
        component: FeedSectionComponent,
        componentInputs: {
          entity: deviceProvider,
        },
        category: 'feed',
        order: 10,
      },
    ];
  }

  /**
   * Create all deviceProvider sections in one call
   * @param deviceProvider The deviceProvider object to display in the sections
   * @returns An array of AdminDetailSection objects representing all deviceProvider sections
   */
  createAllDeviceProviderSections(
    deviceProvider: DeviceProviderResponse | null,
    deviceProviderType: DeviceProviderTypeResponse | null,
  ): AdminDetailSection[] {
    return [
      ...this.createDeviceProviderInformationSection(
        deviceProvider,
        deviceProviderType,
      ),
      ...this.createChangesSection(deviceProvider),
    ];
  }
}
