import { CommonModule } from '@angular/common';
import {
  Component,
  computed,
  effect,
  inject,
  input,
  signal,
} from '@angular/core';

import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';
import { Observable } from 'rxjs';

import {
  DeviceProviderCreateRequest,
  DeviceProviderResponse,
  EntitiesService,
  DeviceTypeResponse,
  DeviceProviderTypeResponse,
} from '../../../../../api';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import {
  createNumberField,
  createTextAreaField,
  createTextField,
  FormFieldConfig,
} from '../../../../../shared/models';
import { isNullOrUndefined } from '../../../../../shared/utils';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { EntitySectionBaseComponent } from '../../base';
import { FormBuilderField } from '../../../../../core/models';
import { Validators } from '@angular/forms';

type DeviceProviderEntity =
  | DeviceProviderResponse
  | Partial<DeviceProviderCreateRequest>;

interface FormField {
  id: string;
  title: string;
  type: string;
  value?: string | number;
  defaultValue?: string | number;
  required?: boolean;
  description?: string;
  min?: number;
  max?: number;
}

@Component({
  selector: 'app-device-provider-parameters-section',
  imports: [
    CommonModule,
    ButtonModule,
    InputTextModule,
    GridComponent,
    DynamicFormComponent,
  ],
  templateUrl: './device-provider-parameters-section.component.html',
  styleUrl: './device-provider-parameters-section.component.scss',
})
export class DeviceProviderParametersSectionComponent extends EntitySectionBaseComponent<DeviceProviderEntity> {
  readonly ownerId = input<string | null>(null);
  readonly deviceProviderType = input<DeviceProviderTypeResponse | null>(null);
  readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);

  protected readonly isLoadingDeviceType = signal<boolean>(false);
  protected readonly deviceType = signal<DeviceTypeResponse | null>(null);
  protected readonly customValues = signal<Record<string, string>>({});

  readonly deviceProviderDeviceParameters = computed(() => {
    const deviceProviderDeviceParameters = this.entity()?.parameters;
    const deviceProviderType = this.deviceProviderType();
    if (
      isNullOrUndefined(deviceProviderDeviceParameters) ||
      isNullOrUndefined(deviceProviderType)
    )
      return [];

    const params: { groups: { fields: FormBuilderField[] }[] } = JSON.parse(
      deviceProviderType.parameters,
    );
    const values: { key: string; value: string }[] = [];
    params.groups.forEach((group) => {
      group.fields.forEach((field: FormBuilderField) => {
        values.push({
          key: field.title,
          value:
            typeof deviceProviderDeviceParameters?.[field.id] === 'object'
              ? JSON.stringify(deviceProviderDeviceParameters?.[field.id])
              : ((deviceProviderDeviceParameters?.[
                  field.id
                ] as unknown as string) ?? 'None'),
        });
      });
    });
    return values;
  });
  selectOptions: Record<string, { label: string; value: string }[]> = {};

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    effect(() => {
      const deviceProviderType = this.deviceProviderType();
      if (deviceProviderType) {
        this.updateFormFields();
      }
    });
  }

  protected override updateFormFields(): void {
    const deviceProvider = this.entity();
    const deviceProviderType = this.deviceProviderType();

    if (!deviceProviderType) {
      return;
    }

    const params: { groups: { fields: FormField[] }[] } = JSON.parse(
      deviceProviderType.parameters,
    );
    const fields: FormFieldConfig[] = params.groups.reduce(
      (acc: FormFieldConfig[], group) => {
        const groupFields = group.fields.map((field: FormField) => {
          if (field.type === 'memo') {
            return createTextAreaField(
              field.id,
              field.title,
              deviceProvider?.parameters?.[field.id] as unknown as string,
              {
                name: field.id,
                validations:
                  (field.required ?? false)
                    ? [
                        {
                          name: 'required',
                          validator: Validators.required,
                          message: `${field.title} is required`,
                        },
                      ]
                    : [],

                hint: field.description,
              },
            );
          }
          if (field.type === 'number') {
            return createNumberField(
              field.id,
              field.title,
              deviceProvider?.parameters?.[field.id] as unknown as number,
              {
                name: field.id,
                validations:
                  (field.required ?? false)
                    ? [
                        {
                          name: 'required',
                          validator: Validators.required,
                          message: `${field.title} is required`,
                        },
                      ]
                    : [],

                hint: field.description,
              },
            );
          }
          return createTextField(
            field.id,
            field.title,
            deviceProvider?.parameters?.[field.id] as unknown as string,
            {
              name: field.id,
              validations:
                (field.required ?? false)
                  ? [
                      {
                        name: 'required',
                        validator: Validators.required,
                        message: `${field.title} is required`,
                      },
                    ]
                  : [],

              hint: field.description,
            },
          );
        });
        return [...acc, ...groupFields];
      },
      [],
    );

    this.formFields = [
      {
        key: 'parameters',
        name: 'parameters',
        type: 'group',
        fields,
      },
    ];
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<DeviceProviderEntity>,
  ) => Observable<DeviceProviderResponse> {
    return (
      this.operationRegistry.getOperation<
        DeviceProviderEntity,
        DeviceProviderCreateRequest,
        DeviceProviderResponse,
        'update'
      >('device-providers', 'update') ??
      ((id, data) => this.entitiesService.updateDeviceProvider(id, data))
    );
  }
}
