import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import {
  AssetTagListItem,
  AssetTagResponse,
  EntitiesService,
} from '../../../../api';
import {
  DataGridParams,
  EntityDataAdapter,
  GridResponse,
} from '../../../../shared/models';

import { ActionIcon, StateOption } from '../../models';
import { EntityConfigAdapter } from '../../models/entity-config-adapter.interface';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';

@Injectable({ providedIn: 'root' })
export class AssetTagsDataAdapter
  implements
    EntityDataAdapter<AssetTagListItem, AssetTagResponse>,
    EntityConfigAdapter
{
  private readonly entitiesService = inject(EntitiesService);
  private readonly entityStateService = inject(EntityDetailsStateService);

  getList(params: DataGridParams): Observable<GridResponse<AssetTagListItem>> {
    const { owner, limit, offset, sortField, filters } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.entitiesService.listAssetTags(
      owner,
      offset,
      limit,
      sortField as string,
      filters,
    );
  }

  getById(id: string): Observable<AssetTagResponse> {
    return this.entitiesService.getAssetTag(id);
  }

  getStateOptions(): StateOption[] {
    return [
      { label: 'Active', value: AssetTagResponse.StateEnum.Active },
      { label: 'Deleted', value: AssetTagResponse.StateEnum.Deleted },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.entitiesService.updateAssetTag(id, {
      state: state as AssetTagResponse.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    return [
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change Asset Tags State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
