import { Injectable, Type } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { take } from 'rxjs';

import { GenericDetailsContainerComponent } from '../components/generic-details-container/generic-details-container.component';
import { EntityDetailsConfig } from '../models/entity-section.interface';
import { AssetDetailsContainerComponent } from '../components/assets/asset-details-container/asset-details-container.component';
import { SimCardDetailsContainerComponent } from '../components/sim-cards/sim-card-details-container/sim-card-details-container.component';
import { AssetTagDetailsContainerComponent } from '../components/asset-tags/asset-tag-details-container/asset-tag-details-container.component';
import { DeviceConfigProfileDetailsContainerComponent } from '../components/device-config-profiles/device-config-profile-details-container/device-config-profile-details-container.component';
import { DeviceDetailsContainerComponent } from '../components/device/device-details-container/device-details-container.component';
import { GeofenceDetailsContainerComponent } from '../components/geofences/geofence-details-container/geofence-details-container.component';
import { DeviceProviderDetailsContainerComponent } from '../components/device-providers/device-provider-details-container/device-provider-details-container.component';
import { SmsGatewayProviderDetailsContainerComponent } from '../components/sms-gateway-providers/sms-gateway-provider-details-container/sms-gateway-provider-details-container.component';
import { IoTypeDetailsContainerComponent } from '../components/io-types/io-type-details-container/io-type-details-container.component';
import { OverspeedProfileDetailsContainerComponent } from '../components/overspeed-profiles/overspeed-profile-details-container/overspeed-profile-details-container.component';
import { AlertDetailsContainerComponent } from '../components/alerts/alert-details-container/alert-details-container.component';
import { UserRoleDetailsContainerComponent } from '../components/user-roles/user-role-details-container/user-role-details-container.component';
import { UserDetailsContainerComponent } from '../components/users/user-details-container/user-details-container.component';
import { CustomFieldDetailsContainerComponent } from '../components/custom-fields/custom-field-details-container/custom-field-details-container.component';
import { isDefined } from '../../../shared/utils';
import { EmailProviderDetailsContainerComponent } from '../components/email-providers/email-provider-details-container/email-provider-details-container.component';
import { ThemeDetailsContainerComponent } from '../components/themes/theme-details-container/theme-details-container.component';
import { CostCentreDetailsContainerComponent } from '../components/cost-centres/cost-centre-details-container/cost-centre-details-container.component';
import { GeofenceGroupDetailsContainerComponent } from '../components/geofence-groups/geofence-group-details-container/geofence-group-details-container.component';
import { AssetGroupDetailsContainerComponent } from '../components/asset-groups/asset-group-details-container/asset-group-details-container.component';
import { AssetCategoryDetailsContainerComponent } from '../components/asset-categories/asset-category-details-container/asset-category-details-container.component';
import { VendorClientDetailsContainerComponent } from '../components/vendor-clients/vendor-client-details-container/vendor-client-details-container.component';
import { DistributorVendorDetailsContainerComponent } from '../components/distributor-vendors/distributor-vendor-details-container/distributor-vendor-details-container.component';
import { AssetRatingProfileDetailsContainerComponent } from '../components/asset-rating-profiles/asset-rating-profile-details-container/asset-rating-profile-details-container.component';
import { SuperAdminDistributorsDetailsContainerComponent } from '../components/super-admin-distributors/super-admin-distributors-details-container/super-admin-distributors-details-container.component';
import { AssetStateProfileDetailsContainerComponent } from '../components/asset-state-profiles/asset-state-profile-details-container/asset-state-profile-details-container.component';
import { CompanyGroupDetailsContainerComponent } from '../components/company-groups/company-group-details-container/company-group-details-container.component';
import { RoadProfileDetailsContainerComponent } from '../components/road-profiles/road-profile-details-container/road-profile-details-container.component';
import { GeoLockProfileDetailsContainerComponent } from '../components/geo-lock-profiles/geo-lock-profile-details-container/geo-lock-profile-details-container.component';
import { DeviceTypesDetailsContainerComponent } from '../components/device-types/device-types-details-container/device-types-details-container.component';

type ComponentRegistry = Record<string, Type<unknown>>;
type ConfigRegistry = Record<string, EntityDetailsConfig>;

@Injectable({ providedIn: 'root' })
export class DetailsComponentFactory {
  private componentRegistry: ComponentRegistry = {};
  private configRegistry: ConfigRegistry = {};
  private entityTypeMapping: Record<string, string> = {
    'client-assets': 'asset',
    'client-sim-cards': 'simcard',
    'client-alerts': 'alert',
    'client-asset-tags': 'asset-tag',
    'client-hardware-devices': 'device',
    'client-geofences': 'geofence',
    'client-io-types': 'io-type',
    'client-sms-gateway-providers': 'sms-gateway-provider',
    'client-device-providers': 'device-provider',
    'client-device-config-profiles': 'device-config-profile',
    'client-overspeed-profiles': 'overspeed-profile',
    'client-user-roles': 'user-role',
    'client-users': 'user',
    'client-custom-fields': 'custom-field',
    'client-cost-centres': 'cost-centre',
    'client-geofence-groups': 'geofence-group',
    'client-asset-groups': 'asset-group',
    'client-asset-categories': 'asset-category',
    'client-asset-rating-profiles': 'asset-rating-profile',
    'client-asset-state-profiles': 'asset-state-profile',
    'client-road-profiles': 'road-profile',
    'client-geo-lock-profiles': 'geo-lock-profile',
    'vendor-io-types': 'io-type',
    'vendor-overspeed-profiles': 'overspeed-profile',
    'vendor-device-config-profiles': 'device-config-profile',
    'vendor-sms-gateway-providers': 'sms-gateway-provider',
    'vendor-custom-fields': 'custom-field',
    'vendor-email-providers': 'email-provider',
    'vendor-users': 'user',
    'vendor-user-roles': 'user-role',
    'vendor-clients': 'client',
    'vendor-themes': 'theme',
    'vendor-company-groups': 'company-group',
    'vendor-road-profiles': 'road-profile',
    'vendor-geo-lock-profiles': 'geo-lock-profile',
    'distributor-device-providers': 'device-provider',
    'distributor-overspeed-profiles': 'overspeed-profile',
    'distributor-road-profiles': 'road-profile',
    'distributor-themes': 'theme',
    'distributor-users': 'user',
    'distributor-user-roles': 'user-role',
    'distributor-company-groups': 'company-group',
    'distributor-vendors': 'vendor',
    'super-admin-distributors': 'distributor',
    'super-admin-device-types': 'device-types',
    'system-users': 'user',
    'system-user-roles': 'user-role',
    // ... Add more mappings as needed
  };

  constructor() {
    this.registerDefaultComponents();
  }

  /**
   * Register default components
   */
  private registerDefaultComponents(): void {
    this.registerComponent('asset', AssetDetailsContainerComponent);
    this.registerComponent('simcard', SimCardDetailsContainerComponent);
    this.registerComponent('asset-tag', AssetTagDetailsContainerComponent);
    this.registerComponent(
      'device-config-profile',
      DeviceConfigProfileDetailsContainerComponent,
    );
    this.registerComponent('device', DeviceDetailsContainerComponent);
    this.registerComponent('geofence', GeofenceDetailsContainerComponent);
    this.registerComponent(
      'device-provider',
      DeviceProviderDetailsContainerComponent,
    );
    this.registerComponent(
      'sms-gateway-provider',
      SmsGatewayProviderDetailsContainerComponent,
    );
    this.registerComponent('io-type', IoTypeDetailsContainerComponent);
    this.registerComponent(
      'overspeed-profile',
      OverspeedProfileDetailsContainerComponent,
    );
    this.registerComponent('alert', AlertDetailsContainerComponent);
    this.registerComponent('user-role', UserRoleDetailsContainerComponent);
    this.registerComponent('user', UserDetailsContainerComponent);
    this.registerComponent(
      'custom-field',
      CustomFieldDetailsContainerComponent,
    );
    this.registerComponent('theme', ThemeDetailsContainerComponent);
    this.registerComponent('cost-centre', CostCentreDetailsContainerComponent);
    this.registerComponent(
      'geofence-group',
      GeofenceGroupDetailsContainerComponent,
    );
    this.registerComponent('asset-group', AssetGroupDetailsContainerComponent);
    this.registerComponent(
      'asset-category',
      AssetCategoryDetailsContainerComponent,
    );
    this.registerComponent(
      'email-provider',
      EmailProviderDetailsContainerComponent,
    );
    this.registerComponent('client', VendorClientDetailsContainerComponent);
    this.registerComponent(
      'vendor',
      DistributorVendorDetailsContainerComponent,
    );
    this.registerComponent(
      'asset-rating-profile',
      AssetRatingProfileDetailsContainerComponent,
    );
    this.registerComponent(
      'distributor',
      SuperAdminDistributorsDetailsContainerComponent,
    );
    this.registerComponent(
      'asset-state-profile',
      AssetStateProfileDetailsContainerComponent,
    );
    this.registerComponent(
      'company-group',
      CompanyGroupDetailsContainerComponent,
    );
    this.registerComponent(
      'road-profile',
      RoadProfileDetailsContainerComponent,
    );
    this.registerComponent(
      'geo-lock-profile',
      GeoLockProfileDetailsContainerComponent,
    );
    this.registerComponent(
      'device-types',
      DeviceTypesDetailsContainerComponent,
    );
  }

  /**
   * Register a component for an entity type
   */
  registerComponent(entityType: string, component: Type<unknown>): void {
    this.componentRegistry[entityType.toLowerCase()] = component;
  }

  /**
   * Register a configuration for an entity type
   */
  registerEntityConfig(entityType: string, config: EntityDetailsConfig): void {
    this.configRegistry[entityType.toLowerCase()] = config;
  }

  /**
   * Get the appropriate details component for an entity type
   */
  getDetailsComponent(entityType: string): Type<unknown> {
    if (!entityType) return GenericDetailsContainerComponent;
    const normalizedType = entityType.toLowerCase();
    return (
      this.componentRegistry[normalizedType] ?? GenericDetailsContainerComponent
    );
  }

  /**
   * Get the configuration for an entity type
   */
  getEntityConfig(entityType: string): EntityDetailsConfig | undefined {
    if (!entityType) return undefined;

    const normalizedType = entityType.toLowerCase();
    return this.configRegistry[normalizedType];
  }

  /**
   * Extract entity type from ActivatedRoute
   * @param route The ActivatedRoute containing route parameters
   * @param paramName The name of the parameter containing the entity type (default: 'type')
   * @returns The entity type as a lowercase string, or empty string if not found
   */
  getEntityTypeFromRoute(
    route: ActivatedRoute | string,
    paramName = 'type',
  ): string {
    // If given a string (for backward compatibility), use the old behavior
    if (typeof route === 'string') {
      return this.normalizeEntityType(route);
    }

    // Extract the entity type from route parameters
    let entityType = '';

    // Try to get from params first (for normal route params)
    route.params.pipe(take(1)).subscribe((params) => {
      if (isDefined(params) && isDefined(params[paramName])) {
        entityType = this.normalizeEntityType(params[paramName]);
      }
    });

    // If not found in params, try queryParams
    if (!entityType) {
      route.queryParams.pipe(take(1)).subscribe((queryParams) => {
        if (isDefined(queryParams) && isDefined(queryParams[paramName])) {
          entityType = this.normalizeEntityType(queryParams[paramName]);
        }
      });
    }

    // If still not found, try route data (for static route configurations like tree tables)
    if (!entityType) {
      const routeData = route.snapshot.data;
      if (isDefined(routeData) && isDefined(routeData[paramName])) {
        entityType = this.normalizeEntityType(routeData[paramName]);
      }
    }

    return entityType;
  }

  /**
   * Normalize entity type using the mapping or by converting to lowercase
   * @param rawType The raw entity type from the route
   * @returns The normalized entity type
   */
  private normalizeEntityType(rawType: string): string {
    if (!rawType) return '';

    const normalizedType = rawType.toLowerCase();
    return this.entityTypeMapping[normalizedType] || normalizedType;
  }

  /**
   * Register a mapping between a route parameter value and an entity type
   * @param routeValue The value found in route parameters
   * @param entityType The internal entity type to map to
   */
  registerEntityTypeMapping(routeValue: string, entityType: string): void {
    this.entityTypeMapping[routeValue.toLowerCase()] = entityType.toLowerCase();
  }

  /**
   * Get the mapping for a route parameter value
   * @param routeValue The value found in route parameters
   * @returns The mapped entity type, or the original value if no mapping exists
   */
  getEntityTypeMapping(routeValue: string): string {
    const normalizedValue = routeValue.toLowerCase();
    return this.entityTypeMapping[normalizedValue] || normalizedValue;
  }
}
