import { Injectable } from '@angular/core';

import { DeviceTypeResponse } from '../../../api';
import { AdminDetailSection } from '../models';
import { isDefined } from '../../../shared/utils';
import { FeatureSectionComponent } from '../components/shared/feature-section/feature-section.component';
import { FeedSectionComponent } from '../components/shared/feed-section/feed-section.component';
import { DeviceTypesInformationSectionComponent } from '../components/device-types/device-types-information-section/device-types-information-section.component';
import { DeviceTypesIoSectionComponent } from '../components/device-types/device-types-io-section/device-types-io-section.component';
import { DeviceTypesFeaturesSectionComponent } from '../components/device-types/device-types-features-section/device-types-features-section.component';
import { DeviceTypesFeatureComponent } from '../components/device-types/device-types-features-section/device-types-feature/device-types-feature.component';
import { DeviceTypesSettingComponent } from '../components/device-types/device-types-settings-section/device-types-setting/device-types-setting.component';
import { DeviceTypesSettingsSectionComponent } from '../components/device-types/device-types-settings-section/device-types-settings-section.component';
import { isNullOrUndefined } from '../../../shared/utils/type-guards';
import { DeviceTypesAccessoriesSectionComponent } from '../components/device-types/device-types-accessories-section/device-types-accessories-section.component';
import { DeviceTypesAccessoryComponent } from '../components/device-types/device-types-accessories-section/device-types-accessory/device-types-accessory.component';
import { DeviceTypesCachedFieldComponent } from '../components/device-types/device-types-io-section/device-types-cached-field/device-types-cached-field.component';

@Injectable({ providedIn: 'root' })
export class DeviceTypesSectionConfigFactory {
  /**
   * Create sections for device type information
   * @param deviceType The device type object to display in the section
   * @returns An array of AdminDetailSection objects representing the device type information sections
   */
  createDeviceTypeInformationSections(
    deviceType: DeviceTypeResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'device-type-details',
        title: 'Device Type Details',
        component: DeviceTypesInformationSectionComponent,
        componentInputs: {
          entity: deviceType,
        },
        category: 'information',
        order: 10,
      },
    ];
  }

  /**
   * Create sections for device type features
   * @param deviceType The device type object to display in the section
   * @returns An array of AdminDetailSection objects representing the device type features sections
   */
  createDeviceTypeFeaturesSection(
    deviceType: DeviceTypeResponse | null,
  ): AdminDetailSection[] {
    if (!deviceType?.features) {
      return [];
    }

    return [
      {
        id: 'device-type-features',
        title: 'Features',
        component: FeatureSectionComponent,
        componentInputs: {
          entity: deviceType,
          entityType: 'device-types',
          features: Object.entries(deviceType.features).map(
            ([key, feature]) => ({
              ...feature,
              key,
            }),
          ),
        },
        category: 'features',
        order: 10,
      },
    ];
  }

  /**
   * Create sections for device type changes
   * @param deviceType The device type object to display in the section
   * @returns An array of AdminDetailSection objects representing the device type changes sections
   */
  createChangesSection(
    deviceType: DeviceTypeResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'device-type-changes',
        title: 'Changes',
        component: FeedSectionComponent,
        componentInputs: {
          entity: deviceType,
        },
        category: 'feed',
        order: 10,
      },
    ];
  }

  createIOCapabilitiesSections(
    deviceType: DeviceTypeResponse | null,
  ): AdminDetailSection[] {
    const ioTypes = [
      {
        key: 'digital_input',
        title: 'Digital Inputs',
        description:
          'Configure digital input capabilities for this device type.',
        icon: 'pi pi-arrow-left',
        iconType: 'primeng',
      },
      {
        key: 'digital_output',
        title: 'Digital Outputs',
        description:
          'Configure digital output capabilities for this device type.',
        icon: 'pi pi-arrow-right',
        iconType: 'primeng',
      },
      {
        key: 'analog_input',
        title: 'Analog Inputs',
        description:
          'Configure analog input capabilities for this device type.',
        icon: 'gauge-simple-high-solid',
        iconType: 'svg',
      },
      {
        key: 'temperature_input',
        title: 'Temperature Sensors',
        description:
          'Configure temperature sensor capabilities for this device type.',
        icon: 'temperature-half-solid',
        iconType: 'svg',
      },
      {
        key: 'can_input',
        title: 'CAN Inputs',
        description: 'Configure CAN input capabilities for this device type.',
        icon: 'oil-can-solid',
        iconType: 'svg',
      },
      {
        key: 'counter_input',
        title: 'Counter Inputs',
        description:
          'Configure counter input capabilities for this device type.',
        icon: 'pi pi-clock',
        iconType: 'primeng',
      },
      {
        key: 'camera_input',
        title: 'Camera Inputs',
        description:
          'Configure camera input capabilities for this device type.',
        icon: 'pi pi-camera',
        iconType: 'primeng',
      },
      {
        key: 'value_input',
        title: 'Value Inputs',
        description: 'Configure value input capabilities for this device type.',
        icon: 'pi pi-list-check',
        iconType: 'primeng',
      },
    ];

    const sections: AdminDetailSection[] = [];

    for (const [index, ioType] of ioTypes.entries()) {
      sections.push({
        id: `io-${ioType.key}`,
        title: ioType.title,
        component: DeviceTypesIoSectionComponent,
        componentInputs: {
          entity: deviceType,
          entityType: 'device-types',
          ioTypeKey: ioType.key,
          displayTitle: ioType.title,
          description: ioType.description,
          icon: ioType.icon,
          iconType: ioType.iconType,
        },
        category: 'io',
        order: (index + 1) * 10 + 20,
      });
    }

    // Add cached fields section as the last IO capability section
    sections.push({
      id: 'io-cached-fields',
      title: 'Cached Fields',
      component: DeviceTypesCachedFieldComponent,
      componentInputs: {
        entity: deviceType,
        entityType: 'device-types',
      },
      category: 'io',
      order: 110, // After all IO types
    });

    return sections;
  }

  createAccessoriesSection(
    deviceType: DeviceTypeResponse | null,
  ): AdminDetailSection[] {
    const sections: AdminDetailSection[] = [];

    // Add the accessories list section first
    sections.push({
      id: 'device-type-accessories',
      title: 'Accessories',
      component: DeviceTypesAccessoriesSectionComponent,
      componentInputs: {
        entity: deviceType,
        entityType: 'device-types',
      },
      category: 'accessories',
      order: 20, // List section comes first
    });

    // Add individual accessory sections if they exist
    if (deviceType?.accessories) {
      const accessories = Object.entries(deviceType.accessories);

      for (const [index, [key, accessory]] of accessories.entries()) {
        if (isNullOrUndefined(accessory)) continue;
        const displayTitle =
          isDefined(accessory.name) && accessory.name.trim() !== ''
            ? accessory.name
            : key
                .replaceAll('_', ' ')
                .replaceAll(/\b\w/g, (l) => l.toUpperCase());

        sections.push({
          id: `accessory-${key}`,
          title: displayTitle,
          component: DeviceTypesAccessoryComponent,
          componentInputs: {
            entity: deviceType,
            entityType: 'device-types',
            accessoryKey: key,
            displayTitle: displayTitle,
          },
          category: 'accessories',
          order: (index + 1) * 10 + 30, // Start at 40, increment by 10
        });
      }
    }

    return sections;
  }

  createFeaturesSection(
    deviceType: DeviceTypeResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'device-type-features',
        title: 'Features',
        component: DeviceTypesFeaturesSectionComponent,
        componentInputs: {
          entity: deviceType,
          entityType: 'device-types',
        },
        category: 'features',
        order: 25, // After accessories (20)
      },
    ];
  }

  createIndividualFeaturesSection(
    deviceType: DeviceTypeResponse | null,
  ): AdminDetailSection[] {
    if (!deviceType?.features) {
      return [];
    }

    const sections: AdminDetailSection[] = [];
    const features = Object.entries(deviceType.features);

    for (const [index, [key, feature]] of features.entries()) {
      if (isNullOrUndefined(feature)) continue;
      const displayTitle =
        isDefined(feature['name']) && feature['name'].trim() !== ''
          ? feature['name']
          : key
              .replaceAll('_', ' ')
              .replaceAll(/\b\w/g, (l) => l.toUpperCase());

      sections.push({
        id: `individual-feature-${key}`,
        title: displayTitle,
        component: DeviceTypesFeatureComponent,
        componentInputs: {
          entity: deviceType,
          entityType: 'device-types',
          featureKey: key,
          displayTitle: displayTitle,
        },
        category: 'features',
        order: (index + 1) * 10 + 30, // After the overview section
      });
    }

    return sections;
  }

  createSettingsSection(
    deviceType: DeviceTypeResponse | null,
  ): AdminDetailSection[] {
    const sections: AdminDetailSection[] = [];

    // Add the settings list section first
    sections.push({
      id: 'device-type-settings',
      title: 'Settings',
      component: DeviceTypesSettingsSectionComponent,
      componentInputs: {
        entity: deviceType,
        entityType: 'device-types',
      },
      category: 'settings',
      order: 20, // List section comes first
    });

    // Add individual setting field sections if they exist
    const parsedDefinition = this.parseSettingsDefinition(deviceType);
    if (parsedDefinition) {
      const groups = this.extractGroupsFromDefinition(parsedDefinition);
      const fieldSections = this.buildSectionsFromGroups(groups, deviceType);
      sections.push(...fieldSections);
    }

    return sections;
  }

  private parseSettingsDefinition(
    deviceType: DeviceTypeResponse | null,
  ): Record<string, unknown> | null {
    if (
      isNullOrUndefined(deviceType?.settingsDefinition) ||
      deviceType.settingsDefinition.trim() === ''
    ) {
      return null;
    }

    try {
      return JSON.parse(deviceType.settingsDefinition);
    } catch {
      return null;
    }
  }

  private extractGroupsFromDefinition(
    parsedDefinition: Record<string, unknown>,
  ): Record<string, unknown>[] {
    if (
      !isDefined(parsedDefinition['groups']) ||
      !Array.isArray(parsedDefinition['groups'])
    ) {
      return [];
    }

    return parsedDefinition['groups'] as Record<string, unknown>[];
  }

  private buildSectionsFromGroups(
    groups: Record<string, unknown>[],
    deviceType: DeviceTypeResponse | null,
  ): AdminDetailSection[] {
    const sections: AdminDetailSection[] = [];

    for (const group of groups) {
      const groupSections = this.buildSectionsFromGroup(
        group,
        deviceType,
        sections.length,
      );
      sections.push(...groupSections);
    }

    return sections;
  }

  private buildSectionsFromGroup(
    group: Record<string, unknown>,
    deviceType: DeviceTypeResponse | null,
    currentSectionCount: number,
  ): AdminDetailSection[] {
    if (!this.isValidGroup(group)) {
      return [];
    }

    const fields = group['fields'] as Record<string, unknown>[];
    return fields.map((field, index) =>
      this.createFieldSection(field, deviceType, currentSectionCount + index),
    );
  }

  private isValidGroup(group: Record<string, unknown>): boolean {
    return (
      isDefined(group) &&
      typeof group === 'object' &&
      isDefined(group['fields']) &&
      Array.isArray(group['fields'])
    );
  }

  private createFieldSection(
    field: Record<string, unknown>,
    deviceType: DeviceTypeResponse | null,
    index: number,
  ): AdminDetailSection {
    const fieldId = this.extractFieldId(field);
    const fieldTitle = this.extractFieldTitle(field);
    const displayTitle = fieldTitle || fieldId || 'Unknown Field';

    return {
      id: `settings-field-${fieldId}`,
      title: displayTitle,
      component: DeviceTypesSettingComponent,
      componentInputs: {
        entity: deviceType,
        entityType: 'device-types',
        fieldId: fieldId,
        displayTitle: displayTitle,
      },
      category: 'settings',
      order: (index + 1) * 10 + 30, // Start at 40, increment by 10
    };
  }

  private extractFieldId(field: Record<string, unknown>): string {
    return isDefined(field['id']) && typeof field['id'] === 'string'
      ? field['id']
      : 'unknown';
  }

  private extractFieldTitle(field: Record<string, unknown>): string {
    return isDefined(field['title']) && typeof field['title'] === 'string'
      ? field['title']
      : this.extractFieldId(field);
  }

  /**
   * Create all device type sections in one call
   * @param deviceType The device type object to display in the sections
   * @returns An array of AdminDetailSection objects representing all device type sections
   */
  createAllDeviceTypeSections(
    deviceType: DeviceTypeResponse | null,
  ): AdminDetailSection[] {
    return [
      ...this.createDeviceTypeInformationSections(deviceType),
      ...this.createSettingsSection(deviceType),
      ...this.createIOCapabilitiesSections(deviceType),
      ...this.createAccessoriesSection(deviceType),
      ...this.createFeaturesSection(deviceType),
      ...this.createIndividualFeaturesSection(deviceType),
      ...this.createChangesSection(deviceType),
    ];
  }
}
