import { Injectable } from '@angular/core';
import { AdminDetailSection } from '../models';

import { UserResponse } from '../../../api';
import { UserDetailsSectionComponent } from '../components/users/user-details-section/user-details-section.component';
import { FeedSectionComponent } from '../components/shared/feed-section/feed-section.component';
import { UserRolesSectionComponent } from '../components/users/user-roles-section/user-roles-section.component';
import { UserApiKeysSectionComponent } from '../components/users/user-api-keys-section/user-api-keys-section.component';
import { UserNotifySectionComponent } from '../components/users/user-notify-section/user-notify-section.component';
import { UserClientPermissionsSectionComponent } from '../components/users/user-client-permissions-section/user-client-permissions-section.component';

@Injectable({ providedIn: 'root' })
export class UserSectionConfigFactory {
  /**
   * Create sections for user information
   * @param user The user object to display in the section
   * @returns An array of AdminDetailSection objects representing the user information sections
   */
  createUserInformationSections(
    user: UserResponse | null,
    ownerId: string,
  ): AdminDetailSection[] {
    return [
      {
        id: 'user-details',
        title: 'User Details',
        component: UserDetailsSectionComponent,
        componentInputs: { entity: user, ownerId },
        category: 'information',
        order: 10,
      },
      {
        id: 'user-roles',
        title: 'User Roles',
        component: UserRolesSectionComponent,
        componentInputs: { entity: user },
        category: 'information',
        order: 20,
      },
      {
        id: 'user-client-permissions',
        title: 'User Client Permissions',
        component: UserClientPermissionsSectionComponent,
        componentInputs: { entity: user },
        category: 'information',
        order: 30,
      },
      {
        id: 'user-api-keys',
        title: 'User API Keys',
        component: UserApiKeysSectionComponent,
        componentInputs: { entity: user },
        category: 'information',
        order: 70,
      },
      {
        id: 'user-notifications',
        title: 'User Notifications',
        component: UserNotifySectionComponent,
        componentInputs: { entity: user },
        category: 'information',
        order: 80,
      },
    ];
  }

  /**
   * Create sections for user changes
   * @param user The user object to display in the section
   * @returns An array of AdminDetailSection objects representing the user changes sections
   */
  createChangesSection(user: UserResponse | null): AdminDetailSection[] {
    return [
      {
        id: 'user-changes',
        title: 'Changes',
        component: FeedSectionComponent,
        componentInputs: {
          entity: user,
        },
        category: 'feed',
        order: 10,
      },
    ];
  }

  /**
   * Create all user sections in one call
   * @param user The user object to display in the sections
   * @returns An array of AdminDetailSection objects representing all user sections
   */
  createAllUserSections(
    user: UserResponse | null,
    ownerId: string,
  ): AdminDetailSection[] {
    return [
      ...this.createUserInformationSections(user, ownerId),
      ...this.createChangesSection(user),
    ];
  }
}
