import { CommonModule } from '@angular/common';
import {
  ChangeDetectorRef,
  Component,
  computed,
  effect,
  inject,
  input,
  signal,
  untracked,
} from '@angular/core';

import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';
import { Observable } from 'rxjs';

import {
  EntitiesService,
  DeviceTypeResponse,
  DeviceTypeIOCapabilities,
  DeviceCreateRequest,
  DeviceResponse,
} from '../../../../../api';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import {
  createDropdownField,
  createNumberField,
  createTextField,
  FormAction,
  FormFieldConfig,
} from '../../../../../shared/models';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { EntitySectionBaseComponent } from '../../base';
import {
  isDefined,
  isEmpty,
  setNestedProperty,
  JsonObject,
} from '../../../../../shared/utils';
import { DynamicFormModalComponent } from '../../../../../shared/components/forms/dynamic-form-modal/dynamic-form-modal.component';
import { FormGroup, Validators } from '@angular/forms';

type DeviceEntity = DeviceResponse | DeviceCreateRequest;
type FormValue = string | number | boolean | null | undefined;

// Define the type parameters for the mixin
@Component({
  selector: 'app-device-parameters-section',
  imports: [
    CommonModule,
    ButtonModule,
    InputTextModule,
    GridComponent,
    DynamicFormComponent,
    DynamicFormModalComponent,
  ],
  templateUrl: './device-parameters-section.component.html',
  styleUrl: './device-parameters-section.component.scss',
})
export class DeviceParametersSectionComponent extends EntitySectionBaseComponent<DeviceEntity> {
  readonly ownerId = input<string | null>(null);
  readonly deviceType = input.required<DeviceTypeResponse | null>();
  readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly cdr = inject(ChangeDetectorRef);

  protected readonly customValues = signal<Record<string, string>>({});
  protected readonly showSecondaryInput = signal<boolean>(false);
  protected readonly secondaryInputValue = signal<string | null>(null);
  protected readonly thresholdValue = signal<number | null>(null);

  protected readonly showThresholdField = computed(() => {
    return (
      this.showSecondaryInput() &&
      isDefined(this.secondaryInputValue()) &&
      this.secondaryInputValue() !== '' &&
      this.secondaryInputValue() !== '_custom_'
    );
  });
  readonly deviceDeviceParameters = computed(() => {
    const deviceDeviceParameters = this.entity()?.parameters;
    if (!isDefined(deviceDeviceParameters)) return [];

    return [
      {
        key: 'Active Input',
        value: isEmpty(deviceDeviceParameters.active_input)
          ? 'None'
          : deviceDeviceParameters.active_input,
      },
      {
        key: 'Hours Input',
        value: isEmpty(deviceDeviceParameters.hours_input)
          ? 'None'
          : deviceDeviceParameters.hours_input,
      },
      {
        key: 'Idling Input',
        value: isEmpty(deviceDeviceParameters.idling_input)
          ? 'None'
          : deviceDeviceParameters.idling_input,
      },
      {
        key: 'Secondary Idling Input',
        value: isEmpty(deviceDeviceParameters['idling_input_2'])
          ? 'None'
          : deviceDeviceParameters['idling_input_2'],
      },
      {
        key: 'Secondary Input Threshold',
        value: isDefined(deviceDeviceParameters['idling_input_2_threshold'])
          ? `> ${deviceDeviceParameters['idling_input_2_threshold']}`
          : 'N/A',
      },
      {
        key: 'Invert Idling Input',
        value:
          (deviceDeviceParameters.idling_input_invert ?? false) ? 'Yes' : 'No',
      },
    ];
  });
  isCustomInputModalOpen = false;
  customInputFormFields: FormFieldConfig[] = [];
  private currentEditField: string | null = null;
  modalActions: FormAction[] = [
    {
      label: 'Cancel',
      action: () => this.onClose(),
      severity: 'secondary',
    },
    {
      label: 'Save',
      action: (formGroup: FormGroup) => {
        this.onSaveCustomInput(formGroup);
      },
      severity: 'primary',
    },
  ];

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    effect(() => {
      const deviceType = this.deviceType();

      if (isDefined(deviceType)) {
        untracked(() => {
          this.updateFormFields();
        });
      }
    });
  }

  protected override onEntityLoaded(entity: DeviceEntity): void {
    // Reset custom values when switching entities
    this.customValues.set({});

    // Initialize secondary input state from entity
    if (entity?.parameters) {
      const hasSecondary =
        isDefined(entity.parameters['idling_input_2']) &&
        entity.parameters['idling_input_2'] !== '';
      this.showSecondaryInput.set(hasSecondary);
      this.secondaryInputValue.set(entity.parameters['idling_input_2'] ?? null);
      this.thresholdValue.set(
        entity.parameters['idling_input_2_threshold'] ?? null,
      );
    } else {
      this.showSecondaryInput.set(false);
      this.secondaryInputValue.set(null);
      this.thresholdValue.set(null);
    }

    super.onEntityLoaded(entity);
  }

  /**
   * Clear threshold field value when secondary input is removed
   */
  private clearThresholdField(): void {
    const form = this.getFormGroup();
    if (form?.get('idling_input_2_threshold')) {
      form.get('idling_input_2_threshold')?.reset();
      form.get('idling_input_2_threshold')?.setValue(null);
    }
    this.thresholdValue.set(null);
  }

  /**
   * Handle AND button click to show secondary input
   */
  onAddSecondaryInput(): void {
    this.showSecondaryInput.set(true);
    this.updateFormFields();
  }

  /**
   * Remove secondary input and threshold fields
   */
  onRemoveSecondaryInput(): void {
    this.showSecondaryInput.set(false);
    this.secondaryInputValue.set(null);
    this.clearThresholdField();

    // Clear the form values
    const form = this.getFormGroup();
    if (form) {
      form.get('idling_input_2')?.reset();
      form.get('idling_input_2')?.setValue(null);
      form.get('idling_input_2_threshold')?.reset();
      form.get('idling_input_2_threshold')?.setValue(null);
      form.get('idling_input_2')?.markAsDirty();
      form.get('idling_input_2_threshold')?.markAsDirty();
    }

    // Rebuild form without secondary fields
    this.updateFormFields();
  }

  private getIOOptions(
    ioCapabilities: DeviceTypeIOCapabilities | undefined,
    currentValue?: string | null,
  ): { label: string; value: string }[] {
    const options: { label: string; value: string }[] = [];

    // Add digital inputs
    if (isDefined(ioCapabilities?.digital_input)) {
      for (const key of Object.keys(ioCapabilities.digital_input)) {
        let label = key;

        // Special handling for known inputs
        if (key === 'ignition') {
          label = 'Ignition Wire';
        } else if (key === 'movement') {
          label = 'Movement Detection';
        }

        options.push({ label, value: key });
      }
    }

    // Add digital outputs
    if (isDefined(ioCapabilities?.digital_output)) {
      for (const key of Object.keys(ioCapabilities.digital_output)) {
        options.push({ label: key, value: key });
      }
    }

    // Sort alphabetically
    options.sort((a, b) => a.label.localeCompare(b.label));

    // Add current value if it's custom (not in the list and not empty/none/emulate/speed)
    if (
      isDefined(currentValue) &&
      !options.some((o) => o.value === currentValue) &&
      !['', 'none', 'emulate', 'speed'].includes(currentValue)
    ) {
      options.push({ label: currentValue, value: currentValue });
    }

    // Add custom option
    options.push({ label: 'Custom...', value: '_custom_' });

    return options;
  }

  protected override updateFormFields(): void {
    const device = this.entity();
    const deviceTypeData = this.deviceType();
    // Merge entity parameters with any custom values entered in this session
    const parameters = {
      ...device?.parameters,
      ...this.customValues(),
    };

    // Get IO options with current active_input value if custom
    const activeInputOptions = this.getIOOptions(
      deviceTypeData?.ioCapabilities,
      parameters.active_input,
    );

    // Get IO options with current hours_input value if custom
    const hoursInputOptions = this.getIOOptions(
      deviceTypeData?.ioCapabilities,
      parameters.hours_input,
    );

    // Get IO options with current idling_input value if custom
    const idlingInputOptions = this.getIOOptions(
      deviceTypeData?.ioCapabilities,
      parameters.idling_input,
    );
    this.formFields = [
      createDropdownField(
        'active_input',
        'Active Input',
        [
          { label: 'None', value: '' },
          { label: 'Emulate', value: 'emulate' },
          ...activeInputOptions,
        ],
        parameters.active_input,
        {
          name: 'active_input',
          props: {
            appendTo: 'body',
          },
          events: {
            onChange: (event) => {
              if (event === '_custom_') {
                this.openCustomInputModal('active_input');
              }
            },
          },
        },
      ),
      createDropdownField(
        'hours_input',
        'Hours Input',
        [
          { label: 'None', value: '' },
          { label: 'Emulate', value: 'emulate' },
          ...hoursInputOptions,
        ],
        parameters.hours_input,
        {
          name: 'hours_input',
          props: {
            appendTo: 'body',
          },
          events: {
            onChange: (event) => {
              if (event === '_custom_') {
                this.openCustomInputModal('hours_input');
              }
            },
          },
        },
      ),
      createDropdownField(
        'idling_input',
        'Idling Input',
        [{ label: 'Speed', value: 'speed' }, ...idlingInputOptions],
        parameters.idling_input,
        {
          name: 'idling_input',
          props: {
            appendTo: 'body',
          },
          events: {
            onChange: (event) => {
              if (event === '_custom_') {
                this.openCustomInputModal('idling_input');
              } else {
                // Trigger re-validation of secondary input to catch "differentFromPrimary" conflicts
                const secondary = this.getFormGroup()?.get('idling_input_2');
                if (secondary) {
                  secondary.updateValueAndValidity();
                }
              }
            },
          },
        },
      ),
    ];

    // Conditionally add secondary input fields if shown
    if (this.showSecondaryInput()) {
      this.formFields.push(
        createDropdownField(
          'idling_input_2',
          'Secondary Idling Input (AND)',
          this.getSecondaryIdlingOptions(
            deviceTypeData?.ioCapabilities,
            parameters['idling_input_2'],
          ),
          parameters['idling_input_2'],
          {
            name: 'idling_input_2',
            props: {
              appendTo: 'body',
              showClear: false, // Disable built-in clear to avoid confusion
            },
            events: {
              onChange: (event) => {
                if (event === '_custom_') {
                  this.openCustomInputModal('idling_input_2');
                } else {
                  this.secondaryInputValue.set(event);
                }
              },
            },
            validations: [
              {
                name: 'differentFromPrimary',
                validator: (control) => {
                  const form = this.getFormGroup();
                  const primary = form?.get('idling_input')?.value;
                  if (
                    isDefined(control.value) &&
                    control.value !== '' &&
                    control.value === primary
                  ) {
                    return { differentFromPrimary: true };
                  }
                  return null;
                },
                message: 'Secondary input must be different from primary input',
              },
            ],
          },
        ),
        createNumberField(
          'idling_input_2_threshold',
          'Threshold (greater than)',
          parameters['idling_input_2_threshold'],
          {
            name: 'idling_input_2_threshold',
            placeholder: 'e.g., 300 for RPM > 300',
            hint: 'Device will idle when this value is exceeded',
            props: {
              minFractionDigits: 0,
              maxFractionDigits: 2,
              min: 0,
            },
            validations: [
              {
                name: 'required',
                validator: Validators.required,
                message:
                  'Threshold is required when secondary input is selected',
              },
            ],
          },
        ),
      );
    }

    // Add the invert field at the end
    this.formFields.push(
      createDropdownField(
        'idling_input_invert',
        'Invert Idling Input',
        [
          { label: 'Yes', value: true },
          { label: 'No', value: false },
        ],
        parameters.idling_input_invert,
        {
          name: 'idling_input_invert',
          props: {
            appendTo: 'body',
          },
        },
      ),
    );
    this.cdr.detectChanges();
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<DeviceEntity>,
  ) => Observable<DeviceResponse> {
    return (
      this.operationRegistry.getOperation<
        DeviceEntity,
        DeviceCreateRequest,
        DeviceResponse,
        'update'
      >('device', 'update') ??
      ((id, data) => this.entitiesService.updateDevice(id, data))
    );
  }

  openCustomInputModal(fieldName: string): void {
    this.currentEditField = fieldName;

    // Get current value as default
    const currentValue = this.entity()?.parameters?.[fieldName] ?? '';

    this.customInputFormFields = [
      createTextField('custom_input', 'Input', currentValue, {
        name: 'custom_input',
      }),
    ];
    this.isCustomInputModalOpen = true;
  }

  onClose() {
    this.isCustomInputModalOpen = false;
    this.currentEditField = null;
  }

  onSaveCustomInput(formGroup: FormGroup): void {
    const customValue = formGroup.get('custom_input')?.value;
    const fieldName = this.currentEditField;

    if (!isDefined(customValue) || !isDefined(fieldName)) {
      return;
    }

    this.updateEntityWithCustomValue(fieldName, customValue);

    // Capture state before rebuilding the form
    const prevState = this.captureFormState();

    // Inject the new custom value into the captured state
    prevState.state[fieldName] = customValue;
    prevState.dirty.add(fieldName);
    prevState.touched.add(fieldName);

    this.updateFormFields();

    // Restore state after form rebuild
    setTimeout(() => {
      this.restoreFormState(prevState);
      this.cdr.detectChanges();
    }, 0);

    this.onClose();
  }

  private updateEntityWithCustomValue(fieldName: string, value: string): void {
    const entity = this.entity();
    if (entity) {
      entity.parameters ??= {};
      entity.parameters[fieldName] = value;
    }

    this.customValues.update((prev) => ({ ...prev, [fieldName]: value }));

    if (fieldName === 'idling_input_2') {
      this.secondaryInputValue.set(value);
    }
  }

  private captureFormState(): {
    state: Record<string, FormValue>;
    dirty: Set<string>;
    touched: Set<string>;
  } {
    const form = this.getFormGroup();
    const state: Record<string, FormValue> = {};
    const dirty = new Set<string>();
    const touched = new Set<string>();

    if (!form) return { state, dirty, touched };

    Object.keys(form.controls).forEach((key) => {
      const control = form.get(key);
      if (control) {
        state[key] = control.value;
        if (control.dirty) dirty.add(key);
        if (control.touched) touched.add(key);
      }
    });

    return { state, dirty, touched };
  }

  private restoreFormState(prevState: {
    state: Record<string, FormValue>;
    dirty: Set<string>;
    touched: Set<string>;
  }): void {
    const form = this.getFormGroup();
    if (!form) return;

    Object.keys(prevState.state).forEach((key) => {
      const control = form.get(key);
      if (control) {
        control.setValue(prevState.state[key], { emitEvent: false });
        if (prevState.dirty.has(key)) control.markAsDirty();
        if (prevState.touched.has(key)) control.markAsTouched();
      }
    });

    form.updateValueAndValidity();
  }

  override transformFormData(formData: JsonObject) {
    // Check the actual form value to see if secondary input is active,
    // since formData only contains changed (dirty) fields
    const formValue = this.getFormValue();
    const secondaryInput = formValue['idling_input_2'];

    if (isEmpty(secondaryInput)) {
      delete formData['idling_input_2'];
      delete formData['idling_input_2_threshold'];
    }

    if (this.injectDataAtPath()) {
      const result = {};
      setNestedProperty(result, this.dataPath(), formData);
      return result;
    } else {
      return formData;
    }
  }

  private getSecondaryIdlingOptions(
    ioCapabilities: DeviceTypeIOCapabilities | undefined,
    currentValue?: string | null,
  ): { label: string; value: string }[] {
    // Get base options (same as primary)
    const options = this.getIOOptions(ioCapabilities, currentValue);

    // Remove "Speed" option for secondary (doesn't make sense with threshold)
    return options.filter((opt) => opt.value !== 'speed');
  }
}
