import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import {
  ClientListItem,
  ClientResponse,
  AccountsService,
} from '../../../../api';
import {
  DataGridParams,
  EntityDataAdapter,
  GridResponse,
} from '../../../../shared/models';
import { ActionIcon, EntityConfigAdapter, StateOption } from '../../models';
import { BroadcastEventMessageService } from '../broadcast-event-messages.service';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';

@Injectable({
  providedIn: 'root',
})
export class ClientsDataAdapter
  implements
    EntityDataAdapter<ClientListItem, ClientResponse>,
    EntityConfigAdapter
{
  private readonly accountsService = inject(AccountsService);
  private readonly entityStateService = inject(EntityDetailsStateService);
  private readonly broadcastService = inject(BroadcastEventMessageService);

  /**
   * Retrieves a list of clients
   * @param params Grid parameters (requires owner to be specified)
   * @returns Observable of client list response
   */
  getList(params: DataGridParams): Observable<GridResponse<ClientListItem>> {
    const { owner, limit, offset, sortField, filters, counts } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.accountsService.listClients(
      owner,
      offset,
      limit,
      sortField as string,
      filters,
      counts,
    );
  }

  getById(id: string): Observable<ClientResponse> {
    return this.accountsService.getClient(id);
  }

  getStateOptions(): StateOption[] {
    return [
      {
        label: 'Active',
        value: ClientResponse.StateEnum.Active,
      },
      {
        label: 'Inactive',
        value: ClientResponse.StateEnum.Inactive,
      },
      {
        label: 'Suspended',
        value: ClientResponse.StateEnum.Suspended,
      },
      {
        label: 'Deleted',
        value: ClientResponse.StateEnum.Deleted,
      },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.accountsService.updateClient(id, {
      state: state as ClientResponse.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    return [
      {
        id: 'viewInTree',
        icon: 'pi pi-sitemap',
        tooltip: 'View this client in the Navigation Tree',
        visible: true,
        action: () => {
          const entity = this.entityStateService.selectedItem();
          if (entity) {
            this.broadcastService.broadcast('view-entity-in-tree', {
              entity,
            });
          }
        },
      },
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change Client State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
