/* eslint-disable @typescript-eslint/no-deprecated */
import {
  Component,
  AfterViewInit,
  ViewChild,
  ElementRef,
  NgZone,
  effect,
  signal,
  input,
  model,
  output,
  OnDestroy,
  inject,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { GoogleMapsModule } from '@angular/google-maps';
import { GoogleMapsLoaderService } from '../../../services/google-maps-loader.service';
import { environment } from '../../../../../environments/environment';
import {
  LatLngLiteral,
  MapMarker,
  MapMouseEvent,
  MapPolygon,
  MarkerClickEvent,
  MarkerDragEvent,
  PolygonChangeEvent,
  GoogleMapOptions,
} from '../../../models/google-maps.interfaces';
import { isDefined, isNullOrUndefined, isString } from '../../../utils';

/**
 * Type definition for marker custom data used in this component
 */
export interface MarkerCustomData {
  id?: number | string;
  type?: string;
  [key: string]: unknown;
}

/**
 * Type definition for polygon custom data used in this component
 */
export interface PolygonCustomData {
  id?: number | string;
  name?: string;
  [key: string]: unknown;
}

@Component({
  selector: 'app-google-map',
  imports: [CommonModule, GoogleMapsModule],
  templateUrl: './google-map.component.html',
  styleUrls: ['./google-map.component.scss'],
})
export class GoogleMapComponent implements AfterViewInit, OnDestroy {
  private readonly ngZone = inject(NgZone);
  private readonly googleMapsLoader = inject(GoogleMapsLoaderService);

  @ViewChild('mapContainer') mapContainer!: ElementRef;

  // Input signals with properly typed generics
  apiKey = input(environment.googleMapsApiKey);
  center = input<LatLngLiteral>({ lat: 40.7128, lng: -74.006 }); // Default: New York
  zoom = input(12);
  mapTypeId = input<google.maps.MapTypeId>('roadmap' as google.maps.MapTypeId);
  markers = model<MapMarker<MarkerCustomData>[]>([]);
  useAdvancedMarkers = input(true); // Toggle between legacy markers and advanced markers
  polygons = model<MapPolygon<PolygonCustomData>[]>([]);
  class = input('w-full h-[500px]');
  enableTrafficLayer = input(false);
  clickableMarkers = input(true);
  fitBoundsToMarkers = input(false);

  // Output events with properly typed generics
  readonly mapClick = output<MapMouseEvent>();
  readonly markerClick = output<MarkerClickEvent<MarkerCustomData>>();
  readonly mapLoaded = output<google.maps.Map>();
  readonly boundsChanged = output<google.maps.LatLngBounds>();
  readonly centerChanged = output<LatLngLiteral>();
  readonly markerDragEnd = output<MarkerDragEvent<MarkerCustomData>>();
  readonly polygonChanged = output<PolygonChangeEvent<PolygonCustomData>>();

  map!: google.maps.Map;
  legacyMarkers: google.maps.Marker[] = [];
  advancedMarkers: google.maps.marker.AdvancedMarkerElement[] = [];
  googlePolygons: google.maps.Polygon[] = [];
  infoWindow: google.maps.InfoWindow | null = null;
  isMapInitialized = signal(false);
  trafficLayer?: google.maps.TrafficLayer;
  private polygonEventListeners: google.maps.MapsEventListener[] = [];
  private boundsChangedTimeout: number | null = null;

  constructor() {
    // Effect for center and zoom
    effect(() => {
      if (!this.isMapInitialized()) return;
      this.map.setCenter(this.center());
      this.map.setZoom(this.zoom());
    });

    // Effect for markers
    effect(() => {
      if (!this.isMapInitialized()) return;
      // Only update markers when markers signal changes
      this.updateMarkers();
    });

    // Effect for polygons
    effect(() => {
      if (!this.isMapInitialized()) return;
      // Only update polygons when polygons signal changes
      this.updatePolygons();
    });

    // Effect for other map properties
    effect(() => {
      if (!this.isMapInitialized()) return;
      this.toggleTrafficLayer();
      this.map.setMapTypeId(this.mapTypeId());
    });
  }

  ngAfterViewInit(): void {
    // Update the API key in the loader service if provided
    if (this.apiKey()) {
      this.googleMapsLoader.setApiKey(this.apiKey());
    }

    // Load Google Maps API and initialize map
    this.googleMapsLoader
      .loadGoogleMaps(['geometry'])
      .then(() => {
        this.initializeMap();
      })
      .catch((error: Error) => {
        console.error('Error loading Google Maps API:', error);
      });
  }

  ngOnDestroy(): void {
    // Remove all event listeners first
    for (const listener of this.polygonEventListeners) {
      google.maps.event.removeListener(listener);
    }
    this.polygonEventListeners = [];

    // Remove all markers
    this.clearMarkers();

    // Remove all polygons
    for (const polygon of this.googlePolygons) {
      polygon.setMap(null);
    }
    this.googlePolygons = [];

    // Remove traffic layer if any
    if (this.trafficLayer) {
      this.trafficLayer.setMap(null);
      this.trafficLayer = undefined;
    }

    // Close and clear info window
    if (this.infoWindow) {
      this.infoWindow.close();
      this.infoWindow = null;
    }

    // Optional: Remove all listeners from the map itself
    if (isDefined(this.map)) {
      google.maps.event.clearInstanceListeners(this.map);
    }
  }

  private initializeMap(retryCount = 0): void {
    if (isNullOrUndefined(this.mapContainer)) {
      if (retryCount < 5) {
        setTimeout(() => this.initializeMap(retryCount + 1), 100);
      } else {
        console.error('Map container not found after multiple attempts.');
      }
      return;
    }

    const mapOptions: GoogleMapOptions = {
      center: this.center(),
      zoom: this.zoom(),
      mapTypeId: this.mapTypeId(),
      streetViewControl: true,
      mapTypeControl: true,
      fullscreenControl: true,
      zoomControl: true,
    };

    this.map = new google.maps.Map(
      this.mapContainer.nativeElement,
      mapOptions as google.maps.MapOptions,
    );
    this.infoWindow = new google.maps.InfoWindow();
    this.isMapInitialized.set(true);

    // Add event listeners
    this.map.addListener('click', (event: google.maps.MapMouseEvent) => {
      this.ngZone.run(() =>
        this.mapClick.emit(event as unknown as MapMouseEvent),
      );
    });

    this.map.addListener('bounds_changed', () => {
      // Clear existing timeout
      if (this.boundsChangedTimeout !== null) {
        window.clearTimeout(this.boundsChangedTimeout);
      }

      // Set new timeout (throttle to 100ms)
      this.boundsChangedTimeout = window.setTimeout(() => {
        this.ngZone.run(() => {
          const bounds = this.map.getBounds();
          if (bounds) {
            this.boundsChanged.emit(bounds);
          }
        });
      }, 100);
    });

    // Initialize traffic layer if needed
    if (this.enableTrafficLayer()) {
      this.toggleTrafficLayer();
    }

    // Add markers
    this.updateMarkers();

    // Add polygons
    this.updatePolygons();

    // Emit map loaded event
    this.ngZone.run(() => this.mapLoaded.emit(this.map));
  }

  private updateMarkers(): void {
    // Clear existing markers
    this.clearMarkers();

    // Get current markers from the input
    const currentMarkers = this.markers();

    // Add new markers
    if (isDefined(currentMarkers) && currentMarkers.length > 0) {
      const bounds = new google.maps.LatLngBounds();

      for (const markerData of currentMarkers) {
        // Extend bounds with the marker position
        bounds.extend(markerData.position);

        if (
          this.useAdvancedMarkers() &&
          isDefined(google.maps.marker.AdvancedMarkerElement)
        ) {
          // Create and add advanced marker
          this.createAdvancedMarker(markerData, bounds);
        } else {
          // Fallback to legacy marker
          this.createLegacyMarker(markerData, bounds);
        }
      }

      // Fit bounds if needed and if there are markers
      if (this.fitBoundsToMarkers() && currentMarkers.length > 0) {
        this.map.fitBounds(bounds);
      }
    }
  }

  private createAdvancedMarker(
    markerData: MapMarker<MarkerCustomData>,
    bounds: google.maps.LatLngBounds,
  ): void {
    // Check if the marker library is available
    if (
      isNullOrUndefined(google.maps.marker) ||
      isNullOrUndefined(google.maps.marker.AdvancedMarkerElement)
    ) {
      console.warn(
        'Advanced Marker Element is not available. Falling back to legacy marker.',
      );
      this.createLegacyMarker(markerData, bounds);
      return;
    }

    // Create pin element for styling
    let pinElement: google.maps.marker.PinElement | undefined;
    if (isDefined(markerData.title)) {
      pinElement = new google.maps.marker.PinElement({
        glyph: markerData.title.charAt(0).toUpperCase(),
        glyphColor: 'white',
        background:
          isDefined(markerData.icon) && typeof markerData.icon === 'string'
            ? markerData.icon
            : '#1E88E5', // Default blue color if no icon color is provided
      });
    }

    // Create marker options
    const markerOptions: google.maps.marker.AdvancedMarkerElementOptions = {
      map: this.map,
      position: markerData.position,
      title: markerData.title,
      content: pinElement ?? undefined,
    };

    // Add draggable property if specified
    if (isDefined(markerData.draggable)) {
      markerOptions.gmpDraggable = true;
    }

    // Create the advanced marker
    const advancedMarker = new google.maps.marker.AdvancedMarkerElement(
      markerOptions,
    );

    // Store reference to the marker
    this.advancedMarkers.push(advancedMarker);

    // Add click event if enabled
    if (this.clickableMarkers()) {
      advancedMarker.addListener('click', () => {
        this.ngZone.run(() => {
          // Show info window if content is provided
          if (isDefined(markerData.infoContent) && this.infoWindow) {
            this.infoWindow.setContent(markerData.infoContent);
            this.infoWindow.open(this.map, advancedMarker);
          }

          // Emit marker click event
          this.markerClick.emit({
            marker: advancedMarker,
            customData: markerData.customData,
          });
        });
      });
    }

    // Add drag end event if marker is draggable
    if (isDefined(markerData.draggable)) {
      advancedMarker.addListener('dragend', () => {
        this.ngZone.run(() => {
          // For AdvancedMarkerElement, position is a direct property, not a method
          const position = advancedMarker.position as google.maps.LatLngLiteral;
          if (isDefined(position)) {
            this.markerDragEnd.emit({
              marker: advancedMarker,
              position: {
                lat: position.lat,
                lng: position.lng,
              },
              customData: markerData.customData,
            });
          }
        });
      });
    }
  }

  private createLegacyMarker(
    markerData: MapMarker<MarkerCustomData>,
    _bounds: google.maps.LatLngBounds,
  ): void {
    // Create the marker options with proper type checking
    const markerOptions: google.maps.MarkerOptions = {
      position: markerData.position,
      map: this.map,
      title: markerData.title ?? '',
      icon: isString(markerData.icon) ? markerData.icon : undefined,
      draggable: markerData.draggable ?? false,
    };

    const marker = new google.maps.Marker(markerOptions);

    // Add marker to the collection
    this.legacyMarkers.push(marker);

    // Add click event if enabled
    if (this.clickableMarkers()) {
      marker.addListener('click', () => {
        this.ngZone.run(() => {
          // Show info window if content is provided
          if (isDefined(markerData.infoContent) && this.infoWindow) {
            this.infoWindow.setContent(markerData.infoContent);
            this.infoWindow.open(this.map, marker);
          }

          // Emit marker click event
          this.markerClick.emit({
            marker,
            customData: markerData.customData,
          });
        });
      });
    }

    // Add drag end event if marker is draggable
    if (isDefined(markerData.draggable)) {
      marker.addListener('dragend', () => {
        this.ngZone.run(() => {
          const position = marker.getPosition();
          if (position) {
            this.markerDragEnd.emit({
              marker,
              position: {
                lat: position.lat(),
                lng: position.lng(),
              },
              customData: markerData.customData,
            });
          }
        });
      });
    }
  }

  private clearMarkers(): void {
    // Clear legacy markers
    for (const marker of this.legacyMarkers) {
      marker.setMap(null);
    }
    this.legacyMarkers = [];

    // Clear advanced markers
    for (const marker of this.advancedMarkers) {
      marker.map = null;
    }
    this.advancedMarkers = [];
  }

  private updatePolygons(): void {
    // Clear existing polygons and their listeners
    this.clearPolygons();

    // Get current polygons from the input
    const currentPolygons = this.polygons();

    // Add new polygons
    if (isDefined(currentPolygons) && currentPolygons.length > 0) {
      for (const polygonData of currentPolygons) {
        const polygon = new google.maps.Polygon({
          paths: polygonData.paths,
          strokeColor: polygonData.strokeColor ?? '#FF0000',
          strokeOpacity: polygonData.strokeOpacity ?? 0.8,
          strokeWeight: polygonData.strokeWeight ?? 2,
          fillColor: polygonData.fillColor ?? '#FF0000',
          fillOpacity: polygonData.fillOpacity ?? 0.35,
          editable: polygonData.editable ?? false,
          draggable: polygonData.draggable ?? false,
          map: this.map,
        });

        // Add polygon to collection
        this.googlePolygons.push(polygon);

        // Add event listener and track it
        const listener = polygon.addListener('mouseup', () => {
          this.ngZone.run(() => {
            const path = polygon.getPath();
            const paths: LatLngLiteral[] = [];

            // Convert MVCArray to array of LatLngLiteral
            for (let i = 0; i < path.getLength(); i++) {
              const point = path.getAt(i);
              paths.push({
                lat: point.lat(),
                lng: point.lng(),
              });
            }

            this.polygonChanged.emit({
              polygon,
              paths,
              customData: polygonData.customData,
            });
          });
        });

        // Store the listener reference for cleanup
        this.polygonEventListeners.push(listener);
      }
    }
  }

  private clearPolygons(): void {
    // Remove event listeners first
    for (const listener of this.polygonEventListeners) {
      google.maps.event.removeListener(listener);
    }
    this.polygonEventListeners = [];

    // Then remove polygons from map
    for (const polygon of this.googlePolygons) {
      polygon.setMap(null);
    }
    this.googlePolygons = [];
  }

  private toggleTrafficLayer(): void {
    if (this.enableTrafficLayer()) {
      this.trafficLayer ??= new google.maps.TrafficLayer();
      this.trafficLayer.setMap(this.map);
    } else if (this.trafficLayer) {
      this.trafficLayer.setMap(null);
    }
  }

  // Public methods that can be called from parent components
  public resizeMap(): void {
    if (this.isMapInitialized()) {
      google.maps.event.trigger(this.map, 'resize');
    }
  }

  public panTo(position: LatLngLiteral): void {
    if (this.isMapInitialized()) {
      this.map.panTo(position);
    }
  }

  setZoom(zoom: number, preventExtreme = true): void {
    if (!this.isMapInitialized()) return;

    let finalZoom = zoom;
    if (preventExtreme) {
      finalZoom = Math.max(3, Math.min(19, zoom)); // Prevent extreme zoom levels
    }

    this.map.setZoom(finalZoom);
  }

  public clearAllMarkers(): void {
    if (this.isMapInitialized()) {
      // Note: In a real implementation, you would need to use a writeable signal or model
      this.clearMarkers();
    }
  }

  public fitBounds(): void {
    if (
      !this.isMapInitialized() ||
      (this.legacyMarkers.length === 0 && this.advancedMarkers.length === 0)
    )
      return;

    const bounds = new google.maps.LatLngBounds();

    // Add legacy marker positions to bounds
    for (const marker of this.legacyMarkers) {
      const position = marker.getPosition();
      if (position) bounds.extend(position);
    }

    // Add advanced marker positions to bounds
    for (const marker of this.advancedMarkers) {
      const position = marker.position;
      if (position) bounds.extend(position);
    }

    this.map.fitBounds(bounds);
  }

  public updatePolygonPaths(index: number, paths: LatLngLiteral[]): void {
    if (this.isMapInitialized() && isDefined(this.polygons()[index])) {
      // Note: In a real implementation, you would need to use a writeable signal or model
      if (isDefined(this.googlePolygons[index])) {
        this.googlePolygons[index].setPaths(paths);
      }
    }
  }

  public setPolygonEditable(index: number, editable: boolean): void {
    if (this.isMapInitialized() && isDefined(this.polygons()[index])) {
      this.googlePolygons[index].setEditable(editable);
      // Note: In a real implementation, you would need to use a writeable signal or model
    }
  }

  public setPolygonDraggable(index: number, draggable: boolean): void {
    if (this.isMapInitialized() && isDefined(this.polygons()[index])) {
      this.googlePolygons[index].setDraggable(draggable);
      // Note: In a real implementation, you would need to use a writeable signal or model
    }
  }

  public removePolygon(index: number): void {
    if (this.isMapInitialized() && isDefined(this.polygons()[index])) {
      this.googlePolygons[index].setMap(null);
      this.googlePolygons.splice(index, 1);
      // Note: In a real implementation, you would need to use a writeable signal or model
    }
  }

  public clearAllPolygons(): void {
    if (this.isMapInitialized()) {
      // Note: In a real implementation, you would need to use a writeable signal or model
      this.clearPolygons();
    }
  }
}
