import { Injectable, inject } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, firstValueFrom } from 'rxjs';
import { map } from 'rxjs/operators';
import { EntitiesService } from '../../api';
import { ApiUrlService } from '../../core/services/api-url.service';

/**
 * Service for managing asset avatar images.
 * Handles loading, uploading, and deleting asset avatars.
 */
@Injectable({
  providedIn: 'root',
})
export class AssetAvatarService {
  private readonly entitiesService = inject(EntitiesService);
  private readonly http = inject(HttpClient);
  private readonly apiUrlService = inject(ApiUrlService);

  private readonly MAX_FILE_SIZE_MB = 10;
  private readonly MAX_FILE_SIZE_BYTES = this.MAX_FILE_SIZE_MB * 1024 * 1024;
  private readonly ALLOWED_MIME_TYPES = [
    'image/jpeg',
    'image/png',
    'image/gif',
  ];

  /**
   * Fetches an asset's avatar image as a data URL.
   * @param id - The asset ID
   * @returns Observable<string> - Data URL of the avatar image
   */
  getAssetAvatar(id: string): Observable<string> {
    const url = `${this.apiUrlService.getCurrentApiUrl()}/entities/assets/${id}/avatar`;

    return this.http
      .get(url, {
        responseType: 'blob',
      })
      .pipe(
        map((blob: Blob) => {
          // Convert blob to data URL
          return URL.createObjectURL(blob);
        }),
      );
  }

  /**
   * Uploads an avatar image for an asset.
   * Validates file size and type before uploading.
   * @param id - The asset ID
   * @param file - The image file to upload
   * @returns Promise<void>
   * @throws Error with descriptive message if validation fails
   */
  async uploadAssetAvatar(id: string, file: File): Promise<void> {
    // Validate file size
    if (file.size > this.MAX_FILE_SIZE_BYTES) {
      throw new Error(
        `File exceeds the maximum size of ${this.MAX_FILE_SIZE_MB} MB. Please choose a smaller file.`,
      );
    }

    // Validate file type
    if (!this.ALLOWED_MIME_TYPES.includes(file.type)) {
      throw new Error('Invalid file type. Please upload a PNG, JPEG, or GIF.');
    }

    // Create FormData for upload
    const formData = new FormData();
    formData.append('imageFile', file, file.name);

    // Construct URL with size parameter
    const url = `${this.apiUrlService.getCurrentApiUrl()}/entities/assets/${id}/avatar?size=large`;

    try {
      await firstValueFrom(this.http.post(url, formData));
    } catch (error) {
      if (error instanceof HttpErrorResponse) {
        if (error.status === 413) {
          throw new Error('File is too large. Server rejected the upload.');
        } else if (error.status === 403) {
          throw new Error('You do not have permission to upload avatars.');
        } else {
          throw new Error(`Failed to upload image: ${error.message}`);
        }
      }
      throw new Error('Failed to upload image. Please try again.');
    }
  }

  /**
   * Deletes an asset's avatar image.
   * @param id - The asset ID
   * @returns Observable<void>
   */
  deleteAssetAvatar(id: string): Observable<void> {
    return this.entitiesService.deleteAssetAvatar(id);
  }
}
