import { CommonModule } from '@angular/common';
import {
  Component,
  computed,
  effect,
  inject,
  input,
  signal,
} from '@angular/core';

import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';
import { Observable } from 'rxjs';

import {
  EmailProviderCreateRequest,
  EmailProviderResponse,
  EntitiesService,
  EmailProviderTypeResponse,
} from '../../../../../api';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import {
  createDropdownField,
  createNumberField,
  createPasswordField,
  createTextAreaField,
  createTextField,
  FormFieldConfig,
  ValidationRule,
} from '../../../../../shared/models';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { EntitySectionBaseComponent } from '../../base';
import { FormBuilderField } from '../../../../../core/models';
import { Validators } from '@angular/forms';

type EmailProviderEntity =
  | EmailProviderResponse
  | Partial<EmailProviderCreateRequest>;

interface FormField {
  id: string;
  title: string;
  type: string;
  value?: string | number;
  defaultValue?: string | number;
  required?: boolean;
  description?: string;
  min?: number;
  max?: number;
  values?: { key: string; value: string; fields: FormField[] }[];
}

@Component({
  selector: 'app-email-provider-parameters-section',
  imports: [
    CommonModule,
    ButtonModule,
    InputTextModule,
    GridComponent,
    DynamicFormComponent,
  ],
  templateUrl: './email-provider-parameters-section.component.html',
  styleUrl: './email-provider-parameters-section.component.scss',
})
export class EmailProviderParametersSectionComponent extends EntitySectionBaseComponent<EmailProviderEntity> {
  readonly ownerId = input<string | null>(null);
  readonly emailProviderType = input<EmailProviderTypeResponse | null>(null);
  readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);

  protected readonly isLoadingEmailType = signal<boolean>(false);
  protected readonly customValues = signal<Record<string, string>>({});

  readonly emailProviderParameters = computed(() => {
    const emailProviderParameters = this.entity()?.parameters;
    const emailProviderType = this.emailProviderType();
    if (
      isNullOrUndefined(emailProviderParameters) ||
      isNullOrUndefined(emailProviderType)
    )
      return [];

    const params: { groups: { fields: FormBuilderField[] }[] } = JSON.parse(
      emailProviderType?.parameterDefinition ?? '',
    );
    const values: { key: string; value: string }[] = [];
    params.groups.forEach((group) => {
      group.fields.forEach((field: FormBuilderField) => {
        let fieldValue = 'None';
        if (field.type === 'password') {
          fieldValue = '****';
        } else {
          const paramValue = emailProviderParameters?.[field.id];
          if (typeof paramValue === 'object') {
            fieldValue = JSON.stringify(paramValue);
          } else if (paramValue !== undefined) {
            fieldValue = paramValue as unknown as string;
          }
        }

        values.push({
          key: field.title,
          value: fieldValue,
        });

        // Handle combo fields with dependent fields
        if (field.type === 'combo' && field.values) {
          const selectedValue = emailProviderParameters?.[
            field.id
          ] as unknown as string;
          const selectedOption = field.values.find(
            (opt) => opt.key === selectedValue,
          );

          if (selectedOption?.fields) {
            selectedOption.fields.forEach((subField) => {
              if (emailProviderParameters?.[subField.id] !== undefined) {
                let fieldValue = 'None';
                const paramValue = emailProviderParameters?.[subField.id];

                if (subField.type === 'password') {
                  fieldValue = '****';
                } else if (typeof paramValue === 'object') {
                  fieldValue = JSON.stringify(paramValue);
                } else if (paramValue !== undefined) {
                  fieldValue = paramValue as unknown as string;
                }

                values.push({
                  key: `${subField.title}`,
                  value: fieldValue,
                });
              }
            });
          }
        }
      });
    });

    return values;
  });
  selectOptions: Record<string, { label: string; value: string }[]> = {};

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    effect(() => {
      const emailProviderType = this.emailProviderType();
      if (isDefined(emailProviderType)) {
        this.updateFormFields();
      }
    });
  }

  protected override updateFormFields(): void {
    const emailProvider = this.entity();
    const emailProviderType = this.emailProviderType();

    if (!emailProviderType) {
      return;
    }

    const params: { groups: { fields: FormField[] }[] } = JSON.parse(
      emailProviderType?.parameterDefinition ?? '{}',
    );

    this.formFields = params.groups.reduce((acc: FormFieldConfig[], group) => {
      const groupFields = group.fields.map((field: FormField) =>
        this.createFormFieldConfig(field, emailProvider),
      );
      return [...acc, ...groupFields];
    }, []);
  }

  private createFormField(
    field: FormField,
    emailProvider?: EmailProviderEntity | null,
  ): FormFieldConfig {
    const baseConfig = {
      name: field.id,
      validations: this.createValidations(field),
      hint: field.description,
    };

    const fieldValue = emailProvider?.parameters?.[field.id];

    switch (field.type) {
      case 'memo':
        return createTextAreaField(
          field.id,
          field.title,
          fieldValue as unknown as string,
          baseConfig,
        );

      case 'number':
        return createNumberField(
          field.id,
          field.title,
          fieldValue as unknown as number,
          {
            ...baseConfig,
            props: { min: field.min, max: field.max },
          },
        );

      case 'combo':
        return createDropdownField(
          field.id,
          field.title,
          field.values?.map((item) => ({
            label: item.value,
            value: item.key,
          })) ?? [],
          fieldValue as unknown as string,
          {
            ...baseConfig,
            props: {
              appendTo: 'body',
            },
            conditional: (field?.values?.length ?? 0) > 0,
            conditions: this.createConditionalFields(field, emailProvider),
          },
        );

      case 'password':
        return createPasswordField(field.id, field.title, {
          ...baseConfig,
          props: {
            type: 'password',
          },
        });

      case 'url':
        return createTextField(
          field.id,
          field.title,
          fieldValue as unknown as string,
          {
            ...baseConfig,
            validations: [
              ...baseConfig.validations,
              {
                name: 'pattern',
                validator: Validators.pattern(/^https?:\/\/.+/),
                message: `${field.title} must be a valid URL`,
              },
            ],
          },
        );

      case 'text':
      default:
        return createTextField(
          field.id,
          field.title,
          fieldValue as unknown as string,
          baseConfig,
        );
    }
  }

  private createValidations(field: FormField) {
    const validations: ValidationRule[] = [];

    if (field.required ?? false) {
      validations.push({
        name: 'required',
        validator: Validators.required,
        message: `${field.title} is required`,
      });
    }

    if (
      field.type === 'number' &&
      (field.min !== undefined || field.max !== undefined)
    ) {
      if (field.min !== undefined) {
        validations.push({
          name: 'min',
          validator: Validators.min(field.min),
          message: `${field.title} must be at least ${field.min}`,
        });
      }
      if (field.max !== undefined) {
        validations.push({
          name: 'max',
          validator: Validators.max(field.max),
          message: `${field.title} must be at most ${field.max}`,
        });
      }
    }

    return validations;
  }

  private createConditionalFields(
    field: FormField,
    emailProvider?: EmailProviderEntity | null,
  ) {
    if (!isDefined(field.values)) {
      return {};
    }

    return field.values.reduce((acc, item) => {
      if (!isDefined(item.fields)) return {};
      return {
        ...acc,
        [item.key]: item.fields.map((f) =>
          this.createFormField(f, emailProvider),
        ),
      };
    }, {});
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<EmailProviderEntity>,
  ) => Observable<EmailProviderResponse> {
    return (
      this.operationRegistry.getOperation<
        EmailProviderEntity,
        EmailProviderCreateRequest,
        EmailProviderResponse,
        'update'
      >('email-providers', 'update') ??
      ((id, data) => this.entitiesService.updateEmailProvider(id, data))
    );
  }

  protected override onFormValueChange(
    formData: Record<string, string | number | object>,
  ): void {
    const transformedData = { ...formData };

    this.formValueChange.emit({ parameters: transformedData });
  }

  createConditionFields(fields: FormField[]): FormFieldConfig[] {
    return fields.map((field) => {
      return this.createFormFieldConfig(field, this.entity());
    });
  }

  private createFormFieldConfig(
    field: FormField,
    emailProvider: EmailProviderEntity | null,
  ): FormFieldConfig {
    if (field.type === 'memo') {
      return createTextAreaField(
        field.id,
        field.title,
        emailProvider?.parameters?.[field.id] as unknown as string,
        {
          name: field.id,
          validations:
            (field.required ?? false)
              ? [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: `${field.title} is required`,
                  },
                ]
              : [],
          hint: field.description,
        },
      );
    }
    if (field.type === 'number') {
      return createNumberField(
        field.id,
        field.title,
        emailProvider?.parameters?.[field.id] as unknown as number,
        {
          name: field.id,
          validations:
            (field.required ?? false)
              ? [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: `${field.title} is required`,
                  },
                ]
              : [],
          hint: field.description,
        },
      );
    }
    if (field.type === 'combo' && field.values) {
      // Handle dropdown field creation
      const options = field.values.map((value) => ({
        label: value.value,
        value: value.key,
      }));

      return createDropdownField(
        field.id,
        field.title,
        options,
        (emailProvider?.parameters?.[field.id] as unknown as string) ||
          (field.defaultValue as string),
        {
          name: field.id,
          validations:
            (field.required ?? false)
              ? [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: `${field.title} is required`,
                  },
                ]
              : [],
          hint: field.description,
          props: {
            appendTo: 'body',
          },
          conditional: true,
          conditions: options.reduce(
            (acc, option) => ({
              ...acc,
              [option.value]: this.createConditionFields(
                field?.values?.find((opt) => opt.key === option.value)
                  ?.fields ?? [],
              ),
            }),
            {},
          ),
        },
      );
    }
    if (field.type === 'password') {
      return createPasswordField(field.id, field.title, {
        name: field.id,
        validations:
          (field.required ?? false)
            ? [
                {
                  name: 'required',
                  validator: Validators.required,
                  message: `${field.title} is required`,
                },
              ]
            : [],
        hint: field.description,
        props: {
          type: 'password',
        },
      });
    }

    // Default text field
    return createTextField(
      field.id,
      field.title,
      emailProvider?.parameters?.[field.id] as unknown as string,
      {
        name: field.id,
        validations:
          (field.required ?? false)
            ? [
                {
                  name: 'required',
                  validator: Validators.required,
                  message: `${field.title} is required`,
                },
              ]
            : [],
        hint: field.description,
      },
    );
  }
}
