import { Component, computed, inject, input, signal } from '@angular/core';

import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';

import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';
import {
  ZoneGroupCreateRequest,
  ZoneGroupResponse,
  EntitiesService,
} from '../../../../../api';
import { GeofenceGroupSectionConfigFactory } from '../../../factories';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';

@Component({
  selector: 'app-geofence-group-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './geofence-group-details-container.component.html',
  styleUrl: './geofence-group-details-container.component.scss',
})
export class GeofenceGroupDetailsContainerComponent {
  readonly entity = input<ZoneGroupResponse | null>(null);
  private readonly geofenceGroupSectionFactory = inject(
    GeofenceGroupSectionConfigFactory,
  );
  readonly entityDetailsState = inject(EntityDetailsStateService);
  private readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly isSoftRefreshing = computed(() =>
    this.entityDetailsState.isSoftRefreshing(),
  );
  readonly isInitialLoading = computed(
    () =>
      this.isLoading() && !this.isSoftRefreshing() && !this.fullGeofenceGroup(),
  );
  readonly fullGeofenceGroup = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();

    if (!entityDetails) {
      return null;
    }
    return entityDetails as ZoneGroupResponse;
  });
  readonly sections = computed(() => {
    const geofenceGroup = this.fullGeofenceGroup();
    if (!geofenceGroup) return [];
    return this.geofenceGroupSectionFactory.createAllGeofenceGroupSections(
      geofenceGroup,
    );
  });
  readonly error = signal<string | null>(null);

  constructor() {
    this.operationRegistry.registerEntityOperations('geofence-group', {
      update: (id, data) => this.entitiesService.updateZoneGroup(id, data),
      create: (data: ZoneGroupCreateRequest) =>
        this.entitiesService.createZoneGroup(data),
      get: (id) => this.entitiesService.getZoneGroup(id),
    });
  }

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as ZoneGroupResponse);
    this.error.set(null);
  }
}
