import { Component, computed, inject, input, signal } from '@angular/core';
import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';
import {
  AssetStateProfileListItem,
  AssetStateProfileResponse,
} from '../../../../../api';
import { AssetStateProfileSectionConfigFactory } from '../../../factories';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';

@Component({
  selector: 'app-asset-state-profile-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './asset-state-profile-details-container.component.html',
  styleUrl: './asset-state-profile-details-container.component.scss',
})
export class AssetStateProfileDetailsContainerComponent {
  readonly entity = input<AssetStateProfileListItem | null>(null);

  private readonly assetStateProfileSectionFactory = inject(
    AssetStateProfileSectionConfigFactory,
  );
  readonly entityDetailsState = inject(EntityDetailsStateService);

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly fullAssetStateProfile = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();

    if (!entityDetails) {
      return null;
    }
    return entityDetails as AssetStateProfileResponse;
  });
  readonly sections = computed(() => {
    const assetStateProfile = this.fullAssetStateProfile();
    if (!assetStateProfile) return [];
    return this.assetStateProfileSectionFactory.createAllAssetStateProfileSections(
      assetStateProfile,
    );
  });
  readonly error = signal<string | null>(null);

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as AssetStateProfileResponse);
    this.error.set(null);
  }
}
