import { inject, Injectable } from '@angular/core';

import {
  AccountsService,
  CostCentreResponse,
  EntitiesService,
} from '../../../api';
import { AdminDetailSection } from '../models';
import { CostCentreDetailsSectionComponent } from '../components/cost-centres/cost-centre-details-section/cost-centre-details-section.component';
import { FeedSectionComponent } from '../components/shared/feed-section/feed-section.component';
import { ContactsSectionComponent } from '../components/shared/contacts-section/contacts-section.component';
import { AssetListSectionComponent } from '../components/shared/asset-list-section/asset-list-section.component';
import { map, firstValueFrom } from 'rxjs';
import { AssetGroupingService } from '../../../shared/services/asset-grouping.service';
import { UserListSectionComponent } from '../components/shared/user-list-section/user-list-section.component';
import { UtilityService } from '../../../core/services/utility.service';
import { RequestCacheService } from '../../../shared/services/request-cache.service';

@Injectable({ providedIn: 'root' })
export class CostCentreSectionConfigFactory {
  private readonly assetGroupingService = inject(AssetGroupingService);
  private readonly entitiesService = inject(EntitiesService);
  private readonly accountsService = inject(AccountsService);
  private readonly utilityService = inject(UtilityService);
  private readonly cacheService = inject(RequestCacheService);

  /**
   * Create sections for cost centre information
   * @param costCentre The cost centre object to display in the section
   * @returns An array of AdminDetailSection objects representing the cost centre information sections
   */
  createCostCentreInformationSection(
    costCentre: CostCentreResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'cost-centre-details',
        title: 'Cost Centre Details',
        component: CostCentreDetailsSectionComponent,
        componentInputs: { entity: costCentre },
        category: 'information',
        order: 10,
      },
      {
        id: 'cost-centre-contacts',
        title: 'Contacts',
        component: ContactsSectionComponent,
        componentInputs: { entity: costCentre, entityType: 'cost-centre' },
        category: 'information',
        order: 20,
      },
    ];
  }

  /**
   * Create sections for cost centre changes
   * @param costCentre The cost centre object to display in the section
   * @returns An array of AdminDetailSection objects representing the cost centre changes sections
   */
  createChangesSection(
    costCentre: CostCentreResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'cost-centre-changes',
        title: 'Changes',
        component: FeedSectionComponent,
        componentInputs: {
          entity: costCentre,
        },
        category: 'feed',
        order: 10,
      },
    ];
  }

  /**
   * Create sections for cost centre assets
   * @param costCentre The cost centre object to display in the section
   * @returns An array of AdminDetailSection objects representing the cost centre assets sections
   */
  createAssetsSection(
    costCentre: CostCentreResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'cost-centre-assets',
        title: 'Assets',
        component: AssetListSectionComponent,
        componentInputs: {
          entity: costCentre, // The current cost centre object
          groupId: 'costCentre.id', // RQL path to filter assets for this cost centre
          groupingEntityType: 'Cost Centre',
          assetLinkField: 'costCentre', // The field on AssetResponse linking to CostCentreResponse
          getGroupingEntitiesFn: (ownerId: string) =>
            this.assetGroupingService.getCostCentresAsTree(ownerId),
          getAvailableAssetsFn: (ownerId: string, _currentEntityId?: string) =>
            this.getAvailableAssets(ownerId),
        },
        category: 'assets',
        order: 10,
      },
    ];
  }

  /**
   * Get available assets for the given owner
   * @param ownerId The owner ID to fetch assets for
   * @returns Promise of simplified asset objects
   */
  private getAvailableAssets(ownerId: string) {
    const filters = {
      state: {
        value: 'active',
        operator: '=',
      },
    };
    const rqlFilter = this.utilityService.constructRqlFilter(filters);
    return this.cacheService.getOrFetchAsync(
      `assets-list-${ownerId}-${rqlFilter}`,
      () =>
        firstValueFrom(
          this.entitiesService
            .listAssets(ownerId, 0, 100, 'name', rqlFilter)
            .pipe(
              map((res) =>
                res.items.map((asset) => ({
                  id: asset.id,
                  name: asset.name,
                })),
              ),
            ),
        ),
      300000,
    );
  }

  /**
   * Create sections for cost centre users
   * @param costCentre The cost centre object to display in the section
   * @returns An array of AdminDetailSection objects representing the cost centre users sections
   */
  createUsersSection(
    costCentre: CostCentreResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'cost-centre-users',
        title: 'Users',
        component: UserListSectionComponent,
        componentInputs: {
          entity: costCentre,
          groupId: 'costCentre.id',
          groupingEntityType: 'Cost Centre',
          userLinkField: 'costCentre',
          getGroupingEntitiesFn: (ownerId: string) =>
            this.assetGroupingService.getCostCentresAsTree(ownerId),
          getAvailableUsersFn: (ownerId: string) =>
            firstValueFrom(
              this.accountsService.listUsers(ownerId, 0, 100, 'name').pipe(
                map((res) =>
                  res.items.map((user) => ({
                    id: user.id,
                    name: user.name,
                  })),
                ),
              ),
            ),
        },
        category: 'users',
        order: 20,
      },
    ];
  }

  /**
   * Create all costCentre sections in one call
   * @param costCentre The costCentre object to display in the sections
   * @returns An array of AdminDetailSection objects representing all costCentre sections
   */
  createAllCostCentreSections(
    costCentre: CostCentreResponse | null,
  ): AdminDetailSection[] {
    return [
      ...this.createCostCentreInformationSection(costCentre),
      ...this.createAssetsSection(costCentre),
      ...this.createUsersSection(costCentre),
      ...this.createChangesSection(costCentre),
    ];
  }
}
