import { Injectable } from '@angular/core';
import { AdminDetailSection } from '../models';
import { GeofenceDetailsSectionComponent } from '../components/geofences/geofence-details-section/geofence-details-section.component';
import { ZoneResponse } from '../../../api';
import { GeofenceMapSectionComponent } from '../components/geofences/geofence-map-section/geofence-map-section.component';
import { FeedSectionComponent } from '../components/shared/feed-section/feed-section.component';

@Injectable({ providedIn: 'root' })
export class GeofenceSectionConfigFactory {
  /**
   * Create sections for geofence information
   * @param geofence The geofence object to display in the section
   * @returns An array of AdminDetailSection objects representing the geofence information sections
   */
  createGeofenceInformationSections(
    geofence: ZoneResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'geofence-details',
        title: 'Geofence Details',
        component: GeofenceDetailsSectionComponent,
        componentInputs: { entity: geofence },
        category: 'information',
        order: 10,
      },
      {
        id: 'geofence-map',
        title: 'Geofence Map',
        component: GeofenceMapSectionComponent,
        componentInputs: { entity: geofence },
        category: 'information',
        order: 20,
      },
    ];
  }

  /**
   * Create sections for geofence changes
   * @param geofence The geofence object to display in the section
   * @returns An array of AdminDetailSection objects representing the geofence changes sections
   */
  createChangesSection(geofence: ZoneResponse | null): AdminDetailSection[] {
    return [
      {
        id: 'geofence-changes',
        title: 'Changes',
        component: FeedSectionComponent,
        componentInputs: {
          entity: geofence,
        },
        category: 'feed',
        order: 10,
      },
    ];
  }

  /**
   * Create all geofence sections in one call
   * @param geofence The geofence object to display in the sections
   * @returns An array of AdminDetailSection objects representing all geofence sections
   */
  createAllGeofenceSections(
    geofence: ZoneResponse | null,
  ): AdminDetailSection[] {
    return [
      ...this.createGeofenceInformationSections(geofence),
      ...this.createChangesSection(geofence),
    ];
  }
}
