import {
  Component,
  computed,
  effect,
  ElementRef,
  inject,
  OnInit,
  signal,
  ViewChild,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { catchError, finalize, forkJoin, of } from 'rxjs';

import { InputTextModule } from 'primeng/inputtext';
import { ButtonModule } from 'primeng/button';
import { DataViewModule } from 'primeng/dataview';
import { CardModule } from 'primeng/card';
import { MenuItem } from 'primeng/api';

import {
  AssetResponse,
  EntitiesService,
  ReminderCreateRequest,
  ReminderListItem,
  ReminderReset,
} from '../../../../../api';
import {
  createDropdownField,
  createTextField,
  createDateField,
  FormFieldConfig,
  createNumberField,
  FormAction,
  createListboxField,
} from '../../../../../shared/models';
import {
  isDefined,
  isEmpty,
  isNullOrUndefined,
} from '../../../../../shared/utils';
import { EntitySectionBaseComponent } from '../../base';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';

import {
  DAY_OF_MONTH_OPTIONS,
  MONTH_OPTIONS,
  ReminderType,
  WEEKDAY_OPTIONS,
} from '../../../models';
import { DynamicFormModalComponent } from '../../../../../shared/components/forms/dynamic-form-modal/dynamic-form-modal.component';
import { AuthDataService } from '../../../../../core/services/auth-data.service';
import { Menu, MenuModule } from 'primeng/menu';
import { SplitButtonModule } from 'primeng/splitbutton';

@Component({
  selector: 'app-asset-reminders-section',
  imports: [
    CommonModule,
    ButtonModule,
    InputTextModule,
    GridComponent,
    DynamicFormModalComponent,
    MenuModule,
    DataViewModule,
    CardModule,
    SplitButtonModule,
  ],
  templateUrl: './asset-reminders-section.component.html',
  styleUrl: './asset-reminders-section.component.scss',
})
export class AssetRemindersSectionComponent
  extends EntitySectionBaseComponent<AssetResponse>
  implements OnInit
{
  @ViewChild('addReminderMenu') addReminderMenu!: Menu;
  @ViewChild('addReminderBtn', { read: ElementRef })
  addReminderBtn!: ElementRef<HTMLButtonElement>;

  private readonly entitiesService = inject(EntitiesService);
  private readonly http = inject(HttpClient);
  private readonly authDataService = inject(AuthDataService);

  // Signals - properly declare isLoadingAsset
  readonly isLoadingAsset = signal<boolean>(false);
  private readonly remindersData = signal<ReminderListItem[]>([]);

  // Modal controls
  readonly showReminderModal = signal<boolean>(false);
  readonly currentModalTitle = signal<string>('Add Reminder');
  readonly currentReminderType = signal<ReminderType | null>(null);

  private readonly newReminders = signal<Partial<ReminderCreateRequest>[]>([]);
  private readonly deletedReminderIds = signal<string[]>([]);

  // Track the currently loaded asset ID to prevent duplicate loads
  private loadedAssetId: string | null = null;

  // Split button menu items
  reminderOptions: MenuItem[] = [
    {
      label: 'Odometer Reminder',
      command: () => this.openReminderModal(ReminderType.ODOMETER),
    },
    {
      label: 'Engine Hours Reminder',
      command: () => this.openReminderModal(ReminderType.ENGINE_HOURS),
    },
    {
      label: 'Time Based Reminder',
      command: () => this.openReminderModal(ReminderType.TIME_BASED),
    },
  ];

  // Computed properties
  readonly reminders = computed(() => {
    const remindersList = this.remindersData();

    return [
      {
        key: 'Reminders',
        value: isEmpty(remindersList)
          ? 'None'
          : remindersList.map((reminder) => reminder.name).join(', '),
      },
    ];
  });

  // Sorted reminders for the list view
  readonly sortedReminders = computed(() => {
    const combined = [
      ...this.remindersData().filter(
        (r) => !this.deletedReminderIds().includes(r.id ?? ''),
      ),
      ...this.newReminders().filter(
        (r) => !this.deletedReminderIds().includes(r['id'] ?? ''),
      ),
    ];
    return combined.sort((a, b) => {
      const dateA =
        a['modifiedDate'] ??
        (isDefined(a['id']?.startsWith('temp-') ?? false) ? Date.now() : 0);
      const dateB =
        b['modifiedDate'] ??
        (isDefined(b['id']?.startsWith('temp-') ?? false) ? Date.now() : 0);
      const timeA =
        typeof dateA === 'string' ? new Date(dateA).getTime() : Number(dateA);
      const timeB =
        typeof dateB === 'string' ? new Date(dateB).getTime() : Number(dateB);
      return timeB - timeA;
    });
  });

  // Check if there are changes to save
  readonly hasChanges = computed(() => {
    return (
      this.newReminders().length > 0 || this.deletedReminderIds().length > 0
    );
  });

  override readonly actions: FormAction[] = [
    {
      label: 'Cancel',
      action: () => {
        this.showReminderModal.set(false);
        this.resetModal();
      },
      severity: 'secondary',
    },
    {
      label: 'Ok',
      action: (formData) => {
        this.onReminderSubmit(formData.value);
      },
      severity: 'primary',
      disabled: (form) => {
        return (
          (form?.invalid ?? false) ||
          ((form?.dirty ?? false) && !(form?.touched ?? false)) ||
          this.isLoadingAsset()
        );
      },
    },
  ];
  timezones: { label: string; value: string }[] = [];
  formHeader = '';

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    // Only watch entity changes, not isLoadingAsset
    effect(() => {
      const asset = this.entity();
      // Only proceed if we have a valid asset with an ID and owner
      if (isDefined(asset?.id) && isDefined(asset?.owner?.id)) {
        // Check if this is a different asset than the one we already loaded
        if (asset.id !== this.loadedAssetId) {
          this.loadReminders(asset);
        }
      } else {
        this.remindersData.set([]);
        this.loadedAssetId = null;
        this.resetChanges();
      }
    });
  }

  ngOnInit(): void {
    this.loadTimezones();
  }

  private loadReminders(asset: AssetResponse) {
    // Skip if already loading or if no asset ID
    if (this.isLoadingAsset()) return;

    // Update the loaded asset ID before making the request
    this.loadedAssetId = asset.id;
    this.isLoadingAsset.set(true);

    this.entitiesService
      .listReminders(asset.owner.id, asset.id)
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        catchError(() => {
          this.isLoadingAsset.set(false);
          return of({ items: [], count: 0 });
        }),
      )
      .subscribe({
        next: (response) => {
          const reminders = response.items ?? [];
          this.remindersData.set(reminders);
          this.isLoadingAsset.set(false);
          this.resetChanges();
        },
      });
  }

  /**
   * Load timezones from JSON file
   */
  private loadTimezones(): void {
    this.http
      .get<{ id: string; name: string }[]>('/assets/docs/timezones.json')
      .subscribe((data) => {
        this.timezones = data
          .map((tz) => ({ label: tz.id, value: tz.id }))
          .sort((a, b) => a.label.localeCompare(b.label));
      });
  }

  /**
   * Create reset counter options based on type
   * @param type - The reminder type
   * @returns Array of options for the dropdown
   */
  private createResetCounterOptions(
    type: ReminderType,
  ): { label: string; value: number }[] {
    return Array.from({ length: 8 }, (_, i) => ({
      label: `${type}_cnt_${i + 1}`,
      value: i + 1,
    }));
  }

  /**
   * Create reset configuration based on type
   * @param resetType - The type of reset (daily, weekly, monthly)
   * @param resetCounterOptions - Options for the reset counter
   * @returns FormFieldConfig for the reset configuration
   */
  private createResetConfig(
    resetType: 'daily' | 'weekly' | 'monthly',
    resetCounterOptions: { label: string; value: number }[],
  ): FormFieldConfig[] {
    // Common structure for all reset types
    const config: FormFieldConfig[] = [
      {
        name: 'reset',
        key: 'reset',
        type: 'group',
        fields: [
          // This will be populated based on reset type
        ],
      },
      createDropdownField(
        'timeZoneId',
        'Timezone',
        this.timezones,
        this.authDataService.getUserProfile()?.timeZoneId,
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          placeholder: 'Select timezone',
          name: 'timeZoneId',
          props: {
            appendTo: 'body',
          },
        },
      ),
    ];

    // Get the reset group to modify
    const resetGroup = config[0];

    // Add type-specific fields
    switch (resetType) {
      case 'daily':
        resetGroup.fields = [
          {
            name: 'daily',
            key: 'daily',
            type: 'group',
            fields: [
              createNumberField('recurrence', 'Every', 1, {
                name: 'recurrence',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Recurrence is required',
                  },
                ],
                props: { suffix: 'Days' },
              }),
            ],
          },
          createDropdownField(
            'counter',
            'Reset Counter',
            resetCounterOptions,
            resetCounterOptions[0].value,
            {
              name: 'counter',
              validations: [
                {
                  name: 'required',
                  validator: Validators.required,
                  message: 'Counter is required',
                },
              ],
              props: { appendTo: 'body' },
            },
          ),
        ];
        break;

      case 'weekly':
        resetGroup.fields = [
          {
            name: 'weekly',
            key: 'weekly',
            type: 'group',
            fields: [
              createListboxField('days', 'Weekdays', [], WEEKDAY_OPTIONS, {
                name: 'days',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Weekdays is required',
                  },
                ],
                props: {
                  checkmark: true,
                  multiple: true,
                },
              }),
            ],
          },
          createDropdownField(
            'counter',
            'Reset Counter Value',
            resetCounterOptions,
            resetCounterOptions[0].value,
            {
              name: 'counter',
              props: { appendTo: 'body' },
              validations: [
                {
                  name: 'required',
                  validator: Validators.required,
                  message: 'Counter is required',
                },
              ],
            },
          ),
        ];
        break;

      case 'monthly':
        resetGroup.fields = [
          {
            name: 'monthly',
            key: 'monthly',
            type: 'group',
            fields: [
              createListboxField('months', 'Months', [], MONTH_OPTIONS, {
                name: 'months',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Months is required',
                  },
                ],
                props: {
                  checkmark: true,
                  multiple: true,
                },
              }),
              createDropdownField(
                'day',
                'Day ',
                DAY_OF_MONTH_OPTIONS,
                DAY_OF_MONTH_OPTIONS[0].value,
                {
                  name: 'day',
                  validations: [
                    {
                      name: 'required',
                      validator: Validators.required,
                      message: 'Day of month is required',
                    },
                  ],
                  props: { appendTo: 'body' },
                },
              ),
            ],
          },
          createDropdownField(
            'counter',
            'Reset Counter',
            resetCounterOptions,
            resetCounterOptions[0].value,
            {
              name: 'counter',
              props: { appendTo: 'body' },
              validations: [
                {
                  name: 'required',
                  validator: Validators.required,
                  message: 'Counter is required',
                },
              ],
            },
          ),
        ];
        break;
    }

    return config;
  }

  /**
   * Create a dropdown field for reset type
   * @param type - The reminder type
   * @returns FormFieldConfig for the dropdown
   */
  private createResetTypeDropdown(type: ReminderType): FormFieldConfig {
    const resetCounterOptions = this.createResetCounterOptions(type);

    return createDropdownField(
      'resetType',
      'Reset',
      [
        { label: 'After each reminder', value: 'none' },
        { label: 'Daily', value: 'daily' },
        { label: 'Weekly', value: 'weekly' },
        { label: 'Monthly', value: 'monthly' },
      ],
      'none',
      {
        name: 'resetType',
        conditional: true,
        conditions: {
          none: [],
          daily: this.createResetConfig('daily', resetCounterOptions),
          weekly: this.createResetConfig('weekly', resetCounterOptions),
          monthly: this.createResetConfig('monthly', resetCounterOptions),
        },
        props: {
          appendTo: 'body',
        },
      },
    );
  }

  /**
   * Create a base reminder form with common fields
   * @param name - The name of the reminder
   * @param modeOptions - Dropdown options for mode selection
   * @param onceFieldCreator - Function to create the once field
   * @param repeatingFieldsCreator - Function to create the repeating fields
   * @returns Array of FormFieldConfig for the form
   */
  private createBaseReminderForm(
    name: string,
    modeOptions: { label: string; value: string }[],
    onceFieldCreator: () => FormFieldConfig,
    repeatingFieldsCreator: () => FormFieldConfig[],
  ): FormFieldConfig[] {
    return [
      createTextField('name', 'Name', name, {
        name: 'name',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Name is required',
          },
        ],
      }),
      createDropdownField('mode', 'Mode', modeOptions, 'once', {
        name: 'mode',
        conditional: true,
        conditions: {
          once: [onceFieldCreator()],
          repeating: repeatingFieldsCreator(),
        },
        props: {
          appendTo: 'body',
        },
      }),
    ];
  }

  /**
   * Open modal for adding a new reminder of specified type
   */
  openReminderModal(type: ReminderType): void {
    this.currentReminderType.set(type);
    this.currentModalTitle.set(
      `Add ${this.getReminderTypeDisplay(type)} Reminder`,
    );
    this.updateFormFields();
    switch (type) {
      case ReminderType.ODOMETER:
        this.formHeader =
          'Odometer reminders will create an event (and optional alert) when the specified odometer value is reached.';
        break;
      case ReminderType.ENGINE_HOURS:
        this.formHeader =
          'Engine hours reminders will create an event (and optional alert) when the specified engine hours value is reached.';
        break;
      case ReminderType.TIME_BASED:
        this.formHeader =
          'Time based reminders will create an event (and optional alert) when the specified date and time is reached.';
    }
    this.showReminderModal.set(true);
  }

  /**
   * Reset the modal state
   */
  resetModal(): void {
    this.currentReminderType.set(null);
    this.formFields = [];
  }

  /**
   * Update form fields based on reminder type
   */
  protected override updateFormFields(): void {
    const type = this.currentReminderType();

    if (type === ReminderType.TIME_BASED) {
      this.formFields = this.createBaseReminderForm(
        'Time Based Reminder',
        [
          { label: 'At specific date/time', value: 'once' },
          { label: 'Every X days', value: 'repeating' },
        ],
        // Once field creator
        () => {
          return {
            name: 'trigger',
            key: 'trigger',
            type: 'group',
            fields: [
              createDateField('value', 'Date', '', {
                name: 'value',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Starting date is required',
                  },
                ],
                props: {
                  appendTo: 'body',
                  showTime: true,
                  showSeconds: true,
                  dateFormat: 'yy/mm/dd',
                },
              }),
            ],
          };
        },
        // Repeating fields creator
        () => [
          {
            name: 'trigger',
            key: 'trigger',
            type: 'group',
            fields: [
              createNumberField('value', 'Days', 1, {
                name: 'value',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Days is required',
                  },
                ],
              }),
              createDateField('start', 'Starting at', new Date(), {
                name: 'start',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Starting date is required',
                  },
                ],
                props: {
                  appendTo: 'body',
                  showTime: true,
                  showSeconds: true,
                  dateFormat: 'yy/mm/dd',
                },
              }),
            ],
          },
        ],
      );
    } else if (type === ReminderType.ODOMETER) {
      this.formFields = this.createBaseReminderForm(
        'Odometer Reminder',
        [
          { label: 'At specific odometer', value: 'once' },
          { label: 'Every X kilometers', value: 'repeating' },
        ],
        // Once field creator
        () => {
          return {
            name: 'trigger',
            key: 'trigger',
            type: 'group',
            fields: [
              createNumberField('value', 'Odometer', undefined, {
                name: 'value',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Odometer is required',
                  },
                ],
              }),
            ],
          };
        },
        // Repeating fields creator
        () => [
          createNumberField('triggerValue', 'Kilometres', undefined, {
            name: 'triggerValue',
            validations: [
              {
                name: 'required',
                validator: Validators.required,
                message: 'Kilometres is required',
              },
            ],
            hint: 'Trigger every X kilometres',
          }),
          createNumberField('triggerStart', 'Starting at', undefined, {
            name: 'triggerStart',
            validations: [
              {
                name: 'required',
                validator: Validators.required,
                message: 'Starting odometer is required',
              },
            ],
            hint: 'Starting odometer value',
          }),
          this.createResetTypeDropdown(type),
        ],
      );
    } else if (type === ReminderType.ENGINE_HOURS) {
      this.formFields = this.createBaseReminderForm(
        'Engine Hours Reminder',
        [
          { label: 'At specific hours', value: 'once' },
          { label: 'Every X hours', value: 'repeating' },
        ],
        // Once field creator
        () => {
          return {
            name: 'trigger',
            key: 'trigger',
            type: 'group',
            fields: [
              createNumberField('value', 'Engine Hours', undefined, {
                name: 'value',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Engine hours is required',
                  },
                ],
              }),
            ],
          };
        },
        // Repeating fields creator
        () => [
          createNumberField('triggerValue', 'Hours', undefined, {
            name: 'triggerValue',
            validations: [
              {
                name: 'required',
                validator: Validators.required,
                message: 'Hours is required',
              },
            ],
            hint: 'Trigger every X hours',
          }),
          createNumberField('triggerStart', 'Starting at', undefined, {
            name: 'triggerStart',
            validations: [
              {
                name: 'required',
                validator: Validators.required,
                message: 'Starting engine hours is required',
              },
            ],
            hint: 'Starting engine hours value',
          }),
          this.createResetTypeDropdown(type),
        ],
      );
    }
  }

  /**
   * Convert a value to a Date object
   */
  private convertToDate(value: unknown): Date | null {
    if (value instanceof Date) {
      return value;
    }
    if (typeof value === 'string' || typeof value === 'number') {
      const date = new Date(value);
      return Number.isNaN(date.getTime()) ? null : date;
    }
    return null;
  }

  /**
   * Get display name for reminder type
   */
  getReminderTypeDisplay(type: string): string {
    switch (type) {
      case ReminderType.ODOMETER:
        return 'Odometer';
      case ReminderType.ENGINE_HOURS:
        return 'Engine Hours';
      case ReminderType.TIME_BASED:
        return 'Time Based';
      default:
        return 'Unknown Type';
    }
  }

  /**
   * Handle reminder submission from modal
   */
  onReminderSubmit(reminder: Partial<ReminderListItem>): void {
    const entity = this.entity();
    if (isNullOrUndefined(entity?.id)) return;

    // Format trigger based on reminder type and mode
    let formattedTrigger = reminder.trigger;
    const reminderType = this.currentReminderType();

    if (isDefined(formattedTrigger)) {
      if (reminderType === ReminderType.TIME_BASED) {
        if (reminder.mode === 'once') {
          // For "once" mode: trigger.value is the date
          const triggerValue = formattedTrigger.value;
          if (isDefined(triggerValue)) {
            const dateValue = this.convertToDate(triggerValue);
            if (dateValue && !Number.isNaN(dateValue.getTime())) {
              formattedTrigger = {
                ...formattedTrigger,
                value: dateValue.toISOString(),
              };
            }
          }
        } else if (reminder.mode === 'repeating') {
          // For "repeating" mode: trigger.value is days (number), trigger.start is the date
          const triggerStart = formattedTrigger.start;
          const triggerValue = formattedTrigger.value;

          formattedTrigger = { ...formattedTrigger };

          // Convert value to string (number of days)
          if (isDefined(triggerValue)) {
            formattedTrigger.value = String(triggerValue);
          }

          // Convert start date to ISO format
          if (isDefined(triggerStart)) {
            const dateValue = this.convertToDate(triggerStart);
            if (dateValue && !Number.isNaN(dateValue.getTime())) {
              formattedTrigger.start = dateValue.toISOString();
            }
          }
        }
      } else if (
        reminderType === ReminderType.ODOMETER ||
        reminderType === ReminderType.ENGINE_HOURS
      ) {
        // For odometer/engine hours repeating mode: map triggerValue/triggerStart to trigger.value/trigger.start
        const reminderData = reminder as Record<string, unknown>;
        if (reminder.mode === 'repeating') {
          formattedTrigger = {
            value: isDefined(reminderData['triggerValue'])
              ? String(reminderData['triggerValue'])
              : undefined,
            start: isDefined(reminderData['triggerStart'])
              ? String(reminderData['triggerStart'])
              : undefined,
          };
        } else {
          // For once mode: trigger.value should be a string
          formattedTrigger = { ...formattedTrigger };
          if (isDefined(formattedTrigger?.value)) {
            formattedTrigger.value = String(formattedTrigger.value);
          }
        }
      }
    }

    // Build reset object with proper structure
    // resetType is a separate form field that determines reset.type
    const resetType = ((reminder as Record<string, unknown>)['resetType'] ??
      'none') as ReminderReset.TypeEnum;
    const defaultReset: ReminderReset = {
      type: resetType,
      counter: 1,
      daily: { recurrence: 1 },
      weekly: {
        days: [...Array(7).keys()].map((x) => x),
      },
      monthly: {
        months: [...Array(12).keys()].map((x) => ++x), // index starts at 1
        day: 1,
      },
    };

    // Merge form reset values with defaults
    const formattedReset: ReminderReset = {
      ...defaultReset,
      ...reminder.reset,
      type: resetType,
    };

    // Create a new reminder with form data
    const newReminder = {
      name: reminder.name,
      target: {
        type: 'asset',
        id: entity.id,
        name: entity.name,
      },
      type: this.currentReminderType(),
      mode: reminder.mode,
      timeZoneId:
        reminder.timeZoneId ??
        this.authDataService.getUserProfile()?.timeZoneId,
      trigger: formattedTrigger,
      reset: formattedReset,
      ownerId: entity?.owner.id,
      enabled: true, // Set to true by default. Only set to false if the reminder has been triggered (which is not the case for new reminders).
    };

    // Add to new reminders list
    this.newReminders.set([...this.newReminders(), newReminder]);

    // Close modal
    this.showReminderModal.set(false);
    this.resetModal();
  }

  /**
   * Delete a reminder
   */
  deleteReminder(reminder: Partial<ReminderListItem>): void {
    // If it's a new reminder (not yet saved), just remove from newReminders array
    if (isNullOrUndefined(reminder.id)) {
      this.newReminders.set(this.newReminders().filter((r) => r !== reminder));
    }
    // If it's an existing reminder, mark it for deletion
    else if (isDefined(reminder.id)) {
      this.deletedReminderIds.set([...this.deletedReminderIds(), reminder.id]);
      // The sortedReminders computed will handle filtering this out
    }

    this.messageService.add({
      severity: 'info',
      summary: 'Reminder Deleted',
      detail: `Reminder "${reminder.name}" has been marked for deletion.`,
    });
  }

  /**
   * Reset all tracked changes
   */
  resetChanges(): void {
    this.newReminders.set([]);
    this.deletedReminderIds.set([]);
  }

  /**
   * Save all changes (create and delete)
   */
  saveAllChanges(): void {
    const asset = this.entity();
    if (isNullOrUndefined(asset?.id)) return;

    this.isLoadingAsset.set(true);

    // Create all new reminders
    const createObservables = this.newReminders().map((reminder) => {
      const reminderData = {
        ...reminder,
        ownerId: asset.owner.id,
      };
      return this.entitiesService.createReminder(reminderData);
    });

    // Delete all marked reminders
    const deleteObservables = this.deletedReminderIds().map((id) => {
      return this.entitiesService.deleteReminder(id);
    });

    // Combine all observables
    forkJoin({
      creates: !isEmpty(createObservables)
        ? forkJoin(createObservables)
        : of(null),
      deletes: !isEmpty(deleteObservables)
        ? forkJoin(deleteObservables)
        : of(null),
    })
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        finalize(() => this.isLoadingAsset.set(false)),
        catchError(() => {
          this.messageService.add({
            severity: 'error',
            summary: 'Error',
            detail: 'Failed to save reminders',
          });

          return of(null);
        }),
      )
      .subscribe({
        next: () => {
          this.messageService.add({
            severity: 'success',
            summary: 'Success',
            detail: 'Reminders updated successfully',
          });

          // Reload reminders to get the updated list
          this.loadedAssetId = null;
          this.loadReminders(asset);

          this.isFormEditable.set(false);
          this.isSubmitting.set(false);
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'asset',
            entityId: asset.id,
          });
        },
        error: () => {
          this.isFormEditable.set(true);
          this.isSubmitting.set(false);
        },
      });
  }

  protected override submitChanges(
    _formData: Record<string, string | number>,
  ): void {
    // We're using our custom saveAllChanges method instead
    this.saveAllChanges();
  }

  onCancel(): void {
    this.isFormEditable.set(false);
    this.resetChanges();
  }
}
