import { Injectable } from '@angular/core';

import { AlertResponse } from '../../../api';
import { AdminDetailSection } from '../models';
import { FeedSectionComponent } from '../components/shared/feed-section/feed-section.component';
import { AlertDetailsSectionComponent } from '../components/alerts/alert-details-section/alert-details-section.component';

@Injectable({ providedIn: 'root' })
export class AlertSectionConfigFactory {
  /**
   * Create sections for alert information
   * @param alert The alert object to display in the section
   * @returns An array of AdminDetailSection objects representing the alert information sections
   */
  createAlertInformationSection(
    alert: AlertResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'alert-details',
        title: 'Alert Config Details',
        component: AlertDetailsSectionComponent,
        componentInputs: { entity: alert },
        category: 'information',
        order: 10,
      },
    ];
  }

  /**
   * Create sections for alert changes
   * @param alert The alert object to display in the section
   * @returns An array of AdminDetailSection objects representing the alert changes sections
   */
  createChangesSection(alert: AlertResponse | null): AdminDetailSection[] {
    return [
      {
        id: 'alert-changes',
        title: 'Changes',
        component: FeedSectionComponent,
        componentInputs: {
          entity: alert,
        },
        category: 'feed',
        order: 10,
      },
    ];
  }

  /**
   * Create all alert sections in one call
   * @param alert The alert object to display in the sections
   * @returns An array of AdminDetailSection objects representing all alert sections
   */
  createAllAlertSections(alert: AlertResponse | null): AdminDetailSection[] {
    return [
      ...this.createAlertInformationSection(alert),
      ...this.createChangesSection(alert),
    ];
  }
}
