import { Injectable } from '@angular/core';

import { VendorResponse } from '../../../api';
import { AdminDetailSection, AdminSectionConfiguration } from '../models';
import { SectionConfigBuilder } from './section-config.factory';
import { FlexibleSectionBuilder } from './flexible-section-builder.factory';
import { FeedSectionComponent } from '../components/shared/feed-section/feed-section.component';
import { FeatureSectionComponent } from '../components/shared/feature-section/feature-section.component';
import { AppFeatures } from '../../../core/models';

@Injectable({ providedIn: 'root' })
export class VendorSectionConfigFactory {
  /**
   * Create all vendor sections in one call (legacy approach)
   * @param vendor The vendor object to display in the sections
   * @returns An array of AdminDetailSection objects representing all vendor sections
   */
  createAllVendorSections(
    vendor: VendorResponse | null,
    appFeatures: string[],
  ): AdminDetailSection[] {
    const features = appFeatures.flatMap((feature) =>
      SectionConfigBuilder.forVendor(vendor)
        .addSection(
          feature,
          'Features',
          FeatureSectionComponent<VendorResponse>,
          {
            category: 'features',
          },
          {
            feature: AppFeatures[feature],
            entityType: 'vendor',
          },
        )
        .build(),
    );
    return [
      ...SectionConfigBuilder.createVendorInformationSections(vendor),
      ...SectionConfigBuilder.createVendorSettingsSections(vendor),
      ...features,
      ...[
        {
          id: 'vendor-changes',
          title: 'Changes',
          component: FeedSectionComponent,
          componentInputs: {
            entity: vendor,
            entityType: 'vendor',
          },
          category: 'feed' as const,
          order: 10,
        },
      ],
    ];
  }

  /**
   * Create all vendor sections with flexible configuration (new approach)
   * @param vendor The vendor object to display in the sections
   * @returns Configuration object with sections and layout
   */
  createFlexibleVendorConfiguration(
    vendor: VendorResponse | null,
    appFeatures: string[],
  ): AdminSectionConfiguration {
    const informationSections =
      SectionConfigBuilder.createVendorInformationSections(vendor);
    const settingsSections =
      SectionConfigBuilder.createVendorSettingsSections(vendor);

    // Create feature sections from the appFeatures array
    const featureSections = appFeatures.flatMap((feature) =>
      SectionConfigBuilder.forVendor(vendor)
        .addSection(
          feature,
          'Features',
          FeatureSectionComponent<VendorResponse>,
          {
            category: 'features',
          },
          {
            feature: AppFeatures[feature],
            entityType: 'vendor',
          },
        )
        .build(),
    );

    return (
      FlexibleSectionBuilder.forEntity(vendor)
        // Add existing information sections
        .withSections(informationSections)

        // Add existing settings sections
        .withSections(settingsSections)

        // Add feature sections
        .withSections(featureSections)

        // Add feed section
        .withSection(
          'vendor-changes',
          'Changes',
          FeedSectionComponent,
          'feed',
          10,
          {
            entityType: 'vendor',
          },
        )

        // Configure layout with custom titles and order
        .withCategory('information', 'Vendor Information', 10)
        .withCategory('settings', 'Vendor Settings', 20)
        .withCategory('features', 'Features', 30)
        .withCategory('feed', 'Recent Changes', 40)
        .withDefaultExpanded('information')
        .withMultipleExpansion(false)
        .build()
    );
  }
}
