import { Injectable } from '@angular/core';

import {
  DeviceResponse,
  DeviceTypeAccessory,
  DeviceTypeResponse,
  IoTypeListItem,
} from '../../../api';
import { AdminDetailSection } from '../models';
import { DeviceParametersSectionComponent } from '../components/device/device-parameters-section/device-parameters-section.component';
import { DeviceIoWhitelistSectionComponent } from '../components/device/device-io-whitelist-section/device-io-whitelist-section.component';
import { DeviceBitmapSectionComponent } from '../components/device/device-bitmap-section/device-bitmap-section.component';
import { DeviceIOSectionComponent } from '../components/device/device-io-section/device-io-section.component';
import { DeviceAccessorySectionComponent } from '../components/device/device-accessory-section/device-accessory-section.component';
import { DeviceDetailsSectionComponent } from '../components/device/device-details-section/device-details-section.component';
import { DeviceLinkedSectionComponent } from '../components/device/device-linked-section/device-linked-section.component';
import { InfoSectionComponent } from '../components/shared/info-section/info-section.component';
import { isDefined } from '../../../shared/utils';
import { FormSectionComponent } from '../components/shared/form-section/form-section.component';
import { FeedSectionComponent } from '../components/shared/feed-section/feed-section.component';
import { DEFAULT_SYSTEM_ID } from '../../../core/models';

@Injectable({ providedIn: 'root' })
export class DeviceSectionConfigFactory {
  /**
   * Create sections for device information
   * @param device The device object to display in the section
   * @returns An array of AdminDetailSection objects representing the device information sections
   */
  createDeviceInformationSection(
    device: DeviceResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'device-details',
        title: 'Device Config Details',
        component: DeviceDetailsSectionComponent,
        componentInputs: { entity: device },
        category: 'information',
        order: 10,
      },
      {
        id: 'device-information',
        title: 'Device Information',
        component: InfoSectionComponent<DeviceResponse>,
        componentInputs: {
          entity: device,
          dataPath: 'device_information',
          entityType: 'device',
        },
        category: 'information',
        order: 20,
      },
      {
        id: 'device-linked-entities',
        title: 'Linked Entities',
        component: DeviceLinkedSectionComponent,
        componentInputs: {
          entity: device,
          entityType: 'device',
          dataPath: 'linked_entities',
        },
        category: 'information',
        order: 30,
      },
    ];
  }

  /**
   * Create sections for device IO
   * @param device The device object to display in the section
   * @returns An array of AdminDetailSection objects representing the device IO sections
   */
  createDeviceIOSection(
    device: DeviceResponse | null,
    deviceType: DeviceTypeResponse | null,
    ioTypes: IoTypeListItem[],
  ): AdminDetailSection[] {
    return [
      {
        id: 'device-io-selection',
        title: 'IO Selection',
        component: DeviceIoWhitelistSectionComponent,
        componentInputs: {
          entity: device,
          entityType: 'device',
          deviceType,
          injectDataAtPath: true,
          dataPath: 'parameters',
        },
        category: 'io',
        order: 10,
      },
      {
        id: 'device-bitmapped-io',
        title: 'Bitmapped IO',
        component: DeviceBitmapSectionComponent,
        componentInputs: {
          entity: device,
          entityType: 'device',
        },
        category: 'io',
        order: 20,
      },
      {
        id: 'device-analog-inputs',
        title: 'Analog Inputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: device,
          entityType: 'device',
          ioType: 'analog_input',
          iconType: 'svg',
          iconName: 'gauge-simple-high-solid',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 30,
      },
      {
        id: 'device-camera-inputs',
        title: 'Camera Inputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: device,
          entityType: 'device',
          ioType: 'camera_input',
          iconType: 'primeng',
          iconName: 'pi pi-camera',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 40,
      },
      {
        id: 'device-can-bus-inputs',
        title: 'CAN Bus Inputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: device,
          entityType: 'device',
          ioType: 'can_input',
          iconType: 'svg',
          iconName: 'oil-can-solid',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 50,
      },
      {
        id: 'device-counter-inputs',
        title: 'Counter Inputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: device,
          entityType: 'device',
          ioType: 'counter_input',
          iconType: 'primeng',
          iconName: 'pi pi-clock',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 60,
      },
      {
        id: 'device-digital-inputs',
        title: 'Digital Inputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: device,
          entityType: 'device',
          ioType: 'digital_input',
          iconType: 'primeng',
          iconName: 'pi pi-arrow-left',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 70,
      },
      {
        id: 'device-digital-outputs',
        title: 'Digital Outputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: device,
          entityType: 'device',
          ioType: 'digital_output',
          iconType: 'primeng',
          iconName: 'pi pi-arrow-right',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 80,
      },
      {
        id: 'device-temperature-inputs',
        title: 'Temperature Inputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: device,
          entityType: 'device',
          ioType: 'temperature_input',
          iconType: 'svg',
          iconName: 'temperature-half-solid',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 90,
      },
      {
        id: 'device-value-inputs',
        title: 'Value Inputs',
        component: DeviceIOSectionComponent,
        componentInputs: {
          entity: device,
          entityType: 'device',
          ioType: 'value_input',
          iconType: 'primeng',
          iconName: 'pi pi-list-check',
          deviceType,
          ioTypes,
        },
        category: 'io',
        order: 100,
      },
    ];
  }

  /**
   * Create sections for device accessories
   * @param device The device object to display in the section
   * @returns An array of AdminDetailSection objects representing the device accessories sections
   */
  createDeviceAccessoriesSection(
    device: DeviceResponse | null,
    deviceTypeAccessories: Record<string, DeviceTypeAccessory> | undefined,
  ): AdminDetailSection[] {
    if (!device) return [];

    const sections: AdminDetailSection[] = [];
    Object.entries(deviceTypeAccessories ?? {}).forEach(([key]) => {
      sections.push({
        id: `device-accessory-${key}`,
        title: deviceTypeAccessories?.[key]?.name ?? key,
        component: DeviceAccessorySectionComponent,
        componentInputs: {
          entity: device,
          entityType: 'device',
          dataPath: `accessories.${key}`,
          deviceTypeAccessory: deviceTypeAccessories?.[key],
          accessoryId: key,
          sectionHeader: deviceTypeAccessories?.[key]?.name ?? key,
        },
        category: 'accessories',
        order: 10 + Number(key),
      });
    });
    return sections;
  }

  /**
   * Create sections for device settings
   * @param device The device object to display in the section
   * @returns An array of AdminDetailSection objects representing the device settings sections
   */
  createSettingsSection(
    device: DeviceResponse | null,
    deviceType: DeviceTypeResponse | null,
  ): AdminDetailSection[] {
    let custom = [];

    if (isDefined(deviceType?.settingsDefinition)) {
      try {
        const def = JSON.parse(deviceType.settingsDefinition);
        custom = def.groups.map(
          (group: { id: string; title: string }) =>
            ({
              id: group.id,
              title: group.title,
              component: FormSectionComponent,
              componentInputs: {
                entity: device,
                formConfig: group,
                entityType: 'device',
                valuesKey: 'settings',
              },
              category: 'settings',
              order: 10 + Number(group.id),
            }) as AdminDetailSection,
        );
      } catch (err) {
        console.warn(
          'Invalid device type settings definition',
          deviceType.settingsDefinition,
        );
        console.error(err);
      }
    }

    return [
      {
        id: 'device-parameters',
        title: 'Device Config Parameters',
        component: DeviceParametersSectionComponent,
        componentInputs: {
          entity: device,
          entityType: 'device',
          dataPath: 'parameters',
          injectDataAtPath: true,
          deviceType,
        },
        category: 'settings',
        order: 10,
      },
      ...custom,
    ];
  }

  /**
   * Create sections for device changes
   * @param device The device object to display in the section
   * @returns An array of AdminDetailSection objects representing the device changes sections
   */
  createChangesSection(device: DeviceResponse | null): AdminDetailSection[] {
    return [
      {
        id: 'device-changes',
        title: 'Changes',
        component: FeedSectionComponent,
        componentInputs: {
          entity: device,
        },
        category: 'feed',
        order: 10,
      },
    ];
  }

  /**
   * Create all device sections in one call
   * @param device The device object to display in the sections
   * @returns An array of AdminDetailSection objects representing all device sections
   */
  createAllDeviceSections(
    device: DeviceResponse | null,
    deviceTypeAccessories: Record<string, DeviceTypeAccessory> | undefined,
    deviceType: DeviceTypeResponse | null,
    ioTypes: IoTypeListItem[],
  ): AdminDetailSection[] {
    const sections = [...this.createDeviceInformationSection(device)];

    const hasConfigProfile =
      isDefined(device?.configProfile?.id) &&
      device?.configProfile?.id !== DEFAULT_SYSTEM_ID;
    if (!hasConfigProfile) {
      sections.push(...this.createSettingsSection(device, deviceType));
      sections.push(...this.createDeviceIOSection(device, deviceType, ioTypes));

      if (
        deviceType?.accessories &&
        Object.keys(deviceType?.accessories)?.length > 0
      ) {
        sections.push(
          ...this.createDeviceAccessoriesSection(device, deviceTypeAccessories),
        );
      }
    }

    sections.push(...this.createChangesSection(device));
    return sections;
  }
}
