import {
  Component,
  computed,
  effect,
  inject,
  input,
  signal,
} from '@angular/core';

import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';

import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';
import {
  DeviceProviderCreateRequest,
  DeviceProviderListItem,
  DeviceProviderResponse,
  EntitiesService,
  DeviceProviderTypeResponse,
} from '../../../../../api';
import { DeviceProviderSectionConfigFactory } from '../../../factories';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { firstValueFrom } from 'rxjs';
import { RequestCacheService } from '../../../../../shared/services/request-cache.service';
import { isDefined } from '../../../../../shared/utils';

@Component({
  selector: 'app-device-provider-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './device-provider-details-container.component.html',
  styleUrl: './device-provider-details-container.component.scss',
})
export class DeviceProviderDetailsContainerComponent {
  readonly entity = input<DeviceProviderListItem | null>(null);

  private readonly deviceProviderSectionFactory = inject(
    DeviceProviderSectionConfigFactory,
  );
  readonly entityDetailsState = inject(EntityDetailsStateService);
  private readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly cacheService = inject(RequestCacheService);

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly isSoftRefreshing = computed(() =>
    this.entityDetailsState.isSoftRefreshing(),
  );
  readonly isInitialLoading = computed(
    () =>
      this.isLoading() &&
      !this.isSoftRefreshing() &&
      !this.fullDeviceProvider(),
  );
  readonly fullDeviceProvider = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();

    if (!entityDetails) {
      return null;
    }
    return entityDetails as DeviceProviderResponse;
  });
  readonly sections = computed(() => {
    const deviceProvider = this.fullDeviceProvider();
    const deviceProviderType = this.deviceProviderType();
    if (!(deviceProvider || isDefined(deviceProviderType))) return [];
    return this.deviceProviderSectionFactory.createAllDeviceProviderSections(
      deviceProvider,
      deviceProviderType,
    );
  });
  readonly error = signal<string | null>(null);
  readonly deviceProviderType = signal<DeviceProviderTypeResponse | null>(null);

  constructor() {
    this.operationRegistry.registerEntityOperations('device-provider', {
      update: (id, data) => this.entitiesService.updateDeviceProvider(id, data),
      create: (data: DeviceProviderCreateRequest) =>
        this.entitiesService.createDeviceProvider(data),
      get: (id) => this.entitiesService.getDeviceProvider(id),
    });

    effect(() => {
      const deviceProvider = this.fullDeviceProvider();
      if (isDefined(deviceProvider?.deviceProviderType?.id)) {
        this.loadDeviceProviderType(
          deviceProvider?.deviceProviderType?.id ?? '',
        ).catch((error) => {
          this.error.set(error.message);
        });
      }
    });
  }

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as DeviceProviderResponse);
    this.error.set(null);
  }

  async loadDeviceProviderType(deviceProviderTypeId: string): Promise<void> {
    await this.cacheService
      .getOrFetchAsync(
        `deviceProviderType-${deviceProviderTypeId}`,
        () =>
          firstValueFrom(
            this.entitiesService.getDeviceProviderType(deviceProviderTypeId),
          ),
        300000,
      )
      .then((response) => {
        this.deviceProviderType.set(response);
      });
  }
}
