import { CommonModule } from '@angular/common';
import {
  ChangeDetectorRef,
  Component,
  computed,
  effect,
  inject,
  input,
  OnInit,
  signal,
} from '@angular/core';
import { ReactiveFormsModule, Validators } from '@angular/forms';
import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';
import { ChipModule } from 'primeng/chip';

import { EntitySectionBaseComponent } from '../../../components/base/entity-section-base';
import {
  AssetCreateRequest,
  AssetListItem,
  AssetTagListItem,
  AssetUpdateRequest,
  EntitiesService,
  IdName,
} from '../../../../../api';
import {
  hasProperty,
  isDefined,
  isEmpty,
  isNullOrUndefined,
} from '../../../../../shared/utils';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { UtilityService } from '../../../../../core/services/utility.service';
import {
  AssetGroupingTreeItem,
  createAutocompleteField,
  createDropdownField,
  createTextField,
  createTreeSelectField,
} from '../../../../../shared/models';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import {
  catchError,
  debounceTime,
  firstValueFrom,
  of,
  Subject,
  switchMap,
} from 'rxjs';
import {
  ASSET_TYPE_WITH_AVATAR,
  COLORS,
  SEARCH_DEBOUNCE_TIME,
} from '../../../../../core/models';
import { AssetGroupingService } from '../../../../../shared/services/asset-grouping.service';
import { AssetAvatarService } from '../../../../../shared/services/asset-avatar.service';
import { TooltipModule } from 'primeng/tooltip';
import { ImageEditorComponent } from '../../../../../shared/components/image-editor/image-editor.component';
import { HttpErrorResponse } from '@angular/common/http';
import { ToastService } from '../../../../../core/services/toast.service';

@Component({
  selector: 'app-asset-details-section',
  imports: [
    CommonModule,
    ReactiveFormsModule,
    ButtonModule,
    InputTextModule,
    ChipModule,
    GridComponent,
    DynamicFormComponent,
    TooltipModule,
    ImageEditorComponent,
  ],
  templateUrl: './asset-details-section.component.html',
  styleUrl: './asset-details-section.component.scss',
})
export class AssetDetailsSectionComponent
  extends EntitySectionBaseComponent<
    AssetListItem | Partial<AssetCreateRequest>
  >
  implements OnInit
{
  readonly ownerId = input<string | null>(null);
  private readonly entitiesService = inject(EntitiesService);
  private readonly utilityService = inject(UtilityService);
  private readonly assetGroupingService = inject(AssetGroupingService);
  private readonly assetAvatarService = inject(AssetAvatarService);
  private readonly toastService = inject(ToastService);

  private readonly imageDataUrl = signal<string | null>(null);
  private currentLoadingAssetId: string | null = null;
  readonly isUploadingImage = signal<boolean>(false);

  private readonly cdr = inject(ChangeDetectorRef);
  protected readonly selectOptionsLoaded = signal<boolean>(false);
  assetTypes: { label: string; value: string }[] = [];
  selectOptions: Record<
    string,
    { label: string; value: string }[] | AssetGroupingTreeItem[]
  > = {};
  colors = [...COLORS]
    .sort((a, b) => a.localeCompare(b))
    .map((color) => ({
      label: this.utilityService.toTitleCase(color),
      value: color,
    }));
  readonly assetDetails = computed(() => {
    const assetDetails = this.entity();
    if (!isDefined(assetDetails)) return [];

    return [
      {
        key: 'Name',
        value: assetDetails.name,
      },
      {
        key: 'Icon Name',
        value: assetDetails.mapIconName ?? 'Default',
      },
      {
        key: 'Tags',
        value: isEmpty(assetDetails.tags)
          ? 'None'
          : assetDetails?.tags?.map((tag) => tag).join(', '),
      },
      {
        key: 'Type',
        value: isDefined(assetDetails.assetType)
          ? assetDetails.assetType.name
          : 'Unknown',
      },
      {
        key: 'Color',
        value: isDefined(assetDetails.color)
          ? this.utilityService.toTitleCase(assetDetails.color)
          : 'None',
      },
      {
        key: 'Cost Centre',
        value: isDefined(assetDetails.costCentre)
          ? assetDetails.costCentre.name
          : 'None',
      },
      {
        key: 'Groups',
        value: isEmpty(assetDetails.groups)
          ? ''
          : assetDetails.groups?.map((group) => group.name).join(', '),
      },
      {
        key: 'Asset Tag',
        value: assetDetails.assetTag ?? 'None',
      },
    ];
  });
  assetTagsSearchResults = signal<AssetTagListItem[] | undefined>(undefined);
  private readonly searchSubject = new Subject<string>();
  isImageEditorVisible = signal(false);

  readonly currentImageUrl = computed(() => {
    return this.imageDataUrl();
  });

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    // React to entity changes
    effect(async () => {
      const entity = this.entity();
      if (entity && !this.createMode()) {
        this.updateFormFields();
        await this.loadAssetImage(); // Load image when entity changes
      }
    });
  }

  ngOnInit() {
    this.setUpAssetTagsSearch();
    this.loadSelectOptions();
  }

  private loadSelectOptions() {
    const asset = this.entity();
    const ownerIdValue = this.ownerId();

    const effectiveOwnerId = asset?.owner?.id ?? ownerIdValue;

    if (isNullOrUndefined(effectiveOwnerId)) {
      this.selectOptions = {};
      this.selectOptionsLoaded.set(true);
      this.updateFormFields();
      return;
    }

    Promise.all([
      this.assetGroupingService.getAssetTypes(effectiveOwnerId),
      firstValueFrom(this.entitiesService.listCostCentres(effectiveOwnerId)),
      this.assetGroupingService.getAssetGroupsAsTree(effectiveOwnerId),
    ])
      .then(([assetTypes, costCentres, assetGroups]) => {
        this.selectOptions = {
          assetTypes: assetTypes
            .map((item) => ({
              label: item.name ?? '',
              value: item.id,
            }))
            .sort((a, b) => a.label.localeCompare(b.label)),
          costCentres: costCentres.items
            .map((costCentre) => ({
              label: costCentre.name ?? '',
              value: costCentre.id,
            }))
            .sort((a, b) => a.label.localeCompare(b.label)),
          groups: assetGroups,
        };

        this.selectOptionsLoaded.set(true);
        this.updateFormFields();
      })
      .catch(() => {
        this.selectOptionsLoaded.set(false);
      });
  }

  private setUpAssetTagsSearch(): void {
    this.searchSubject
      .pipe(
        debounceTime(SEARCH_DEBOUNCE_TIME),
        takeUntilDestroyed(this.destroyRef),
        switchMap((searchTerm) => {
          const owner = this.entity()?.owner;

          if (isNullOrUndefined(owner?.id)) {
            return of({ items: [] });
          }

          const trimmedSearchTerm = searchTerm.trim();

          if (trimmedSearchTerm.length === 0) {
            return of({ items: [] });
          }

          const filters = {
            state: { value: 'active', operator: '=' },
            name: { value: `*${trimmedSearchTerm}*`, operator: '=' },
          };
          const rqlFilter = this.utilityService.constructRqlFilter(filters);

          return this.entitiesService
            .listAssetTags(owner.id, 0, 10, 'name', rqlFilter)
            .pipe(
              catchError(() => {
                return of({ items: [] });
              }),
            );
        }),
      )
      .subscribe({
        next: (data) => {
          if (isDefined(data) && hasProperty(data, 'items')) {
            this.assetTagsSearchResults.set(
              isNullOrUndefined(data.items) ? [] : data.items,
            );
          }
        },
        error: (error) => {
          console.error('Error searching asset tags:', error);
          this.assetTagsSearchResults.set([]);
        },
      });
  }

  protected override updateFormFields(): void {
    const asset = this.entity();
    this.formFields = [
      createTextField('name', 'Name', asset?.name, {
        name: 'name',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Name is required',
          },
        ],
      }),
      createDropdownField(
        'mapIconName',
        'Icon Name',
        [
          { label: 'Default', value: 'Default' },
          { label: 'Airplane', value: 'Airplane' },
          { label: 'Ambulance (small)', value: 'Ambulance (small)' },
          { label: 'Ambulance', value: 'Ambulance' },
          { label: 'Boat', value: 'Boat' },
          { label: 'Bus', value: 'Bus' },
          { label: 'Car', value: 'Car' },
          { label: 'Cement Mixer', value: 'Cement Mixer' },
          { label: 'Container', value: 'Container' },
          { label: 'Dump Truck', value: 'Dump Truck' },
          { label: 'Excavator', value: 'Excavator' },
          { label: 'Haul Truck', value: 'Haul Truck' },
          { label: 'Lawn Mower', value: 'Lawn Mower' },
          { label: 'Man', value: 'Man' },
          { label: 'Motorcycle', value: 'Motorcycle' },
          { label: 'Parcel', value: 'Parcel' },
          { label: 'Pickup Truck', value: 'Pickup Truck' },
          { label: 'School Bus (small)', value: 'School Bus (small)' },
          { label: 'School Bus', value: 'School Bus' },
          { label: 'Tractor', value: 'Tractor' },
          { label: 'Trailer', value: 'Trailer' },
          { label: 'Truck (small)', value: 'Truck (small)' },
          { label: 'Truck', value: 'Truck' },
          { label: 'Van (small)', value: 'Van (small)' },
          { label: 'Van', value: 'Van' },
        ],
        asset?.mapIconName ?? 'Default',
        {
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Icon name is required',
            },
          ],
          props: {
            appendTo: 'body',
          },
        },
      ),
      createAutocompleteField('tags', 'Tags', asset?.tags ?? [], [], () => {}, {
        layout: 'horizontal',
        labelWidth: '1/3',
        name: 'tags',
        placeholder: 'Enter Tag(s) ...',
        hint: ' Tip: Press Enter or Tab to add a tag',
        props: {
          appendTo: 'body',
          typeahead: false,
          multiple: true,
          dropdown: false,
          forceSelection: false,
          tooltip:
            '<strong>How to add tags:</strong><br>' +
            ' Type your tag text<br>' +
            ' Press <kbd>Enter</kbd> or <kbd>Tab</kbd> to add a tag<br>' +
            ' Click the  on any tag to remove it<br>',
        },
      }),
      createDropdownField(
        'assetType',
        'Type',
        (this.selectOptions?.['assetTypes'] as {
          label: string;
          value: string;
        }[]) ?? [],
        asset?.assetType?.id,
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          name: 'assetType',
          props: {
            appendTo: 'body',
          },
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Asset type is required',
            },
          ],
        },
      ),
      createDropdownField('color', 'Color', this.colors, asset?.color, {
        layout: 'horizontal',
        labelWidth: '1/3',
        name: 'color',
        props: {
          appendTo: 'body',
        },
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Color is required',
          },
        ],
      }),
      createDropdownField(
        'costCentre',
        'Cost Centre',
        (this.selectOptions?.['costCentres'] as {
          label: string;
          value: string;
        }[]) ?? [],
        asset?.costCentre?.id,
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          name: 'costCentre',
          props: {
            appendTo: 'body',
          },
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Cost centre is required',
            },
          ],
        },
      ),
      createTreeSelectField(
        'groups',
        'Groups',
        this.getSelectedGroupNodes(
          asset?.groups,
          this.selectOptions?.['groups'] as AssetGroupingTreeItem[],
        ),
        this.selectOptions?.['groups'] ?? [],
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          name: 'groups',
          props: {
            appendTo: 'body',
            placeholder: 'Select Group',
            multiple: true,
            showClear: true,
            selectionMode: 'checkbox',
            filter: true,
          },
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'At least one group is required',
            },
          ],
        },
      ),
      createAutocompleteField(
        'assetTag',
        'Asset Tag',
        typeof asset?.assetTag === 'string' ? asset.assetTag : undefined,
        () => this.assetTagsSearchResults() ?? [],
        this.searchAssetTag.bind(this),
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          name: 'assetTag',
          props: {
            appendTo: 'body',
            showClear: true,
            multiple: false,
            field: 'name',
            completeOnFocus: false,
            typeahead: true,
            showEmptyMessage: true,
            emptyMessage: 'No asset tags found',
          },
        },
      ),
    ];
    this.cdr.detectChanges();
  }

  protected override submitChanges(
    formData: Record<string, string | number>,
  ): void {
    const asset = this.entity();
    if (isNullOrUndefined(asset)) return;
    this.entitiesService
      .updateAsset(asset.id, this.transformData(formData))
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe({
        next: () => {
          this.isFormEditable.set(false);
          this.isSubmitting.set(false);
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'asset',
            entityId: asset.id,
          });
        },
        error: () => {
          this.isFormEditable.set(true);
          this.isSubmitting.set(false);
        },
      });
  }

  protected override onFormValueChange(
    formData: Record<string, string | number | object | null | undefined>,
  ): void {
    const transformedData = this.transformData(formData);
    // Emit the transformed values
    this.formValueChange.emit(transformedData);
  }

  private getSelectedGroupNodes(
    assetGroups: IdName[] | undefined,
    treeOptions: AssetGroupingTreeItem[] | undefined,
  ): AssetGroupingTreeItem[] {
    if (!assetGroups || !treeOptions) return [];

    const selectedNodes: AssetGroupingTreeItem[] = [];

    // Recursive function to find node by ID in the tree structure
    const findNodeById = (
      nodes: AssetGroupingTreeItem[],
      id: string,
    ): AssetGroupingTreeItem | undefined => {
      for (const node of nodes) {
        if (node.id === id) {
          return node;
        }
        if (node.children && node.children.length > 0) {
          const found = findNodeById(node.children, id);
          if (found) return found;
        }
      }
      return undefined;
    };

    assetGroups.forEach((group) => {
      const foundNode = findNodeById(treeOptions, group.id);
      if (foundNode) {
        selectedNodes.push(foundNode);
      }
    });

    return selectedNodes;
  }

  protected transformData(
    formData: Record<string, string | number | object | null | undefined>,
  ): Partial<AssetUpdateRequest | AssetCreateRequest> {
    const transformedData = { ...formData };

    // Transform assetType ID to IdName object
    if (
      isDefined(transformedData['assetType']) &&
      typeof transformedData['assetType'] === 'string'
    ) {
      const assetTypeOption = this.selectOptions?.['assetTypes']?.find(
        (option) => option.value === transformedData['assetType'],
      );

      if (assetTypeOption) {
        transformedData['assetType'] = {
          id: assetTypeOption.value,
          name: assetTypeOption.label,
        };
      }
    }

    // Transform costCentre ID to IdName object
    if (
      isDefined(transformedData['costCentre']) &&
      typeof transformedData['costCentre'] === 'string'
    ) {
      const costCentreOption = this.selectOptions?.['costCentres']?.find(
        (option) => option.value === transformedData['costCentre'],
      );

      if (costCentreOption) {
        transformedData['costCentre'] = {
          id: costCentreOption.value,
          name: costCentreOption.label,
        };
      }
    }

    // Transform groups array - handle both flat and nested tree structures
    if (Array.isArray(transformedData['groups'])) {
      // Recursive function to find group in tree structure
      const findGroupInTree = (
        nodes: AssetGroupingTreeItem[],
        targetId: string,
      ): AssetGroupingTreeItem | undefined => {
        for (const node of nodes) {
          if (node.id === targetId) {
            return node;
          }
          if (node.children && node.children.length > 0) {
            const found = findGroupInTree(node.children, targetId);
            if (found) return found;
          }
        }
        return undefined;
      };

      transformedData['groups'] = transformedData['groups']
        .map((group) => {
          // Handle case where group is already a TreeNode object
          const groupId =
            typeof group === 'object' && isDefined(group.id) ? group.id : group;

          const groupOption = findGroupInTree(
            (this.selectOptions?.['groups'] as AssetGroupingTreeItem[]) ?? [],
            groupId,
          );

          return groupOption
            ? {
                id: groupOption.id,
                name: groupOption.label,
              }
            : undefined;
        })
        .filter((group): group is { id: string; name: string } =>
          isDefined(group),
        );
    }

    // Transform tags to ensure it's an array of strings
    if (Array.isArray(transformedData['tags'])) {
      transformedData['tags'] = transformedData['tags']
        .filter((tag): tag is string => typeof tag === 'string')
        .filter((tag) => tag.trim() !== '');
    } else if (typeof transformedData['tags'] === 'string') {
      transformedData['tags'] = transformedData['tags'].trim()
        ? [transformedData['tags']]
        : [];
    } else if (!isDefined(transformedData['tags'])) {
      transformedData['tags'] = [];
    }

    if (transformedData['assetTag'] === '') {
      transformedData['assetTag'] = null;
    }

    return transformedData as AssetUpdateRequest | AssetCreateRequest;
  }

  searchAssetTag(searchTerm: string) {
    this.assetTagsSearchResults.set(undefined);
    this.searchSubject.next(searchTerm);
  }

  // Image functionality methods
  private async loadAssetImage(): Promise<void> {
    const asset = this.entity();
    if (!isDefined(asset) || !isDefined(asset.id)) {
      this.imageDataUrl.set(null);
      this.currentLoadingAssetId = null;
      return;
    }

    this.isLoading.set(true);

    // Track which asset we're loading to prevent race conditions
    const assetId = asset.id;
    this.currentLoadingAssetId = assetId;

    try {
      const dataUrl = await firstValueFrom(
        this.assetAvatarService.getAssetAvatar(assetId),
      );

      // Only update if we're still loading the same asset
      // (user might have switched to a different asset while loading)
      if (this.currentLoadingAssetId === assetId) {
        this.imageDataUrl.set(dataUrl);
      }
    } catch (error) {
      // Only update if we're still loading the same asset
      if (this.currentLoadingAssetId === assetId) {
        // If image fails to load (404, 401, etc.), set to null to show fallback
        this.imageDataUrl.set(null);

        // Log non-404 errors for debugging
        if (error instanceof HttpErrorResponse && error.status !== 404) {
          console.error('Failed to load asset avatar:', error);
          this.toastService.showError(
            'Failed to load asset avatar',
            error.message,
          );
        }
      }
    } finally {
      this.isLoading.set(false);
    }
  }

  openImageEditor(): void {
    if (this.canEditAvatar() && !this.isLoading() && !this.isUploadingImage()) {
      this.isImageEditorVisible.set(true);
    }
  }

  onImageEditorVisibleChange(visible: boolean): void {
    this.isImageEditorVisible.set(visible);
  }

  /**
   * Checks if current user can edit avatar for this asset
   */
  canEditAvatar(): boolean {
    const asset = this.entity();
    if (isNullOrUndefined(asset?.assetType?.name)) {
      return false;
    }

    // Only allow avatar editing for specific asset types
    if (asset.assetType.name.toLowerCase() !== ASSET_TYPE_WITH_AVATAR) {
      return false;
    }

    return true;
  }

  async removeAssetAvatar(): Promise<void> {
    const asset = this.entity();
    if (isNullOrUndefined(asset?.id)) return;

    // Authorization check
    if (!this.canEditAvatar()) {
      this.toastService.showError(
        'Permission Denied',
        'You do not have permission to delete this avatar.',
      );
      return;
    }

    this.isUploadingImage.set(true);
    this.toastService.showInfo(
      'Deleting Asset Avatar',
      'Deleting Asset Avatar. Please wait.',
    );

    this.assetAvatarService.deleteAssetAvatar(asset.id).subscribe({
      next: () => {
        this.toastService.showSuccess(
          'Successful',
          'The Asset Avatar was successfully deleted.',
        );
        this.isImageEditorVisible.set(false);
        this.isUploadingImage.set(false);
        this.imageDataUrl.set(null);
        this.broadcastService.broadcast('entity-updated', {
          entityType: 'asset',
          entityId: asset.id,
        });
      },
      error: (error) => {
        this.toastService.showError(
          'Error',
          `An error occurred while trying to delete the asset avatar: ${error}`,
        );
        this.isUploadingImage.set(false);
      },
    });
  }

  async saveImage(file: File): Promise<void> {
    const asset = this.entity();
    if (isNullOrUndefined(asset?.id)) return;

    // Authorization check
    if (!this.canEditAvatar()) {
      this.toastService.showError(
        'Permission Denied',
        'You do not have permission to upload an avatar.',
      );
      return;
    }

    try {
      this.isUploadingImage.set(true);

      await this.assetAvatarService.uploadAssetAvatar(asset.id, file);

      this.toastService.showSuccess(
        'Upload Successful',
        'Image uploaded successfully.',
      );
      this.isImageEditorVisible.set(false);

      // Reload the image to show the new one
      await this.loadAssetImage();
    } catch (error) {
      const errorMessage =
        error instanceof Error
          ? error.message
          : 'Failed to upload image. Please try again.';
      this.toastService.showError('Upload Failed', errorMessage);
    } finally {
      this.isUploadingImage.set(false);
    }
  }
}
