import {
  Component,
  computed,
  TemplateRef,
  ViewChild,
  OnInit,
  inject,
} from '@angular/core';
import {
  ThemeSettingsSectionBase,
  ThemeFieldConfig,
} from '../../base/theme-settings-section-base';
import { ThemeCreateRequest, ThemeResponse } from '../../../../../api';
import { FormFieldConfig } from '../../../../../shared/models';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { CommonModule } from '@angular/common';
import { Observable } from 'rxjs';
import { isDefined } from '../../../../../shared/utils';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { ThemeValueDisplayComponent } from '../theme-value-display/theme-value-display.component';
import { ThemeColorService } from '../../../services/theme/theme-color.service';

// Constants for theme property paths
const TABLE_THEME_PATHS = {
  BACKGROUND_COLOR: ['table', 'background', 'color'],
  BORDER_COLOR: ['table', 'border', 'color'],
  HEADER_FONT_SIZE: ['table', 'header', 'font', 'size'],
  HEADER_FONT_WEIGHT: ['table', 'header', 'font', 'weight'],
  HEADER_FONT_TRANSFORM: ['table', 'header', 'font', 'transform'],
  HEADER_FONT_COLOR: ['table', 'header', 'font', 'color'],
  HEADER_BACKGROUND_COLOR: ['table', 'header', 'background', 'color'],
  ACTIVE_FONT_COLOR: ['table', 'active', 'font', 'color'],
  ACTIVE_BACKGROUND_COLOR: ['table', 'active', 'background', 'color'],
} as const;

@Component({
  selector: 'app-theme-settings-table-section',
  imports: [
    DynamicFormComponent,
    FontAwesomeModule,
    CommonModule,
    GridComponent,
    ThemeValueDisplayComponent,
  ],
  templateUrl: './theme-settings-table-section.component.html',
  styleUrl: './theme-settings-table-section.component.scss',
})
export class ThemeSettingsTableSectionComponent
  extends ThemeSettingsSectionBase
  implements OnInit
{
  @ViewChild('valueTemplate', { static: true })
  valueTemplate!: TemplateRef<unknown>;
  private readonly themeColorService = inject(ThemeColorService);
  readonly themeDetails = computed(() => {
    const themeDetails = this.entity();
    if (!isDefined(themeDetails)) return [];

    const themeSettings = this.themeEngine.themeSettingsMergedWithDefault(
      themeDetails?.settings ?? {},
    );

    const detailConfigs = this.createThemeDetailsFromConfig(
      this.getFieldConfigurations(),
    );
    return detailConfigs.map((config) => ({
      key: config.key,
      value: this.formatThemeValue(themeSettings, config),
    }));
  });

  constructor() {
    super('table');
  }

  ngOnInit(): void {
    this.initializeColumns();
  }

  private initializeColumns(): void {
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      {
        field: 'value',
        header: '',
        cellTemplate: this.valueTemplate,
      },
    ];
  }

  protected getFieldConfigurations(): ThemeFieldConfig[] {
    return [
      {
        id: 'background.color',
        label: 'Background Color',
        path: TABLE_THEME_PATHS.BACKGROUND_COLOR,
        type: 'color',
        group: 'table',
        presets: ['main', 'content'],
      },
      {
        id: 'border.color',
        label: 'Border Color',
        path: TABLE_THEME_PATHS.BORDER_COLOR,
        type: 'color',
        group: 'table',
      },
      {
        id: 'header.font.size',
        label: 'Header Font Size',
        path: TABLE_THEME_PATHS.HEADER_FONT_SIZE,
        type: 'size',
      },
      {
        id: 'header.font.weight',
        label: 'Header Font Weight',
        path: TABLE_THEME_PATHS.HEADER_FONT_WEIGHT,
        type: 'font-weight',
      },
      {
        id: 'header.font.transform',
        label: 'Header Font Case',
        path: TABLE_THEME_PATHS.HEADER_FONT_TRANSFORM,
        type: 'font-transform',
      },
      {
        id: 'header.font.color',
        label: 'Header Font Color',
        path: TABLE_THEME_PATHS.HEADER_FONT_COLOR,
        type: 'color',
        group: 'table',
        presets: ['main', 'content', 'header'],
      },
      {
        id: 'header.background.color',
        label: 'Header Background',
        path: TABLE_THEME_PATHS.HEADER_BACKGROUND_COLOR,
        type: 'color',
        group: 'table',
        presets: ['main', 'content'],
      },
      {
        id: 'active.font.color',
        label: 'Active Font Color',
        path: TABLE_THEME_PATHS.ACTIVE_FONT_COLOR,
        type: 'color',
        group: 'table',
        presets: ['main', 'content', 'header'],
        customColors: { 'Primary Contrast': 'var(--color-primary-contrast)' },
      },
      {
        id: 'active.background.color',
        label: 'Active Background',
        path: TABLE_THEME_PATHS.ACTIVE_BACKGROUND_COLOR,
        type: 'color',
        group: 'table',
        presets: ['main', 'content'],
        customColors: {
          Primary: this.themeColorService.computeColor(
            'var(--color-primary-color)',
          ),
        },
      },
    ];
  }

  getFields(entity: ThemeResponse): FormFieldConfig[] {
    return this.createFormFieldsFromConfig(
      entity,
      this.getFieldConfigurations(),
    );
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<ThemeResponse>,
  ) => Observable<ThemeResponse> {
    return (
      this.operationRegistry.getOperation<
        ThemeResponse,
        ThemeCreateRequest,
        ThemeResponse,
        'update'
      >('theme', 'update') ??
      ((id) =>
        this.entitiesService.updateTheme(id, {
          settings: {
            table: this.formChanges,
          },
        }))
    );
  }
}
