import {
  ChangeDetectorRef,
  Component,
  computed,
  effect,
  inject,
  resource,
  signal,
  untracked,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { Validators } from '@angular/forms';
import { map, Observable, of, firstValueFrom } from 'rxjs';

// PrimeNG imports
import { ButtonModule } from 'primeng/button';

// Core imports
import {
  AccountsService,
  AssetTagCreateRequest,
  CustomFieldDefinition,
  EntitiesService,
  IdName,
} from '../../../../../api';
import {
  createDateField,
  createDropdownField,
  createNumberField,
  createTextAreaField,
  createTextField,
  createToggleSwitchField,
} from '../../../../../shared/models';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { EntitySectionBaseComponent } from '../../base';
import { AdminStateService } from '../../../services/state/admin-state.service';

// Lazy loaded components
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { RequestCacheService } from '../../../../../shared/services/request-cache.service';

interface EntityWithFields {
  id?: string;
  assetType?: IdName;
  fields?: Record<string, string | boolean | number | object>;
}

@Component({
  selector: 'app-info-section',
  imports: [CommonModule, ButtonModule, GridComponent, DynamicFormComponent],
  templateUrl: './info-section.component.html',
  styleUrl: './info-section.component.scss',
})
export class InfoSectionComponent<
  T extends EntityWithFields,
> extends EntitySectionBaseComponent<T> {
  private readonly entitiesService = inject(EntitiesService);
  private readonly adminStateService = inject(AdminStateService);
  private readonly cacheService = inject(RequestCacheService);
  private readonly accountsService = inject(AccountsService);
  private readonly cdr = inject(ChangeDetectorRef);

  private readonly customFieldsResource = resource({
    params: () => ({ request: this.adminStateService.selectedClient()?.id }),
    loader: ({ params }) => {
      const clientId = params.request;
      if (isNullOrUndefined(clientId)) {
        return firstValueFrom(of([]));
      }
      return firstValueFrom(
        this.cacheService
          .getOrFetch(`custom-fields-${clientId}`, () =>
            this.accountsService.getClient(clientId),
          )
          .pipe(
            map((client) => {
              const entityDetails = this.entity();
              return [
                ...(client?.customFields?.[this.entityType()] ?? []),
                ...((entityDetails?.assetType &&
                  client?.customFields?.[entityDetails.assetType.id]) ??
                  []),
              ];
            }),
          ),
      );
    },
  });

  readonly entityCustomFields = signal<CustomFieldDefinition[]>([]);

  readonly entityDetails = computed(() => {
    const entityDetails = this.entity();
    const selectedClient = this.adminStateService.selectedClient();

    if (isNullOrUndefined(entityDetails) || isNullOrUndefined(selectedClient)) {
      return [];
    }

    // Return the details without side effects
    return this.entityCustomFields().map((field) => ({
      key: field['title'],
      value: entityDetails['fields']?.[field['id']] ?? 'None',
    }));
  });

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    effect(() => {
      const customFields = this.customFieldsResource.value();
      const entityDetails = this.entity();

      if (customFields && entityDetails) {
        this.entityCustomFields.set(customFields);
        untracked(() => {
          this.updateFormFields();
          this.cdr.detectChanges();
        });
      }
    });
  }

  protected override updateFormFields(): void {
    const entity = this.entity();
    this.formFields = this.entityCustomFields().map((field) =>
      this.createFormField(field, entity),
    );
  }

  private createFormField(
    field: CustomFieldDefinition,
    entity: EntityWithFields | null,
  ) {
    const fieldValue = entity?.fields?.[field['id']];
    const formattedValue = this.formatFieldValue(fieldValue);

    switch (field['type']) {
      case 'number':
        return this.createNumberFieldConfig(field, formattedValue);
      case 'combo':
        return this.createDropdownFieldConfig(field, formattedValue);
      case 'memo':
        return this.createTextAreaFieldConfig(field, formattedValue);
      case 'checkbox':
        return this.createToggleSwitchFieldConfig(field, formattedValue);
      case 'date':
        return this.createDateFieldConfig(field, entity, false);
      case 'datetime':
        return this.createDateFieldConfig(field, entity, true);
      default:
        return this.createTextFieldConfig(field, formattedValue);
    }
  }

  private formatFieldValue(
    fieldValue: string | boolean | number | object | undefined | null,
  ): string | boolean | number | object {
    if (!isDefined(fieldValue)) {
      return '';
    }
    return typeof fieldValue === 'object'
      ? JSON.stringify(fieldValue)
      : fieldValue;
  }

  private getFieldValidations(field: CustomFieldDefinition) {
    return field['required']
      ? [
          {
            name: 'required',
            validator: Validators.required,
            message: `${field['title']} is required`,
          },
        ]
      : [];
  }

  private createNumberFieldConfig(
    field: CustomFieldDefinition,
    formattedValue: string | number | boolean | object,
  ) {
    return createNumberField(
      String(field['id']),
      String(field['title']),
      formattedValue as number,
      {
        name: String(field['id']),
        validations: this.getFieldValidations(field),
      },
    );
  }

  private createDropdownFieldConfig(
    field: CustomFieldDefinition,
    formattedValue: string | number | boolean | object,
  ) {
    return createDropdownField(
      String(field['id']),
      String(field['title']),
      field.values?.map((value) => ({
        label: String(value.key),
        value: String(value.value),
      })) ?? [],
      formattedValue as string,
      {
        name: String(field['id']),
        validations: this.getFieldValidations(field),
      },
    );
  }

  private createTextAreaFieldConfig(
    field: CustomFieldDefinition,
    formattedValue: string | number | boolean | object,
  ) {
    return createTextAreaField(
      String(field?.id),
      String(field['title']),
      formattedValue as string,
      {
        name: String(field?.id),
        validations: this.getFieldValidations(field),
        hint: field['description'],
      },
    );
  }

  private createToggleSwitchFieldConfig(
    field: CustomFieldDefinition,
    formattedValue: string | number | boolean | object,
  ) {
    return createToggleSwitchField(
      field.id,
      field.title,
      formattedValue as boolean,
      {
        name: field.id,
        validations: this.getFieldValidations(field),
        hint: field['description'],
      },
    );
  }

  private createDateFieldConfig(
    field: CustomFieldDefinition,
    entity: EntityWithFields | null,
    showTime: boolean,
  ) {
    return createDateField(
      field.id,
      field.title,
      entity?.fields?.[field.id] as unknown as Date,
      {
        name: field.id,
        validations: this.getFieldValidations(field),
        hint: field['hint'],
        props: {
          showTime,
          timeOnly: false,
          dateFormat: 'mm/dd/yy',
          hourFormat: '24',
          showIcon: true,
          appendTo: 'body',
        },
      },
    );
  }

  private createTextFieldConfig(
    field: CustomFieldDefinition,
    formattedValue: string | number | boolean | object,
  ) {
    return createTextField(
      String(field['id']),
      String(field['title']),
      formattedValue as string,
      {
        name: String(field['id']),
        validations: this.getFieldValidations(field),
      },
    );
  }

  override getUpdateMethod(): (
    id: string,
    payload: object | Record<string, string | number | boolean>,
  ) => Observable<unknown> {
    switch (this.entityType()) {
      case 'asset':
        return (id, payload) =>
          this.entitiesService.updateAsset(id, {
            fields: payload as Record<string, object>,
          });
      case 'simcard':
        return (id, payload) =>
          this.entitiesService.updateSimCard(id, {
            fields: payload as Record<string, object>,
          });
      case 'device':
        return (id, payload) =>
          this.entitiesService.updateDevice(id, {
            fields: payload as Record<string, object>,
          });
      case 'assettag':
        return (id, payload) =>
          this.entitiesService.updateAssetTag(
            id,
            payload as AssetTagCreateRequest,
          );
      default:
        throw new Error(`Update for '${this.entityType()}' not yet supported`);
    }
  }

  protected override onFormValueChange(
    formData: Record<string, string | number | object>,
  ): void {
    const transformedData = { fields: formData };

    // Emit the transformed values
    this.formValueChange.emit(transformedData);
  }
}
