import { CommonModule } from '@angular/common';
import {
  OnInit,
  Component,
  computed,
  inject,
  TemplateRef,
  ViewChild,
} from '@angular/core';

import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { Observable } from 'rxjs';

import { ThemeResponse, ThemeCreateRequest } from '../../../../../api';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { FormFieldConfig } from '../../../../../shared/models';
import { ThemeColorService } from '../../../services/theme/theme-color.service';
import {
  ThemeSettingsSectionBase,
  ThemeFieldConfig,
} from '../../base/theme-settings-section-base';
import { ThemeValueDisplayComponent } from '../theme-value-display/theme-value-display.component';
import { isDefined } from '../../../../../shared/utils';

// Constants for theme property paths
const TOP_NAVIGATION_THEME_PATHS = {
  FONT_FAMILY: ['topnavigation', 'font', 'family'],
  FONT_SIZE: ['topnavigation', 'font', 'size'],
  FONT_WEIGHT: ['topnavigation', 'font', 'weight'],
  FONT_COLOR: ['topnavigation', 'font', 'color'],
  BACKGROUND_COLOR: ['topnavigation', 'background', 'color'],
  BORDER_COLOR: ['topnavigation', 'border', 'color'],
  BORDER_WIDTH: ['topnavigation', 'border', 'width'],
  ACTIVE_STYLE: ['topnavigation', 'active', 'style'],
  ACTIVE_FONT_COLOR: ['topnavigation', 'active', 'font', 'color'],
  ACTIVE_BACKGROUND_COLOR: ['topnavigation', 'active', 'background', 'color'],
  ACTIVE_TAB_FONT_COLOR: ['topnavigation', 'active', 'tab', 'font', 'color'],
  ACTIVE_TAB_BACKGROUND_COLOR: [
    'topnavigation',
    'active',
    'tab',
    'background',
    'color',
  ],
  ACTIVE_INDICATOR_COLOR: ['topnavigation', 'active', 'indicator', 'color'],
  LOGO_STYLE: ['topnavigation', 'logo', 'style'],
} as const;
@Component({
  selector: 'app-theme-settings-top-navigation-section',
  imports: [
    DynamicFormComponent,
    FontAwesomeModule,
    CommonModule,
    GridComponent,
    ThemeValueDisplayComponent,
  ],
  templateUrl: './theme-settings-top-navigation-section.component.html',
  styleUrl: './theme-settings-top-navigation-section.component.scss',
})
export class ThemeSettingsTopNavigationSectionComponent
  extends ThemeSettingsSectionBase
  implements OnInit
{
  @ViewChild('valueTemplate', { static: true })
  valueTemplate!: TemplateRef<unknown>;
  private readonly themeColorService = inject(ThemeColorService);
  readonly themeDetails = computed(() => {
    const themeDetails = this.entity();
    if (!isDefined(themeDetails)) return [];

    const themeSettings = this.themeEngine.themeSettingsMergedWithDefault(
      themeDetails?.settings ?? {},
    );

    const detailConfigs = this.createThemeDetailsFromConfig(
      this.getFieldConfigurations(),
    );
    return detailConfigs.map((config) => ({
      key: config.key,
      value: this.formatThemeValue(themeSettings, config),
    }));
  });

  constructor() {
    super('topnavigation');
  }

  ngOnInit(): void {
    this.initializeColumns();
  }

  private initializeColumns(): void {
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      {
        field: 'value',
        header: '',
        cellTemplate: this.valueTemplate,
      },
    ];
  }

  protected getFieldConfigurations(): ThemeFieldConfig[] {
    return [
      {
        id: 'font.family',
        label: 'Font Family',
        path: TOP_NAVIGATION_THEME_PATHS.FONT_FAMILY,
        type: 'font-family',
        presets: ['main', 'content', 'header'],
      },
      {
        id: 'font.size',
        label: 'Font Size',
        path: TOP_NAVIGATION_THEME_PATHS.FONT_SIZE,
        type: 'size',
      },
      {
        id: 'font.weight',
        label: 'Font Weight',
        path: TOP_NAVIGATION_THEME_PATHS.FONT_WEIGHT,
        type: 'font-weight',
        presets: ['main', 'content'],
      },
      {
        id: 'font.color',
        label: 'Font Color',
        path: TOP_NAVIGATION_THEME_PATHS.FONT_COLOR,
        type: 'color',
        group: 'topnavigation',
        presets: ['main', 'content', 'header'],
      },
      {
        id: 'background.color',
        label: 'Background Color',
        path: TOP_NAVIGATION_THEME_PATHS.BACKGROUND_COLOR,
        type: 'color',
        group: 'topnavigation',
        presets: ['header', 'main', 'content'],
      },
      {
        id: 'border.color',
        label: 'Border Color',
        path: TOP_NAVIGATION_THEME_PATHS.BORDER_COLOR,
        type: 'color',
        group: 'topnavigation',
      },
      {
        id: 'border.width',
        label: 'Border Width',
        path: TOP_NAVIGATION_THEME_PATHS.BORDER_WIDTH,
        type: 'size',
        props: { unit: 'px' },
      },
      {
        id: 'active.font.color',
        label: 'Active Font Color',
        path: TOP_NAVIGATION_THEME_PATHS.ACTIVE_FONT_COLOR,
        type: 'color',
        group: 'topnavigation',
        presets: ['topnavigation', 'main', 'content', 'header'],
        customColors: { 'Primary Contrast': 'var(--color-primary-contrast)' },
      },
      {
        id: 'active.background.color',
        label: 'Active Background',
        path: TOP_NAVIGATION_THEME_PATHS.ACTIVE_BACKGROUND_COLOR,
        type: 'color',
        group: 'topnavigation',
        presets: ['topnavigation', 'tab', 'content'],
        customColors: {
          Primary: this.themeColorService.computeColor(
            'var(--color-primary-color)',
          ),
        },
      },
      {
        id: 'active.tab.font.color',
        label: 'Active Tab Font Color',
        path: TOP_NAVIGATION_THEME_PATHS.ACTIVE_TAB_FONT_COLOR,
        type: 'color',
        group: 'topnavigation',
        presets: ['topnavigation', 'main', 'content', 'header'],
        customColors: {
          Primary: this.themeColorService.computeColor(
            'var(--color-primary-color)',
          ),
        },
      },
      {
        id: 'active.tab.background.color',
        label: 'Active Tab Background',
        path: TOP_NAVIGATION_THEME_PATHS.ACTIVE_TAB_BACKGROUND_COLOR,
        type: 'color',
        group: 'topnavigation',
        presets: ['topnavigation', 'tab', 'content'],
        customColors: {
          Primary: this.themeColorService.computeColor(
            'var(--color-primary-color)',
          ),
        },
      },
      {
        id: 'active.indicator.color',
        label: 'Active Indicator Color',
        path: TOP_NAVIGATION_THEME_PATHS.ACTIVE_INDICATOR_COLOR,
        type: 'color',
        group: 'topnavigation',
        customColors: {
          Primary: this.themeColorService.computeColor(
            'var(--color-primary-color)',
          ),
        },
      },
      {
        id: 'logo.style',
        label: 'Logo Style',
        path: TOP_NAVIGATION_THEME_PATHS.LOGO_STYLE,
        type: 'dropdown',
        group: 'topnavigation',
        options: [
          { value: 'none', label: 'None' },
          { value: 'left', label: 'Left' },
          { value: 'right', label: 'Right' },
        ],
      },
    ];
  }

  getFields(entity: ThemeResponse): FormFieldConfig[] {
    return this.createFormFieldsFromConfig(
      entity,
      this.getFieldConfigurations(),
    );
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<ThemeResponse>,
  ) => Observable<ThemeResponse> {
    return (
      this.operationRegistry.getOperation<
        ThemeResponse,
        ThemeCreateRequest,
        ThemeResponse,
        'update'
      >('theme', 'update') ??
      ((id) =>
        this.entitiesService.updateTheme(id, {
          settings: {
            topnavigation: this.formChanges,
          },
        }))
    );
  }
}
