import { Injectable, inject } from '@angular/core';
import { Observable } from 'rxjs';
import {
  DeviceProviderListItem,
  DeviceProviderListResponse,
  DeviceProviderResponse,
  DeviceProvidersService,
} from '../../../../api';
import { GridDataService } from '../../../../shared/models';
import { ActionIcon, EntityConfigAdapter, StateOption } from '../../models';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';

@Injectable({
  providedIn: 'root',
})
export class DeviceProvidersDataAdapter
  implements GridDataService<DeviceProviderListItem>, EntityConfigAdapter
{
  private readonly service = inject(DeviceProvidersService);
  private readonly entityStateService = inject(EntityDetailsStateService);

  getList(params: {
    owner: string;
    offset: number;
    page: number;
    limit: number;
    sortField?: string;
    sortOrder?: number;
    filters?: string;
    recurse?: boolean;
  }): Observable<DeviceProviderListResponse> {
    return this.service.listDeviceProviders(
      params.owner,
      params.recurse,
      params.offset,
      params.limit,
      params.sortField,
      params.filters,
    );
  }

  getById(id: string): Observable<DeviceProviderResponse> {
    return this.service.getDeviceProvider(id);
  }

  getStateOptions(): StateOption[] {
    return [
      { label: 'Active', value: DeviceProviderResponse.StateEnum.Active },
      { label: 'Suspended', value: DeviceProviderResponse.StateEnum.Suspended },
      { label: 'Deleted', value: DeviceProviderResponse.StateEnum.Deleted },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.service.updateDeviceProvider(id, {
      state: state as DeviceProviderResponse.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    return [
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change Device Provider State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
