import { Component, computed, inject, input, signal } from '@angular/core';

import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';

import {
  GeoLockProfileListItem,
  GeoLockProfileResponse,
} from '../../../../../api';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';
import { GeoLockProfileSectionConfigFactory } from '../../../factories';

@Component({
  selector: 'app-geo-lock-profile-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './geo-lock-profile-details-container.component.html',
  styleUrl: './geo-lock-profile-details-container.component.scss',
})
export class GeoLockProfileDetailsContainerComponent {
  readonly entity = input<GeoLockProfileListItem | null>(null);

  private readonly geoLockProfileSectionFactory = inject(
    GeoLockProfileSectionConfigFactory,
  );
  readonly entityDetailsState = inject(EntityDetailsStateService);

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly fullGeoLockProfile = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();

    if (!entityDetails) {
      return null;
    }
    return entityDetails as GeoLockProfileResponse;
  });
  readonly sections = computed(() => {
    const geoLockProfile = this.fullGeoLockProfile();
    if (!geoLockProfile) return [];
    return this.geoLockProfileSectionFactory.createAllGeoLockProfileSections(
      geoLockProfile,
    );
  });
  readonly error = signal<string | null>(null);

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as GeoLockProfileResponse);
    this.error.set(null);
  }
}
