import { Component, computed, inject } from '@angular/core';
import { EntitySectionBaseComponent } from '../../base';
import { faBell } from '@fortawesome/free-solid-svg-icons';

import {
  UserListItem,
  UserCreateRequest,
  AccountsService,
  NotificationSettings,
  NotificationActions,
  UserResponse,
} from '../../../../../api';
import { isDefined, isString } from '../../../../../shared/utils';
import {
  createDateField,
  createDropdownField,
} from '../../../../../shared/models';
import { CommonModule } from '@angular/common';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { ButtonModule } from 'primeng/button';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { Observable } from 'rxjs';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';

@Component({
  selector: 'app-user-notify-section',
  imports: [
    CommonModule,
    ButtonModule,
    GridComponent,
    DynamicFormComponent,
    FontAwesomeModule,
  ],
  templateUrl: './user-notify-section.component.html',
  styleUrl: './user-notify-section.component.scss',
})
export class UserNotifySectionComponent extends EntitySectionBaseComponent<
  UserListItem | UserCreateRequest
> {
  private readonly accountsService = inject(AccountsService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);

  faBell = faBell;

  readonly userNotificationDetails = computed(() => {
    const userNotifications: NotificationSettings =
      this.entity()?.notifySettings;

    if (!isDefined(userNotifications)) {
      return [];
    }
    return [
      { key: 'Allow SMS from', value: userNotifications.smsTime?.from },
      { key: 'Allow SMS to', value: userNotifications.smsTime?.to },
      {
        key: 'Low Priority',
        value: this.formatLabel(userNotifications.actions?.low),
      },
      {
        key: 'Medium Priority',
        value: this.formatLabel(userNotifications.actions?.medium),
      },
      {
        key: 'High Priority',
        value: this.formatLabel(userNotifications.actions?.high),
      },
    ];
  });

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '', transform: 'capitalize' },
    ];
  }

  protected override updateFormFields(): void {
    const user = this.entity();
    this.formFields = [
      {
        name: 'smsTime',
        key: 'smsTime',
        type: 'group',
        fields: [
          createDateField(
            'from',
            'Allow SMS from',
            user?.notifySettings?.smsTime?.from,
            {
              name: 'from',
              placeholder: 'Select time',
              props: {
                timeOnly: true,
                showSeconds: true,
              },
              style: { width: '100%' },
            },
          ),
          createDateField(
            'to',
            'Allow SMS to',
            user?.notifySettings?.smsTime?.to,
            {
              name: 'to',
              placeholder: 'Select time',
              props: {
                timeOnly: true,
                showSeconds: true,
              },
              style: { width: '100%' },
            },
          ),
        ],
      },
      {
        key: 'actions',
        type: 'group',
        name: 'actions',
        fields: [
          createDropdownField(
            'low',
            'Low Priority',
            Object.values(NotificationActions.LowEnum).map((value) => ({
              label: this.formatLabel(value),
              value,
            })),
            user?.notifySettings?.actions?.low,
            {
              name: 'low',
              props: {
                appendTo: 'body',
              },
            },
          ),
          createDropdownField(
            'medium',
            'Medium Priority',
            Object.values(NotificationActions.MediumEnum).map((value) => ({
              label: this.formatLabel(value),
              value,
            })),
            user?.notifySettings?.actions?.medium,
            {
              name: 'medium',
              props: {
                appendTo: 'body',
              },
            },
          ),
          createDropdownField(
            'high',
            'High Priority',
            Object.values(NotificationActions.HighEnum).map((value) => ({
              label: this.formatLabel(value),
              value,
            })),
            user?.notifySettings?.actions?.high,
            {
              name: 'high',
              props: {
                appendTo: 'body',
              },
            },
          ),
        ],
      },
    ];
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<UserResponse | UserCreateRequest>,
  ) => Observable<UserResponse> {
    return (
      this.operationRegistry.getOperation<
        UserCreateRequest | UserResponse,
        UserCreateRequest,
        UserResponse,
        'update'
      >('users', 'update') ??
      ((id, data) =>
        this.accountsService.updateUser(id, {
          notifySettings: data,
        }))
    );
  }

  formatLabel(value?: string | null): string {
    if (!isString(value)) return '';

    switch (value.toLowerCase()) {
      case 'emailsms':
        return 'Email & SMS';
      case 'sms':
        return 'SMS';
      default:
        return value.charAt(0).toUpperCase() + value.slice(1);
    }
  }

  protected override transformFormData(
    formData:
      | Record<string, unknown>
      | Record<string, Record<string, unknown>[]>,
  ) {
    const data = formData as NotificationSettings;

    if (isDefined(data['smsTime'])) {
      const smsTime = data['smsTime'] as Record<string, string>; // Type assertion
      const from = smsTime?.['from'];
      const to = smsTime?.['to'];

      // Regular expression to check for "HH:mm:ss" format
      const timeOnlyRegex = /^\d{2}:\d{2}:\d{2}$/;

      // Only convert if 'from' is a string and not already in "HH:mm:ss" format
      if (!timeOnlyRegex.test(from)) {
        smsTime['from'] = this.convertToTimeOnly(from);
      }

      // Only convert if 'to' is a string and not already in "HH:mm:ss" format
      if (!timeOnlyRegex.test(to)) {
        smsTime['to'] = this.convertToTimeOnly(to);
      }
    }

    return data;
  }

  convertToTimeOnly(dateTimeString: string): string {
    const date = new Date(dateTimeString);
    // Check if the date is valid before trying to get hours, minutes, seconds
    if (isNaN(date.getTime())) {
      // dateTimeString was not a parsable date string (and not "HH:mm:ss" due to prior check)
      // Return the original string or a sensible default/error.
      // For example, returning the original string:
      console.warn(
        `Could not parse '${dateTimeString}' as a valid date. Returning original string.`,
      );
      return dateTimeString;
      // Or, return a default time or error placeholder:
      // return "00:00:00"; // Or some error indicator
    }
    const hours = String(date.getHours()).padStart(2, '0');
    const minutes = String(date.getMinutes()).padStart(2, '0');
    const seconds = String(date.getSeconds()).padStart(2, '0');
    return `${hours}:${minutes}:${seconds}`;
  }
}
