import { CommonModule } from '@angular/common';
import {
  Component,
  computed,
  effect,
  inject,
  input,
  untracked,
} from '@angular/core';

import { ButtonModule } from 'primeng/button';
import { Observable } from 'rxjs';

import {
  EntitiesService,
  DeviceTypeResponse,
  DeviceCreateRequest,
  DeviceResponse,
} from '../../../../../api';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import {
  createDropdownField,
  createListboxField,
} from '../../../../../shared/models';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import {
  isDefined,
  isEmpty,
  isNullOrUndefined,
  setNestedProperty,
} from '../../../../../shared/utils';
import { getIoKeys } from '../../../helpers';
import { DeviceEntity } from '../../../models';
import { EntitySectionBaseComponent } from '../../base';
import { FormGroup } from '@angular/forms';

@Component({
  selector: 'app-device-io-whitelist-section',
  imports: [CommonModule, ButtonModule, GridComponent, DynamicFormComponent],
  templateUrl: './device-io-whitelist-section.component.html',
  styleUrl: './device-io-whitelist-section.component.scss',
})
export class DeviceIoWhitelistSectionComponent extends EntitySectionBaseComponent<DeviceEntity> {
  readonly ownerId = input<string | null>(null);
  readonly deviceType = input.required<DeviceTypeResponse | null>();
  readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);

  readonly ioWhitelistDisplay = computed(() => {
    const params = this.entity()?.parameters;
    if (isNullOrUndefined(params?.io_whitelist)) return [];

    const result = [
      {
        key: 'Selection',
        value: isEmpty(params.io_whitelist) ? 'Default' : 'Custom',
      },
    ];

    // Only add the Inputs row if there are actually inputs to show
    if (!isEmpty(params.io_whitelist)) {
      result.push({
        key: 'Inputs',
        value: params.io_whitelist.join(', '),
      });
    }

    return result;
  });

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: 'Mode', transform: 'uppercase' },
      { field: 'value', header: 'Configuration' },
    ];

    effect(() => {
      const deviceType = this.deviceType();
      const entity = this.entity();
      if (isDefined(deviceType) && isDefined(entity)) {
        untracked(() => {
          this.updateFormFields();
        });
      }
    });
  }

  protected override updateFormFields(): void {
    const device = this.entity();
    const deviceTypeData = this.deviceType();
    const parameters = device?.parameters ?? {};

    // Get all available I/O keys from the device type
    const allIOKeys = getIoKeys(deviceTypeData, device, true);

    // Check if whitelist exists
    const hasWhitelist =
      Array.isArray(parameters.io_whitelist) &&
      parameters.io_whitelist.length > 0;

    // Set mode based on whitelist existence
    const currentMode = hasWhitelist ? 'custom' : 'default';

    // Get current whitelist or default inputs if no whitelist
    const currentWhitelist = hasWhitelist
      ? (parameters.io_whitelist ?? [])
      : getIoKeys(deviceTypeData, device, false);

    this.formFields = [
      createDropdownField(
        'mode',
        'Selection Mode',
        [
          { label: 'Default', value: 'default' },
          { label: 'Custom', value: 'custom' },
        ],
        currentMode,
        {
          name: 'mode',
          props: {
            appendTo: 'body',
          },
          conditional: true,
          conditions: {
            default: [], // No additional fields for default mode
            custom: [
              createListboxField(
                'whitelist',
                'Inputs',
                currentWhitelist,
                allIOKeys.map((io) => ({ label: io, value: io })),
                {
                  name: 'whitelist',
                  props: {
                    appendTo: 'body',
                    multiple: true,
                    filter: true,
                    filterPlaceholder: 'Search I/O',
                    optionLabel: 'label',
                    style: { height: '250px' },
                  },
                },
              ),
            ],
          },
        },
      ),
    ];
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<DeviceEntity>,
  ) => Observable<DeviceResponse> {
    return (
      this.operationRegistry.getOperation<
        DeviceEntity,
        DeviceCreateRequest,
        DeviceResponse,
        'update'
      >('device', 'update') ??
      ((id, data) => this.entitiesService.updateDevice(id, data))
    );
  }

  protected override onSubmit(formGroup: FormGroup): void {
    if (isNullOrUndefined(this.entity()) || !formGroup.valid) return;

    this.isSubmitting.set(true);

    const formData: Record<string, string | number | object> = {};

    Object.keys(formGroup.controls).forEach((key) => {
      const control = formGroup.controls[key];

      formData[key] = control.value;
    });
    if (Object.keys(formData).length === 0) {
      this.isFormEditable.set(false);
      this.isSubmitting.set(false);
      return;
    }

    this.submitChanges(formData);
  }

  override transformFormData(
    formData: Record<string, string | boolean>,
  ): Partial<DeviceEntity> {
    // Extract mode and whitelist values
    const mode = formData['mode'];
    const whitelist = formData['whitelist'] ?? [];

    // Create result object based on mode
    const result: Partial<DeviceEntity> = {};

    // Set whitelist property based on mode
    const updatedData = {
      io_whitelist: mode === 'custom' ? whitelist : null,
    };

    // Use path-based update if needed
    if (this.injectDataAtPath()) {
      setNestedProperty(result, this.dataPath(), updatedData);
    } else {
      Object.assign(result, updatedData);
    }

    return result;
  }
}
