import {
  Component,
  computed,
  OnInit,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import {
  ThemeSettingsSectionBase,
  ThemeFieldConfig,
} from '../../base/theme-settings-section-base';
import { ThemeCreateRequest, ThemeResponse } from '../../../../../api';
import { FormFieldConfig } from '../../../../../shared/models';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { CommonModule } from '@angular/common';
import { DROP_SHADOW_OPTIONS } from '../../../models';
import { Observable } from 'rxjs';
import { isDefined } from '../../../../../shared/utils';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { ThemeValueDisplayComponent } from '../theme-value-display/theme-value-display.component';

// Constants for theme property paths
const BUTTON_THEME_PATHS = {
  FONT_FAMILY: ['button', 'font', 'family'],
  FONT_SIZE: ['button', 'font', 'size'],
  FONT_WEIGHT: ['button', 'font', 'weight'],
  FONT_TRANSFORM: ['button', 'font', 'transform'],
  BORDER_RADIUS: ['button', 'border', 'radius'],
  BORDER_SHADOW: ['button', 'border', 'shadow'],
  HOVER_BORDER_SHADOW: ['button', 'hover', 'border', 'shadow'],
  ACTIVE_BORDER_SHADOW: ['button', 'active', 'border', 'shadow'],
} as const;

@Component({
  selector: 'app-theme-settings-button-section',
  imports: [
    DynamicFormComponent,
    FontAwesomeModule,
    CommonModule,
    GridComponent,
    ThemeValueDisplayComponent,
  ],
  templateUrl: './theme-settings-button-section.component.html',
  styleUrl: './theme-settings-button-section.component.scss',
})
export class ThemeSettingsButtonSectionComponent
  extends ThemeSettingsSectionBase
  implements OnInit
{
  @ViewChild('valueTemplate', { static: true })
  valueTemplate!: TemplateRef<unknown>;

  readonly themeDetails = computed(() => {
    const themeDetails = this.entity();
    if (!isDefined(themeDetails)) return [];

    const themeSettings = this.themeEngine.themeSettingsMergedWithDefault(
      themeDetails?.settings ?? {},
    );

    const detailConfigs = this.createThemeDetailsFromConfig(
      this.getFieldConfigurations(),
    );
    return detailConfigs.map((config) => ({
      key: config.key,
      value: this.formatThemeValue(themeSettings, config),
    }));
  });

  constructor() {
    super('button');
  }

  ngOnInit(): void {
    this.initializeColumns();
  }

  private initializeColumns(): void {
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      {
        field: 'value',
        header: '',
        cellTemplate: this.valueTemplate,
      },
    ];
  }

  protected getFieldConfigurations(): ThemeFieldConfig[] {
    return [
      {
        id: 'font.family',
        label: 'Font Family',
        path: BUTTON_THEME_PATHS.FONT_FAMILY,
        type: 'font-family',
      },
      {
        id: 'font.size',
        label: 'Font Size',
        path: BUTTON_THEME_PATHS.FONT_SIZE,
        type: 'size',
      },
      {
        id: 'font.weight',
        label: 'Font Weight',
        path: BUTTON_THEME_PATHS.FONT_WEIGHT,
        type: 'font-weight',
      },
      {
        id: 'border.radius',
        label: 'Border Radius',
        path: BUTTON_THEME_PATHS.BORDER_RADIUS,
        type: 'size',
        props: { unit: 'px' },
      },
      {
        id: 'border.shadow',
        label: 'Shadow',
        path: BUTTON_THEME_PATHS.BORDER_SHADOW,
        type: 'dropdown',
        options: DROP_SHADOW_OPTIONS,
      },
      {
        id: 'hover.border.shadow',
        label: 'Hover Shadow',
        path: BUTTON_THEME_PATHS.HOVER_BORDER_SHADOW,
        type: 'dropdown',
        options: DROP_SHADOW_OPTIONS,
      },
      {
        id: 'active.border.shadow',
        label: 'Active Shadow',
        path: BUTTON_THEME_PATHS.ACTIVE_BORDER_SHADOW,
        type: 'dropdown',
        options: DROP_SHADOW_OPTIONS,
      },
    ];
  }

  getFields(entity: ThemeResponse): FormFieldConfig[] {
    return this.createFormFieldsFromConfig(
      entity,
      this.getFieldConfigurations(),
    );
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<ThemeResponse>,
  ) => Observable<ThemeResponse> {
    return (
      this.operationRegistry.getOperation<
        ThemeResponse,
        ThemeCreateRequest,
        ThemeResponse,
        'update'
      >('theme', 'update') ??
      ((id) =>
        this.entitiesService.updateTheme(id, {
          settings: {
            button: this.formChanges,
          },
        }))
    );
  }
}
