import { Component, inject } from '@angular/core';
import { GoogleMapsZoneComponent } from '../../../../../shared/components/maps/google-maps-zone/google-maps-zone.component';
import {
  EntitiesService,
  ZoneListItem,
  ZoneResponse,
} from '../../../../../api';
import { EntitySectionBaseComponent } from '../../base';
import { isNullOrUndefined } from '../../../../../shared/utils';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Component({
  selector: 'app-geofence-map-section',
  imports: [GoogleMapsZoneComponent],
  templateUrl: './geofence-map-section.component.html',
  styleUrl: './geofence-map-section.component.scss',
})
export class GeofenceMapSectionComponent extends EntitySectionBaseComponent<
  ZoneListItem | ZoneResponse
> {
  private readonly entitiesService = inject(EntitiesService);

  protected override updateFormFields(): void {
    // No form fields to update in this section
  }

  protected override submitChanges(
    formData: Record<string, string | number>,
  ): void {
    const geofence = this.entity();
    if (isNullOrUndefined(geofence)) return;
    this.entitiesService
      .updateZone(geofence.id, formData)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe({
        next: (response) => {
          this.isFormEditable.set(false);
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'geofence',
            operation: 'update',
            entityId: geofence.id,
            data: response,
          });
        },
        error: () => {
          this.isFormEditable.set(true);
        },
      });
  }
}
