import {
  DeviceTypeListItem,
  DeviceTypeResponse,
  DeviceTypesService,
  DeviceTypeUpdateRequest,
} from '../../../../api';
import { Injectable, inject } from '@angular/core';

import { BaseTreeAdapter } from './base-tree.adapter.service';
import { forkJoin, map, Observable, of, switchMap } from 'rxjs';
import { ActionIcon, StateOption } from '../../models';
import { EntityConfigAdapter } from '../../models/entity-config-adapter.interface';
import { AuthDataService } from '../../../../core/services/auth-data.service';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';
import { DataGridParams, TreeTableResponse } from '../../../../shared/models';
import { isNullOrUndefined } from '../../../../shared/utils';

@Injectable({
  providedIn: 'root',
})
export class DeviceTypesDataAdapter
  extends BaseTreeAdapter<
    DeviceTypeListItem,
    { items: DeviceTypeListItem[]; count: number },
    DeviceTypeResponse
  >
  implements EntityConfigAdapter
{
  private readonly deviceTypesService = inject(DeviceTypesService);
  private readonly authDataService = inject(AuthDataService);
  private readonly entityStateService = inject(EntityDetailsStateService);

  protected fetchEntities(
    owner: string,
    offset: number | undefined,
    limit: number | undefined,
    sortField: string | undefined,
    filters: string | undefined,
  ): Observable<{ items: DeviceTypeListItem[]; count: number }> {
    return this.deviceTypesService.listDeviceTypes(
      owner,
      true, // recurse to get all levels
      offset,
      limit,
      sortField,
      filters,
    );
  }

  override getList(
    params: DataGridParams,
  ): Observable<TreeTableResponse<DeviceTypeListItem>> {
    const { owner, limit, sortField, filters } = params;
    const pageSize = limit || 100;

    if (isNullOrUndefined(owner)) {
      return of({ nodes: [], items: [], count: 0, limit: pageSize });
    }

    // Always start from offset 0 to fetch all items for complete tree
    const firstOffset = 0;

    return this.fetchEntities(
      owner,
      firstOffset,
      pageSize,
      sortField as string,
      filters,
    ).pipe(
      switchMap((firstPage) => {
        const totalCount = firstPage.count;

        // If all items fit in first page, return immediately
        if (firstPage.items.length >= totalCount) {
          return of(firstPage);
        }

        // Calculate how many more pages we need
        const itemsRemaining = totalCount - firstPage.items.length;
        const pagesNeeded = Math.ceil(itemsRemaining / pageSize);
        const pageRequests: Observable<{
          items: DeviceTypeListItem[];
          count: number;
        }>[] = [];

        // Create requests for remaining pages
        for (let i = 1; i <= pagesNeeded; i++) {
          const pageOffset = firstOffset + i * pageSize;
          pageRequests.push(
            this.fetchEntities(
              owner,
              pageOffset,
              pageSize,
              sortField as string,
              filters,
            ),
          );
        }

        // Fetch all remaining pages in parallel
        return forkJoin(pageRequests).pipe(
          map((pages: { items: DeviceTypeListItem[]; count: number }[]) => {
            // Combine first page + all other pages
            const allItems = [
              ...firstPage.items,
              ...pages.flatMap((p) => p.items),
            ];
            return { items: allItems, count: totalCount };
          }),
        );
      }),
      map((response) => {
        // Build tree from ALL items (this is inherited from BaseTreeAdapter)
        const nodes = this.buildEntityTree(response.items);
        return {
          nodes,
          items: response.items,
          count: response.count,
          limit: pageSize,
        };
      }),
    );
  }

  getById(id: string): Observable<DeviceTypeResponse> {
    return this.deviceTypesService.getDeviceType(id);
  }

  getStateOptions(): StateOption[] {
    return [
      { label: 'Active', value: DeviceTypeResponse.StateEnum.Active },
      { label: 'Deleted', value: DeviceTypeResponse.StateEnum.Deleted },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.deviceTypesService.updateDeviceType(id, {
      state: state as DeviceTypeUpdateRequest.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    if (!this.authDataService.hasPermission('device-types:create')) {
      return [];
    }

    return [
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change Device Type State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
