import { Injectable } from '@angular/core';

import { CompanyGroupResponse } from '../../../api';
import { AdminDetailSection, AdminSectionConfiguration } from '../models';
import { FlexibleSectionBuilder } from './flexible-section-builder.factory';
import { CompanyGroupDetailsSectionComponent } from '../components/company-groups/company-group-details-section/company-group-details-section.component';
import { FeedSectionComponent } from '../components/shared/feed-section/feed-section.component';
import { CompanyGroupEmailTemplateSectionComponent } from '../components/company-groups/company-group-email-template-section/company-group-email-template-section.component';
import { FeatureSectionComponent } from '../components/shared/feature-section/feature-section.component';
import {
  AppFeatures,
  BackendFeatureSection,
  FeatureValue,
} from '../../../core/models';

@Injectable({ providedIn: 'root' })
export class CompanyGroupSectionConfigFactory {
  /**
   * Create sections for company group information
   * @param companyGroup The company group object to display in the section
   * @returns An array of AdminDetailSection objects representing the company group information sections
   */
  createCompanyGroupInformationSection(
    companyGroup: CompanyGroupResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'company-group-details',
        title: 'Company Group Details',
        component: CompanyGroupDetailsSectionComponent,
        componentInputs: { entity: companyGroup },
        category: 'information',
        order: 10,
      },
    ];
  }

  /**
   * Create sections for company group email customization
   * @param companyGroup The company group object to display in the section
   * @returns An array of AdminDetailSection objects representing the company group email customization sections
   */
  createCompanyGroupEmailCustomizationSection(
    companyGroup: CompanyGroupResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'company-group-email-customization-user-invite',
        title: 'User Invite Email',
        component: CompanyGroupEmailTemplateSectionComponent,
        componentInputs: { entity: companyGroup, emailType: 'user_invite' },
        category: 'customization',
        order: 10,
      },
      {
        id: 'company-group-email-customization-user-reset',
        title: 'User Reset Email',
        component: CompanyGroupEmailTemplateSectionComponent,
        componentInputs: { entity: companyGroup, emailType: 'user_reset' },
        category: 'customization',
        order: 20,
      },
      {
        id: 'company-group-email-customization-forgot-password',
        title: 'Forgot Password Email',
        component: CompanyGroupEmailTemplateSectionComponent,
        componentInputs: { entity: companyGroup, emailType: 'forgot_password' },
        category: 'customization',
        order: 30,
      },
    ];
  }

  /**
   * Create sections for company group changes
   * @param companyGroup The company group object to display in the section
   * @returns An array of AdminDetailSection objects representing the company group changes sections
   */
  createChangesSection(
    companyGroup: CompanyGroupResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'company-group-changes',
        title: 'Changes',
        component: FeedSectionComponent,
        componentInputs: {
          entity: companyGroup,
        },
        category: 'feed',
        order: 10,
      },
    ];
  }

  createFeaturesSection(
    companyGroup: CompanyGroupResponse | null,
    appFeatures: string[],
  ): AdminDetailSection[] {
    const features: AdminDetailSection[] = appFeatures.map((feature) => ({
      id: `company-group-feature-${feature}`,
      title: feature,
      component: FeatureSectionComponent,
      componentInputs: {
        entity: companyGroup,
        feature: AppFeatures[feature],
        entityType: 'company-group',
      },
      category: 'features',
      order: 10,
    }));
    return features;
  }

  createBackendSection(
    companyGroup: CompanyGroupResponse | null,
    backend: {
      bucket: string;
      sections: Record<string, BackendFeatureSection>;
      defaults: Record<string, FeatureValue>;
    },
  ): AdminDetailSection[] {
    return [
      ...Object.keys(backend.sections).map((key) => ({
        id: `company-group-backend-${key}`,
        title: key,
        component: FeatureSectionComponent,
        componentInputs: {
          entity: companyGroup,
          feature: backend.sections[key],
          entityType: 'company-group',
        },
        category: 'backend',
        order: 10,
      })),
    ];
  }

  /**
   * Create all companyGroup sections in one call
   * @param companyGroup The companyGroup object to display in the sections
   * @returns An array of AdminDetailSection objects representing all companyGroup sections
   */
  createAllCompanyGroupSections(
    companyGroup: CompanyGroupResponse | null,
    appFeatures: string[],
    backend: {
      bucket: string;
      sections: Record<string, BackendFeatureSection>;
      defaults: Record<string, FeatureValue>;
    },
  ): AdminDetailSection[] {
    return [
      ...this.createCompanyGroupInformationSection(companyGroup),
      ...this.createFeaturesSection(companyGroup, appFeatures),
      ...this.createBackendSection(companyGroup, backend),
      ...this.createCompanyGroupEmailCustomizationSection(companyGroup),
      ...this.createChangesSection(companyGroup),
    ];
  }

  /**
   * Create company group configuration with flexible layout and custom order
   * Order: Information -> Applications -> Backend -> Email Customization -> Changes
   * @param companyGroup The companyGroup object to display in the sections
   * @param appFeatures Array of application features to display
   * @param backend Backend configuration object
   * @returns Configuration object with sections and custom layout
   */
  createFlexibleCompanyGroupConfiguration(
    companyGroup: CompanyGroupResponse | null,
    appFeatures: string[],
    backend: {
      bucket: string;
      sections: Record<string, BackendFeatureSection>;
      defaults: Record<string, FeatureValue>;
    },
  ): AdminSectionConfiguration {
    return (
      FlexibleSectionBuilder.forEntity(companyGroup)
        // Information sections (order: 10)
        .withSections(this.createCompanyGroupInformationSection(companyGroup))

        // Features sections with custom category name "Applications" (order: 20)
        .withSections(
          this.createFeaturesSection(companyGroup, appFeatures).map(
            (section) => ({
              ...section,
              category: 'applications',
            }),
          ),
        )

        // Backend sections (order: 30)
        .withSections(this.createBackendSection(companyGroup, backend))

        // Email customization sections (order: 40)
        .withSections(
          this.createCompanyGroupEmailCustomizationSection(companyGroup),
        )

        // Changes section (order: 50)
        .withSections(this.createChangesSection(companyGroup))

        // Configure custom category order and titles
        .withCategory('information', 'Information', 10)
        .withCategory('applications', 'Applications', 20) // Custom title for features
        .withCategory('backend', 'Backend', 30)
        .withCategory('customization', 'Email Customization', 40)
        .withCategory('feed', 'Changes', 50)

        // Set default expanded category and behavior
        .withDefaultExpanded('information')
        .withMultipleExpansion(false)
        .build()
    );
  }
}
