import { inject, Injectable } from '@angular/core';

import { AssetGroupResponse, EntitiesService } from '../../../api';
import { AdminDetailSection } from '../models';
import { AssetGroupDetailsSectionComponent } from '../components/asset-groups/asset-group-details-section/asset-group-details-section.component';
import { FeedSectionComponent } from '../components/shared/feed-section/feed-section.component';
import { firstValueFrom, map } from 'rxjs';
import { AssetGroupingService } from '../../../shared/services/asset-grouping.service';
import { AssetListSectionComponent } from '../components/shared/asset-list-section/asset-list-section.component';
import { UtilityService } from '../../../core/services/utility.service';
import { RequestCacheService } from '../../../shared/services/request-cache.service';

@Injectable({ providedIn: 'root' })
export class AssetGroupSectionConfigFactory {
  private readonly assetGroupingService = inject(AssetGroupingService);
  private readonly entitiesService = inject(EntitiesService);
  private readonly utilityService = inject(UtilityService);
  private readonly cacheService = inject(RequestCacheService);

  /**
   * Create sections for asset group information
   * @param assetGroup The asset group object to display in the section
   * @returns An array of AdminDetailSection objects representing the asset group information sections
   */
  createAssetGroupInformationSection(
    assetGroup: AssetGroupResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'asset-group-details',
        title: 'Asset Group Details',
        component: AssetGroupDetailsSectionComponent,
        componentInputs: { entity: assetGroup },
        category: 'information',
        order: 10,
      },
    ];
  }

  /**
   * Create sections for asset group changes
   * @param assetGroup The asset group object to display in the section
   * @returns An array of AdminDetailSection objects representing the asset group changes sections
   */
  createChangesSection(
    assetGroup: AssetGroupResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'asset-group-changes',
        title: 'Changes',
        component: FeedSectionComponent,
        componentInputs: {
          entity: assetGroup,
        },
        category: 'feed',
        order: 10,
      },
    ];
  }

  /**
   * Create sections for asset group assets
   * @param assetGroup The asset group object to display in the section
   * @returns An array of AdminDetailSection objects representing the asset group assets sections
   */
  createAssetsSection(
    assetGroup: AssetGroupResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'asset-group-assets',
        title: 'Assets',
        component: AssetListSectionComponent,
        componentInputs: {
          entity: assetGroup,
          groupId: 'groups.id',
          groupingEntityType: 'Asset Group',
          assetLinkField: 'groups',
          getGroupingEntitiesFn: (ownerId: string) =>
            this.assetGroupingService.getAssetGroupsAsTree(ownerId),
          getAvailableAssetsFn: (ownerId: string, _currentEntityId?: string) =>
            this.getAvailableAssets(ownerId),
        },
        category: 'assets',
        order: 10,
      },
    ];
  }

  /**
   * Get available assets for the given owner
   * @param ownerId The owner ID to fetch assets for
   * @returns Promise of simplified asset objects
   */
  private getAvailableAssets(ownerId: string) {
    const filters = {
      state: {
        value: 'active',
        operator: '=',
      },
    };
    const rqlFilter = this.utilityService.constructRqlFilter(filters);
    return this.cacheService.getOrFetchAsync(
      `assets-list-${ownerId}-${rqlFilter}`,
      () =>
        firstValueFrom(
          this.entitiesService
            .listAssets(ownerId, 0, 100, 'name', rqlFilter)
            .pipe(
              map((res) =>
                res.items.map((asset) => ({
                  id: asset.id,
                  name: asset.name,
                })),
              ),
            ),
        ),
      300000,
    );
  }

  /**
   * Create all assetGroup sections in one call
   * @param assetGroup The assetGroup object to display in the sections
   * @returns An array of AdminDetailSection objects representing all assetGroup sections
   */
  createAllAssetGroupSections(
    assetGroup: AssetGroupResponse | null,
  ): AdminDetailSection[] {
    return [
      ...this.createAssetGroupInformationSection(assetGroup),
      ...this.createAssetsSection(assetGroup),
      ...this.createChangesSection(assetGroup),
    ];
  }
}
