import {
  Component,
  computed,
  effect,
  inject,
  input,
  signal,
} from '@angular/core';
import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';

import { ThemeListItem, ThemeResponse } from '../../../../../api';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';
import { ThemeSectionConfigFactory } from '../../../factories';
import { ThemeDataService } from '../../../../../shared/services/theme-data.service';
import { isDefined } from '../../../../../shared/utils';

@Component({
  selector: 'app-theme-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './theme-details-container.component.html',
  styleUrl: './theme-details-container.component.scss',
})
export class ThemeDetailsContainerComponent {
  readonly entity = input<ThemeListItem | null>(null);

  private readonly themeSectionFactory = inject(ThemeSectionConfigFactory);
  readonly entityDetailsState = inject(EntityDetailsStateService);
  private readonly themeService = inject(ThemeDataService);

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly isSoftRefreshing = computed(() =>
    this.entityDetailsState.isSoftRefreshing(),
  );
  readonly isInitialLoading = computed(
    () => this.isLoading() && !this.isSoftRefreshing() && !this.fullTheme(),
  );
  readonly fullTheme = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();

    if (!entityDetails) {
      return null;
    }
    return entityDetails as ThemeResponse;
  });
  readonly sections = computed(() => {
    const theme = this.fullTheme();
    if (!theme) return [];
    return this.themeSectionFactory.createAllThemeSections(theme);
  });
  readonly error = signal<string | null>(null);

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as ThemeResponse);
    this.error.set(null);
  }

  constructor() {
    effect(() => {
      const entity = this.entity();

      if (isDefined(entity)) {
        this.themeService.setTheme(entity);
      }
    });
  }
}
