import {
  Component,
  computed,
  effect,
  inject,
  input,
  signal,
} from '@angular/core';

import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';

import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';
import {
  SmsGatewayProviderCreateRequest,
  SmsGatewayProviderListItem,
  SmsGatewayProviderResponse,
  EntitiesService,
  SmsGatewayProviderTypeResponse,
  SmsGatewayProviderUpdateRequest,
  IdName,
} from '../../../../../api';
import { SmsGatewayProviderSectionConfigFactory } from '../../../factories';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { firstValueFrom } from 'rxjs';
import { RequestCacheService } from '../../../../../shared/services/request-cache.service';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { ApiSchemaService } from '../../../../../core/services/api-schema.service';

@Component({
  selector: 'app-sms-gateway-provider-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './sms-gateway-provider-details-container.component.html',
  styleUrl: './sms-gateway-provider-details-container.component.scss',
})
export class SmsGatewayProviderDetailsContainerComponent {
  readonly entity = input<SmsGatewayProviderListItem | null>(null);

  private readonly smsGatewayProviderSectionFactory = inject(
    SmsGatewayProviderSectionConfigFactory,
  );
  readonly entityDetailsState = inject(EntityDetailsStateService);
  private readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly cacheService = inject(RequestCacheService);
  private readonly apiSchemaService = inject(ApiSchemaService);
  private readonly entityName = 'smsgatewayprovider';

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly isSoftRefreshing = computed(() =>
    this.entityDetailsState.isSoftRefreshing(),
  );
  readonly isInitialLoading = computed(
    () =>
      this.isLoading() &&
      !this.isSoftRefreshing() &&
      !this.fullSmsGatewayProvider(),
  );
  readonly fullSmsGatewayProvider = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();

    if (!entityDetails) {
      return null;
    }
    return entityDetails as SmsGatewayProviderResponse;
  });
  readonly sections = computed(() => {
    const smsGatewayProvider = this.fullSmsGatewayProvider();
    const smsGatewayProviderType = this.smsGatewayProviderType();
    if (!(smsGatewayProvider || isDefined(smsGatewayProviderType))) return [];
    return this.smsGatewayProviderSectionFactory.createAllSmsGatewayProviderSections(
      smsGatewayProvider,
      smsGatewayProviderType,
    );
  });
  readonly error = signal<string | null>(null);
  readonly smsGatewayProviderType =
    signal<SmsGatewayProviderTypeResponse | null>(null);

  constructor() {
    this.operationRegistry.registerEntityOperations('sms-gateway-provider', {
      update: (id, data) =>
        this.entitiesService.updateSmsGatewayProvider(
          id,
          data as SmsGatewayProviderUpdateRequest,
        ),
      create: (data: SmsGatewayProviderCreateRequest) =>
        this.entitiesService.createSmsGatewayProvider(data),
      get: (id) => this.entitiesService.getSmsGatewayProvider(id),
    });

    effect(() => {
      const smsGatewayProvider = this.fullSmsGatewayProvider();
      let typeIdToLoad: string | undefined | null = null;

      if (smsGatewayProvider) {
        const typeObject = this.apiSchemaService.getEntityType<
          Record<string, unknown>,
          IdName
        >(smsGatewayProvider as Record<string, unknown>, this.entityName);
        typeIdToLoad = typeObject?.id;
      }

      if (isDefined(typeIdToLoad) && typeIdToLoad !== '') {
        this.loadSmsGatewayProviderType(typeIdToLoad).catch((error) => {
          this.error.set(
            error.message ?? 'Failed to load SMS gateway provider type',
          );
        });
      } else if (
        smsGatewayProvider &&
        (isNullOrUndefined(typeIdToLoad) || typeIdToLoad === '')
      ) {
        this.smsGatewayProviderType.set(null);
      }
    });
  }

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as SmsGatewayProviderResponse);
    this.error.set(null);
  }

  async loadSmsGatewayProviderType(
    smsGatewayProviderTypeId: string,
  ): Promise<void> {
    if (!smsGatewayProviderTypeId) {
      this.smsGatewayProviderType.set(null);
      return;
    }
    await this.cacheService
      .getOrFetchAsync(
        `smsGatewayProviderType-${smsGatewayProviderTypeId}`,
        () =>
          firstValueFrom(
            this.entitiesService.getSmsGatewayProviderType(
              smsGatewayProviderTypeId,
            ),
          ),
        300000,
      )
      .then((response) => {
        this.smsGatewayProviderType.set(response);
      });
  }
}
