import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import {
  SmsGatewayProviderListItem,
  EntitiesService,
  SmsGatewayProviderResponse,
} from '../../../../api';
import {
  DataGridParams,
  GridDataService,
  GridResponse,
} from '../../../../shared/models';
import { ActionIcon, EntityConfigAdapter, StateOption } from '../../models';
import { AuthDataService } from '../../../../core/services/auth-data.service';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';

@Injectable({
  providedIn: 'root',
})
export class SmsGatewayProvidersDataAdapter
  implements GridDataService<SmsGatewayProviderListItem>, EntityConfigAdapter
{
  private readonly entitiesService = inject(EntitiesService);
  private readonly authDataService = inject(AuthDataService);
  private readonly entityStateService = inject(EntityDetailsStateService);

  getList(
    params: DataGridParams,
  ): Observable<GridResponse<SmsGatewayProviderListItem>> {
    const { owner, limit, offset, sortField, filters } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.entitiesService.listSmsGatewayProviders(
      owner,
      offset,
      limit,
      sortField as string,
      filters,
    );
  }

  getById(id: string): Observable<SmsGatewayProviderResponse> {
    return this.entitiesService.getSmsGatewayProvider(id);
  }

  getStateOptions(): StateOption[] {
    return [
      { label: 'Active', value: SmsGatewayProviderResponse.StateEnum.Active },
      {
        label: 'Suspended',
        value: SmsGatewayProviderResponse.StateEnum.Suspended,
      },
      { label: 'Deleted', value: SmsGatewayProviderResponse.StateEnum.Deleted },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.entitiesService.updateSmsGatewayProvider(id, {
      state: state as SmsGatewayProviderResponse.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    if (!this.authDataService.hasPermission('sms-gateway-providers:create')) {
      return [];
    }

    return [
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change SMS Gateway Provider State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
