/**
 * Type representing all possible device configuration types
 */
export type IODeviceConfig =
  | DeviceDigitalConfig
  | DeviceOutputConfig
  | DeviceAnalogConfig
  | DeviceTemperatureConfig
  | DeviceCANConfig
  | DeviceCounterConfig
  | DeviceCameraConfig
  | DeviceValueConfig;

/**
 * Get IO keys from device type and device entity.
 *
 * This function retrieves the IO keys based on the device type and device entity.
 * It considers the IO capabilities defined in the device type and any accessories associated with the device.
 * It also filters the keys based on the specified IO types and whether to include optional keys.
 *
 * @param deviceType - The device type response object.
 * @param device - The device entity object.
 * @param includeOptional - Flag to include optional keys (default: true).
 * @param ioTypes - Array of IO types to filter the keys (default: ['analog_input', 'digital_input', 'digital_output', 'can_input', 'counter_input', 'temperature_input', 'value_input', 'camera_input']).
 *
 * @return An array of unique IO keys sorted in ascending order.
 */
export function getIoKeys(
  deviceType: DeviceTypeResponse | null,
  device: DeviceEntity | null,
  includeOptional = true,
  ioTypes: (keyof DeviceIOParameters)[] = [
    'analog_input',
    'digital_input',
    'digital_output',
    'can_input',
    'counter_input',
    'temperature_input',
    'value_input',
    'camera_input',
  ],
): string[] {
  if (!deviceType) {
    return [];
  }

  const collectedIos: string[] = [];

  /**
   * Helper function to process IO capabilities
   */
  const processIOCapabilities = (
    ioProperties: Record<string, { optional?: boolean }>,
  ) => {
    const ios = Object.keys(ioProperties);
    for (const io of ios) {
      if (includeOptional || !(ioProperties[io].optional ?? false)) {
        collectedIos.push(io);
      }
    }
  };

  // Process each IO type in the device type's capabilities
  for (const ioType of ioTypes) {
    // Add IOs from device type
    const typeCapabilities = deviceType.ioCapabilities?.[ioType] ?? {};
    processIOCapabilities(typeCapabilities);

    // Add IOs from accessories
    if (device?.accessories) {
      Object.keys(device.accessories).forEach((accessoryId) => {
        const accessoryCapabilities =
          deviceType.accessories?.[accessoryId]?.ioCapabilities?.[ioType] ?? {};
        processIOCapabilities(accessoryCapabilities);
      });
    }
  }

  // Add bitmap targets if they match requested IO types
  const bitmaps = device?.parameters?.bitmaps ?? [];
  const bitmapTargets = bitmaps
    .filter((bitmap) =>
      ioTypes.includes(bitmap.target_type as keyof DeviceIOParameters),
    )
    .map((bitmap) => bitmap.target_input);

  collectedIos.push(...bitmapTargets);

  // Remove duplicates and sort
  const result = [...new Set(collectedIos)].sort((a, b) => a.localeCompare(b));

  return result;
}

/**
 * Get the IO whitelist for a specific device type and device.
 *
 * This function retrieves the IO whitelist for a specific device type and device.
 * It considers the IO capabilities defined in the device type and any accessories associated with the device.
 * It also filters the keys based on the specified IO type and applies any whitelist if present.
 *
 * @param deviceType - The device type response object.
 * @param device - The device entity object.
 * @param ioType - The IO type to filter the keys (e.g., 'analog_input', 'digital_input', etc.).
 * @param currentInputId - Optional: The current input ID to scope custom inputs to
 *
 * @return An array of IO keys that are allowed based on the whitelist and device capabilities.
 *         If no whitelist is present, it returns all default inputs plus custom inputs.
 */
export function getDeviceIOWhitelist(
  deviceType: DeviceTypeResponse,
  device: DeviceEntity,
  ioType: keyof DeviceIOParameters,
  currentInputId?: string,
): string[] {
  const allInputs = getIoKeys(deviceType, device, true, [ioType]);
  const defaultInputs = getIoKeys(deviceType, device, false, [ioType]);

  // Extract custom inputs - only include those relevant to the current input
  const customInputs: string[] = [];
  const ioConfig = device?.parameters?.io?.[ioType];

  if (isDefined(ioConfig) && isDefined(currentInputId)) {
    // Only include custom inputs that are configured for the current input ID
    const currentInputConfig = ioConfig[currentInputId];
    if (
      isDefined(currentInputConfig?.input) &&
      !allInputs.includes(currentInputConfig.input)
    ) {
      customInputs.push(currentInputConfig.input);
    }

    // Also check for custom calibrations with the pattern custom_*_currentInputId
    Object.keys(ioConfig).forEach((key) => {
      if (key.startsWith('custom_') && key.endsWith(`_${currentInputId}`)) {
        const calibration = ioConfig[key];
        if (
          isDefined(calibration?.input) &&
          !customInputs.includes(calibration.input)
        ) {
          customInputs.push(calibration.input);
        }
      }
    });
  } else if (isDefined(ioConfig) && !isDefined(currentInputId)) {
    // If no currentInputId is provided, include all custom inputs (backward compatibility)
    for (const key of Object.keys(ioConfig)) {
      const inputConfig = ioConfig[key];
      const inputName = inputConfig?.input;

      if (isDefined(inputName) && !allInputs.includes(inputName)) {
        customInputs.push(inputName);
      }
    }
  }

  // Apply whitelist if present
  const whitelist = device?.parameters?.io_whitelist ?? [];

  if (whitelist.length > 0) {
    // If whitelist exists, only allow inputs in the whitelist plus custom inputs
    return [
      ...allInputs.filter((input) => whitelist.includes(input)),
      ...customInputs,
    ];
  } else {
    // No whitelist, allow all default inputs plus custom inputs
    return [...defaultInputs, ...customInputs];
  }
}

/**
 * Get default calibration based on IO type
 *
 * This function returns a default calibration object for the specified IO type.
 * The calibration object contains default values for various properties
 * such as input, min/max volts, min/max value, multiplier, and invert.
 *
 * @param ioType - The IO type for which to get the default calibration.
 *                Possible values include 'analog_input', 'can_input', 'counter_input',
 *                'digital_input', 'digital_output', and 'camera_input'.
 *
 * @return A default calibration object for the specified IO type.
 *         The object contains properties such as input, minVolts, maxVolts,
 *         minValue, maxValue, multiplier, and invert.
 */
export function getDefaultCalibration(
  ioType: keyof DeviceIOParameters,
): IODeviceConfig {
  switch (ioType) {
    case 'analog_input':
      return {
        input: '',
        minVolts: 0,
        maxVolts: 100,
        minValue: 0,
        maxValue: 100,
      };
    case 'can_input':
      return { input: '', minValue: 0, maxValue: 100, multiplier: 1 };
    case 'counter_input':
      return { input: '', multiplier: 1 };
    case 'digital_input':
      return { input: '', invert: false };
    case 'digital_output':
      return { input: '', invert: false, pulsed: false };
    case 'camera_input':
      return { input: '', enableLiveStream: false };
    default:
      return { input: '' };
  }
}

/**
 * Add calibration parameters based on IO type
 *
 * This function updates the calibration object with parameters
 * based on the specified IO type and form group values.
 * It sets properties such as min/max volts, min/max value,
 * multiplier, invert, and enableLiveStream based on the IO type.
 *
 * @param calibration - The calibration object to update.
 * @param ioType - The IO type for which to add calibration parameters.
 *               Possible values include 'analog_input', 'can_input',
 *              'counter_input', 'digital_input', 'digital_output',
 *              and 'camera_input'.
 * @param formGroup - The form group containing the values to set in the calibration object.
 *
 * @return void
 */
export function addCalibrationParams(
  calibration: IODeviceConfig,
  ioType: keyof DeviceIOParameters,
  formGroup: FormGroup,
): void {
  switch (ioType) {
    case 'analog_input':
      calibration.minVolts = Number(formGroup.get('minVolts')?.value ?? 0);
      calibration.maxVolts = Number(formGroup.get('maxVolts')?.value ?? 100);
      calibration.minValue = Number(formGroup.get('minValue')?.value ?? 0);
      calibration.maxValue = Number(formGroup.get('maxValue')?.value ?? 100);
      break;

    case 'can_input':
      calibration.minValue = Number(formGroup.get('minValue')?.value ?? 0);
      calibration.maxValue = Number(formGroup.get('maxValue')?.value ?? 100);
      calibration.multiplier = Number(formGroup.get('multiplier')?.value ?? 1);
      break;

    case 'counter_input':
      calibration.multiplier = Number(formGroup.get('multiplier')?.value ?? 1);
      break;

    case 'digital_input':
      calibration.invert = Boolean(formGroup.get('invert')?.value ?? false);
      break;

    case 'digital_output':
      calibration.invert = Boolean(formGroup.get('invert')?.value ?? false);
      calibration.pulsed = Boolean(formGroup.get('pulsed')?.value ?? false);
      break;

    case 'camera_input':
      calibration.enableLiveStream = Boolean(
        formGroup.get('enableLiveStream')?.value ?? false,
      );
      break;
  }
}

/**
 * Get the display name for a given IO type
 *
 * This function retrieves the display name for a given IO type
 * based on a provided mapping object.
 *
 * @param ioType - The IO type for which to get the display name.
 *               It can be a key of DeviceIOParameters or a string.
 * @param ioTypeMap - An object mapping IO types to their display names.
 *
 * @return The display name for the given IO type.
 *        If the IO type is not found in the mapping, it returns the IO type itself.
 */
export function getIoTypeDisplayName(
  ioType: keyof DeviceIOParameters | string,
  ioTypeMap: { [K in keyof DeviceIOParameters]: string },
): string {
  return ioTypeMap[ioType] ?? ioType;
}

/**
 * Clear IO configuration for a specific input
 *
 * This function clears the IO configuration for a specific input
 * by deleting the input from the IO values object.
 * If there are no more inputs of that type, it removes the entire IO type from the object.
 *
 * @param ioValues - The IO values object containing the configuration.
 * @param ioType - The IO type to clear (e.g., 'analog_input', 'digital_input', etc.).
 * @param inputName - The name of the input to clear.
 *
 * @return The updated IO values object after clearing the specified input.
 *         If the input is not found, it returns the original IO values object.
 *         If the IO type becomes empty, it removes the IO type from the object.
 */
export function clearIOConfiguration(
  ioValues: DeviceIOParameters,
  ioType: keyof DeviceIOParameters,
  inputName: string,
): DeviceIOParameters {
  if (isDefined(ioValues[ioType]?.[inputName])) {
    delete ioValues[ioType][inputName];

    // If no more inputs of this type, clean up the object
    if (Object.keys(ioValues[ioType]).length === 0) {
      delete ioValues[ioType];
    }
  }

  return { ...ioValues };
}

/**
 * Get custom inputs that have been configured
 *
 * This function retrieves custom inputs that have been configured
 * for a specific IO type.
 * It filters out inputs that are already present in the provided list of all inputs.
 *
 * @param ioValues - The IO values object containing the configuration.
 * @param ioType - The IO type to check for custom inputs (e.g., 'analog_input', 'digital_input', etc.).
 * @param allInputs - An array of all inputs to filter out.
 *
 * @return An array of custom inputs that have been configured
 */
export function getCustomInputs(
  ioValues: DeviceIOParameters,
  ioType: keyof DeviceIOParameters,
  allInputs: string[],
): string[] {
  const currentConfig: IODeviceConfig = ioValues[ioType] ?? {};

  return Object.values(currentConfig)
    .map((config) => config.input)
    .filter(Boolean)
    .filter((input) => !allInputs.includes(input)) as string[];
}

/**
 * Update IO values from the device entity
 *
 * This function updates the IO values based on the device entity.
 * If the device entity is null or doesn't have parameters,
 * it returns an empty object.
 *
 * @param device - The device entity object.
 *
 * @return An object containing the IO values from the device entity.
 *         If the device entity is null or doesn't have parameters, it returns an empty object.
 *         The returned object contains the IO parameters from the device entity.
 */
export function updateIOValues(
  device: DeviceEntity | null,
): DeviceIOParameters {
  return device?.parameters?.io ? { ...device.parameters.io } : {};
}

