/**
 * Generate form fields for calibration based on IO type
 *
 * This function generates form fields for calibration based on the specified IO type.
 * It creates text fields, number fields, and dropdown fields as needed.
 *
 * @param ioType - The IO type for which to generate form fields.
 *               Possible values include 'analog_input', 'digital_input', etc.
 * @param calibration - The calibration object containing the initial values for the form fields.
 *
 * @return An array of FormFieldConfig objects representing the generated form fields.
 *         Each object contains properties such as type, label, value, name, validations, and hint.
 *         The generated form fields can be used in a form group for user input.
 *         The function handles different IO types and creates appropriate fields for each.
 */
export function generateFormFields(
  ioType: keyof DeviceIOParameters,
  calibration: IODeviceConfig,
  deviceType: DeviceTypeResponse | null,
  unit?: string,
): FormFieldConfig[] {
  const fieldGenerators: Record<
    keyof DeviceIOParameters,
    () => FormFieldConfig[]
  > = {
    analog_input: () => [
      { ...commonFields['input'], value: calibration.input },
      { ...commonFields['minVolts'], value: calibration.minVolts },
      { ...commonFields['maxVolts'], value: calibration.maxVolts },
      {
        ...commonFields['minValue'],
        value: calibration.minValue,
        props: { suffix: unit },
      },
      {
        ...commonFields['maxValue'],
        value: calibration.maxValue,
        props: { suffix: unit },
      },
    ],
    digital_input: () => [
      { ...commonFields['input'], value: calibration.input },
      { ...commonFields['invert'], value: calibration.invert },
    ],
    digital_output: () => [
      { ...commonFields['input'], value: calibration.input },
      { ...commonFields['invert'], value: calibration.invert },
      { ...commonFields['pulsed'], value: calibration.pulsed },
    ],
    can_input: () => [
      { ...commonFields['input'], value: calibration.input },
      {
        ...commonFields['minValue'],
        value: calibration.minValue,
        props: { suffix: unit },
      },
      {
        ...commonFields['maxValue'],
        value: calibration.maxValue,
        props: { suffix: unit },
      },
      { ...commonFields['multiplier'], value: calibration.multiplier },
    ],
    counter_input: () => [
      { ...commonFields['input'], value: calibration.input },
      { ...commonFields['multiplier'], value: calibration.multiplier },
    ],
    camera_input: () => {
      const hasLiveStreamCapability =
        deviceType?.accessories &&
        Object.values(deviceType.accessories).some(
          (accessory) =>
            accessory.features?.['video_live_stream'] &&
            accessory.ioCapabilities?.camera_input?.[calibration.input],
        );
      return (hasLiveStreamCapability ?? false)
        ? [
            { ...commonFields['input'], value: calibration.input },
            {
              ...commonFields['enableLiveStream'],
              value: calibration.enableLiveStream,
            },
          ]
        : [{ ...commonFields['input'], value: calibration.input }];
    },
    value_input: () => [{ ...commonFields['input'], value: calibration.input }],
    temperature_input: () => [
      { ...commonFields['input'], value: calibration.input },
    ],
  };

  return fieldGenerators[ioType]?.() ?? [];
}

/**
 * Format IO summary for display
 *
 * This function formats the IO summary for display purposes.
 * It takes an array of IO types, the IO values, and the current IO type,
 * and returns an array of objects containing the key and value for each IO type.
 *
 * @param ioTypes - An array of IO types to format.
 * @param ioValues - The IO values object containing the configuration.
 * @param currentIoType - The current IO type to format.
 *
 * @return An array of objects containing the key and value for each IO type.
 *        Each object has properties 'key' and 'value' representing the IO type name and its corresponding value.
 *        The function maps over the IO types and retrieves the input value from the IO values object.
 *       If the input value is not found, it defaults to 'None'.
 */
export function formatIoSummary(
  ioTypes: IoTypeListItem[],
  ioValues: DeviceIOParameters,
  currentIoType: keyof DeviceIOParameters,
): { key: string; value: string }[] {
  return ioTypes.map((ioType) => ({
    key: ioType.name ?? '',
    value: ioValues[currentIoType]?.[ioType.id]?.input ?? 'None',
  }));
}

/**
 * Calculate changes in IO parameters with deep comparison
 *
 * This function recursively calculates the changes in IO parameters by comparing
 * the current values with the original values. For each changed input, it includes
 * the complete input configuration (both changed and unchanged properties) while
 * omitting inputs that haven't changed.
 *
 * @param currentValues - The current IO parameters.
 * @param originalValues - The original IO parameters to compare against.
 *
 * @return An object containing only the inputs that have changed, with their complete configuration.
 *         If no changes are found, it returns an empty object.
 */
function processInputChanges(
  currentIoType: Record<string, unknown>,
  originalIoType: Record<string, unknown>,
): [Record<string, unknown>, boolean] {
  const ioTypeChanges: Record<string, unknown> = {};
  let hasChanges = false;

  // Check each input within this IO type
  for (const inputId in currentIoType) {
    const currentInput = currentIoType[inputId];
    const originalInput = originalIoType[inputId];

    if (
      isNullOrUndefined(originalInput) ||
      JSON.stringify(currentInput) !== JSON.stringify(originalInput)
    ) {
      ioTypeChanges[inputId] = currentInput;
      hasChanges = true;
    }
  }

  return [ioTypeChanges, hasChanges];
}

function processRemovedInputs(
  originalIoType: Record<string, unknown>,
  currentIoType: Record<string, unknown>,
  ioTypeChanges: Record<string, unknown>,
): boolean {
  let hasChanges = false;
  for (const inputId in originalIoType) {
    if (!(inputId in currentIoType)) {
      ioTypeChanges[inputId] = null;
      hasChanges = true;
    }
  }
  return hasChanges;
}

export function calculateIoChanges(
  currentValues: DeviceIOParameters,
  originalValues: DeviceIOParameters | null,
): Partial<DeviceIOParameters> {
  const changes: Partial<DeviceIOParameters> = {};
  if (!originalValues) return changes;

  // Process each IO type
  for (const ioType in currentValues) {
    const currentIoType = currentValues[ioType];
    const originalIoType = originalValues[ioType] ?? {};

    const [ioTypeChanges, changesFound] = processInputChanges(
      currentIoType,
      originalIoType,
    );
    const removedFound = processRemovedInputs(
      originalIoType,
      currentIoType,
      ioTypeChanges,
    );

    if (changesFound || removedFound) {
      changes[ioType] = ioTypeChanges;
    }
  }

  // Check for removed IO types
  for (const ioType in originalValues) {
    if (!(ioType in currentValues)) {
      changes[ioType] = null;
    }
  }

  return changes;
}
