import {
  Component,
  computed,
  inject,
  signal,
  OnInit,
  input,
  OnDestroy,
  ChangeDetectorRef,
  effect,
  untracked,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import {
  Observable,
  Subject,
  debounceTime,
  firstValueFrom,
  of,
  switchMap,
} from 'rxjs';

// PrimeNG imports
import { ButtonModule } from 'primeng/button';
import { MessageModule } from 'primeng/message';

// FontAwesome
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { faClipboardList } from '@fortawesome/free-solid-svg-icons';

// API imports
import {
  AccountsService,
  UserListItem,
  UserCreateRequest,
  UserResponse,
  UserPermissions,
  UserClientPermissions,
  ClientListItem,
  ClientsService,
  EventActorFilter,
} from '../../../../../api';

// Shared imports
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import {
  hasProperty,
  isDefined,
  isEmpty,
  isNullOrUndefined,
} from '../../../../../shared/utils';

// Component imports
import { EntitySectionBaseComponent } from '../../base';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { RequestCacheService } from '../../../../../shared/services/request-cache.service';
import {
  createAssetFilterField,
  createAutocompleteField,
  createModalListField,
  FormFieldConfig,
  ListFieldModalConfig,
} from '../../../../../shared/models';
import { ModalListFormComponent } from '../../../../../shared/components/forms/modal-list-form/modal-list-form.component';
import { Validators } from '@angular/forms';
import { AuthDataService } from '../../../../../core/services/auth-data.service';
import { UtilityService } from '../../../../../core/services/utility.service';
import {
  CLIENT_SEARCH_LIMIT,
  SEARCH_DEBOUNCE_TIME,
} from '../../../../../core/models';

@Component({
  selector: 'app-user-client-permissions-section',
  imports: [
    CommonModule,
    ButtonModule,
    GridComponent,
    MessageModule,
    FontAwesomeModule,
    DynamicFormComponent,
  ],
  templateUrl: './user-client-permissions-section.component.html',
  styleUrl: './user-client-permissions-section.component.scss',
})
export class UserClientPermissionsSectionComponent
  extends EntitySectionBaseComponent<UserListItem | UserCreateRequest>
  implements OnInit, OnDestroy
{
  readonly ownerId = input<string | null>(null);
  private readonly accountsService = inject(AccountsService);
  private readonly requestCache = inject(RequestCacheService);
  private readonly utilityService = inject(UtilityService);
  private readonly clientsService = inject(ClientsService);
  private readonly authDataService = inject(AuthDataService);

  private readonly cdr = inject(ChangeDetectorRef);
  faClipboardList = faClipboardList;
  private readonly clientNames = signal<Record<string, string>>({});
  private clientCacheKeys: string[] = [];
  readonly userClientPermissions = computed(() => {
    const user = this.entity();
    const permissions = (user as unknown as UserResponse)?.permissions?.clients;
    const names = this.clientNames();

    if (!isDefined(permissions)) {
      return [];
    }

    const clientIds = Object.keys(permissions);

    if (clientIds.length === 0) {
      return [
        {
          key: 'Clients',
          value: 'No Clients',
        },
      ];
    }

    if (isEmpty(names)) {
      return [
        {
          key: 'Clients',
          value: 'Loading client names...',
        },
      ];
    }

    // Map client IDs to names, fallback to ID if name not loaded yet
    const clientNames = clientIds.map((id) => names[id] || id).join(', ');

    return [
      {
        key: 'Clients',
        value: clientNames,
      },
    ];
  });
  clientPermissionsModalConfig:
    | ListFieldModalConfig<
        | { clientId: string; filter: EventActorFilter[] }
        | { client: { id: string; name: string }; filter: EventActorFilter[] }
      >
    | undefined;
  clientSearchResults = signal<ClientListItem[] | undefined>([]);
  private readonly searchSubject = new Subject<string>();
  private readonly isLoadingClients = signal<boolean>(false);

  // Track the selected client ID for the modal form
  // This is used to pass the client ID as ownerId to the asset filter field
  // and to disable the client autocomplete once a selection is made
  private readonly selectedClientId = signal<string | undefined>(undefined);
  // Force modal refresh when client changes
  private readonly modalRefreshTrigger = signal<number>(0);

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    // React when a client gets selected to rebuild modal field configs
    effect(() => {
      this.selectedClientId(); // dependency tracking
      if (this.clientPermissionsModalConfig) this.rebuildModalFields();
    });

    effect(() => {
      const entity = this.entity();
      if (isDefined(entity) && !this.createMode()) {
        untracked(() => {
          this.updateFormFields();
        });
      }
    });
  }

  ngOnInit() {
    void this.loadClientPermissions();
    this.setupClientSearch();
  }

  override ngOnDestroy() {
    super.ngOnDestroy();
    // Clear all client cache entries created by this component
    for (const key of this.clientCacheKeys) {
      this.requestCache.clearCache(key);
    }
    this.clientCacheKeys = [];
  }

  protected override updateFormFields(): void {
    // Reset the selected client ID and modal refresh trigger when updating form fields
    this.selectedClientId.set(undefined);
    this.modalRefreshTrigger.set(0);

    const userPermissions: Record<string, UserClientPermissions> =
      this.entity()?.permissions?.clients ?? {};

    // Transform backend format to form format:
    // Backend: { clientId: { assets: [...] } }
    // Form: [{ clientId, filter: [...] }]
    const permissionsArray = Object.entries(userPermissions).map(
      ([clientId, permissions]) => ({
        clientId,
        filter: permissions.assets, // Map assets -> filter for the form
      }),
    );

    // Create a reactive modal configuration that updates when selectedClientId changes
    this.clientPermissionsModalConfig = {
      modalComponent: ModalListFormComponent,
      modalTitle: 'Client Permissions',
      listItemFormatter: (
        item:
          | { clientId: string; filter: EventActorFilter[] }
          | {
              client: { id: string; name: string };
              filter: EventActorFilter[];
            },
      ) => {
        // Handle both existing (has clientId) and new (has client object) items
        const id = 'clientId' in item ? item.clientId : item.client.id;
        const clientName =
          'clientId' in item
            ? (this.clientNames()[item.clientId] ?? id)
            : item.client.name;

        return {
          id,
          display: `${clientName}`,
          icon: 'pi pi-building',
        };
      },
      modalFields: (): FormFieldConfig[] => this.createModalFields(),
    };

    this.formFields = [
      createModalListField('permissions', 'Clients', permissionsArray, {
        name: 'permissions',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Client permissions are required',
          },
        ],
        props: {
          modalConfig: this.clientPermissionsModalConfig,
        },
      }) as FormFieldConfig<unknown>,
    ];
  }

  private rebuildModalFields(): void {
    // Reassign a fresh modalFields function so consumers re-evaluate
    if (this.clientPermissionsModalConfig) {
      this.clientPermissionsModalConfig = {
        ...this.clientPermissionsModalConfig,
        modalFields: () => this.createModalFields(),
      } as typeof this.clientPermissionsModalConfig;
      // Nudge formFields array reference (if modal component checks it)
      this.formFields = [...this.formFields];
      this.cdr.detectChanges();
    }
  }

  private createModalFields(): FormFieldConfig[] {
    const currentClientId = this.selectedClientId();

    // Create the autocomplete field
    const clientField = createAutocompleteField(
      'client',
      'Client',
      '',
      () => this.clientSearchResults() ?? [],
      this.searchClient.bind(this),
      {
        name: 'client',
        props: {
          forceSelection: true,
          field: 'name',
          placeholder: 'Search for clients...',
          completeOnFocus: true,
          typeahead: true,
          showEmptyMessage: true,
          appendTo: 'body',
        },
        autocompleteLoading: () => this.isLoadingClients(), // Reactive loading state
        hint: 'Enter a client name.',
        events: {
          onChange: (value: string) => {
            this.updateSelectedClientId(value);
            // Trigger modal refresh to update fields with new client ID
            this.modalRefreshTrigger.update((count) => count + 1);
            this.cdr.detectChanges();
          },
        },
      },
    );

    // Set readonly if client is already selected
    clientField.readonly = currentClientId !== undefined;

    // Create the asset filter field
    const assetFilterField = createAssetFilterField(
      'filter',
      'Asset Filter',
      undefined,
      ['asset'],
      {
        name: 'filter',
        required: true,
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Asset filter is required',
          },
        ],
        props: {
          multiSelect: true,
          showGroupTabs: true,
          ownerId: currentClientId, // Pass the selected client ID as ownerId
        },
        readonly: currentClientId === undefined, // Disable if no client selected
      },
    );

    return [clientField, assetFilterField];
  }

  private updateSelectedClientId(value: string) {
    let clientId: string | undefined;

    try {
      // Try to parse as JSON first (if it's a stringified object)
      const parsedValue = JSON.parse(value);
      if (
        parsedValue !== null &&
        typeof parsedValue === 'object' &&
        'id' in parsedValue
      ) {
        clientId = parsedValue.id as string;
      }
    } catch {
      // If parsing fails, treat value as a direct client ID or search term
      // For autocomplete, we usually get the selected object, but handle both cases
      if (value && value.trim() !== '') {
        // Check if this value matches any of our search results
        const searchResults = this.clientSearchResults();
        const matchedClient = searchResults?.find(
          (client) => client.id === value || client.name === value,
        );
        if (matchedClient) {
          clientId = matchedClient.id;
        }
      }
    }

    this.selectedClientId.set(clientId);
  }

  protected override submitChanges(formData: Record<string, unknown>): void {
    const user = this.entity();
    if (isNullOrUndefined(user)) return;

    // Get the original permissions to detect deletions
    const originalPermissions: Record<string, UserClientPermissions> =
      (user as unknown as UserResponse)?.permissions?.clients ?? {};

    // Transform formData permissions array to UserPermissions format
    const permissionsArray = formData['permissions'] as {
      clientId?: string;
      client?: { id: string };
      filter: unknown;
    }[];
    const clientPermissions: Record<string, UserClientPermissions | null> = {};

    // Add/update permissions from form data
    for (const item of permissionsArray) {
      // Get clientId from either the clientId property (existing) or client.id (new/edited)
      const clientId = item.clientId ?? item.client?.id;

      // Transform filter to assets
      clientPermissions[clientId as string] = {
        assets: item.filter as EventActorFilter[],
      };
    }

    // Mark deleted clients as null
    for (const originalClientId of Object.keys(originalPermissions)) {
      if (!(originalClientId in clientPermissions)) {
        clientPermissions[originalClientId] = null;
      }
    }

    const userPermissions: UserPermissions = {
      clients: clientPermissions as Record<string, UserClientPermissions>,
    };

    this.accountsService
      .updateUser(user.id, {
        permissions: userPermissions,
      })
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe({
        next: (response) => {
          // Load client names from the response data directly
          this.loadClientPermissionsFromResponse(response)
            .then(() => {
              // Broadcast the update so entity gets refreshed
              this.broadcastService.broadcast('entity-updated', {
                entityType: 'user',
                operation: 'update',
                entityId: user.id,
                data: response,
              });

              // Now disable the form
              this.isFormEditable.set(false);
              this.isSubmitting.set(false);
            })
            .catch((error) => {
              console.error(
                'Error loading client permissions after save:',
                error,
              );
              this.isFormEditable.set(false); // Still disable even if loading fails
              this.isSubmitting.set(false);
            });
        },
        error: () => {
          this.isFormEditable.set(true);
          this.isSubmitting.set(false);
        },
      });
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<UserResponse | UserCreateRequest>,
  ) => Observable<UserResponse> {
    return (id, data) => this.accountsService.updateUser(id, data);
  }

  async loadClientPermissions(): Promise<void> {
    const user = this.entity();
    await this.loadClientPermissionsFromResponse(
      user as unknown as UserResponse,
    );
  }

  private async loadClientPermissionsFromResponse(
    user: UserResponse,
  ): Promise<void> {
    const permissions = user?.permissions?.clients;

    if (!isDefined(permissions)) {
      return;
    }

    const clientIds = Object.keys(permissions);
    const namePromises = clientIds.map(async (clientId) => {
      const name = await this.loadClientName(clientId);
      return { id: clientId, name };
    });

    try {
      const clientNamesArray = await Promise.all(namePromises);
      const namesRecord: Record<string, string> = {};

      for (const { id, name } of clientNamesArray) {
        namesRecord[id] = name;
      }

      this.clientNames.set(namesRecord);
    } catch (error) {
      console.error('Error loading client names:', error);
    }
  }

  async loadClientName(clientId: string): Promise<string> {
    if (!clientId) return 'Unknown Client';

    const cacheKey = `client-name/${clientId}`;

    // Track this cache key for cleanup
    if (!this.clientCacheKeys.includes(cacheKey)) {
      this.clientCacheKeys.push(cacheKey);
    }

    try {
      const client = await this.requestCache.getOrFetchAsync(cacheKey, () =>
        firstValueFrom(this.accountsService.getClient(clientId)),
      );
      return client.name ?? clientId;
    } catch {
      return 'Unknown Client';
    }
  }

  private setupClientSearch(): void {
    this.searchSubject
      .pipe(
        debounceTime(SEARCH_DEBOUNCE_TIME),
        takeUntilDestroyed(this.destroyRef),
        switchMap((searchTerm) => {
          // Set loading to true when starting search
          this.isLoadingClients.set(true);

          const owner = this.authDataService.getUserProfile()?.owner;
          if (!isDefined(owner) || !hasProperty(owner, 'id')) {
            throw new Error('Owner ID not found');
          }

          const trimmedSearchTerm = searchTerm.trim();

          if (trimmedSearchTerm.length === 0) {
            return of({ items: [] });
          }

          const filters = {
            state: { value: 'active', operator: '=' },
            name: { value: `*${searchTerm}*`, operator: '=' },
          };
          const rqlFilter = this.utilityService.constructRqlFilter(filters);

          return this.clientsService.listClients(
            owner.id,
            0,
            CLIENT_SEARCH_LIMIT,
            'name',
            rqlFilter,
          );
        }),
      )
      .subscribe({
        next: (data) => {
          // Set loading to false when search completes
          this.isLoadingClients.set(false);

          if (isDefined(data) && hasProperty(data, 'items')) {
            this.clientSearchResults.set(
              isNullOrUndefined(data.items) ? [] : data.items,
            );
            this.cdr.detectChanges();
          }
        },
        error: (error) => {
          // Set loading to false on error
          this.isLoadingClients.set(false);

          console.error('Error searching clients:', error);
          this.clientSearchResults.set([]);
        },
      });
  }

  searchClient(searchTerm: string) {
    if (!searchTerm || searchTerm.trim() === '' || searchTerm.length < 3) {
      this.clientSearchResults.set([]);
      this.isLoadingClients.set(false);
    } else {
      this.searchSubject.next(searchTerm.trim());
    }
  }
}
