import { Component, computed, inject, input, signal } from '@angular/core';
import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';

import { UserListItem, UserResponse } from '../../../../../api';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';
import { UserSectionConfigFactory } from '../../../factories';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-user-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './user-details-container.component.html',
  styleUrl: './user-details-container.component.scss',
})
export class UserDetailsContainerComponent {
  readonly entity = input<UserListItem | null>(null);

  private readonly userSectionFactory = inject(UserSectionConfigFactory);
  readonly entityDetailsState = inject(EntityDetailsStateService);
  private readonly activatedRoute = inject(ActivatedRoute);

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly isSoftRefreshing = computed(() =>
    this.entityDetailsState.isSoftRefreshing(),
  );
  readonly isInitialLoading = computed(
    () => this.isLoading() && !this.isSoftRefreshing() && !this.fullUser(),
  );
  readonly fullUser = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();

    if (!entityDetails) {
      return null;
    }
    return entityDetails as UserResponse;
  });
  readonly sections = computed(() => {
    const user = this.fullUser();

    if (!user) return [];
    const routeParams = this.getRouteParams();

    const ownerId =
      routeParams['clientId'] ??
      routeParams['vendorId'] ??
      routeParams['distributorId'] ??
      'none';
    return this.userSectionFactory.createAllUserSections(user, `${ownerId}`);
  });
  readonly error = signal<string | null>(null);

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as UserResponse);
    this.error.set(null);
  }

  private getRouteParams(): Record<string, string | number> {
    // Start from the root and collect all parameters from the entire route tree
    let rootRoute = this.activatedRoute;
    while (rootRoute.parent) {
      rootRoute = rootRoute.parent;
    }

    const allParams: Record<string, string | number> = {};

    // Recursively collect parameters from all route segments
    this.collectParamsFromRoute(rootRoute, allParams);

    return allParams;
  }

  private collectParamsFromRoute(
    route: ActivatedRoute,
    allParams: Record<string, string | number>,
  ): void {
    // Add current route's parameters
    Object.assign(
      allParams,
      route.snapshot.params as Record<string, string | number>,
    );

    // Recursively collect from all children
    route.children.forEach((childRoute) => {
      this.collectParamsFromRoute(childRoute, allParams);
    });
  }
}
