import { Component, computed, effect, inject, input } from '@angular/core';
import { Validators } from '@angular/forms';

import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';

import {
  ZoneGroupCreateRequest,
  EntitiesService,
  ZoneGroupResponse,
} from '../../../../../api';
import {
  AssetGroupingItem,
  createDropdownField,
  createTextField,
} from '../../../../../shared/models';
import { EntitySectionBaseComponent } from '../../base';
import { isDefined } from '../../../../../shared/utils';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { CommonModule } from '@angular/common';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { Observable } from 'rxjs';
import { faAtlas } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { AssetGroupingService } from '../../../../../shared/services/asset-grouping.service';

type ZoneGroupEntity = ZoneGroupResponse | Partial<ZoneGroupCreateRequest>;

@Component({
  selector: 'app-geofence-group-details-section',
  imports: [
    CommonModule,
    ButtonModule,
    InputTextModule,
    GridComponent,
    DynamicFormComponent,
    FontAwesomeModule,
  ],
  templateUrl: './geofence-group-details-section.component.html',
  styleUrl: './geofence-group-details-section.component.scss',
})
export class GeofenceGroupDetailsSectionComponent extends EntitySectionBaseComponent<ZoneGroupEntity> {
  readonly ownerId = input<string | null>(null);
  readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly assetGroupingService = inject(AssetGroupingService);

  readonly geofenceGroupDetails = computed(() => {
    const geofenceGroupDetails = this.entity();
    if (!isDefined(geofenceGroupDetails)) return [];

    return [
      {
        key: 'Parent',
        value: geofenceGroupDetails.parent?.name ?? 'Root',
      },
      { key: 'Name', value: geofenceGroupDetails.name },
    ];
  });

  faAtlas = faAtlas;
  zoneGroups: AssetGroupingItem[] = [];

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    effect(() => {
      const ownerId = this.entity()?.owner?.id ?? this.ownerId();
      if (isDefined(ownerId)) {
        this.fetchZoneGroups(ownerId)
          .then((groups) => {
            this.zoneGroups = groups;
            this.updateFormFields();
          })
          .catch(() => {});
      }
    });
  }

  private fetchZoneGroups(ownerId: string): Promise<AssetGroupingItem[]> {
    return this.assetGroupingService.getZoneGroups(ownerId);
  }

  protected override updateFormFields(): void {
    const geofenceGroup = this.entity();
    const parentOptions = [
      { label: 'Root', value: 'root' },
      ...this.zoneGroups.map((group) => ({
        label: group.name,
        value: group.id,
      })),
    ];
    this.formFields = [
      createDropdownField(
        'parent',
        'Parent',
        parentOptions,
        geofenceGroup?.parent?.id ?? 'root',
        {
          placeholder: 'Select Parent Group',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Parent group is required',
            },
          ],
          props: {
            appendTo: 'body',
          },
        },
      ),
      createTextField('name', 'Name', geofenceGroup?.name, {
        name: 'name',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Name is required',
          },
        ],
      }),
    ];
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<ZoneGroupEntity>,
  ) => Observable<ZoneGroupResponse> {
    return (
      this.operationRegistry.getOperation<
        ZoneGroupEntity,
        ZoneGroupCreateRequest,
        ZoneGroupResponse,
        'update'
      >('geofence-group', 'update') ??
      ((id, data) => this.entitiesService.updateZoneGroup(id, data))
    );
  }

  protected override onFormValueChange(
    formData: Record<string, string | number | object | null>,
  ): void {
    const transformedData = this.transformFormData(formData);

    this.formValueChange.emit(transformedData);
  }

  protected override transformFormData(
    formData: Record<string, string | number | object | null>,
  ) {
    const transformedData = { ...formData };
    if (isDefined(transformedData['parent'])) {
      const parentId = transformedData['parent'] as string;
      const parent = this.zoneGroups?.find((option) => option.id === parentId);
      if (parent) {
        transformedData['parent'] = {
          id: parent.id,
          name: parent.name,
        };
      } else if (parentId === 'root') {
        transformedData['parent'] = null; // Set to null for root
      }
    }
    return transformedData;
  }
}
