import {
  Component,
  computed,
  effect,
  inject,
  input,
  signal,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { faKey } from '@fortawesome/free-solid-svg-icons';

import {
  UserRoleListItem,
  UserRoleCreateRequest,
  AccountsService,
} from '../../../../../api';
import { createListboxField } from '../../../../../shared/models';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { EntitySectionBaseComponent } from '../../base';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { CommonModule } from '@angular/common';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { ButtonModule } from 'primeng/button';
import { MessageModule } from 'primeng/message';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { firstValueFrom } from 'rxjs';
import { LEGACY_RIGHTS } from '../../../models';
import { DEFAULT_SYSTEM_ID } from '../../../../../core/models';

@Component({
  selector: 'app-user-role-rights-section',
  imports: [
    CommonModule,
    ButtonModule,
    GridComponent,
    DynamicFormComponent,
    MessageModule,
    FontAwesomeModule,
  ],
  templateUrl: './user-role-rights-section.component.html',
  styleUrl: './user-role-rights-section.component.scss',
})
export class UserRoleRightsSectionComponent extends EntitySectionBaseComponent<
  UserRoleListItem | UserRoleCreateRequest
> {
  readonly ownerId = input<string | null>(null);
  private readonly accountsService = inject(AccountsService);

  private readonly resolvedCompanyType = signal<string | undefined>(undefined);
  faKey = faKey;

  readonly userRoleDetails = computed(() => {
    const userRole = this.entity();
    const companyType = this.resolvedCompanyType();

    if (!isDefined(userRole) || !isDefined(companyType)) {
      return [];
    }

    const legacyRights =
      LEGACY_RIGHTS[companyType as keyof typeof LEGACY_RIGHTS];

    const details: { key: string; value: string }[] = [];

    if (isDefined(legacyRights)) {
      legacyRights.forEach((right) => {
        details.push({
          key: right.title,
          value: userRole.legacyRights?.[right.name]?.join(', ') ?? 'None',
        });
      });
    }
    return details;
  });

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '', transform: 'capitalize' },
    ];

    effect(() => {
      const userRole = this.entity();
      const ownerId = userRole?.owner?.id;

      if (isDefined(ownerId)) {
        if (ownerId === DEFAULT_SYSTEM_ID) {
          this.resolvedCompanyType.set('system');
        } else {
          this.resolvedCompanyType.set(undefined);
          firstValueFrom(this.accountsService.getCompany(ownerId))
            .then((response) => {
              if (response.type) {
                this.resolvedCompanyType.set(response.type);
                this.updateFormFields();
              } else {
                console.warn(
                  'Company response or type is undefined for owner ID:',
                  ownerId,
                );
                this.resolvedCompanyType.set(undefined);
              }
            })
            .catch((error) => {
              console.error(
                'Failed to fetch company type for owner ID:',
                ownerId,
                error,
              );
              this.resolvedCompanyType.set(undefined);
            });
        }
      } else {
        this.resolvedCompanyType.set(undefined);
      }
    });
  }

  protected override updateFormFields(): void {
    const userRole: Record<string, string[]> = this.entity()?.legacyRights;
    const companyType = this.resolvedCompanyType();
    if (!isDefined(companyType)) {
      this.formFields = [];
      return;
    }

    const listBoxOptions = companyType
      ? LEGACY_RIGHTS[companyType as keyof typeof LEGACY_RIGHTS]
      : [];

    this.formFields = listBoxOptions.map((right) => {
      const selectedRights = userRole?.[right.name] ?? [];
      return createListboxField(
        right.name,
        right.title,
        selectedRights,
        right.rights.map((r) => ({
          label: r.title,
          value: r.name,
        })),
        {
          name: right.name,
          props: {
            checkmark: true,
            multiple: true,
          },
        },
      );
    });
  }

  protected override submitChanges(
    formData: Record<string, string | string[] | number>,
  ): void {
    const userRole = this.entity();
    if (isNullOrUndefined(userRole)) return;
    this.accountsService
      .updateUserRole(userRole.id, {
        legacyRights: formData as Record<string, string[]>,
      })
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe({
        next: (response) => {
          this.isFormEditable.set(false);
          this.isSubmitting.set(false);
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'user-role',
            operation: 'update',
            entityId: userRole.id,
            data: response,
          });
        },
        error: () => {
          this.isFormEditable.set(true);
          this.isSubmitting.set(false);
        },
      });
  }
}
