import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import {
  OverspeedProfileListItem,
  OverspeedProfileResponse,
  EntitiesService,
} from '../../../../api';
import {
  DataGridParams,
  EntityDataAdapter,
  GridResponse,
} from '../../../../shared/models';
import { ActionIcon, EntityConfigAdapter, StateOption } from '../../models';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';

@Injectable({
  providedIn: 'root',
})
export class OverspeedProfilesDataAdapter
  implements
    EntityDataAdapter<OverspeedProfileListItem, OverspeedProfileResponse>,
    EntityConfigAdapter
{
  private readonly entitiesService = inject(EntitiesService);
  private readonly entityStateService = inject(EntityDetailsStateService);

  getList(
    params: DataGridParams,
  ): Observable<GridResponse<OverspeedProfileListItem>> {
    const { owner, limit, offset, sortField, filters, recurse } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.entitiesService.listOverspeedProfiles(
      owner,
      recurse,
      offset,
      limit,
      sortField as string,
      filters,
    );
  }

  getById(id: string): Observable<OverspeedProfileResponse> {
    return this.entitiesService.getOverspeedProfile(id);
  }

  getStateOptions(): StateOption[] {
    return [
      { label: 'Active', value: OverspeedProfileResponse.StateEnum.Active },
      { label: 'Deleted', value: OverspeedProfileResponse.StateEnum.Deleted },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.entitiesService.updateOverspeedProfile(id, {
      state: state as OverspeedProfileResponse.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    return [
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change Overspeed Profile State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
