import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import { ThemeListItem, ThemeResponse, EntitiesService } from '../../../../api';
import {
  DataGridParams,
  GridDataService,
  GridResponse,
} from '../../../../shared/models';

@Injectable({
  providedIn: 'root',
})
export class ThemesDataAdapter implements GridDataService<ThemeListItem> {
  private readonly entitiesService = inject(EntitiesService);

  getList(params: DataGridParams): Observable<GridResponse<ThemeListItem>> {
    const { owner, limit, offset, sortField, filters, recurse } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.entitiesService.listThemes(
      owner,
      recurse,
      offset,
      limit,
      sortField as string,
      filters,
    );
  }

  getById(id: string): Observable<ThemeResponse> {
    return this.entitiesService.getTheme(id);
  }
}
