import {
  Component,
  computed,
  DestroyRef,
  inject,
  OnInit,
  signal,
  input,
} from '@angular/core';
import {
  DeviceTypeCreateRequest,
  DeviceTypeResponse,
  DeviceTypesService,
} from '../../../../../api';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { DeviceTypesSectionConfigFactory } from '../../../factories';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { BroadcastEventMessageService } from '../../../services/broadcast-event-messages.service';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { ToastService } from '../../../../../core/services/toast.service';
import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';
import { MessageModule } from 'primeng/message';

interface EntityUpdatedMessage {
  entityType: string;
  data: DeviceTypeResponse;
}

@Component({
  selector: 'app-device-types-details-container',
  imports: [DetailsAccordionContainerComponent, MessageModule],
  templateUrl: './device-types-details-container.component.html',
  styleUrl: 'device-types-details-container.component.scss',
})
export class DeviceTypesDetailsContainerComponent implements OnInit {
  constructor() {
    this.operationRegistry.registerEntityOperations('device-types', {
      update: (id, data) => this.deviceTypesService.updateDeviceType(id, data),
      create: (data: DeviceTypeCreateRequest) =>
        this.deviceTypesService.createDeviceType(data),
      get: (id) => this.deviceTypesService.getDeviceType(id),
    });
  }
  readonly entity = input<DeviceTypeResponse | null>(null);

  private readonly entityDetailsState = inject(EntityDetailsStateService);
  private readonly deviceTypesSectionFactory = inject(
    DeviceTypesSectionConfigFactory,
  );
  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly broadcastService = inject(BroadcastEventMessageService);
  private readonly toastService = inject(ToastService);
  private readonly destroyRef = inject(DestroyRef);
  private readonly deviceTypesService = inject(DeviceTypesService);

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly isSoftRefreshing = computed(() =>
    this.entityDetailsState.isSoftRefreshing(),
  );
  readonly isInitialLoading = computed(
    () =>
      this.isLoading() && !this.isSoftRefreshing() && !this.fullDeviceType(),
  );

  readonly fullDeviceType = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();
    if (isNullOrUndefined(entityDetails)) return null;

    return entityDetails as DeviceTypeResponse;
  });

  readonly sections = computed(() => {
    const fullDeviceType = this.fullDeviceType();
    if (!fullDeviceType) return [];
    return this.deviceTypesSectionFactory.createAllDeviceTypeSections(
      fullDeviceType,
    );
  });

  readonly error = signal<string | null>(null);
  private isEntityUpdatedMessage(
    message: unknown,
  ): message is EntityUpdatedMessage {
    return (
      isDefined(message) &&
      message !== null &&
      typeof message === 'object' &&
      'entityType' in message &&
      'data' in message &&
      (message as EntityUpdatedMessage).entityType === 'device-types'
    );
  }

  ngOnInit(): void {
    this.broadcastService
      .on('entity-updated')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((message) => {
        if (this.isEntityUpdatedMessage(message)) {
          this.toastService.showSuccess(
            'Success',
            'Device Type updated successfully',
          );
          this.onEntityChange(message.data);
        }
      });
  }

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as DeviceTypeResponse);
    this.error.set(null);
  }
}
