import { Injectable, inject, signal } from '@angular/core';
import { firstValueFrom } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { AccountsService, CompanyResponse } from '../../../../../../api';
import { AssetGroupingService } from '../../../../../../shared/services/asset-grouping.service';
import { RequestCacheService } from '../../../../../../shared/services/request-cache.service';
import { AssetGroupingTreeItem } from '../../../../../../shared/models';
import { isDefined } from '../../../../../../shared/utils';
import { DEFAULT_SYSTEM_ID } from '../../../../../../core/models';

@Injectable({
  providedIn: 'root',
})
export class UserDataLoadingService {
  private readonly accountsService = inject(AccountsService);
  private readonly assetGroupService = inject(AssetGroupingService);
  private readonly http = inject(HttpClient);
  private readonly cacheService = inject(RequestCacheService);

  // Loading state signals
  private readonly timezonesLoaded = signal(false);
  private readonly costCentersLoaded = signal(false);
  private readonly companyDataLoaded = signal(false);

  // Data signals
  private readonly companyData = signal<CompanyResponse | null>(null);
  private readonly timezones = signal<{ label: string; value: string }[]>([]);
  private readonly costCenters = signal<AssetGroupingTreeItem[]>([]);

  // Getters for readonly access
  get isDataLoaded(): boolean {
    return (
      this.timezonesLoaded() &&
      this.costCentersLoaded() &&
      this.companyDataLoaded()
    );
  }

  get timezonesData() {
    return this.timezones.asReadonly();
  }

  get costCentersData() {
    return this.costCenters.asReadonly();
  }

  get companyInfo() {
    return this.companyData.asReadonly();
  }

  get loadingStates() {
    return {
      timezones: this.timezonesLoaded.asReadonly(),
      costCenters: this.costCentersLoaded.asReadonly(),
      company: this.companyDataLoaded.asReadonly(),
    };
  }

  /**
   * Load all required data for the user form
   */
  async loadUserFormData(ownerId?: string | null): Promise<void> {
    // Start loading timezones immediately
    const timezonePromise = this.loadTimezones();

    // Load company data if ownerId is provided
    let companyPromise: Promise<void> = Promise.resolve();
    if (isDefined(ownerId)) {
      companyPromise = this.loadCompanyData(ownerId);
    } else {
      this.companyData.set(null);
      this.companyDataLoaded.set(true);
    }

    // Wait for company data to determine if we need cost centers
    await companyPromise;
    const company = this.companyData();

    // Load cost centers if needed
    let costCenterPromise: Promise<void> = Promise.resolve();
    if (isDefined(ownerId) && isDefined(company) && company.type === 'client') {
      costCenterPromise = this.loadCostCentres(ownerId);
    } else {
      this.costCenters.set([]);
      this.costCentersLoaded.set(true);
    }

    // Wait for all data to load
    await Promise.all([timezonePromise, costCenterPromise]);
  }

  /**
   * Load timezone data
   */
  private async loadTimezones(): Promise<void> {
    try {
      this.timezonesLoaded.set(false);
      const data = await firstValueFrom(
        this.http.get<{ id: string; name: string }[]>(
          '/assets/docs/timezones.json',
        ),
      );

      const timezones = data
        .map((tz) => ({ label: tz.id, value: tz.id }))
        .sort((a, b) => a.label.localeCompare(b.label));

      this.timezones.set(timezones);
      this.timezonesLoaded.set(true);
    } catch (error) {
      console.error('Failed to load timezones:', error);
      this.timezones.set([]);
      this.timezonesLoaded.set(true);
      throw error;
    }
  }

  /**
   * Load company data with caching
   */
  private async loadCompanyData(ownerId: string): Promise<void> {
    try {
      this.companyDataLoaded.set(false);
      this.companyData.set(null);

      const company = await this.cacheService.getOrFetchAsync(
        `company-${ownerId}`,
        () => firstValueFrom(this.accountsService.getCompany(ownerId)),
        300000, // Cache for 5 minutes
      );

      this.companyData.set(company);
      this.companyDataLoaded.set(true);
    } catch (error) {
      console.error('Failed to load company data:', error);
      this.companyData.set(null);
      this.companyDataLoaded.set(true);
      throw error;
    }
  }

  /**
   * Load cost centres data
   */
  private async loadCostCentres(ownerId: string): Promise<void> {
    try {
      this.costCentersLoaded.set(false);

      const [costCentres, costCentreTree] = await Promise.all([
        this.assetGroupService.getCostCentres(ownerId),
        this.assetGroupService.getCostCentresAsTree(ownerId),
      ]);
      const userCostCentre = costCentres.find(
        (centre) => centre.parent === DEFAULT_SYSTEM_ID,
      );
      const costCenters = this.assetGroupService.findCCInCCTree(
        costCentreTree,
        userCostCentre,
      );

      this.costCenters.set(costCenters);
      this.costCentersLoaded.set(true);
    } catch (error) {
      console.error('Failed to load cost centres:', error);
      this.costCenters.set([]);
      this.costCentersLoaded.set(true);
      throw error;
    }
  }

  /**
   * Reset all loading states and data
   */
  reset(): void {
    this.timezonesLoaded.set(false);
    this.costCentersLoaded.set(false);
    this.companyDataLoaded.set(false);
    this.companyData.set(null);
    this.timezones.set([]);
    this.costCenters.set([]);
  }
}
