import { Component, computed, inject, input, signal } from '@angular/core';
import { CustomFieldResponse } from '../../../models';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { CustomFieldSectionConfigFactory } from '../../../factories';
import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';
import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';

@Component({
  selector: 'app-custom-field-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './custom-field-details-container.component.html',
  styleUrl: './custom-field-details-container.component.scss',
})
export class CustomFieldDetailsContainerComponent {
  readonly entity = input<CustomFieldResponse | null>(null);

  private readonly customFieldSectionFactory = inject(
    CustomFieldSectionConfigFactory,
  );
  readonly entityDetailsState = inject(EntityDetailsStateService);

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly isSoftRefreshing = computed(() =>
    this.entityDetailsState.isSoftRefreshing(),
  );
  readonly isInitialLoading = computed(
    () =>
      this.isLoading() && !this.isSoftRefreshing() && !this.fullCustomField(),
  );
  readonly fullCustomField = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();

    if (!entityDetails) {
      return null;
    }
    return entityDetails as CustomFieldResponse;
  });
  readonly sections = computed(() => {
    const customField = this.fullCustomField();
    if (!customField) return [];
    return this.customFieldSectionFactory.createAllCustomFieldSections(
      customField,
    );
  });
  readonly error = signal<string | null>(null);

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as CustomFieldResponse);
    this.error.set(null);
  }
}
