import {
  Component,
  computed,
  effect,
  inject,
  input,
  OnInit,
} from '@angular/core';
import { DeviceTypeResponse, DeviceTypesService } from '../../../../../api';
import { EntitySectionBaseComponent } from '../../base';
import {
  isNullOrUndefined,
  isDefined,
  isObject,
} from '../../../../../shared/utils';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { tap } from 'rxjs';
import { Validators } from '@angular/forms';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import {
  createModalListField,
  createTextField,
  createToggleSwitchField,
  createNumberField,
  FormFieldConfig,
  ListFieldModalConfig,
} from '../../../../../shared/models';
import { ModalListFormComponent } from '../../../../../shared/components/forms/modal-list-form/modal-list-form.component';
import { ConfirmationService } from 'primeng/api';

interface IOInputItem {
  name: string;
  optional: boolean;
  // Digital output specific fields
  canPulse?: boolean;
  canToggle?: boolean;
  maxPulseSeconds?: number;
  minPulseSeconds?: number;
}

interface IOTypeConfig {
  additionalFields: string[];
  getDefaultValues: () => Partial<IOInputItem>;
  getModalFields: (item: IOInputItem | undefined) => FormFieldConfig[];
  addPropertiesToDisplayText: (item: IOInputItem, parts: string[]) => void;
  addPropertiesToSaveObject: (
    item: IOInputItem,
    inputObject: Record<string, unknown>,
  ) => void;
  addPropertiesToItem: (
    item: Record<string, unknown>,
    baseItem: IOInputItem,
  ) => void;
}

@Component({
  selector: 'app-device-types-io-section',
  imports: [DynamicFormComponent, GridComponent],
  templateUrl: './device-types-io-section.component.html',
  styleUrl: './device-types-io-section.component.scss',
})
export class DeviceTypesIoSectionComponent
  extends EntitySectionBaseComponent<DeviceTypeResponse>
  implements OnInit
{
  private readonly ioTypeConfigs: Record<string, IOTypeConfig> = {
    digital_output: {
      additionalFields: [
        'canPulse',
        'canToggle',
        'maxPulseSeconds',
        'minPulseSeconds',
      ],
      getDefaultValues: () => ({
        canPulse: false,
        canToggle: true,
        maxPulseSeconds: 0,
        minPulseSeconds: 0,
      }),
      getModalFields: (item: IOInputItem | undefined) => [
        createToggleSwitchField(
          'canToggle',
          'Can Toggle',
          item?.canToggle ?? true,
          {
            hint: 'When enabled, this output can be toggled on/off.',
            props: {
              layout: 'horizontal',
              labelWidth: '1/3',
              onLabel: 'Yes',
              offLabel: 'No',
            },
          },
        ),
        createToggleSwitchField(
          'canPulse',
          'Can Pulse',
          item?.canPulse ?? false,
          {
            hint: 'When enabled, this output can pulse for a specified duration.',
            props: {
              layout: 'horizontal',
              labelWidth: '1/3',
              onLabel: 'Yes',
              offLabel: 'No',
            },
          },
        ),
        createNumberField(
          'minPulseSeconds',
          'Min Pulse Seconds',
          item?.minPulseSeconds ?? 0,
          {
            hint: 'Minimum pulse duration in seconds.',
            validations: [
              {
                name: 'min',
                validator: Validators.min(0),
                message: 'Min pulse seconds must be 0 or greater',
              },
              {
                name: 'max',
                validator: Validators.max(86400),
                message: 'Min pulse seconds must be 86400 or less',
              },
            ],
            props: {
              layout: 'horizontal',
              labelWidth: '1/3',
              min: 0,
              step: 1,
            },
          },
        ),
        createNumberField(
          'maxPulseSeconds',
          'Max Pulse Seconds',
          item?.maxPulseSeconds ?? 0,
          {
            hint: 'Maximum pulse duration in seconds.',
            validations: [
              {
                name: 'min',
                validator: Validators.min(0),
                message: 'Max pulse seconds must be 0 or greater',
              },
              {
                name: 'max',
                validator: Validators.max(86400),
                message: 'Max pulse seconds must be 86400 or less',
              },
            ],
            props: {
              layout: 'horizontal',
              labelWidth: '1/3',
              min: 0,
              step: 1,
            },
          },
        ),
      ],
      addPropertiesToDisplayText: (item: IOInputItem, parts: string[]) => {
        this.addToggleProperty(item, parts);
        this.addPulseProperty(item, parts);
        this.addPulseDurationProperty(item, parts);
      },
      addPropertiesToSaveObject: (
        item: IOInputItem,
        inputObject: Record<string, unknown>,
      ) => {
        if (item.canPulse !== undefined) {
          inputObject['canPulse'] = item.canPulse;
        }
        if (item.canToggle !== undefined) {
          inputObject['canToggle'] = item.canToggle;
        }
        if (item.maxPulseSeconds !== undefined) {
          inputObject['maxPulseSeconds'] = item.maxPulseSeconds;
        }
        if (item.minPulseSeconds !== undefined) {
          inputObject['minPulseSeconds'] = item.minPulseSeconds;
        }
      },
      addPropertiesToItem: (
        item: Record<string, unknown>,
        baseItem: IOInputItem,
      ) => {
        if (item['canPulse'] !== undefined) {
          baseItem.canPulse = Boolean(item['canPulse']);
        }
        if (item['canToggle'] !== undefined) {
          baseItem.canToggle = Boolean(item['canToggle']);
        }
        if (item['maxPulseSeconds'] !== undefined) {
          baseItem.maxPulseSeconds = Number(item['maxPulseSeconds']);
        }
        if (item['minPulseSeconds'] !== undefined) {
          baseItem.minPulseSeconds = Number(item['minPulseSeconds']);
        }
      },
    },
    // Add more I/O types here as needed
  };
  constructor() {
    super();

    // Update columns dynamically when data changes
    effect(() => {
      this.columns = this.dynamicColumns();
    });

    effect(() => {
      const entity = this.entity();
      if (entity && !this.createMode()) {
        this.updateFormFields();
      }
    });
  }

  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly deviceTypesService = inject(DeviceTypesService);
  private readonly confirmationService = inject(ConfirmationService);

  override entity = input<DeviceTypeResponse | null>(null);

  // Configuration inputs
  ioTypeKey = input.required<string>(); // 'digital_input', 'digital_output', etc.
  displayTitle = input.required<string>(); // 'Digital Inputs', 'Digital Outputs', etc.
  description = input.required<string>(); // Description text
  icon = input<string>('pi pi-microchip'); // Icon class
  iconType = input<string>('primeng'); // 'primeng' or 'svg'

  ioInputModalConfig: ListFieldModalConfig<IOInputItem> | undefined;

  private readonly defaultIOConfig: IOTypeConfig = {
    additionalFields: [],
    getDefaultValues: () => ({}),
    getModalFields: () => [],
    addPropertiesToDisplayText: () => {},
    addPropertiesToSaveObject: () => {},
    addPropertiesToItem: () => {},
  };

  private getCurrentIOConfig(): IOTypeConfig {
    return this.ioTypeConfigs[this.ioTypeKey()] ?? this.defaultIOConfig;
  }

  // Generic computed property that works for any I/O type
  readonly ioTypeDetails = computed(() => {
    const deviceType = this.entity();
    const ioCapabilities = deviceType?.ioCapabilities;
    const ioTypeData =
      ioCapabilities?.[this.ioTypeKey() as keyof typeof ioCapabilities];

    if (isNullOrUndefined(ioTypeData) || !isObject(ioTypeData)) return [];

    return Object.entries(ioTypeData).map(([name, properties]) => {
      const propertiesObj = properties as Record<string, unknown>;
      const optional = isDefined(propertiesObj?.['optional'])
        ? Boolean(propertiesObj['optional'])
        : false;

      // Create a flattened object with all properties
      const result: Record<string, unknown> = {
        name,
        status: optional ? 'Optional' : 'Required',
      };

      // Add all other properties dynamically
      if (isDefined(propertiesObj)) {
        for (const [key, value] of Object.entries(propertiesObj)) {
          if (key !== 'optional') {
            // Skip optional since we handle it as status
            result[key] = value;
          }
        }
      }

      return result;
    });
  });

  readonly hasData = computed(() => {
    return this.ioTypeDetails().length > 0;
  });

  // Convert ioTypeDetails to IOInputItem format for the modal
  readonly ioInputItems = computed(() => {
    return this.ioTypeDetails().map((item) => {
      const baseItem: IOInputItem = {
        name: item['name'] as string,
        optional: item['status'] === 'Optional',
      };

      this.getCurrentIOConfig().addPropertiesToItem(item, baseItem);

      return baseItem;
    });
  });

  // Dynamically generate columns based on available properties
  readonly dynamicColumns = computed(() => {
    const data = this.ioTypeDetails();
    if (data.length === 0) return [];

    // Start with name and status columns
    const columns = [
      { field: 'name', header: 'Name' },
      { field: 'status', header: 'Required/Optional' },
    ];

    // Collect all unique property keys from all items
    const allKeys = new Set<string>();
    for (const item of data) {
      for (const key of Object.keys(item)) {
        if (key !== 'name' && key !== 'status') {
          allKeys.add(key);
        }
      }
    }

    // Add columns for each unique property
    for (const key of Array.from(allKeys).sort((a, b) => a.localeCompare(b))) {
      columns.push({
        field: key,
        header: this.formatColumnHeader(key),
      });
    }

    return columns;
  });

  private formatColumnHeader(key: string): string {
    // Convert camelCase or snake_case to Title Case
    return key
      .replaceAll(/([A-Z])/g, ' $1') // Add space before capitals
      .replaceAll('_', ' ') // Replace underscores with spaces
      .toLowerCase()
      .split(' ')
      .map((word) => word.charAt(0).toUpperCase() + word.slice(1))
      .join(' ');
  }

  protected override updateFormFields(): void {
    this.ioInputModalConfig = {
      modalComponent: ModalListFormComponent,
      modalTitle: `Add ${this.displayTitle().toLowerCase().slice(0, -1)}`,
      defaultItemProvider: () => this.getDefaultItem(),
      listItemFormatter: (item: IOInputItem) => ({
        id: item.name,
        title: item.name,
        display: this.formatDisplayText(item),
        icon: this.icon(),
      }),
      modalFields: (item: IOInputItem | undefined): FormFieldConfig[] => {
        return this.getModalFields(item);
      },
      onSubmit: async (item: IOInputItem) => {
        // Validate the item
        if (!item.name?.trim()) {
          return false;
        }

        return true; // Close modal
      },
      onDeleteItem: async (item: IOInputItem) => {
        return new Promise<boolean>((resolve) => {
          this.confirmationService.confirm({
            header: 'Confirmation',
            message: `Are you sure you want to delete "${item.name}"?`,
            closable: true,
            closeOnEscape: true,
            icon: 'pi pi-exclamation-triangle',
            rejectButtonProps: {
              label: 'Cancel',
              severity: 'secondary',
              outlined: true,
            },
            acceptButtonProps: {
              label: 'Delete',
              severity: 'danger',
            },
            accept: async () => {
              resolve(true); // Allow default deletion
            },
            reject: () => {
              resolve(false); // Cancel deletion
            },
          });
        });
      },
      subHeader: '',
      showEditButton: true,
    };

    this.formFields = [
      createModalListField<IOInputItem>(
        'ioInputs',
        this.displayTitle(),
        this.ioInputItems(),
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          name: 'ioInputs',
          props: {
            modalConfig: this.ioInputModalConfig,
          },
        },
      ) as FormFieldConfig<unknown>,
    ];
  }

  protected override transformFormData(
    formData: Record<string, unknown>,
  ): Partial<DeviceTypeResponse> {
    // If ioInputs field exists and is an array, transform it to ioCapabilities object format
    if (
      isDefined(formData['ioInputs']) &&
      Array.isArray(formData['ioInputs'])
    ) {
      const ioInputsArray = formData['ioInputs'] as IOInputItem[];
      const entity = this.entity();
      const ioTypeKey = this.ioTypeKey();

      // Start with existing ioCapabilities to preserve other I/O types
      const ioCapabilities: Record<
        string,
        Record<string, Record<string, unknown>>
      > = isDefined(entity?.ioCapabilities) ? { ...entity.ioCapabilities } : {};

      // Initialize the current I/O type object if it doesn't exist
      if (!isDefined(ioCapabilities[ioTypeKey])) {
        ioCapabilities[ioTypeKey] = {};
      }

      // Create new object for current I/O type
      const currentIOType: Record<string, Record<string, unknown>> = {};

      // Transform each input item to the proper structure
      for (const item of ioInputsArray) {
        if (isDefined(item.name)) {
          const inputObject: Record<string, unknown> = {
            optional: item.optional,
          };

          // Add I/O type specific properties
          this.getCurrentIOConfig().addPropertiesToSaveObject(
            item,
            inputObject,
          );

          currentIOType[item.name] = inputObject;
        }
      }

      // Replace the current I/O type while preserving others
      ioCapabilities[ioTypeKey] = currentIOType;

      return {
        ioCapabilities: ioCapabilities as DeviceTypeResponse['ioCapabilities'],
      };
    }

    return formData as Partial<DeviceTypeResponse>;
  }

  private formatDisplayText(item: IOInputItem): string {
    const parts: string[] = [];

    // Add required/optional status
    parts.push(item.optional ? 'Optional' : 'Required');

    // Add I/O type specific properties
    this.getCurrentIOConfig().addPropertiesToDisplayText(item, parts);

    return parts.join(', ');
  }

  private addToggleProperty(item: IOInputItem, parts: string[]): void {
    if (item.canToggle !== undefined) {
      parts.push(`Toggle: ${item.canToggle ? 'Yes' : 'No'}`);
    }
  }

  private addPulseProperty(item: IOInputItem, parts: string[]): void {
    if (item.canPulse !== undefined) {
      parts.push(`Pulse: ${item.canPulse ? 'Yes' : 'No'}`);
    }
  }

  private addPulseDurationProperty(item: IOInputItem, parts: string[]): void {
    if (!this.shouldShowPulseDuration(item)) {
      return;
    }

    const min = item.minPulseSeconds ?? 0;
    const max = item.maxPulseSeconds ?? 0;

    if (min === max) {
      parts.push(`Duration: ${min}s`);
    } else {
      parts.push(`Duration: ${min}-${max}s`);
    }
  }

  private shouldShowPulseDuration(item: IOInputItem): boolean {
    return (
      item.canPulse === true &&
      (isDefined(item.minPulseSeconds) || isDefined(item.maxPulseSeconds))
    );
  }

  private getDefaultItem(): IOInputItem {
    const baseItem: IOInputItem = { name: '', optional: false };
    const defaults = this.getCurrentIOConfig().getDefaultValues();
    return { ...baseItem, ...defaults };
  }

  private getModalFields(item: IOInputItem | undefined): FormFieldConfig[] {
    const fields: FormFieldConfig[] = [
      createTextField('name', 'Input Name', item?.name ?? '', {
        placeholder: 'Enter input name...',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Input name is required',
          },
        ],
      }),
      createToggleSwitchField('optional', 'Optional', item?.optional ?? false, {
        hint: 'When enabled, this input is optional. When disabled, this input is required.',
        props: {
          layout: 'horizontal',
          labelWidth: '1/3',
          onLabel: 'Optional',
          offLabel: 'Required',
        },
      }),
    ];

    // Add I/O type specific fields
    fields.push(...this.getCurrentIOConfig().getModalFields(item));

    return fields;
  }

  protected override getUpdateMethod() {
    return (
      this.operationRegistry.getOperation('device-types', 'update') ??
      ((id, data) =>
        this.deviceTypesService.updateDeviceType(id, data).pipe(
          tap((response) => {
            this.entityChange.emit(response);
            this.broadcastService.broadcast('entity-updated', {
              entityType: 'device-types',
              operation: 'update',
              entityId: response.id,
              data: response,
            });
          }),
        ))
    );
  }

  ngOnInit(): void {
    this.updateFormFields();
  }
}
