import {
  Component,
  effect,
  inject,
  input,
  OnInit,
  ChangeDetectorRef,
} from '@angular/core';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { map, Observable } from 'rxjs';
import {
  EntitiesService,
  AssetResponse,
  AssetListItem,
} from '../../../../../api';
import {
  BaseEntity,
  createDropdownField,
  FormAction,
} from '../../../../../shared/models';
import {
  isDefined,
  isEmpty,
  isNullOrUndefined,
} from '../../../../../shared/utils';
import { EntitySectionBaseComponent } from '../../base';
import { UtilityService } from '../../../../../core/services/utility.service';
import { TableRowSelectEvent } from 'primeng/table';
import { DynamicFormModalComponent } from '../../../../../shared/components/forms/dynamic-form-modal/dynamic-form-modal.component';
import { FormGroup } from '@angular/forms';
import { TooltipModule } from 'primeng/tooltip';
import { DEFAULT_LIST_SIZE } from '../../../../../core/models';
import { RequestCacheService } from '../../../../../shared/services/request-cache.service';
import { ConfirmationService } from 'primeng/api';

@Component({
  selector: 'app-asset-list-section',
  imports: [GridComponent, DynamicFormModalComponent, TooltipModule],
  templateUrl: './asset-list-section.component.html',
  styleUrl: './asset-list-section.component.scss',
})
export class AssetListSectionComponent
  extends EntitySectionBaseComponent<BaseEntity>
  implements OnInit
{
  override readonly entity = input<BaseEntity | null>(null);
  readonly groupId = input<string | null>(null);
  readonly groupingEntityType = input<string>('Group');
  readonly assetLinkField = input.required<string>();
  readonly index = input<number | null>(null);
  readonly formatter = input<((value: unknown) => string) | null>(null);
  readonly getGroupingEntitiesFn =
    input.required<
      (ownerId: string) => Promise<{ id: string; name: string }[]>
    >();
  readonly getAvailableAssetsFn =
    input.required<
      (
        ownerId: string,
        currentEntityId?: string,
      ) => Promise<{ id: string; name: string }[]>
    >();

  readonly entitiesService = inject(EntitiesService);
  private readonly utilityService = inject(UtilityService);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly cacheService = inject(RequestCacheService);
  private readonly arrayLinkFields = ['categories', 'groups'];
  private readonly confirmationService = inject(ConfirmationService);

  private handleFormSubmit(form: FormGroup | undefined): void {
    switch (this.modalType) {
      case 'move':
        this.handleAssetMove(form);
        break;
      case 'add':
        this.handleAssetAdd(form);
        break;
      case 'delete':
        if (this.selectedAsset) {
          this.handleAssetDelete(this.selectedAsset);
        }
    }
  }

  private handleAssetMove(form: FormGroup | undefined): void {
    if (isNullOrUndefined(this.selectedAsset) || isNullOrUndefined(form))
      return;
    const currentAssetLinkField = this.assetLinkField();

    const assetId = this.selectedAsset.id;
    const selectedGroupId = form.get('group')?.value as string | undefined;

    if (isNullOrUndefined(selectedGroupId) || isEmpty(selectedGroupId)) return;

    const groupObject = this.groupingOptions.find(
      (g) => g.id === selectedGroupId,
    );

    if (groupObject) {
      if (this.arrayLinkFields.includes(currentAssetLinkField)) {
        this.entitiesService.getAsset(assetId).subscribe((asset) => {
          const existingItems =
            (asset[currentAssetLinkField] as
              | { id: string; name: string }[]
              | undefined) ?? [];
          const currentEntityId = this.entity()?.id;

          // Remove current entity's link and add the new one
          const newItems = existingItems.filter(
            (item) => item.id !== currentEntityId,
          );

          if (!newItems.some((item) => item?.id === groupObject.id)) {
            newItems.push(groupObject);
          }

          this.updateAsset(assetId, {
            [currentAssetLinkField]: newItems,
          } as Partial<AssetResponse>);
        });
      } else {
        const payload = { [currentAssetLinkField]: groupObject };
        this.updateAsset(assetId, payload as Partial<AssetResponse>);
      }
    } else {
      console.error('Selected group not found in grouping options list.');
    }
  }

  private handleAssetAdd(form: FormGroup | undefined): void {
    if (isNullOrUndefined(form)) return;
    const currentAssetLinkField = this.assetLinkField();

    const assetIdToAdd = form.get('asset')?.value as string | undefined;
    const currentEntity = this.entity();
    const currentEntityObject = currentEntity
      ? { id: currentEntity.id, name: currentEntity.name }
      : null;

    if (
      !currentEntityObject ||
      isNullOrUndefined(currentEntityObject.id) ||
      isEmpty(currentEntityObject.id) ||
      isNullOrUndefined(assetIdToAdd) ||
      isEmpty(assetIdToAdd)
    )
      return;

    if (this.arrayLinkFields.includes(currentAssetLinkField)) {
      this.entitiesService.getAsset(assetIdToAdd).subscribe((asset) => {
        const newEntity = currentEntityObject;
        const existingItems =
          (asset[currentAssetLinkField] as
            | ({ id: string; name?: string | null } | null)[]
            | undefined) ?? [];

        const index = this.index() ?? 0;

        const newItems = [...existingItems];
        while (newItems.length <= index) {
          newItems.push(null);
        }
        newItems[index] = newEntity;
        this.updateAsset(assetIdToAdd, {
          [currentAssetLinkField]: newItems,
        } as Partial<AssetResponse>);
      });
    } else {
      const payload = { [currentAssetLinkField]: currentEntityObject };
      this.updateAsset(assetIdToAdd, payload as Partial<AssetResponse>);
    }
  }

  private handleAssetDelete(asset: AssetListItem): void {
    if (isNullOrUndefined(asset) || isEmpty(asset.id)) return;

    const currentAssetLinkField = this.assetLinkField();
    const currentEntityId = this.entity()?.id;
    const index = this.index();

    if (isNullOrUndefined(currentEntityId) || isEmpty(currentEntityId)) return;

    if (this.arrayLinkFields.includes(currentAssetLinkField)) {
      this.entitiesService.getAsset(asset.id).subscribe((assetDetails) => {
        const existingItems =
          (assetDetails[currentAssetLinkField] as
            | ({ id: string; name: string } | null)[]
            | undefined) ?? [];

        const updatedItems = existingItems.map((item) =>
          item?.id === currentEntityId ? null : item,
        );

        // If the item wasn't found in the array but we have a specific index, set that index to null
        if (index !== null && index >= 0) {
          const itemFound = existingItems.some(
            (item) => item?.id === currentEntityId,
          );
          if (!itemFound) {
            // Ensure array is large enough
            while (updatedItems.length <= index) {
              updatedItems.push(null);
            }
            updatedItems[index] = null;
          }
        }

        this.updateAsset(asset.id, {
          [currentAssetLinkField]: updatedItems,
        } as Partial<AssetResponse>);
      });
    } else {
      this.updateAsset(asset.id, {
        [currentAssetLinkField]: null,
      } as Partial<AssetResponse>);
    }
  }

  override readonly actions: FormAction[] = [
    {
      label: 'Cancel',
      action: () => {
        this.onClose();
      },
      severity: 'secondary',
    },
    {
      label: 'Ok',
      action: (form) => {
        this.handleFormSubmit(form);
      },
      severity: 'primary',
      disabled: (form?: FormGroup) => isNullOrUndefined(form) || form.pristine,
    },
  ];
  assetList: AssetListItem[] = [];
  selectedAsset: AssetListItem | null = null;
  groupingOptions: { id: string; name: string }[] = [];
  availableAssetsForLinking: { id: string; name: string }[] = [];

  modalType: 'move' | 'add' | 'delete' | null = null;
  get isModalVisible(): boolean {
    return this.modalType !== null;
  }
  get modalHeader(): string {
    switch (this.modalType) {
      case 'move':
        return `Move To Different ${this.groupingEntityType()}`;
      case 'add':
        return 'Add Asset';
      case 'delete':
        return 'Delete Asset';
      default:
        return '';
    }
  }
  constructor() {
    super();

    effect(() => {
      const ownerId = this.entity()?.owner?.id;
      const currentGroupIdRqlPath: string | null = this.groupId();
      const currentEntityId = this.entity()?.id;

      if (
        isNullOrUndefined(ownerId) ||
        isNullOrUndefined(currentGroupIdRqlPath) ||
        isNullOrUndefined(currentEntityId)
      ) {
        this.assetList = [];
        this.cdr.markForCheck();
        return;
      }
      const filters = {
        state: {
          value: 'deleted',
          operator: '!=',
          wrap: true,
        },
        [currentGroupIdRqlPath]: {
          value: currentEntityId,
          operator: '=',
        },
      };
      const rqlFilter = this.utilityService.constructRqlFilter(filters);
      return this.cacheService
        .getOrFetch(
          `assets-${ownerId}-${rqlFilter}`,
          () => this.fetchAssets(ownerId, `${rqlFilter}/*`),
          3000,
        )
        .subscribe((assets) => {
          this.assetList = assets;
          this.cdr.markForCheck();
        });
    });
  }

  ngOnInit(): void {
    const owner = this.entity()?.owner?.id;
    const currentEntityId = this.entity()?.id;

    this.columns = [
      {
        field: 'name',
        header: 'Name',
        filter: true,
        filterType: 'text',
        filterMatchMode: 'contains',
        transform: 'text-ellipsis',
      },
      {
        field: this.assetLinkField(), // Placeholder, actual field on AssetListItem might vary
        header: this.groupingEntityType() ?? 'Group', // Dynamic header
        valueGetter: (rowData: unknown) => {
          const formatter = this.formatter();
          if (formatter) {
            return formatter(rowData);
          }

          const linkField = this.assetLinkField();
          const asset = rowData as AssetListItem;
          const index = this.index();

          if (
            isDefined(asset) &&
            typeof asset === 'object' &&
            Object.hasOwn(asset, linkField)
          ) {
            const linkedValue = asset[linkField];

            if (
              this.arrayLinkFields.includes(linkField) &&
              Array.isArray(linkedValue) &&
              index !== null
            ) {
              const linkedEntity = linkedValue[index] as { name?: string };
              return linkedEntity?.name ?? '';
            } else if (!this.arrayLinkFields.includes(linkField)) {
              const linkedEntity = linkedValue as { name?: string };
              return linkedEntity?.name ?? '';
            }
          }
          return '';
        },
        filter: true,
        filterType: 'multiselect',
        filterMatchMode: 'in',
        filterConfig: {
          type: 'multiselect',
          options: [], // These options could be dynamically populated based on groupingOptions
        },
      },
    ];

    if (typeof owner === 'string' && owner) {
      // Ensure owner is a non-empty string
      const groupEntitiesFn = this.getGroupingEntitiesFn();
      // groupEntitiesFn is a required input, so it should exist.
      groupEntitiesFn(owner)
        .then((groups) => {
          this.groupingOptions = groups;
          if (this.modalType === 'move') this.updateFormFields();
          this.cdr.markForCheck();
        })
        .catch((error) => {
          console.error(
            `Failed to load ${this.groupingEntityType()} groups:`,
            error,
          );
          this.groupingOptions = [];
          this.cdr.markForCheck();
        });

      const availableAssetsFn = this.getAvailableAssetsFn();
      availableAssetsFn(owner, currentEntityId)
        .then((assets) => {
          this.availableAssetsForLinking = assets;
          if (this.modalType === 'add') this.updateFormFields();
          this.cdr.markForCheck();
        })
        .catch((error) => {
          console.error('Failed to load available assets for linking:', error);
          this.availableAssetsForLinking = [];
          this.cdr.markForCheck();
        });
    } else {
      console.warn(
        'Owner ID is not available or invalid, cannot fetch grouping entities or available assets.',
      );
      this.groupingOptions = [];
      this.availableAssetsForLinking = [];
      this.cdr.markForCheck();
    }
  }

  fetchAssets(
    ownerId: string,
    filter: string | undefined,
  ): Observable<AssetListItem[]> {
    return this.entitiesService

      .listAssets(ownerId, 0, DEFAULT_LIST_SIZE, 'name', filter)
      .pipe(map((response) => response.items));
  }

  protected override updateFormFields(): void {
    const groupDropdownOptions = (this.groupingOptions ?? []).map((g) => ({
      label: g.name,
      value: g.id,
    }));

    const assetDropdownOptions = (this.availableAssetsForLinking ?? []).map(
      (a) => ({
        label: a.name,
        value: a.id,
      }),
    );

    switch (this.modalType) {
      case 'move':
        this.formFields = [
          createDropdownField(
            'group',
            `${this.groupingEntityType()}`,
            groupDropdownOptions,
            '',
            {
              layout: 'horizontal',
              labelWidth: '1/3',
              name: 'group',
              props: {
                appendTo: 'body',
              },
            },
          ),
        ];
        break;
      case 'add':
        this.formFields = [
          createDropdownField('asset', `Asset`, assetDropdownOptions, '', {
            layout: 'horizontal',
            labelWidth: '1/3',
            name: 'asset',
            props: {
              appendTo: 'body',
            },
          }),
        ];
        break;
      default:
        this.formFields = [];
        break;
    }
    this.cdr.markForCheck();
  }

  updateAsset(id: string, data: Partial<AssetResponse>) {
    this.entitiesService.updateAsset(id, data).subscribe({
      next: () => {
        this.broadcastService.broadcast('entity-updated', {
          id,
          changes: data,
          entityType: 'asset',
          parentId: this.entity()?.id,
          groupingType: this.groupingEntityType(),
        });
        this.modalType = null;

        const ownerId = this.entity()?.owner?.id;
        const currentGroupIdRqlPath: string | null = this.groupId();
        const currentEntityId = this.entity()?.id;

        if (
          typeof ownerId === 'string' &&
          ownerId &&
          typeof currentGroupIdRqlPath === 'string' &&
          currentGroupIdRqlPath &&
          typeof currentEntityId === 'string' &&
          currentEntityId
        ) {
          const filters = {
            state: { value: 'deleted', operator: '!=', wrap: true },
            [currentGroupIdRqlPath]: { value: currentEntityId, operator: '=' },
          };
          const rqlFilter = this.utilityService.constructRqlFilter(filters);
          this.fetchAssets(ownerId, `${rqlFilter}/*`).subscribe((assets) => {
            this.assetList = assets;
            this.cdr.markForCheck();
          });
        }

        const availableAssetsFn = this.getAvailableAssetsFn();
        if (typeof ownerId === 'string' && ownerId) {
          // Check ownerId is a valid string
          availableAssetsFn(ownerId, currentEntityId)
            .then((assets) => {
              this.availableAssetsForLinking = assets;
              this.cdr.markForCheck();
            })
            .catch((error) => {
              console.error(
                'Error refreshing available assets after update:',
                error,
              );
            });
        }
        this.cdr.markForCheck();
      },
      error: (err) => {
        console.error('Failed to update asset:', err);
        // Potentially show a user-facing error message
      },
    });
  }

  confirmAssetMove() {
    this.modalType = 'move';
    this.updateFormFields();
  }

  confirmAssetAdd() {
    this.modalType = 'add';
    this.updateFormFields();
  }

  confirmAssetDelete() {
    this.confirmationService.confirm({
      message: `Are you sure you want to delete the asset "${this.selectedAsset?.name}"?`,
      header: 'Confirm Delete',
      rejectButtonProps: {
        label: 'No',
        severity: 'secondary',
      },
      accept: () => {
        this.handleAssetDelete(this.selectedAsset as AssetListItem);
      },
    });
  }

  onClose() {
    this.modalType = null;
    this.selectedAsset = null;
    this.updateFormFields(); // Reset form fields by rebuilding them
  }

  handleRowSelect(event: TableRowSelectEvent): void {
    if (isEmpty(event) || isEmpty(event.data)) return;

    this.selectedAsset = event.data as AssetListItem;
  }

  handleRowUnselect(): void {
    this.selectedAsset = null;
  }
}
