import {
  ChangeDetectorRef,
  Component,
  computed,
  effect,
  inject,
  input,
  OnInit,
  signal,
  untracked,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { Validators } from '@angular/forms';

import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';

import {
  DeviceConfigProfileListItem,
  DeviceConfigProfileCreateRequest,
  EntitiesService,
  DeviceConfigProfileResponse,
  DeviceTypeListItem,
} from '../../../../../api';
import {
  createDropdownField,
  createTextField,
} from '../../../../../shared/models';
import { EntitySectionBaseComponent } from '../../base';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { CommonModule } from '@angular/common';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { Observable } from 'rxjs';

type DeviceConfigProfileEntity =
  | DeviceConfigProfileListItem
  | Partial<DeviceConfigProfileCreateRequest>;

@Component({
  selector: 'app-device-config-profile-details-section',
  imports: [
    CommonModule,
    ButtonModule,
    InputTextModule,
    GridComponent,
    DynamicFormComponent,
  ],
  templateUrl: './device-config-profile-details-section.component.html',
  styleUrl: './device-config-profile-details-section.component.scss',
})
export class DeviceConfigProfileDetailsSectionComponent
  extends EntitySectionBaseComponent<DeviceConfigProfileEntity>
  implements OnInit
{
  readonly ownerId = input<string | null>(null);
  readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);

  private readonly cdr = inject(ChangeDetectorRef);
  protected readonly selectOptionsLoaded = signal<boolean>(false);

  readonly deviceConfigProfileDetails = computed(() => {
    const deviceConfigProfileDetails = this.entity();
    if (!isDefined(deviceConfigProfileDetails)) return [];

    return [
      { key: 'Name', value: deviceConfigProfileDetails.name },
      {
        key: 'Device Type',
        value: deviceConfigProfileDetails.deviceType?.name ?? 'None',
      },
    ];
  });
  selectOptions: Record<
    string,
    { label: string; value: string; data: unknown }[]
  > = {};

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    effect(() => {
      const selectOptionsLoaded = this.selectOptionsLoaded();
      if (selectOptionsLoaded) {
        untracked(() => {
          this.updateFormFields();
          this.cdr.detectChanges();
        });
      }
    });

    effect(() => {
      const entity = this.entity();
      if (isDefined(entity) && !this.createMode()) {
        untracked(() => {
          this.updateFormFields();
        });
      }
    });
  }

  ngOnInit(): void {
    this.loadSelectOptions();
  }

  private loadSelectOptions(): void {
    const deviceConfigProfile = this.entity();
    const ownerIdValue = this.ownerId();

    const effectiveOwnerId = deviceConfigProfile?.owner?.id ?? ownerIdValue;

    if (isNullOrUndefined(effectiveOwnerId)) {
      this.selectOptions = {};
      this.selectOptionsLoaded.set(true);
      this.updateFormFields();
      return;
    }

    this.entitiesService
      .listDeviceTypes(effectiveOwnerId)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((result) => {
        this.selectOptions = {
          deviceTypes: result.items
            .filter(
              (deviceType): deviceType is DeviceTypeListItem =>
                deviceType.state === 'active' &&
                (!this.createMode() ||
                  (isDefined(deviceType.parent) &&
                    deviceType.owner.id === effectiveOwnerId)),
            )

            .map((item) => ({
              label: `${item?.parent?.name} - ${item?.name}`,
              value: item.id,
              data: item,
            }))
            .sort((a, b) => a.label.localeCompare(b.label)),
        };

        this.selectOptionsLoaded.set(true);
      });
  }

  protected override updateFormFields(): void {
    const deviceConfigProfile = this.entity();
    this.formFields = [
      createTextField('name', 'Name', deviceConfigProfile?.name, {
        name: 'name',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Name is required',
          },
        ],
      }),

      createDropdownField(
        'deviceType',
        'Device Type',
        this.selectOptions?.['deviceTypes'],
        deviceConfigProfile?.deviceType?.id,
        {
          name: 'deviceType',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Device Type is required',
            },
          ],
          disabled: !this.createMode(),
          props: {
            appendTo: 'body',
          },
        },
      ),
    ];
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<DeviceConfigProfileEntity>,
  ) => Observable<DeviceConfigProfileResponse> {
    return (
      this.operationRegistry.getOperation<
        DeviceConfigProfileEntity,
        DeviceConfigProfileCreateRequest,
        DeviceConfigProfileResponse,
        'update'
      >('device-config-profile', 'update') ??
      ((id, data) => this.entitiesService.updateDeviceConfigProfile(id, data))
    );
  }

  protected override onFormValueChange(
    formData: Record<string, string | number | object>,
  ): void {
    const transformedData = this.transformFormData(formData);

    this.formValueChange.emit(transformedData);
  }

  protected override transformFormData(
    formData: Record<string, string | number | object>,
  ) {
    const transformedData = { ...formData };
    if (isDefined(transformedData['deviceType'])) {
      const tagTypeId = transformedData['deviceType'] as string;
      const tagType = this.selectOptions?.['deviceTypes']?.find(
        (option) => option.value === tagTypeId,
      );
      if (tagType) {
        transformedData['deviceType'] = {
          id: tagTypeId,
          name: (tagType.data as DeviceTypeListItem)?.name,
        };
      }
    }
    return transformedData;
  }
}
