import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import {
  DistributorListItem,
  DistributorResponse,
  AccountsService,
} from '../../../../api';
import {
  DataGridParams,
  EntityDataAdapter,
  GridResponse,
} from '../../../../shared/models';
import { ActionIcon, EntityConfigAdapter, StateOption } from '../../models';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';
import { BroadcastEventMessageService } from '../broadcast-event-messages.service';

@Injectable({
  providedIn: 'root',
})
export class DistributorsDataAdapter
  implements
    EntityDataAdapter<DistributorListItem, DistributorResponse>,
    EntityConfigAdapter
{
  private readonly accountsService = inject(AccountsService);
  private readonly entityStateService = inject(EntityDetailsStateService);
  private readonly broadcastService = inject(BroadcastEventMessageService);

  /**
   * Retrieves a list of distributors
   * @param params Grid parameters (requires owner to be specified)
   * @returns Observable of distributor list response
   */
  getList(
    params: DataGridParams,
  ): Observable<GridResponse<DistributorListItem>> {
    const { owner, limit, offset, sortField, filters, counts } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.accountsService.listDistributors(
      owner,
      offset,
      limit,
      sortField as string,
      filters,
      counts,
    );
  }

  getById(id: string): Observable<DistributorResponse> {
    return this.accountsService.getDistributor(id);
  }

  getStateOptions(): StateOption[] {
    return [
      {
        label: 'Active',
        value: DistributorResponse.StateEnum.Active,
      },
      {
        label: 'Deleted',
        value: DistributorResponse.StateEnum.Deleted,
      },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.accountsService.updateDistributor(id, {
      state: state as DistributorResponse.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    return [
      {
        id: 'viewInTree',
        icon: 'pi pi-sitemap',
        tooltip: 'View this distributor in the Navigation Tree',
        visible: true,
        action: () => {
          const entity = this.entityStateService.selectedItem();
          if (entity) {
            this.broadcastService.broadcast('view-entity-in-tree', {
              entity,
            });
          }
        },
      },
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change Distributor State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
