import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import {
  DeviceListItem,
  DeviceResponse,
  EntitiesService,
} from '../../../../api';
import {
  DataGridParams,
  GridDataService,
  GridResponse,
} from '../../../../shared/models';

import { ActionIcon, StateOption } from '../../models';
import { EntityConfigAdapter } from '../../models/entity-config-adapter.interface';
import { AuthDataService } from '../../../../core/services/auth-data.service';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';

@Injectable({
  providedIn: 'root',
})
export class DevicesDataAdapter
  implements GridDataService<DeviceListItem>, EntityConfigAdapter
{
  private readonly entitiesService = inject(EntitiesService);
  private readonly authDataService = inject(AuthDataService);
  private readonly entityStateService = inject(EntityDetailsStateService);

  getList(params: DataGridParams): Observable<GridResponse<DeviceListItem>> {
    const { owner, limit, offset, sortField, filters } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.entitiesService.listDevices(
      owner,
      offset,
      limit,
      sortField as string,
      filters,
    );
  }

  getById(id: string): Observable<DeviceResponse> {
    return this.entitiesService.getDevice(id);
  }

  getStateOptions(): StateOption[] {
    return [
      { label: 'Active', value: DeviceResponse.StateEnum.Active },
      { label: 'Inactive', value: DeviceResponse.StateEnum.Inactive },
      { label: 'Suspended', value: DeviceResponse.StateEnum.Suspended },
      { label: 'Deleted', value: DeviceResponse.StateEnum.Deleted },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.entitiesService.updateDevice(id, {
      state: state as DeviceResponse.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    if (!this.authDataService.hasPermission('devices:create')) {
      return [];
    }

    return [
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change Device State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
