import {
  AfterViewInit,
  Component,
  computed,
  effect,
  inject,
  input,
  signal,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import { Validators } from '@angular/forms';
import { CommonModule } from '@angular/common';

import { v4 as uuidv4 } from 'uuid';
import { Observable } from 'rxjs';

import { Contact, EntitiesService } from '../../../../../api';
import { UtilityService } from '../../../../../core/services/utility.service';
import {
  createTextField,
  createDropdownField,
  FormFieldConfig,
  createModalListField,
  ListFieldModalConfig,
  BaseEntity,
} from '../../../../../shared/models';
import {
  isDefined,
  mobileValidator,
  phoneValidator,
} from '../../../../../shared/utils';
import { EntitySectionBaseComponent } from '../../base';
import { ModalListFormComponent } from '../../../../../shared/components/forms/modal-list-form/modal-list-form.component';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';

interface Entity extends BaseEntity {
  contacts?: Contact[];
}
@Component({
  selector: 'app-contacts-section',
  imports: [CommonModule, GridComponent, DynamicFormComponent],
  templateUrl: './contacts-section.component.html',
  styleUrl: './contacts-section.component.scss',
})
export class ContactsSectionComponent
  extends EntitySectionBaseComponent<Entity>
  implements AfterViewInit
{
  entityId = input.required<string>();
  @ViewChild('iconTemplate', { static: true })
  iconTemplate: TemplateRef<unknown> | undefined;
  private readonly entitiesService = inject(EntitiesService);
  readonly utilityService = inject(UtilityService);
  protected readonly selectOptionsLoaded = signal<boolean>(false);

  readonly contactDetails = computed(() => {
    const contacts = this.entity()?.contacts;
    if (!isDefined(contacts)) return [];

    return contacts.flatMap((contact) => {
      const details = [];
      if (isDefined(contact.name)) details.push(`Name: ${contact.name}`);
      if (isDefined(contact.emailAddress))
        details.push(`Email: ${contact.emailAddress}`);
      if (isDefined(contact.phoneMobile))
        details.push(`Mobile: ${contact.phoneMobile}`);
      if (isDefined(contact.phoneOffice))
        details.push(`Office: ${contact.phoneOffice}`);
      if (isDefined(contact.notes)) details.push(`Notes: ${contact.notes}`);

      const additionalInfo =
        details.length > 0 ? `\n${details.join('\n')}` : '';
      return [{ key: '', value: additionalInfo }];
    });
  });

  contacts: Contact[] = [];
  showModal = false;
  contactTypes = [
    'owner',
    'emergency',
    'administrative',
    'technical',
    'operational',
    'management',
  ];

  constructor() {
    super();

    // React to entity changes
    effect(() => {
      const contacts = this.entity()?.contacts;
      if (contacts) {
        this.contacts = contacts;
        this.updateFormFields();
      }
    });
  }

  override ngAfterViewInit(): void {
    super.ngAfterViewInit();
    this.columns = [
      {
        field: 'key',
        header: '',
        transform: 'uppercase',
        cellTemplate: this.iconTemplate,
      },
      { field: 'value', header: '', transform: 'whitespace-pre-line' },
    ];
  }

  protected override updateFormFields(): void {
    const contactsModalConfig: ListFieldModalConfig<Contact> = {
      modalComponent: ModalListFormComponent,
      modalTitle: 'Contact Details',
      defaultItemProvider: () => ({
        id: uuidv4(),
        name: '',
        type: 'owner',
        emailAddress: '',
        phoneMobile: '',
        phoneOffice: '',
        notes: '',
      }),
      listItemFormatter: (item: Contact) => {
        const details = [];
        if (isDefined(item.name)) details.push(`Name: ${item.name}`);
        if (isDefined(item.emailAddress))
          details.push(`Email: ${item.emailAddress}`);
        if (isDefined(item.phoneMobile))
          details.push(`Mobile: ${item.phoneMobile}`);
        if (isDefined(item.phoneOffice))
          details.push(`Office: ${item.phoneOffice}`);
        if (isDefined(item.notes)) details.push(`Notes: ${item.notes}`);

        const additionalInfo =
          details.length > 0 ? `\n${details.join('\n')}` : '';

        return {
          id: item.id,
          display: `${this.utilityService.toTitleCase(item.type)}: ${item.name}${additionalInfo}`,
          icon: 'pi pi-id-card',
        };
      },
      modalFields: (_item: Contact | undefined): FormFieldConfig[] => {
        return [
          createTextField('id', 'ID', _item?.id, {
            name: 'id',
            hidden: true,
          }),
          createTextField('name', 'Name', '', {
            name: 'name',
            validations: [
              {
                name: 'required',
                validator: Validators.required,
                message: 'Name is required',
              },
            ],
          }),
          createDropdownField(
            'type',
            'Type',
            this.contactTypes.map((type) => ({
              label: this.utilityService.toTitleCase(type),
              value: type,
            })),
            '',
            {
              name: 'type',
              props: {
                appendTo: 'body',
              },
            },
          ),
          createTextField('emailAddress', 'Email', '', {
            name: 'emailAddress',
            validations: [
              {
                name: 'email',
                validator: Validators.email,
                message: 'Invalid email format',
              },
            ],
          }),
          createTextField('phoneMobile', 'Mobile', '', {
            name: 'phoneMobile',
            validations: [
              {
                name: 'mobile',
                validator: mobileValidator(),
                message: 'Invalid mobile number (10-15 digits required)',
              },
            ],
          }),
          createTextField('phoneOffice', 'Phone', '', {
            name: 'phoneOffice',
            validations: [
              {
                name: 'phone',
                validator: phoneValidator(),
                message: 'Invalid phone number (7-15 digits required)',
              },
            ],
          }),
          createTextField('notes', 'Notes', '', {
            name: 'notes',
          }),
        ];
      },
      subHeader: 'Complete the following fields describing the contact.',
    };

    this.formFields = [
      createModalListField<Contact>('contacts', 'Contacts', this.contacts, {
        name: 'contacts',
        props: {
          modalConfig: contactsModalConfig,
        },
      }) as FormFieldConfig<unknown>,
    ];
  }

  override getUpdateMethod(): (
    id: string,
    payload: Partial<Entity>,
  ) => Observable<unknown> {
    if (this.entityType() === 'cost-centre') {
      return (id, payload) =>
        this.entitiesService.updateCostCentre(id, payload);
    }

    throw new Error(`Update for '${this.entityType()}' not yet supported`);
  }

  protected override onFormValueChange(
    formData: Record<string, string | number | object>,
  ): void {
    // Emit the transformed values
    this.formValueChange.emit({
      contacts: formData,
    });
  }

  onClose() {
    this.showModal = false;
  }
}
