import { CommonModule } from '@angular/common';
import { Component, computed, effect, inject, input } from '@angular/core';
import { Validators } from '@angular/forms';
import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';

import { EntitySectionBaseComponent } from '../../../components/base/entity-section-base';
import {
  IoTypeCreateRequest,
  IoTypeListItem,
  EntitiesService,
} from '../../../../../api';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import {
  createDropdownField,
  createNumberField,
  createTextAreaField,
  createTextField,
} from '../../../../../shared/models';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { UtilityService } from '../../../../../core/services/utility.service';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { ApiSchemaService } from '../../../../../core/services/api-schema.service';

export type PropertyValueType = string | number | object;

@Component({
  selector: 'app-io-type-details-section',
  imports: [
    CommonModule,
    ButtonModule,
    InputTextModule,
    GridComponent,
    DynamicFormComponent,
    FontAwesomeModule,
  ],
  templateUrl: './io-type-details-section.component.html',
  styleUrl: './io-type-details-section.component.scss',
})
export class IoTypeDetailsSectionComponent extends EntitySectionBaseComponent<
  IoTypeListItem | Partial<IoTypeCreateRequest>
> {
  readonly ownerId = input<string | null>(null);
  private readonly entitiesService = inject(EntitiesService);
  private readonly utilityService = inject(UtilityService);
  private readonly apiSchemaService = inject(ApiSchemaService);

  readonly typeOptions = [
    { key: 'digital_input', label: 'Digital Input' },
    { key: 'digital_output', label: 'Digital Output' },
    { key: 'analog_input', label: 'Analog Input' },
    { key: 'temperature_input', label: 'Temperature Input' },
    {
      key: 'can_input',
      label: 'CAN Input',
    },
    {
      key: 'counter_input',
      label: 'Counter Input',
    },
    { key: 'camera_input', label: 'Camera Input' },
    { key: 'value_input', label: 'Value Input' },
  ];
  readonly smoothingOptions = [
    { label: 'None', value: 'none' },
    { label: 'Moving Average (5 samples)', value: 'moving_average_5' },
    { label: 'Moving Average (10 samples)', value: 'moving_average_10' },
    { label: 'Moving Average (20 samples)', value: 'moving_average_20' },
  ];
  readonly rateTypeOptions = [
    { value: 'none', label: 'None' },
    { value: 'val_per_km', label: 'Value Per Kilometer' },
    { value: 'km_per_val', label: 'Kilometer Per Value' },
    { value: 'val_per_mile', label: 'Value Per Mile' },
    { value: 'mile_per_val', label: 'Miles Per Value' },
    { value: 'val_per_hour', label: 'Value Per Hour' },
    { value: 'hour_per_val', label: 'Hours Per Value' },
  ];

  readonly ioTypeDetails = computed(() => {
    const ioTypeDetails = this.entity();
    if (!isDefined(ioTypeDetails)) return [];

    // Get the type using ApiSchemaService for consistency
    const typeValue = this.apiSchemaService.getEntityType<
      Record<string, unknown>,
      IoTypeListItem.TypeEnum
    >(ioTypeDetails as Record<string, unknown>, 'Iotype');

    const result: Partial<IoTypeListItem> = {
      name: ioTypeDetails.name,
      type: typeValue,
      active: ioTypeDetails.text ? (ioTypeDetails.text.active ?? null) : null,
      inactive: ioTypeDetails.text
        ? (ioTypeDetails.text.inactive ?? null)
        : null,
      smoothingType: ioTypeDetails.smoothingType,
      unit: ioTypeDetails.unit,
      rateUnit: ioTypeDetails.rate?.unit,
      rateMultiplier: ioTypeDetails.rate?.multiplier,
      rateType: ioTypeDetails.rate?.type,
      lookups: ioTypeDetails.lookups,
    };
    for (const key of Object.keys(result)) {
      if (result[key] === undefined || result[key] === null) {
        delete result[key];
      }
    }

    return Object.keys(result).map((key) => {
      if (key === 'rateType') {
        return {
          key: this.utilityService.decamelize(key),
          value: this.utilityService.decamelize(result[key]),
        };
      }
      if (key === 'type') {
        return {
          key: this.utilityService.decamelize(key),
          value: this.typeOptions.find((type) => type.key === result[key])
            ?.label,
        };
      }
      if (key === 'smoothingType') {
        const typeValue = this.apiSchemaService.getEntityType<
          Record<string, unknown>,
          IoTypeListItem.TypeEnum
        >(result as Record<string, unknown>, 'IoTypeListItem');
        if (
          typeValue === IoTypeListItem.TypeEnum.AnalogInput ||
          typeValue === IoTypeListItem.TypeEnum.TemperatureInput ||
          typeValue === IoTypeListItem.TypeEnum.CanInput
        ) {
          return {
            key: 'Filter',
            value: this.smoothingOptions.find(
              (smoothing) => smoothing.value === result[key],
            )?.label,
          };
        }
        return;
      }
      return {
        key: this.utilityService.decamelize(key),
        value: result[key],
      };
    });
  });

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '', transform: 'capitalize' },
    ];

    effect(() => {
      const ioTypeDetails = this.ioTypeDetails();
      if (ioTypeDetails.length > 0 && !this.createMode()) {
        this.updateFormFields();
      }
    });
  }

  protected override updateFormFields(): void {
    const ioType = this.entity();

    // Get the type using ApiSchemaService for consistency
    const typeValue = ioType
      ? this.apiSchemaService.getEntityType<
          Record<string, unknown>,
          IoTypeListItem.TypeEnum
        >(ioType as Record<string, unknown>, 'Iotype')
      : null;

    this.formFields = [
      createTextField('name', 'Name', ioType?.name, {
        name: 'name',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Name is required',
          },
        ],
      }),
      createDropdownField(
        'type',
        'Type',
        this.typeOptions.map((option) => ({
          label: option.label,
          value: option.key,
        })),
        typeValue,
        {
          name: 'type',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Type is required',
            },
          ],
          conditional: true,
          conditions: {
            digital_input: [
              createTextField('active', 'Active Text', ioType?.text?.active, {
                name: 'active',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Active Text is required',
                  },
                ],
              }),
              createTextField(
                'inactive',
                'Inactive Text',
                ioType?.text?.inactive,
                {
                  name: 'inactive',
                  validations: [
                    {
                      name: 'required',
                      validator: Validators.required,
                      message: 'Inactive Text is required',
                    },
                  ],
                },
              ),
            ],
            digital_output: [
              createTextField('active', 'Active Text', ioType?.text?.active, {
                name: 'active',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Active Text is required',
                  },
                ],
              }),
              createTextField(
                'inactive',
                'Inactive Text',
                ioType?.text?.inactive,
                {
                  name: 'inactive',
                  validations: [
                    {
                      name: 'required',
                      validator: Validators.required,
                      message: 'Inactive Text is required',
                    },
                  ],
                },
              ),
            ],
            analog_input: [
              createTextField('unit', 'Unit', ioType?.unit, {
                name: 'unit',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Unit is required',
                  },
                ],
              }),
              createDropdownField(
                'smoothingType',
                'Smoothing Type',
                this.smoothingOptions,
                ioType?.smoothingType,
                {
                  name: 'smoothingType',
                  validations: [
                    {
                      name: 'required',
                      validator: Validators.required,
                      message: 'Smoothing Type is required',
                    },
                  ],
                  props: {
                    appendTo: 'body',
                  },
                },
              ),
            ],
            temperature_input: [
              createDropdownField(
                'smoothingType',
                'Smoothing Type',
                this.smoothingOptions,
                ioType?.smoothingType,
                {
                  name: 'smoothingType',
                  validations: [
                    {
                      name: 'required',
                      validator: Validators.required,
                      message: 'Smoothing Type is required',
                    },
                  ],
                  props: {
                    appendTo: 'body',
                  },
                },
              ),
            ],
            can_input: [
              createTextField('active', 'Active Text', ioType?.text?.active, {
                name: 'active',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Active Text is required',
                  },
                ],
              }),
              createTextField(
                'inactive',
                'Inactive Text',
                ioType?.text?.inactive,
                {
                  name: 'inactive',
                  validations: [
                    {
                      name: 'required',
                      validator: Validators.required,
                      message: 'Inactive Text is required',
                    },
                  ],
                },
              ),
              createTextField('unit', 'Unit', ioType?.unit, {
                name: 'unit',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Unit is required',
                  },
                ],
              }),
              createDropdownField(
                'smoothingType',
                'Smoothing Type',
                this.smoothingOptions,
                ioType?.smoothingType,
                {
                  name: 'smoothingType',
                  validations: [
                    {
                      name: 'required',
                      validator: Validators.required,
                      message: 'Smoothing Type is required',
                    },
                  ],
                  props: {
                    appendTo: 'body',
                  },
                },
              ),
            ],
            counter_input: [
              createTextField('unit', 'Unit', ioType?.unit, {
                name: 'unit',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Unit is required',
                  },
                ],
              }),

              createDropdownField(
                'rateType',
                'Rate Type',
                this.rateTypeOptions,
                ioType?.rate?.type,
                {
                  name: 'rateType',
                  validations: [
                    {
                      name: 'required',
                      validator: Validators.required,
                      message: 'Rate Type is required',
                    },
                  ],
                  props: {
                    appendTo: 'body',
                  },
                },
              ),
              createNumberField(
                'rateMultiplier',
                'Rate Multiplier',
                ioType?.rate?.multiplier ?? undefined,
                {
                  name: 'rateMultiplier',
                  validations: [
                    {
                      name: 'required',
                      validator: Validators.required,
                      message: 'Rate Multiplier is required',
                    },
                  ],
                },
              ),
              createTextField('rateUnit', 'Rate Unit', ioType?.rate?.unit, {
                name: 'rateUnit',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Unit is required',
                  },
                ],
              }),
            ],
            camera_input: [],
            value_input: [
              createTextAreaField('lookups', 'Lookups', '', {
                name: 'lookups',
                validations: [
                  {
                    name: 'required',
                    validator: Validators.required,
                    message: 'Lookups are required',
                  },
                ],
                hint: 'Paste values in CSV format, i.e. "key","value"',
              }),
            ],
          },
          props: {
            appendTo: 'body',
          },
          disabled: !this.createMode(),
        },
      ),
    ];
  }

  protected override submitChanges(
    formData: Record<string, string | number>,
  ): void {
    const ioType = this.entity();
    if (isNullOrUndefined(ioType)) return;

    const transformedData = this.transformFormData(formData);
    this.entitiesService
      .updateIoType(ioType.id, transformedData)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe({
        next: (response) => {
          this.isFormEditable.set(false);
          this.isSubmitting.set(false);
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'iotype',
            operation: 'update',
            entityId: ioType.id,
            data: response,
          });
        },
        error: () => {
          this.isFormEditable.set(true);
          this.isSubmitting.set(false);
        },
      });
  }

  private processTextProperty(
    data: Record<string, PropertyValueType>,
    property: string,
    textKey: string,
  ): void {
    if (!isDefined(data[property])) return;

    if (isNullOrUndefined(data['text'])) {
      data['text'] = {};
    }

    if (typeof data[property] === 'string') {
      (data['text'] as Record<string, string>)[textKey] = data[property].trim();
    }
    delete data[property];
  }

  private processRateProperty(
    data: Record<string, PropertyValueType>,
    property: string,
    rateKey: string,
    isNumeric = false,
  ): void {
    if (!isDefined(data[property])) return;

    if (isNullOrUndefined(data['rate'])) {
      data['rate'] = {};
    }

    const isCorrectType = isNumeric
      ? typeof data[property] === 'number'
      : typeof data[property] === 'string';

    if (isCorrectType) {
      const value = isNumeric
        ? data[property]
        : (data[property] as string).trim();
      (data['rate'] as Record<string, PropertyValueType>)[rateKey] = value;
    }
    delete data[property];
  }

  protected override onFormValueChange(
    formData: Record<string, PropertyValueType>,
  ): void {
    const transformedData = this.transformFormData(formData);

    // Emit the transformed data for further processing
    this.formValueChange.emit(transformedData);
  }

  protected override transformFormData(
    formData: Record<string, PropertyValueType>,
  ) {
    const transformedData = { ...formData };

    // Handle type property using ApiSchemaService for consistency
    if (typeof transformedData['type'] === 'string') {
      this.apiSchemaService.setEntityType<
        Record<string, unknown>,
        IoTypeListItem.TypeEnum
      >(
        transformedData,
        'IoType',
        transformedData['type'] as IoTypeListItem.TypeEnum,
      );

      // Remove the original 'type' property if it differs from the target property name
      const typePropertyName =
        this.apiSchemaService.getTypePropertyKey('iotype');
      if (typePropertyName !== 'type' && 'type' in transformedData) {
        delete transformedData['type'];
      }
    }

    // Process text properties
    this.processTextProperty(transformedData, 'active', 'active');
    this.processTextProperty(transformedData, 'inactive', 'inactive');

    // Process rate properties
    this.processRateProperty(transformedData, 'rateType', 'type');
    this.processRateProperty(
      transformedData,
      'rateMultiplier',
      'multiplier',
      true,
    );
    this.processRateProperty(transformedData, 'rateUnit', 'unit');
    return transformedData;
  }
}
