import { Component, computed, effect, inject, input } from '@angular/core';
import { Validators } from '@angular/forms';

import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';

import {
  CompanyGroupCreateRequest,
  EntitiesService,
  CompanyGroupResponse,
  AccountsService,
  CompanyGroupListResponse,
} from '../../../../../api';
import {
  AssetGroupingItem,
  createDropdownField,
  createTextField,
} from '../../../../../shared/models';
import { EntitySectionBaseComponent } from '../../base';
import { isDefined } from '../../../../../shared/utils';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { CommonModule } from '@angular/common';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { firstValueFrom, Observable } from 'rxjs';
import { faFolder } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { AssetGroupingService } from '../../../../../shared/services/asset-grouping.service';
import { DEFAULT_SYSTEM_ID } from '../../../../../core/models';

type CompanyGroupEntity =
  | CompanyGroupResponse
  | Partial<CompanyGroupCreateRequest>;

@Component({
  selector: 'app-company-group-details-section',
  imports: [
    CommonModule,
    ButtonModule,
    InputTextModule,
    GridComponent,
    DynamicFormComponent,
    FontAwesomeModule,
  ],
  templateUrl: './company-group-details-section.component.html',
  styleUrl: './company-group-details-section.component.scss',
})
export class CompanyGroupDetailsSectionComponent extends EntitySectionBaseComponent<CompanyGroupEntity> {
  readonly ownerId = input<string | null>(null);
  readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly accountsService = inject(AccountsService);

  readonly companyGroupDetails = computed(() => {
    const companyGroupDetails = this.entity();
    if (!isDefined(companyGroupDetails)) return [];

    return [
      { key: 'Parent', value: companyGroupDetails.parent?.name ?? 'Root' },
      { key: 'Name', value: companyGroupDetails.name },
    ];
  });

  faFolder = faFolder;
  companyGroupGroups: AssetGroupingItem[] = [];

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    effect(() => {
      const ownerId = this.entity()?.owner?.id ?? this.ownerId();
      if (isDefined(ownerId)) {
        this.fetchCompanyGroups(ownerId)
          .then((groups) => {
            this.companyGroupGroups = AssetGroupingService.toAssetGroupingItems(
              groups.items,
            );
            this.updateFormFields();
          })
          .catch(() => {});
      }
    });
  }

  private fetchCompanyGroups(
    ownerId: string,
  ): Promise<CompanyGroupListResponse> {
    return firstValueFrom(this.accountsService.listCompanyGroups(ownerId));
  }

  protected override updateFormFields(): void {
    const companyGroup = this.entity();
    const parentOptions = [
      { label: 'Root', value: DEFAULT_SYSTEM_ID },
      ...this.companyGroupGroups.map((group) => ({
        label: group.name,
        value: group.id,
      })),
    ];
    this.formFields = [
      createDropdownField(
        'parent',
        'Parent',
        parentOptions,
        companyGroup?.parent?.id ?? DEFAULT_SYSTEM_ID,
        {
          placeholder: 'Select Parent Group',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Parent group is required',
            },
          ],
          props: {
            appendTo: 'body',
          },
        },
      ),
      createTextField('name', 'Name', companyGroup?.name, {
        name: 'name',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Name is required',
          },
        ],
      }),
    ];
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<CompanyGroupEntity>,
  ) => Observable<CompanyGroupResponse> {
    return (
      this.operationRegistry.getOperation<
        CompanyGroupEntity,
        CompanyGroupCreateRequest,
        CompanyGroupResponse,
        'update'
      >('company-group', 'update') ??
      ((id, data) => this.accountsService.updateCompanyGroup(id, data))
    );
  }

  protected override onFormValueChange(
    formData: Record<string, string | number | object | null>,
  ): void {
    const transformedData = this.transformFormData(formData);

    this.formValueChange.emit(transformedData);
  }

  protected override transformFormData(
    formData: Record<string, string | number | object | null>,
  ) {
    const transformedData = { ...formData };
    if (isDefined(transformedData['parent'])) {
      const parentId = transformedData['parent'] as string;
      const parent = this.companyGroupGroups?.find(
        (option) => option.id === parentId,
      );
      if (parent) {
        transformedData['parent'] = {
          id: parent.id,
          name: parent.name,
        };
      } else if (parentId === DEFAULT_SYSTEM_ID) {
        transformedData['parent'] = null; // Set to null for root
      }
    }
    return transformedData;
  }
}
