import { isDefined } from '../../../shared/utils';

export type FormFieldMapping<T> = Record<
  string,
  {
    requestProperty: keyof T;
    transform?: (value: unknown) => unknown;
    validate?: (value: unknown) => boolean;
    required?: boolean;
  }
>;

export class FormToRequestMapper<TRequest> {
  constructor(private readonly mapping: FormFieldMapping<TRequest>) {}

  transform(formData: Record<string, unknown>): Partial<TRequest> {
    const result: Partial<TRequest> = {};

    for (const [formField, config] of Object.entries(this.mapping)) {
      const formValue = formData[formField];

      // Skip if value is undefined and not required
      if (!isDefined(formValue) && config.required !== true) {
        continue;
      }

      // Validate if validator provided
      if (config.validate && !config.validate(formValue)) {
        throw new Error(`Validation failed for field: ${formField}`);
      }

      // Transform value if transformer provided
      const finalValue = config.transform
        ? config.transform(formValue)
        : formValue;

      result[config.requestProperty] = finalValue as TRequest[keyof TRequest];
    }

    return result;
  }
}

export class RequestValidator {
  static validate<T>(
    data: unknown,
    schema: Record<keyof T, (value: unknown) => boolean>,
  ): data is T {
    if (data === null || data === undefined || typeof data !== 'object') {
      return false;
    }

    const obj = data as Record<string, unknown>;

    for (const [key, validator] of Object.entries(schema)) {
      const validatorFn = validator as (value: unknown) => boolean;
      if (!validatorFn(obj[key])) {
        console.error(`Validation failed for property: ${key}`);
        return false;
      }
    }

    return true;
  }
}
