import { Injectable, inject } from '@angular/core';
import { Observable, of } from 'rxjs';

import { isNullOrUndefined } from '../../../../shared/utils';
import { AlertResponse, EntitiesService } from '../../../../api';
import {
  DataGridParams,
  GridDataService,
  GridResponse,
} from '../../../../shared/models';
import { AuthDataService } from '../../../../core/services/auth-data.service';
import { ActionIcon, EntityConfigAdapter, StateOption } from '../../models';
import { EntityDetailsStateService } from '../entity/entity-details.state.service';
import {
  ExtendedAlertListItem,
  ExtendedAlertResponse,
} from '../../models/alerts.model';

@Injectable({
  providedIn: 'root',
})
export class AlertsDataAdapter
  implements GridDataService<ExtendedAlertListItem>, EntityConfigAdapter
{
  private readonly entitiesService = inject(EntitiesService);
  private readonly authDataService = inject(AuthDataService);
  private readonly entityStateService = inject(EntityDetailsStateService);

  getList(
    params: DataGridParams,
  ): Observable<GridResponse<ExtendedAlertListItem>> {
    const { owner, limit, offset, sortField, filters } = params;

    if (isNullOrUndefined(owner)) {
      return of({ items: [], count: 0 });
    }

    return this.entitiesService.listAlerts(
      owner,
      offset,
      limit,
      sortField as string,
      filters,
    ) as Observable<GridResponse<ExtendedAlertListItem>>;
  }

  getById(id: string): Observable<ExtendedAlertResponse> {
    return this.entitiesService.getAlert(
      id,
    ) as Observable<ExtendedAlertResponse>;
  }

  getStateOptions(): StateOption[] {
    return [
      {
        label: 'Active',
        value: AlertResponse.StateEnum.Active,
      },
      {
        label: 'Deleted',
        value: AlertResponse.StateEnum.Deleted,
      },
    ];
  }

  updateState(id: string, state: unknown): Observable<unknown> {
    return this.entitiesService.updateAlert(id, {
      state: state as AlertResponse.StateEnum,
    });
  }

  getActionIcons(_entityId: string): ActionIcon[] {
    if (!this.authDataService.hasPermission('alerts.create')) {
      return [];
    }
    return [
      {
        id: 'changeState',
        icon: 'pi pi-trash',
        tooltip: 'Change Alert State',
        visible: true,
        showPopover: true,
        popoverType: 'state',
        action: () => {
          this.entityStateService.togglePopover('changeState');
        },
      },
    ];
  }
}
