import { CommonModule } from '@angular/common';
import {
  Component,
  computed,
  effect,
  inject,
  input,
  untracked,
} from '@angular/core';
import { Validators } from '@angular/forms';
import { ButtonModule } from 'primeng/button';

import { EntitySectionBaseComponent } from '../../../components/base/entity-section-base';
import {
  SimCardCreateRequest,
  SimCardListItem,
  EntitiesService,
} from '../../../../../api';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { createTextField } from '../../../../../shared/models';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';

@Component({
  selector: 'app-sim-card-details-section',
  imports: [CommonModule, ButtonModule, GridComponent, DynamicFormComponent],
  templateUrl: './sim-card-details-section.component.html',
  styleUrl: './sim-card-details-section.component.scss',
})
export class SimCardDetailsSectionComponent extends EntitySectionBaseComponent<
  SimCardListItem | Partial<SimCardCreateRequest>
> {
  readonly ownerId = input<string | null>(null);
  private readonly entitiesService = inject(EntitiesService);

  readonly simCardDetails = computed(() => {
    const simCardDetails = this.entity();
    if (!isDefined(simCardDetails)) return [];

    return [
      { key: 'Serial Number', value: simCardDetails.name },
      { key: 'Network', value: simCardDetails.networkName },
      { key: 'Number', value: simCardDetails.number },
      { key: 'Description', value: simCardDetails.description },
    ];
  });

  constructor() {
    super();

    // Setup initial columns
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      { field: 'value', header: '' },
    ];

    effect(() => {
      const entity = this.entity();
      if (isDefined(entity) && !this.createMode()) {
        untracked(() => {
          this.updateFormFields();
        });
      }
    });
  }

  protected override updateFormFields(): void {
    const simCard = this.entity();
    this.formFields = [
      createTextField('name', 'Serial Number', simCard?.name, {
        name: 'name',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Serial Number is required',
          },
        ],
      }),
      createTextField('networkName', 'Network', simCard?.networkName, {
        name: 'networkName',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Network is required',
          },
        ],
      }),
      createTextField('number', 'Number', simCard?.number, {
        name: 'number',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Number is required',
          },
        ],
      }),
      createTextField('description', 'Description', simCard?.description, {
        name: 'description',
        validations: [],
      }),
    ];
  }

  protected override submitChanges(
    formData: Record<string, string | number>,
  ): void {
    const simCard = this.entity();
    if (isNullOrUndefined(simCard)) return;
    this.entitiesService
      .updateSimCard(simCard.id, formData)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe({
        next: (response) => {
          this.isFormEditable.set(false);
          this.broadcastService.broadcast('entity-updated', {
            entityType: 'simcard',
            operation: 'update',
            entityId: simCard.id,
            data: response,
          });
        },
        error: () => {
          this.isFormEditable.set(true);
        },
      });
  }
}
