import {
  Component,
  computed,
  TemplateRef,
  ViewChild,
  OnInit,
} from '@angular/core';
import { CommonModule } from '@angular/common';

import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { Observable } from 'rxjs';

import {
  ThemeSettingsSectionBase,
  ThemeFieldConfig,
} from '../../base/theme-settings-section-base';
import { ThemeCreateRequest, ThemeResponse } from '../../../../../api';
import { FormFieldConfig } from '../../../../../shared/models';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { isDefined, isEmpty } from '../../../../../shared/utils';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { ThemeValueDisplayComponent } from '../theme-value-display/theme-value-display.component';

// Constants for theme property paths
const COLOR_THEME_PATHS = {
  PRIMARY: ['color', 'primary', 'color'],
  SECONDARY: ['color', 'secondary', 'color'],
  SUCCESS: ['color', 'success', 'color'],
  INFO: ['color', 'info', 'color'],
  NOTIFY: ['color', 'notify', 'color'],
  WARNING: ['color', 'warning', 'color'],
  DANGER: ['color', 'danger', 'color'],
} as const;

@Component({
  selector: 'app-theme-settings-color-section',
  imports: [
    DynamicFormComponent,
    FontAwesomeModule,
    CommonModule,
    GridComponent,
    ThemeValueDisplayComponent,
  ],
  templateUrl: './theme-settings-color-section.component.html',
  styleUrl: './theme-settings-color-section.component.scss',
})
export class ThemeSettingsColorSectionComponent
  extends ThemeSettingsSectionBase
  implements OnInit
{
  @ViewChild('valueTemplate', { static: true })
  valueTemplate!: TemplateRef<unknown>;

  readonly themeDetails = computed(() => {
    const themeDetails = this.entity();
    if (!isDefined(themeDetails)) return [];

    const themeSettings = this.themeEngine.themeSettingsMergedWithDefault(
      themeDetails?.settings ?? {},
    );

    const detailConfigs = this.createThemeDetailsFromConfig(
      this.getFieldConfigurations(),
    );
    return detailConfigs.map((config) => ({
      key: config.key,
      value: this.formatThemeValue(themeSettings, config),
    }));
  });

  constructor() {
    super('color');
  }

  ngOnInit(): void {
    this.initializeColumns();
  }

  private initializeColumns(): void {
    this.columns = [
      { field: 'key', header: '', transform: 'uppercase' },
      {
        field: 'value',
        header: '',
        cellTemplate: this.valueTemplate,
      },
    ];
  }

  protected getFieldConfigurations(): ThemeFieldConfig[] {
    return [
      {
        id: 'primary.color',
        label: 'Primary Color',
        path: COLOR_THEME_PATHS.PRIMARY,
        type: 'color',
        group: 'color',
      },
      {
        id: 'secondary.color',
        label: 'Secondary Color',
        path: COLOR_THEME_PATHS.SECONDARY,
        type: 'color',
        group: 'color',
      },
      {
        id: 'success.color',
        label: 'Success Color',
        path: COLOR_THEME_PATHS.SUCCESS,
        type: 'color',
        group: 'color',
      },
      {
        id: 'info.color',
        label: 'Info Color',
        path: COLOR_THEME_PATHS.INFO,
        type: 'color',
        group: 'color',
      },
      {
        id: 'notify.color',
        label: 'Notify Color',
        path: COLOR_THEME_PATHS.NOTIFY,
        type: 'color',
        group: 'color',
      },
      {
        id: 'warning.color',
        label: 'Warning Color',
        path: COLOR_THEME_PATHS.WARNING,
        type: 'color',
        group: 'color',
      },
      {
        id: 'danger.color',
        label: 'Danger Color',
        path: COLOR_THEME_PATHS.DANGER,
        type: 'color',
        group: 'color',
      },
    ];
  }

  getFields(entity: ThemeResponse): FormFieldConfig[] {
    return this.createFormFieldsFromConfig(
      entity,
      this.getFieldConfigurations(),
    );
  }

  override getUpdateMethod(): (
    id: string,
    data: Partial<ThemeResponse>,
  ) => Observable<ThemeResponse> {
    return (
      this.operationRegistry.getOperation<
        ThemeResponse,
        ThemeCreateRequest,
        ThemeResponse,
        'update'
      >('theme', 'update') ??
      ((id) => {
        if (isEmpty(this.formChanges)) {
          throw new Error('No form changes to save');
        }
        return this.entitiesService.updateTheme(id, {
          settings: {
            color: this.formChanges,
          },
        });
      })
    );
  }
}
