import { Injectable } from '@angular/core';
import { Validators } from '@angular/forms';
import {
  createAutocompleteField,
  createDropdownField,
  createPasswordField,
  createTextField,
  createToggleSwitchField,
  createTreeSelectField,
  FormFieldConfig,
  AssetGroupingTreeItem,
} from '../../../../../../shared/models';
import {
  UserListItem,
  UserCreateRequest,
  CompanyResponse,
  ClientListItem,
} from '../../../../../../api';
import { isDefined } from '../../../../../../shared/utils';

@Injectable({
  providedIn: 'root',
})
export class UserFormService {
  /**
   * Creates form fields for user details based on current state
   */
  createUserFormFields(config: {
    user: UserListItem | UserCreateRequest | null;
    company: CompanyResponse | null;
    createMode: boolean;
    costCenters: AssetGroupingTreeItem[];
    timezones: { label: string; value: string }[];
    clientsSearchResults: ClientListItem[] | undefined;
    searchClient: (term: string) => void;
    isLoadingClients?: () => boolean;
  }): FormFieldConfig[] {
    const {
      user,
      company,
      createMode,
      costCenters,
      timezones,
      clientsSearchResults,
      searchClient,
      isLoadingClients,
    } = config;

    if (!isDefined(user)) return [];

    const fields: FormFieldConfig[] = [];

    // Basic fields
    fields.push(
      this.createNameField(user),
      this.createEmailField(user),
      this.createMobileField(user),
    );

    // Password field only in edit mode
    if (!createMode) {
      fields.push(this.createPasswordField());
    }

    // Cost centre field for client companies
    if (isDefined(company) && company.type === 'client') {
      fields.push(this.createCostCentreField(user, costCenters));
    }

    // Common fields
    fields.push(this.createTimezoneField(user, timezones));

    // Default client field for non-client companies
    if (isDefined(company) && company.type !== 'client') {
      fields.push(
        this.createDefaultClientField(
          user,
          clientsSearchResults,
          searchClient,
          isLoadingClients,
        ),
      );
    }

    // Invite toggle for create mode
    if (createMode) {
      fields.push(this.createInviteField());
    }

    return fields;
  }

  private createNameField(
    user: UserListItem | UserCreateRequest,
  ): FormFieldConfig {
    return createTextField('name', 'Name', user?.name, {
      name: 'name',
      validations: [
        {
          name: 'required',
          validator: Validators.required,
          message: 'Name is required',
        },
      ],
    });
  }

  private createEmailField(
    user: UserListItem | UserCreateRequest,
  ): FormFieldConfig {
    return createTextField(
      'emailAddress',
      'Email Address',
      user?.emailAddress,
      {
        name: 'emailAddress',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Email Address is required',
          },
          {
            name: 'email',
            validator: Validators.email,
            message: 'Email Address is invalid',
          },
        ],
      },
    );
  }

  private createMobileField(
    user: UserListItem | UserCreateRequest,
  ): FormFieldConfig {
    return createTextField('mobile', 'Mobile Number', user?.mobile, {
      name: 'mobile',
    });
  }

  private createPasswordField(): FormFieldConfig {
    return createPasswordField('password', 'Password', {
      name: 'password',
    });
  }

  private createCostCentreField(
    user: UserListItem | UserCreateRequest,
    costCenters: AssetGroupingTreeItem[],
  ): FormFieldConfig {
    return createTreeSelectField(
      'costCentre',
      'Cost Centre',
      user?.costCentre
        ? {
            id: user.costCentre.id,
            label: user.costCentre.name,
            data: user.costCentre,
          }
        : null,
      costCenters,
      {
        name: 'costCentre',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Cost Centre is required',
          },
        ],
        props: {
          appendTo: 'body',
          placeholder: 'Select Cost Centre',
          multiple: false,
          showClear: true,
          selectionMode: 'single',
          filter: true,
        },
      },
    );
  }

  private createTimezoneField(
    user: UserListItem | UserCreateRequest,
    timezones: { label: string; value: string }[],
  ): FormFieldConfig {
    return createDropdownField(
      'timeZoneId',
      'Time Zone',
      timezones,
      user?.timeZoneId,
      {
        layout: 'horizontal',
        labelWidth: '1/3',
        placeholder: 'Select timezone',
        name: 'timeZoneId',
        props: {
          appendTo: 'body',
        },
      },
    );
  }

  private createDefaultClientField(
    user: UserListItem | UserCreateRequest,
    clientsSearchResults: ClientListItem[] | undefined,
    searchClient: (term: string) => void,
    isLoadingClients?: () => boolean,
  ): FormFieldConfig {
    return createAutocompleteField(
      'defaultClient',
      'Default Client',
      user?.defaultClient,
      clientsSearchResults,
      searchClient,
      {
        layout: 'horizontal',
        labelWidth: '1/3',
        name: 'defaultClient',
        autocompleteLoading: isLoadingClients,
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Default Client is required',
          },
        ],
        placeholder: 'Enter client name...',
        props: {
          appendTo: 'body',
          showClear: true,
          multiple: false,
          field: 'name',
          completeOnFocus: false,
          typeahead: true,
          showEmptyMessage: true,
          emptyMessage: 'No clients found',
        },
      },
    );
  }

  private createInviteField(): FormFieldConfig {
    return createToggleSwitchField('invite', 'Send Invite', true, {
      name: 'invite',
      hint: 'If enabled, the user will receive an email inviting them to activate their account.',
      props: {
        layout: 'horizontal',
        labelWidth: '1/3',
      },
    });
  }
}
