import { Injectable } from '@angular/core';
import { AdminDetailSection } from '../models';

import { ThemeResponse } from '../../../api';
import { ThemeDetailsSectionComponent } from '../components/themes/theme-details-section/theme-details-section.component';
import { FeedSectionComponent } from '../components/shared/feed-section/feed-section.component';
import { ThemeSettingsMainSectionComponent } from '../components/themes/theme-settings-main-section/theme-settings-main-section.component';
import { ThemeSettingsColorSectionComponent } from '../components/themes/theme-settings-color-section/theme-settings-color-section.component';
import { ThemeSettingsContentSectionComponent } from '../components/themes/theme-settings-content-section/theme-settings-content-section.component';
import { ThemeSettingsNavigationSectionComponent } from '../components/themes/theme-settings-navigation-section/theme-settings-navigation-section.component';
import { ThemeSettingsTopNavigationSectionComponent } from '../components/themes/theme-settings-top-navigation-section/theme-settings-top-navigation-section.component';
import { ThemeSettingsHeaderSectionComponent } from '../components/themes/theme-settings-header-section/theme-settings-header-section.component';
import { ThemeSettingsFooterSectionComponent } from '../components/themes/theme-settings-footer-section/theme-settings-footer-section.component';
import { ThemeSettingsModalSectionComponent } from '../components/themes/theme-settings-modal-section/theme-settings-modal-section.component';
import { ThemeSettingsSidebarSectionComponent } from '../components/themes/theme-settings-sidebar-section/theme-settings-sidebar-section.component';
import { ThemeSettingsButtonSectionComponent } from '../components/themes/theme-settings-button-section/theme-settings-button-section.component';
import { ThemeSettingsInputSectionComponent } from '../components/themes/theme-settings-input-section/theme-settings-input-section.component';
import { ThemeSettingsTabSectionComponent } from '../components/themes/theme-settings-tab-section/theme-settings-tab-section.component';
import { ThemeSettingsAccordionSectionComponent } from '../components/themes/theme-settings-accordion-section/theme-settings-accordion-section.component';
import { ThemeSettingsTableSectionComponent } from '../components/themes/theme-settings-table-section/theme-settings-table-section.component';
import { ThemeSettingsTreeSectionComponent } from '../components/themes/theme-settings-tree-section/theme-settings-tree-section.component';
import { ThemeSettingsListSectionComponent } from '../components/themes/theme-settings-list-section/theme-settings-list-section.component';
import { ThemeSettingsFeedSectionComponent } from '../components/themes/theme-settings-feed-section/theme-settings-feed-section.component';
import { ThemeSettingsGraphSectionComponent } from '../components/themes/theme-settings-graph-section/theme-settings-graph-section.component';
import { ThemeSettingsLoaderSectionComponent } from '../components/themes/theme-settings-loader-section/theme-settings-loader-section.component';

@Injectable({ providedIn: 'root' })
export class ThemeSectionConfigFactory {
  /**
   * Create sections for theme information
   * @param theme The theme object to display in the section
   * @returns An array of AdminDetailSection objects representing the theme information sections
   */
  createThemeInformationSections(
    theme: ThemeResponse | null,
  ): AdminDetailSection[] {
    return [
      {
        id: 'theme-details',
        title: 'Theme Details',
        component: ThemeDetailsSectionComponent,
        componentInputs: { entity: theme },
        category: 'information',
        order: 10,
      },
      {
        id: 'theme-main-settings',
        title: 'Main Settings',
        component: ThemeSettingsMainSectionComponent,
        componentInputs: { entity: theme },
        category: 'information',
        order: 20,
      },
      {
        id: 'theme-colors',
        title: 'Theme Colors',
        component: ThemeSettingsColorSectionComponent,
        componentInputs: { entity: theme },
        category: 'information',
        order: 30,
      },
      {
        id: 'theme-content',
        title: 'Theme Content',
        component: ThemeSettingsContentSectionComponent,
        componentInputs: { entity: theme },
        category: 'information',
        order: 40,
      },
      {
        id: 'theme-navigation',
        title: 'Theme Navigation',
        component: ThemeSettingsNavigationSectionComponent,
        componentInputs: { entity: theme },
        category: 'information',
        order: 50,
      },
      {
        id: 'theme-top-navigation',
        title: 'Theme Top Navigation',
        component: ThemeSettingsTopNavigationSectionComponent,
        componentInputs: { entity: theme },
        category: 'information',
        order: 60,
      },
      {
        id: 'theme-header',
        title: 'Theme Header',
        component: ThemeSettingsHeaderSectionComponent,
        componentInputs: { entity: theme },
        category: 'information',
        order: 70,
      },
      {
        id: 'theme-footer',
        title: 'Theme Footer',
        component: ThemeSettingsFooterSectionComponent,
        componentInputs: { entity: theme },
        category: 'information',
        order: 80,
      },
      {
        id: 'theme-modal',
        title: 'Theme Modal',
        component: ThemeSettingsModalSectionComponent,
        componentInputs: { entity: theme },
        category: 'information',
        order: 90,
      },
      {
        id: 'theme-sidebar',
        title: 'Theme Sidebar',
        component: ThemeSettingsSidebarSectionComponent,
        componentInputs: { entity: theme },
        category: 'information',
        order: 100,
      },
    ];
  }

  /**
   * Create sections for theme changes
   * @param theme The theme object to display in the section
   * @returns An array of AdminDetailSection objects representing the theme changes sections
   */
  createChangesSection(theme: ThemeResponse | null): AdminDetailSection[] {
    return [
      {
        id: 'theme-changes',
        title: 'Changes',
        component: FeedSectionComponent,
        componentInputs: {
          entity: theme,
        },
        category: 'feed',
        order: 10,
      },
    ];
  }

  createControlsSection(theme: ThemeResponse | null): AdminDetailSection[] {
    return [
      {
        id: 'theme-buttons',
        title: 'Buttons',
        component: ThemeSettingsButtonSectionComponent,
        componentInputs: { entity: theme },
        category: 'controls',
        order: 10,
      },
      {
        id: 'theme-inputs',
        title: 'Inputs',
        component: ThemeSettingsInputSectionComponent,
        componentInputs: { entity: theme },
        category: 'controls',
        order: 20,
      },
      {
        id: 'theme-tabs',
        title: 'Tabs',
        component: ThemeSettingsTabSectionComponent,
        componentInputs: { entity: theme },
        category: 'controls',
        order: 30,
      },
      {
        id: 'theme-accordion',
        title: 'Accordion',
        component: ThemeSettingsAccordionSectionComponent,
        componentInputs: { entity: theme },
        category: 'controls',
        order: 40,
      },
      {
        id: 'theme-tables',
        title: 'Tables',
        component: ThemeSettingsTableSectionComponent,
        componentInputs: { entity: theme },
        category: 'controls',
        order: 50,
      },
      {
        id: 'theme-trees',
        title: 'Trees',
        component: ThemeSettingsTreeSectionComponent,
        componentInputs: { entity: theme },
        category: 'controls',
        order: 60,
      },
      {
        id: 'theme-lists',
        title: 'Lists',
        component: ThemeSettingsListSectionComponent,
        componentInputs: { entity: theme },
        category: 'controls',
        order: 70,
      },
      {
        id: 'theme-feed',
        title: 'Feed',
        component: ThemeSettingsFeedSectionComponent,
        componentInputs: {
          entity: theme,
        },
        category: 'controls',
        order: 80,
      },
      {
        id: 'theme-graphs',
        title: 'Graphs',
        component: ThemeSettingsGraphSectionComponent,
        componentInputs: { entity: theme },
        category: 'controls',
        order: 90,
      },
      {
        id: 'theme-loaders',
        title: 'Loaders',
        component: ThemeSettingsLoaderSectionComponent,
        componentInputs: { entity: theme },
        category: 'controls',
        order: 100,
      },
    ];
  }

  /**
   * Create all theme sections in one call
   * @param theme The theme object to display in the sections
   * @returns An array of AdminDetailSection objects representing all theme sections
   */
  createAllThemeSections(theme: ThemeResponse | null): AdminDetailSection[] {
    return [
      ...this.createThemeInformationSections(theme),
      ...this.createControlsSection(theme),
      ...this.createChangesSection(theme),
    ];
  }
}
