import {
  Component,
  computed,
  OnInit,
  inject,
  effect,
  input,
} from '@angular/core';
import { DeviceTypeResponse } from '../../../../../api/model/deviceTypeResponse';
import { DeviceTypeFeature } from '../../../../../api/model/deviceTypeFeature';
import { EntitySectionBaseComponent } from '../../base';
import {
  isNullOrUndefined,
  isDefined,
  isArray,
} from '../../../../../shared/utils';
import {
  createModalListField,
  createDropdownField,
  FormFieldConfig,
  ListFieldModalConfig,
} from '../../../../../shared/models';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { DeviceTypesService } from '../../../../../api';
import { tap } from 'rxjs';
import { Validators } from '@angular/forms';
import { ToastService } from '../../../../../core/services/toast.service';
import { DynamicFormComponent } from '../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../../../shared/components/grid/grid.component';
import { ModalListFormComponent } from '../../../../../shared/components/forms/modal-list-form/modal-list-form.component';
import { ConfirmationService } from 'primeng/api';

interface FeatureItem {
  key: string;
  enabled: boolean;
}

@Component({
  selector: 'app-device-types-features-section',
  imports: [DynamicFormComponent, GridComponent],
  templateUrl: './device-types-features-section.component.html',
  styleUrl: './device-types-features-section.component.scss',
})
export class DeviceTypesFeaturesSectionComponent
  extends EntitySectionBaseComponent<DeviceTypeResponse>
  implements OnInit
{
  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly deviceTypesService = inject(DeviceTypesService);
  private readonly toastService = inject(ToastService);
  private readonly confirmationService = inject(ConfirmationService);

  constructor() {
    super();
    // Grid columns for view mode - display features as comma-separated list
    this.columns = [
      { field: 'key', header: 'Property' },
      { field: 'value', header: 'Value' },
    ];

    effect(() => {
      const entity = this.entity();
      if (entity && !this.createMode()) {
        this.updateFormFields();
      }
    });
  }

  override entity = input<DeviceTypeResponse | null>(null);

  featureModalConfig: ListFieldModalConfig<FeatureItem> | undefined;

  // Available device features
  readonly availableFeatures = [
    'poll',
    'toggle_outputs',
    'upload_settings',
    'garmin_fmi',
    'voice_dialback',
    'camera_take_still',
    'device_console',
  ];

  // Display features as a simple grid with comma-separated list
  readonly featuresDisplay = computed(() => {
    const deviceType = this.entity();
    const features = deviceType?.features;
    if (isNullOrUndefined(features)) {
      return [{ key: 'Features', value: 'No features configured' }];
    }

    const featureNames = Object.keys(features)
      .filter((key) => isDefined(features[key]))
      .map((key) => this.getFeatureDisplayName(key))
      .join(', ');

    return [
      {
        key: 'Features',
        value: featureNames || 'No features configured',
      },
    ];
  });

  getFeatureDisplayName(key: string): string {
    return key.replaceAll('_', ' ').replaceAll(/\b\w/g, (l) => l.toUpperCase());
  }

  // Get currently enabled features as list for modal
  readonly currentFeaturesList = computed(() => {
    const features = this.entity()?.features ?? {};
    return Object.keys(features)
      .filter((key) => isDefined(features[key]))
      .map((key) => ({
        key,
        enabled: true,
      }));
  });

  protected override updateFormFields(): void {
    this.featureModalConfig = {
      modalComponent: ModalListFormComponent,
      modalTitle: 'Add Feature',
      defaultItemProvider: () => this.getDefaultItem(),
      listItemFormatter: (item: FeatureItem) => ({
        id: item.key,
        title: this.getFeatureDisplayName(item.key),
        display: item.key,
        icon: 'pi pi-cog',
      }),
      modalFields: (item: FeatureItem | undefined): FormFieldConfig[] => {
        return this.getModalFields(item);
      },
      onSubmit: async (item: FeatureItem) => {
        // Validate the item
        if (!item.key?.trim()) {
          return false;
        }

        // Check for duplicates
        const existing = this.currentFeaturesList().find(
          (existing) => existing.key === item.key,
        );
        if (existing) {
          this.toastService.showError(
            'Duplicate Feature',
            'This feature is already enabled.',
          );
          return false;
        }

        return true; // Close modal
      },
      onDeleteItem: async (item: FeatureItem) => {
        return new Promise<boolean>((resolve) => {
          this.confirmationService.confirm({
            header: 'Confirmation',
            message: `Are you sure you want to disable "${this.getFeatureDisplayName(item.key)}"?`,
            closable: true,
            closeOnEscape: true,
            icon: 'pi pi-exclamation-triangle',
            rejectButtonProps: {
              label: 'Cancel',
              severity: 'secondary',
              outlined: true,
            },
            acceptButtonProps: {
              label: 'Disable',
              severity: 'danger',
            },
            accept: () => {
              resolve(true); // Allow default deletion
            },
            reject: () => {
              resolve(false); // Cancel deletion
            },
          });
        });
      },
      subHeader: '',
      showEditButton: false,
    };

    this.formFields = [
      createModalListField<FeatureItem>(
        'features',
        'Features',
        this.currentFeaturesList(),
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          name: 'features',
          props: {
            modalConfig: this.featureModalConfig,
          },
        },
      ) as FormFieldConfig<unknown>,
    ];
  }

  private getDefaultItem(): FeatureItem {
    return { key: '', enabled: true };
  }

  private getModalFields(item: FeatureItem | undefined): FormFieldConfig[] {
    // Get available options (features not already enabled)
    const currentFeatures = new Set(
      this.currentFeaturesList().map((f) => f.key),
    );
    const availableOptions = this.availableFeatures
      .filter(
        (feature) => !currentFeatures.has(feature) || feature === item?.key,
      )
      .map((feature) => ({
        label: this.getFeatureDisplayName(feature),
        value: feature,
      }));

    return [
      createDropdownField('key', 'Feature', availableOptions, item?.key ?? '', {
        placeholder: 'Select a feature...',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Feature selection is required',
          },
        ],
        props: {
          layout: 'horizontal',
          labelWidth: '1/3',
        },
      }),
    ];
  }

  protected override transformFormData(
    formData: Record<string, unknown>,
  ): Partial<DeviceTypeFeature> {
    // If features field exists and is an array, transform it to object format
    if (isDefined(formData['features']) && isArray(formData['features'])) {
      const featuresArray = formData['features'] as FeatureItem[];
      const entity = this.entity();

      // Start with existing features to preserve settings and webhooks
      const featuresObject: Record<string, DeviceTypeFeature | null> =
        isDefined(entity?.features) ? { ...entity.features } : {};

      // Update features based on the form array
      // First, remove features that are no longer in the array
      this.updateFeaturesObjectFromFormArray(featuresArray, featuresObject);

      return {
        features: featuresObject,
      };
    }

    return formData as Partial<DeviceTypeResponse>;
  }

  private updateFeaturesObjectFromFormArray(
    featuresArray: FeatureItem[],
    featuresObject: Record<string, DeviceTypeFeature | null>,
  ) {
    const featureKeys = new Set(featuresArray.map((f) => f.key));
    for (const existingKey of Object.keys(featuresObject)) {
      if (!featureKeys.has(existingKey)) {
        featuresObject[existingKey] = null;
      }
    }

    // Then, add or preserve features from the array
    for (const feature of featuresArray) {
      if (isDefined(feature.key)) {
        // If feature already exists, keep its settings and webhook
        // If it's new, create with empty settings
        if (!isDefined(featuresObject[feature.key])) {
          featuresObject[feature.key] = {
            settings: {},
            webhooks: null,
          };
        }
        // Existing features are already preserved from entity.features
      }
    }
  }

  protected override getUpdateMethod() {
    return (
      this.operationRegistry.getOperation('device-types', 'update') ??
      ((id, data) =>
        this.deviceTypesService.updateDeviceType(id, data).pipe(
          tap((response) => {
            this.entityChange.emit(response);
            this.broadcastService.broadcast('entity-updated', {
              entityType: 'device-types',
              operation: 'update',
              entityId: response.id,
              data: response,
            });
          }),
        ))
    );
  }

  ngOnInit(): void {
    this.updateFormFields();
  }
}
