import { Component, computed, inject, input, signal } from '@angular/core';
import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';

import {
  OverspeedProfileListItem,
  OverspeedProfileResponse,
} from '../../../../../api';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';
import { OverspeedProfileSectionConfigFactory } from '../../../factories';

@Component({
  selector: 'app-overspeed-profile-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './overspeed-profile-details-container.component.html',
  styleUrl: './overspeed-profile-details-container.component.scss',
})
export class OverspeedProfileDetailsContainerComponent {
  readonly entity = input<OverspeedProfileListItem | null>(null);

  private readonly overspeedProfileSectionFactory = inject(
    OverspeedProfileSectionConfigFactory,
  );
  readonly entityDetailsState = inject(EntityDetailsStateService);

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly isSoftRefreshing = computed(() =>
    this.entityDetailsState.isSoftRefreshing(),
  );
  readonly isInitialLoading = computed(
    () =>
      this.isLoading() &&
      !this.isSoftRefreshing() &&
      !this.fullOverspeedProfile(),
  );
  readonly fullOverspeedProfile = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();

    if (!entityDetails) {
      return null;
    }
    return entityDetails as OverspeedProfileResponse;
  });
  readonly sections = computed(() => {
    const overspeedProfile = this.fullOverspeedProfile();
    if (!overspeedProfile) return [];
    return this.overspeedProfileSectionFactory.createAllOverspeedProfileSections(
      overspeedProfile,
    );
  });
  readonly error = signal<string | null>(null);

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as OverspeedProfileResponse);
    this.error.set(null);
  }
}
