import { Type } from '@angular/core';
import {
  AdminDetailSection,
  EntitySection,
  AdminSectionLayout,
  AdminSectionCategory,
  AdminSectionConfiguration,
} from '../models';

export class FlexibleSectionBuilder<T = unknown> {
  private sections: AdminDetailSection[] = [];
  private categories: AdminSectionCategory[] = [];
  private defaultExpandedCategory?: string;
  private allowMultipleExpanded = false;
  private showCategoryIcons = false;

  constructor(private entity: T | null = null) {
    // Initialize with common default categories
    this.withDefaultCategories();
  }

  /**
   * Add a section to the builder
   */
  withSection(
    id: string,
    title: string,
    component: Type<EntitySection<unknown>>,
    category = 'information',
    order = 10,
    additionalInputs: Record<string, unknown> = {},
  ): this {
    this.sections.push({
      id,
      title,
      component,
      category,
      order,
      componentInputs: {
        entity: this.entity,
        ...additionalInputs,
      },
    });
    return this;
  }

  /**
   * Add a section with full configuration options
   */
  withAdvancedSection(
    section: Omit<AdminDetailSection, 'componentInputs'> & {
      componentInputs?: Record<string, unknown>;
    },
  ): this {
    this.sections.push({
      ...section,
      componentInputs: {
        entity: this.entity,
        ...section.componentInputs,
      },
    });
    return this;
  }

  /**
   * Define or override a category
   */
  withCategory(
    key: string,
    label: string,
    order: number,
    options?: {
      visible?: boolean | ((entity: unknown) => boolean);
      icon?: string;
      description?: string;
    },
  ): this {
    const existingIndex = this.categories.findIndex((c) => c.key === key);
    const category: AdminSectionCategory = {
      key,
      label,
      order,
      ...options,
    };

    if (existingIndex >= 0) {
      this.categories[existingIndex] = category;
    } else {
      this.categories.push(category);
    }
    return this;
  }

  /**
   * Remove a category
   */
  withoutCategory(key: string): this {
    this.categories = this.categories.filter((c) => c.key !== key);
    return this;
  }

  /**
   * Set the default expanded category
   */
  withDefaultExpanded(categoryKey: string): this {
    this.defaultExpandedCategory = categoryKey;
    return this;
  }

  /**
   * Allow multiple accordion panels to be expanded simultaneously
   */
  withMultipleExpansion(allow = true): this {
    this.allowMultipleExpanded = allow;
    return this;
  }

  /**
   * Show category icons in the accordion headers
   */
  withCategoryIcons(show = true): this {
    this.showCategoryIcons = show;
    return this;
  }

  /**
   * Add multiple sections at once
   */
  withSections(sections: AdminDetailSection[]): this {
    sections.forEach((section) => {
      this.sections.push({
        ...section,
        componentInputs: {
          entity: this.entity,
          ...section.componentInputs,
        },
      });
    });
    return this;
  }

  /**
   * Add default categories commonly used across entities
   */
  private withDefaultCategories(): this {
    this.categories = [
      { key: 'information', label: 'Information', order: 10 },
      { key: 'settings', label: 'Settings', order: 20 },
      { key: 'events', label: 'Events', order: 30 },
      { key: 'statistics', label: 'Statistics', order: 40 },
      { key: 'io', label: 'IO', order: 50 },
      { key: 'accessories', label: 'Accessories', order: 60 },
      { key: 'controls', label: 'Controls', order: 70 },
      { key: 'assets', label: 'Assets', order: 80 },
      { key: 'users', label: 'Users', order: 90 },
      { key: 'features', label: 'Features', order: 100 },
      { key: 'customization', label: 'Email Customization', order: 110 },
      { key: 'feed', label: 'Changes', order: 120 },
      { key: 'backend', label: 'Backend', order: 130 },
      { key: 'custom', label: 'Custom', order: 140 },
    ];
    return this;
  }

  /**
   * Build the final configuration
   */
  build(): AdminSectionConfiguration {
    // Filter out categories that have no sections
    const usedCategories = this.categories
      .filter((category) =>
        this.sections.some((section) => section.category === category.key),
      )
      .sort((a, b) => a.order - b.order);

    const layout: AdminSectionLayout = {
      categories: usedCategories,
      defaultExpandedCategory: this.defaultExpandedCategory,
      allowMultipleExpanded: this.allowMultipleExpanded,
      showCategoryIcons: this.showCategoryIcons,
    };

    return {
      sections: this.sections,
      layout,
    };
  }

  /**
   * Legacy method to just return sections (for backward compatibility)
   */
  buildSections(): AdminDetailSection[] {
    return this.sections;
  }

  /**
   * Create a builder for a specific entity type
   */
  static forEntity<TEntity>(
    entity: TEntity | null,
  ): FlexibleSectionBuilder<TEntity> {
    return new FlexibleSectionBuilder<TEntity>(entity);
  }
}
