import {
  ChangeDetectorRef,
  Component,
  OnDestroy,
  OutputRefSubscription,
  ViewChild,
  afterNextRender,
  effect,
  inject,
  input,
  model,
  output,
  signal,
} from '@angular/core';
import { firstValueFrom } from 'rxjs';

import {
  GoogleMapComponent,
  PolygonCustomData,
} from '../google-map/google-map.component';
import {
  EntitiesService,
  ZoneListItem,
  ZoneResponse,
  ZoneUpdateRequest,
} from '../../../../api';
import {
  LatLngLiteral,
  MapPolygon,
  PolygonChangeEvent,
} from '../../../models/google-maps.interfaces';
import { isDefined, isNullOrUndefined } from '../../../utils';

/**
 * A component that provides zone editing functionality using Google Maps.
 * It allows displaying, editing and saving of geofence zones.
 */
@Component({
  selector: 'app-google-maps-zone',
  imports: [GoogleMapComponent],
  templateUrl: './google-maps-zone.component.html',
  styleUrls: ['./google-maps-zone.component.scss'],
})
export class GoogleMapsZoneComponent implements OnDestroy {
  readonly entity = input.required<ZoneResponse | ZoneListItem>();
  readonly editable = model(false);

  readonly onEditing = output<boolean>();
  readonly onSaving = output<boolean>();
  readonly onSaved = output<ZoneResponse | ZoneListItem>();

  @ViewChild('map') map!: GoogleMapComponent;

  private readonly entitiesService = inject(EntitiesService);
  private readonly changeDetectorRef = inject(ChangeDetectorRef);

  errorMessage: string | null = null;
  private zonePolygon: MapPolygon<PolygonCustomData> | null = null;
  private routePolyline: google.maps.Polyline | null = null;
  private polygonChangedCleanup: OutputRefSubscription | undefined;
  private routeEventListeners: google.maps.MapsEventListener[] = [];
  center = signal({ lat: 40.7128, lng: -74.006 });
  private fitBoundsTimeout: string | number | NodeJS.Timeout | undefined;

  constructor() {
    // Create a signal to track map initialization
    const isMapInitialized = signal(false);

    // First effect - handle when entity changes or becomes available
    effect(() => {
      const currentEntity = this.entity();
      const mapIsReady = isMapInitialized();

      if (isDefined(currentEntity?.points) && currentEntity.points.length > 0) {
        const Lng =
          currentEntity.points.reduce((sum, p) => sum + p.x, 0) /
          currentEntity.points.length;
        const Lat =
          currentEntity.points.reduce((sum, p) => sum + p.y, 0) /
          currentEntity.points.length;
        this.center.set({ lat: Lat, lng: Lng });
      }

      if (mapIsReady && isDefined(currentEntity)) {
        // Debounce loadZone and other operations
        setTimeout(() => {
          // Ensure the entity hasn't changed again during the timeout
          if (this.entity() !== currentEntity) {
            return;
          }

          // Always call loadZone to reflect the current entity state,
          // especially if its geometry (points) might have changed.
          this.loadZone();

          if (
            isDefined(currentEntity.points) &&
            currentEntity.points.length > 0
          ) {
            const paths = currentEntity.points.map((p) => ({
              lat: p.y,
              lng: p.x,
            }));
            clearTimeout(this.fitBoundsTimeout);
            this.fitBoundsTimeout = setTimeout(() => {
              if (this.entity() === currentEntity) {
                // Re-check entity before expensive operation
                this.fitZoneToViewport(paths, currentEntity);
              }
            }, 200); // Debounce fitting
          }

          // Set up subscription for polygon changes
          this.setupPolygonChangeSubscription(currentEntity);
        }, 100); // Debounce the entire block of operations
      }
    });

    // Second effect to handle the map initialization state
    afterNextRender(() => {
      if (isDefined(this.map)) {
        // Check if map is already initialized (rare but possible)
        if (this.map.isMapInitialized()) {
          isMapInitialized.set(true);
        }

        // Set up a subscription to know when the map becomes initialized
        this.map.mapLoaded.subscribe(() => {
          isMapInitialized.set(true);
        });
      }
    });

    // Handle editable status changes
    effect(() => {
      const entity = this.entity();
      if (isDefined(this.map) && isDefined(entity)) {
        if (entity.zoneType === 'route') {
          this.updateRouteEditableStatus();
        } else if (this.zonePolygon) {
          this.updatePolygonEditableStatus();
        }
      }
    });
  }

  // Helper method to set up polygon change subscription
  private setupPolygonChangeSubscription(
    entity: ZoneResponse | ZoneListItem,
  ): void {
    if (entity?.zoneType !== 'route') {
      // Clean up any existing subscription first
      if (this.polygonChangedCleanup) {
        this.polygonChangedCleanup.unsubscribe();
        this.polygonChangedCleanup = undefined;
      }

      this.polygonChangedCleanup = this.map.polygonChanged.subscribe(
        (event) => {
          if (isDefined(entity) && event.customData?.id === entity.id) {
            const updatedZone = {
              ...entity,
              points: event.paths.map((path) => ({
                x: path.lng,
                y: path.lat,
              })),
            };
            this.onSaved.emit(updatedZone);
          }
        },
      );
    }
  }

  ngOnDestroy(): void {
    this.clearRouteListeners();

    // Clean up route polyline if it exists
    if (this.routePolyline) {
      this.routePolyline.setMap(null);
      this.routePolyline = null;
    }

    if (this.polygonChangedCleanup) {
      this.polygonChangedCleanup.unsubscribe();
      this.polygonChangedCleanup = undefined;
    }

    if (isDefined(this.fitBoundsTimeout)) {
      clearTimeout(this.fitBoundsTimeout);
    }
  }

  /**
   * Loads the zone onto the map
   */
  loadZone(): void {
    const entity = this.entity(); // Get the most current entity
    if (
      isNullOrUndefined(this.map) ||
      !this.map.isMapInitialized() ||
      isNullOrUndefined(entity)
    ) {
      return;
    }

    this.clearMapElements();

    const paths = (entity.points ?? []).map((p) => ({ lat: p.y, lng: p.x }));

    if (entity.zoneType === 'route') {
      this.displayRoute(paths);
    } else {
      // Ensure the new zonePolygon reflects the current editable state as well
      let color = '';

      // Fallback to zone type specific colors or a general default
      switch (entity.zoneType) {
        case 'nogo':
          color = '#FF0000'; // Red for no-go zones
          break;
        case 'keepin':
          color = '#4CAF50'; // Green for keep-in zones
          break;
        default:
          color = '#1E88E5'; // Default blue for other types (e.g., 'polygon')
          break;
      }

      this.zonePolygon = {
        paths: paths,
        editable: this.editable(),
        draggable: this.editable(),
        fillOpacity: 0.35,
        strokeWeight: 2,
        customData: { entityType: 'zone', entityId: entity.id },
        fillColor: color,
        strokeColor: color,
      };

      if (this.zonePolygon.paths.length > 0) {
        this.map.polygons.set([this.zonePolygon]);
      } else {
        this.map.polygons.set([]); // Clear if no paths
      }
    }
    // No explicit changeDetectorRef.detectChanges() needed if driven by signals
  }

  /**
   * Automatically fit the zone to the viewport with appropriate zoom
   */
  private fitZoneToViewport(
    paths: LatLngLiteral[],
    _entity: ZoneResponse | ZoneListItem,
  ): void {
    if (
      isNullOrUndefined(this.map?.map) ||
      typeof google === 'undefined' ||
      paths.length === 0
    )
      return;

    try {
      // Create bounds object
      const bounds = new google.maps.LatLngBounds();

      // Add all points to bounds
      paths.forEach((point) => bounds.extend(point));

      // Calculate area of the zone to determine padding
      const area = this.calculateArea(paths);
      let padding = 50; // Default padding in pixels

      // Adjust padding based on zone size
      if (area < 0.01) {
        padding = 100; // More padding for small zones
      } else if (area > 10) {
        padding = 10; // Less padding for large zones
      }

      // Set center for underlying signal
      const center = bounds.getCenter();
      if (isDefined(center)) {
        this.center.set({
          lat: center.lat(),
          lng: center.lng(),
        });
      }

      // IMPORTANT: Remove any existing timeout for a cleaner approach
      if (isDefined(this.fitBoundsTimeout)) {
        clearTimeout(this.fitBoundsTimeout);
      }

      // Use a short timeout to ensure the map is ready before fitting bounds
      // This is crucial for entity switching
      this.fitBoundsTimeout = setTimeout(() => {
        // Important: Force a new bounds instance to ensure Google Maps treats it as a change
        const newBounds = new google.maps.LatLngBounds();
        paths.forEach((point) => newBounds.extend(point));

        // Apply the fit bounds with padding
        this.map.map.fitBounds(newBounds, padding);

        // For very small zones or points, set minimum zoom after a brief delay
        if (paths.length === 1 || area < 0.0001) {
          setTimeout(() => {
            const currentZoom = this.map.map.getZoom() ?? 0;
            if (currentZoom > 18) {
              this.map.setZoom(18);
            }
          }, 150);
        }

        // For very large zones, limit maximum zoom out
        if (area > 1000) {
          setTimeout(() => {
            const currentZoom = this.map.map.getZoom() ?? 0;
            if (currentZoom < 5) {
              this.map.setZoom(5);
            }
          }, 150);
        }

        // Force UI update
        this.changeDetectorRef.markForCheck();
      }, 50);
    } catch (err) {
      console.error('Error fitting bounds:', err);
    }
  }

  /**
   * Calculate approximate area of polygon/route in square kilometers
   */
  private calculateArea(paths: LatLngLiteral[]): number {
    if (paths.length < 3) return 0;

    try {
      if (typeof google !== 'undefined') {
        // For polygons, use Google's built-in area calculation
        const googlePaths = paths.map(
          (p) => new google.maps.LatLng(p.lat, p.lng),
        );
        return (
          google.maps.geometry.spherical.computeArea(googlePaths) / 1000000
        ); // Convert to sq km
      } else {
        // Fallback to approximate calculation using bounding box
        let minLat = paths[0].lat,
          maxLat = paths[0].lat;
        let minLng = paths[0].lng,
          maxLng = paths[0].lng;

        paths.forEach((p) => {
          minLat = Math.min(minLat, p.lat);
          maxLat = Math.max(maxLat, p.lat);
          minLng = Math.min(minLng, p.lng);
          maxLng = Math.max(maxLng, p.lng);
        });

        // Rough approximation of area in square kilometers
        const latDiff = maxLat - minLat;
        const lngDiff = maxLng - minLng;
        return latDiff * lngDiff * 111 * 111; // 1 degree is roughly 111km
      }
    } catch (err) {
      console.error('Error calculating area:', err);
      return 0;
    }
  }
  /**
   * Clears existing map elements (polygons, polylines)
   */
  private clearMapElements(): void {
    // Clear polygon if exists
    if (this.zonePolygon) {
      try {
        const currentPolygons = this.map.polygons() ?? [];
        const filteredPolygons = currentPolygons.filter(
          (p) => p.customData?.id !== this.entity()?.id,
        );
        // Update polygons signal
        this.map.polygons.set(filteredPolygons);
        this.zonePolygon = null;
      } catch (err) {
        console.error('Error clearing polygon:', err);
      }
    }

    // Clear polyline if exists
    if (this.routePolyline) {
      try {
        this.routePolyline.setMap(null);
        this.routePolyline = null;
      } catch (err) {
        console.error('Error clearing polyline:', err);
      }
    }
  }
  /**
   * Displays a route as a polyline
   */
  private displayRoute(paths: LatLngLiteral[]): void {
    if (typeof google === 'undefined') {
      return;
    }

    // Clear previous listeners
    this.clearRouteListeners();

    try {
      // Create a polyline (route)
      this.routePolyline = new google.maps.Polyline({
        path: paths,
        geodesic: true,
        strokeColor: '#4CAF50', // Green for routes
        strokeOpacity: 1.0,
        strokeWeight: 4,
        editable: this.editable(),
        map: this.map.map,
      });

      // Add click listener to the route
      const clickListener = this.routePolyline.addListener('click', () => {
        // Handle route click, if needed
      });
      this.routeEventListeners.push(clickListener);

      // Add path changed listener if editable
      if (this.editable()) {
        const setListener = google.maps.event.addListener(
          this.routePolyline.getPath(),
          'set_at',
          () => this.handleRoutePathChanged(),
        );
        this.routeEventListeners.push(setListener);

        const insertListener = google.maps.event.addListener(
          this.routePolyline.getPath(),
          'insert_at',
          () => this.handleRoutePathChanged(),
        );
        this.routeEventListeners.push(insertListener);

        const removeListener = google.maps.event.addListener(
          this.routePolyline.getPath(),
          'remove_at',
          () => this.handleRoutePathChanged(),
        );
        this.routeEventListeners.push(removeListener);
      }
    } catch (err) {
      console.error('Failed to display route:', err);
      this.errorMessage = `Failed to display route: ${err instanceof Error ? err.message : 'Unknown error'}`;
    }
  }

  private clearRouteListeners(): void {
    if (this.routeEventListeners.length > 0) {
      this.routeEventListeners.forEach((listener) =>
        google.maps.event.removeListener(listener),
      );
      this.routeEventListeners = [];
    }
  }

  /**
   * Handles route path changes when edited
   */
  private handleRoutePathChanged(): void {
    if (!this.routePolyline) return;

    const path = this.routePolyline.getPath();
    const points = [];

    for (let i = 0; i < path.getLength(); i++) {
      const point = path.getAt(i);
      points.push({
        x: point.lng(),
        y: point.lat(),
      });
    }

    // Create an updated version of the zone with new points
    const updatedZone = {
      ...this.entity(),
      points,
    };

    // Emit the updated zone data
    this.onSaving.emit(true);
    this.onSaved.emit(updatedZone);
  }

  /**
   * Updates the polyline editable status
   */
  private updateRouteEditableStatus(): void {
    if (isNullOrUndefined(this.map) || isNullOrUndefined(this.routePolyline))
      return;

    const editable = this.editable();
    this.routePolyline.setEditable(editable);
    this.routePolyline.setOptions({
      draggable: editable,
      editable: editable,
    });
  }

  /**
   * Displays a zone as a polygon
   */
  private displayPolygon(paths: LatLngLiteral[]): void {
    // Determine color based on zone type
    let strokeColor = '#1E88E5'; // Default blue
    let fillColor = '#1E88E5';

    const entity = this.entity();
    if (entity.zoneType === 'nogo') {
      strokeColor = '#FF0000'; // Red for no-go zones
      fillColor = '#FF0000';
    } else if (entity.zoneType === 'keepin') {
      strokeColor = '#4CAF50'; // Green for keep-in zones
      fillColor = '#4CAF50';
    }

    this.zonePolygon = {
      paths,
      strokeColor,
      strokeOpacity: 0.8,
      strokeWeight: 2,
      fillColor,
      fillOpacity: 0.35,
      editable: this.editable(),
      draggable: this.editable(),
      customData: {
        id: entity.id,
        name: entity.name ?? '',
      },
    };

    // Update polygons on the map
    const currentPolygons = this.map.polygons() ?? [];
    const filteredPolygons = currentPolygons.filter(
      (p) => p.customData?.id !== entity.id,
    );

    // Create a completely new array to ensure Angular detects the change
    this.map.polygons.set([...filteredPolygons, this.zonePolygon]);

    // Force update polygons in map
    setTimeout(() => {
      this.changeDetectorRef.markForCheck();
    }, 0);
  }

  /**
   * Updates the polygon editable and draggable status based on the editable input
   */
  updatePolygonEditableStatus(): void {
    if (isNullOrUndefined(this.map) || isNullOrUndefined(this.zonePolygon)) {
      return;
    }

    // Find the index of our polygon
    const currentPolygons = this.map.polygons() ?? [];
    const index = currentPolygons.findIndex(
      (p) => p.customData?.id === this.entity().id,
    );

    if (index !== -1) {
      // Update the polygon properties
      const updatedPolygon = {
        ...currentPolygons[index],
        editable: this.editable(),
        draggable: this.editable(),
      };

      // Update the polygon in the array
      const updatedPolygons = [...currentPolygons];
      updatedPolygons[index] = updatedPolygon;

      // Update the polygons in the map
      this.map.polygons.set(updatedPolygons);

      // Also update internal reference
      this.zonePolygon = updatedPolygon;

      // Update UI elements if any Google Maps Polygon instances exist
      if (
        isDefined(this.map.googlePolygons) &&
        this.map.googlePolygons.length > index
      ) {
        this.map.googlePolygons[index].setEditable(this.editable());
        this.map.googlePolygons[index].setDraggable(this.editable());
      }
    }
  }

  /**
   * Saves the current state of the zone
   */
  async saveZone(): Promise<void> {
    const entity = this.entity();
    if (isNullOrUndefined(entity)) {
      return;
    }

    try {
      this.onSaving.emit(true);

      const points = [];

      if (entity.zoneType === 'route' && this.routePolyline) {
        // Extract points from route polyline
        const path = this.routePolyline.getPath();

        for (let i = 0; i < path.getLength(); i++) {
          const point = path.getAt(i);
          points.push({
            x: point.lng(),
            y: point.lat(),
          });
        }
      } else if (this.zonePolygon) {
        // Find the corresponding Google Maps polygon to get latest paths
        const currentPolygon = this.map.googlePolygons?.find(
          (_, index) =>
            this.map.polygons()[index].customData?.id === this.entity().id,
        );

        if (!currentPolygon) {
          throw new Error('Polygon not found');
        }

        // Extract points from the polygon
        const path = currentPolygon.getPath();

        for (let i = 0; i < path.getLength(); i++) {
          const point = path.getAt(i);
          points.push({
            x: point.lng(),
            y: point.lat(),
          });
        }
      } else {
        throw new Error('No polygon or route data found');
      }

      // Create update request
      const updateRequest: ZoneUpdateRequest = {
        points,
      };

      const updatedZone = await firstValueFrom(
        this.entitiesService.updateZone(entity.id, updateRequest),
      );

      // Emit the updated zone
      this.onSaved.emit(updatedZone);
    } catch (error) {
      this.showError(error as Error);
    }
  }

  /**
   * Starts editing mode
   */
  startEditing(): void {
    if (!this.editable()) {
      this.editable.set(true);

      if (this.entity()?.zoneType === 'route') {
        this.updateRouteEditableStatus();
      } else {
        this.updatePolygonEditableStatus();
      }

      this.onEditing.emit(true);
    }
  }

  /**
   * Stops editing mode
   */
  stopEditing(): void {
    if (this.editable()) {
      this.editable.set(false);

      if (this.entity()?.zoneType === 'route') {
        this.updateRouteEditableStatus();
      } else {
        this.updatePolygonEditableStatus();
      }

      this.onEditing.emit(false);
    }
  }

  /**
   * Helper method to display errors
   */
  private showError(error: Error): void {
    this.errorMessage =
      error?.message ?? 'An error occurred while saving the zone';
    this.changeDetectorRef.markForCheck();
  }

  /**
   * Handles polygon change events from the map
   */
  onPolygonChanged(event: PolygonChangeEvent<PolygonCustomData>): void {
    if (event.customData?.id === this.entity()?.id) {
      // Update internal representation if needed
      this.zonePolygon = {
        ...this.zonePolygon,
        paths: event.paths,
      };
    }
  }
}
