import {
  Injectable,
  signal,
  Type,
  inject,
  DestroyRef,
  computed,
  Injector,
} from '@angular/core';
import { Router } from '@angular/router';

import {
  isDefined,
  isNullOrUndefined,
  isString,
} from '../../../../shared/utils';
import { DetailsComponentFactory } from '../../factories';
import { BaseEntity } from '../../../../shared/models';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { BroadcastEventMessageService } from '../broadcast-event-messages.service';

export interface EntityUpdatePayload {
  entityType: string;
  operation: string;
  data: BaseEntity;
}

export interface EntityDeletePayload {
  entityType: string;
  operation: string;
  entityId: string;
  data: BaseEntity;
}

@Injectable({ providedIn: 'root' })
export class EntityDetailsStateService {
  // State signals
  readonly showPanel = signal<boolean>(false);
  readonly selectedItem = signal<BaseEntity | null>(null);
  readonly detailsComponent = signal<Type<unknown> | null>(null);
  readonly detailsInputs = signal<Record<string, unknown>>({});
  readonly isLoading = signal<boolean>(false);
  readonly activePopover = signal<string | null>(null);

  private readonly detailsFactory = inject(DetailsComponentFactory);
  private readonly broadcastService = inject(BroadcastEventMessageService);
  private readonly destroyRef = inject(DestroyRef);
  private readonly router = inject(Router);
  private readonly injector = inject(Injector);

  readonly selectedItemTitle = computed(() => {
    const item = this.selectedItem();

    // Case 1: No item selected
    if (isNullOrUndefined(item)) {
      return 'Details';
    }

    // Case 2: Item has a name property
    if (isDefined(item['name']) && isString(item['name'])) {
      return item['name'];
    }

    // Case 3: Item has a entityType property - get formatted entity type
    if (isDefined(item['entityType']) && isString(item['entityType'])) {
      // Format the entity type (capitalize first letter or apply other formatting)
      const formattedType = this.formatEntityType(item['entityType']);
      return `${formattedType} Details`;
    }

    // Default case
    return 'Item Details';
  });

  readonly selectedItemType = computed(() => {
    const item = this.selectedItem();
    return item ? String(item['entityType']) : '';
  });

  readonly selectedItemId = computed(() => {
    const item = this.selectedItem();
    return item ? String(item['id']) : '';
  });

  // Keep existing methods for backward compatibility
  getSelectedItemTitle(): string {
    return this.selectedItemTitle();
  }

  getSelectedItemType(): string {
    return this.selectedItemType();
  }

  getSelectedItemId(): string {
    return this.selectedItemId();
  }

  constructor() {
    // Listen for entity updates to refresh the details panel
    this.broadcastService
      .on('entity-updated')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((event) => {
        // Check if the updated entity matches the currently selected one
        const current = this.selectedItem();
        if (
          current &&
          current.id === (event as unknown as EntityUpdatePayload)['data']?.id
        ) {
          // Trigger a refresh of the entity
          this.refreshSelectedItem(
            (event as unknown as EntityUpdatePayload)['data'],
          );
        }
      });

    // Listen for entity deletions to close the details panel
    this.broadcastService
      .on('entity-deleted')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((event) => {
        // Check if the deleted entity matches the currently selected one
        const current = this.selectedItem();
        const eventPayload = event as EntityDeletePayload;
        if (current && current.id === eventPayload.entityId) {
          // Close the details panel since the entity no longer exists
          this.closeDetailsPanel();
        }
      });
  }

  /**
   * Refreshes the currently selected item by fetching updated data
   */
  refreshSelectedItem(data: BaseEntity): void {
    const item = this.selectedItem();
    if (!item) return;
    this.selectedItem.set(data);
  }

  /**
   * Soft refresh that updates entity data without affecting UI state like accordion expansion
   * This preserves the current accordion states when refreshing data
   */
  softRefreshSelectedItem(_updatedData?: BaseEntity): void {
    const currentItem = this.selectedItem();
    if (!currentItem) return;

    // Always set loading state first
    this.isLoading.set(true);

    // Even if we have updatedData from the grid, still fetch complete entity
    const entityType = this.selectedItemType();
    const entityId = this.selectedItemId();

    if (entityType && entityId) {
      // Use dynamic import to avoid circular dependency
      import('./entity-details-loader.service')
        .then(({ EntityDetailsLoaderService }) => {
          const loaderService = this.injector.get(EntityDetailsLoaderService);

          // Always fetch complete entity data regardless of whether updatedData is provided
          loaderService
            .softLoadEntityDetailsObservable(entityType, entityId)
            .pipe(takeUntilDestroyed(this.destroyRef))
            .subscribe({
              next: (completeEntity: BaseEntity | null) => {
                if (isDefined(completeEntity)) {
                  // Update with complete data
                  this.selectedItem.set(completeEntity);

                  // Keep existing inputs object but update its properties
                  const currentInputs = this.detailsInputs();
                  if (isDefined(currentInputs)) {
                    currentInputs['entity'] = completeEntity;
                    // Signal change without creating new object reference
                    this.detailsInputs.set(currentInputs);
                  } else {
                    this.detailsInputs.set({ entity: completeEntity });
                  }
                }
                this.isLoading.set(false);
              },
              error: () => this.isLoading.set(false),
            });
        })
        .catch(() => this.isLoading.set(false));
    } else {
      this.isLoading.set(false);
    }
  }

  /**
   * Computed property to track if we're doing a soft refresh
   * (loading while an item is selected - shows subtle indicator instead of full loader)
   */
  readonly isSoftRefreshing = computed(
    () => this.isLoading() && this.selectedItem() !== null,
  );

  /**
   * Opens the details panel with the specified item and component
   */
  openDetailsPanel(
    item: BaseEntity,
    componentType: Type<unknown> | null,
  ): void {
    this.selectedItem.set(item);

    // Get the appropriate component based on item entityType and provided component
    const component =
      componentType ??
      this.detailsFactory.getDetailsComponent(String(item['entityType']));
    this.detailsComponent.set(component);
    this.detailsInputs.set({ entity: item });
    this.showPanel.set(true);
  }

  /**
   * Closes the details panel (UI state only)
   */
  closeDetailsPanel(): void {
    this.showPanel.set(false);
    this.selectedItem.set(null);
    this.detailsComponent.set(null);
    this.detailsInputs.set({});
  }

  /**
   * Format entity type string for display
   * @param type Raw entity type string
   * @returns Formatted entity type
   */
  private formatEntityType(type: string): string {
    // Simple formatting: capitalize first letter
    if (!type) return 'Item';

    // Convert kebab-case or snake_case to capitalized words
    return type.replaceAll(/[-_]/g, ' ').replaceAll(/\w\S*/g, (word) => {
      return word.charAt(0).toUpperCase() + word.substring(1).toLowerCase();
    });
  }
  updateEntity(updatedEntity: BaseEntity): void {
    // Create a fresh copy to ensure change detection
    const updatedCopy: BaseEntity = { ...updatedEntity };
    this.selectedItem.set(updatedCopy);
    this.detailsInputs.set({ entity: updatedCopy });

    // Clear loading state if we were soft refreshing
    if (this.isLoading()) {
      this.isLoading.set(false);
    }
  }

  /**
   * Toggle popover visibility
   */
  togglePopover(id: string | null): void {
    if (this.activePopover() === id) {
      this.activePopover.set(null);
    } else {
      this.activePopover.set(id);
    }
  }
}
