import { Component, computed, inject, input, signal } from '@angular/core';
import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';
import {
  AssetRatingProfileListItem,
  AssetRatingProfileResponse,
} from '../../../../../api';
import { AssetRatingProfileSectionConfigFactory } from '../../../factories/asset-rating-profile-section-config.factory';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';

@Component({
  selector: 'app-asset-rating-profile-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './asset-rating-profile-details-container.component.html',
  styleUrl: './asset-rating-profile-details-container.component.scss',
})
export class AssetRatingProfileDetailsContainerComponent {
  readonly entity = input<AssetRatingProfileListItem | null>(null);

  private readonly assetRatingProfileSectionFactory = inject(
    AssetRatingProfileSectionConfigFactory,
  );
  readonly entityDetailsState = inject(EntityDetailsStateService);

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly fullAssetRatingProfile = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();

    if (!entityDetails) {
      return null;
    }
    return entityDetails as AssetRatingProfileResponse;
  });
  readonly sections = computed(() => {
    const assetRatingProfile = this.fullAssetRatingProfile();
    if (!assetRatingProfile) return [];
    return this.assetRatingProfileSectionFactory.createAllAssetRatingProfileSections(
      assetRatingProfile,
    );
  });
  readonly error = signal<string | null>(null);

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as AssetRatingProfileResponse);
    this.error.set(null);
  }
}
