import {
  Component,
  computed,
  effect,
  inject,
  input,
  OnInit,
  AfterViewInit,
} from '@angular/core';
import {
  DeviceTypeAccessory,
  DeviceTypeFeature,
  DeviceTypeResponse,
  DeviceTypesService,
} from '../../../../../../api';
import { EntitySectionBaseComponent } from '../../../base';
import {
  isNullOrUndefined,
  isDefined,
  isEmpty,
} from '../../../../../../shared/utils';
import { EntityOperationRegistryService } from '../../../../services/entity/entity-operation-registry.service';
import { tap } from 'rxjs';
import {
  createModalListField,
  createTextField,
  createToggleSwitchField,
  createDropdownField,
  createNumberField,
  FormFieldConfig,
  GridColumn,
  ListFieldModalConfig,
} from '../../../../../../shared/models';
import { GridComponent } from '../../../../../../shared/components/grid/grid.component';
import { AccordionModule } from 'primeng/accordion';
import { DynamicFormComponent } from '../../../../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { ModalListFormComponent } from '../../../../../../shared/components/forms/modal-list-form/modal-list-form.component';
import { ConfirmationService } from 'primeng/api';
import { Validators } from '@angular/forms';

interface IOTypeItem {
  type: string; // The IO type key like 'digital_input', 'analog_input'
  label: string; // Display label like 'Digital Input', 'Analog Input'
  instances: IOFieldItem[]; // The actual IO instances data as array
}

interface IOFieldItem {
  key: string;
  optional: boolean;
}

interface SettingItem {
  id: string;
  title: string;
  type: 'combo' | 'number' | 'text' | 'boolean'; // Define supported types
  values?: { key: string; value: string }[]; // For combo fields
  unit?: string; // For number fields
  min?: number; // For number fields
  max?: number; // For number fields
  width?: number; // Field width
  defaultValue?: unknown; // Default value
}

interface FeatureItem {
  key: string;
  value: string;
}

@Component({
  selector: 'app-device-types-accessory',
  imports: [GridComponent, AccordionModule, DynamicFormComponent],
  templateUrl: './device-types-accessory.component.html',
  styleUrl: './device-types-accessory.component.scss',
})
export class DeviceTypesAccessoryComponent
  extends EntitySectionBaseComponent<DeviceTypeResponse>
  implements OnInit, AfterViewInit
{
  constructor() {
    super();

    // Grid columns for view mode
    this.columns = [
      { field: 'key', header: 'Property' },
      { field: 'value', header: 'Value' },
    ];

    // Column definitions for each grid
    this.ioCapabilitiesColumns = [
      { field: 'type', header: 'Type' },
      { field: 'details', header: 'Available Inputs' },
    ];

    this.settingsDefinitionColumns = [
      { field: 'setting', header: 'Setting' },
      { field: 'value', header: 'Value' },
    ];

    this.featuresColumns = [
      { field: 'feature', header: 'Feature' },
      { field: 'value', header: 'Value' },
      { field: 'type', header: 'Type' },
      { field: 'enabled', header: 'Enabled' },
    ];

    effect(() => {
      const entity = this.entity();
      if (entity && !this.createMode()) {
        this.updateFormFields();
      }
    });
  }

  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly deviceTypesService = inject(DeviceTypesService);
  private readonly confirmationService = inject(ConfirmationService);

  override entity = input<DeviceTypeResponse | null>(null);

  // Column definitions for grids
  ioCapabilitiesColumns: GridColumn[] = [];
  settingsDefinitionColumns: { field: string; header: string }[] = [];
  featuresColumns: { field: string; header: string }[] = [];

  // Modal Configs
  ioTypeModalConfig: ListFieldModalConfig<IOTypeItem> | undefined;
  ioCapabilityModalConfig: ListFieldModalConfig<IOFieldItem> | undefined;
  settingsModalConfig: ListFieldModalConfig<SettingItem> | undefined;
  featuresModalConfig: ListFieldModalConfig<FeatureItem> | undefined;

  // Configuration inputs
  accessoryKey = input.required<string>(); // The accessory key (e.g., 'mritagreader')
  displayTitle = input.required<string>(); // Display name (e.g., 'MRI Tag Reader')

  // Get the specific accessory data
  readonly accessoryData = computed(() => {
    const deviceType = this.entity();
    const accessories = deviceType?.accessories;
    const key = this.accessoryKey();

    if (isNullOrUndefined(accessories) || !key) return null;

    return accessories[key] ?? null;
  });

  // Display accessory details in a simple grid format
  readonly accessoryDetails = computed(() => {
    const accessory = this.accessoryData();
    if (isNullOrUndefined(accessory)) return [];

    const details = [
      {
        key: 'Name',
        value:
          isDefined(accessory.name) && accessory.name.trim() !== ''
            ? accessory.name
            : 'Unnamed Accessory',
      },
    ];

    // Settings definition
    if (isDefined(accessory.settingsDefinition)) {
      details.push({
        key: 'Settings Definition',
        value:
          isDefined(accessory.settingsDefinition) &&
          accessory.settingsDefinition.trim() !== ''
            ? 'Configured'
            : 'Not configured',
      });
    } else {
      details.push({ key: 'Settings Definition', value: 'None' });
    }

    // I/O Capabilities summary
    if (isDefined(accessory.ioCapabilities)) {
      const ioSummary = this.getIOCapabilitiesSummary(accessory.ioCapabilities);
      details.push({
        key: 'I/O Capabilities',
        value: ioSummary || 'None configured',
      });
    } else {
      details.push({ key: 'I/O Capabilities', value: 'None' });
    }

    // Features summary
    if (isDefined(accessory.features)) {
      const featureCount = Object.keys(accessory.features).length;
      const featureNames = Object.keys(accessory.features).join(', ');
      details.push({
        key: 'Features',
        value:
          featureCount > 0
            ? `${featureCount} features: ${featureNames}`
            : 'None',
      });
    } else {
      details.push({ key: 'Features', value: 'None' });
    }

    return details;
  });

  readonly hasData = computed(() => {
    return this.accessoryData() !== null;
  });

  // IO Capabilities grid data
  readonly ioCapabilitiesGridData = computed(() => {
    const accessory = this.accessoryData();
    if (isNullOrUndefined(accessory?.ioCapabilities)) return [];

    const ioCapabilities = accessory.ioCapabilities as Record<string, unknown>;
    const result: { type: string; details: string }[] = [];

    const ioTypes = [
      { key: 'digital_input', label: 'Digital Input' },
      { key: 'digital_output', label: 'Digital Output' },
      { key: 'analog_input', label: 'Analog Input' },
      { key: 'temperature_input', label: 'Temperature Input' },
      { key: 'can_input', label: 'CAN Input' },
      { key: 'counter_input', label: 'Counter Input' },
      { key: 'camera_input', label: 'Camera Input' },
      { key: 'value_input', label: 'Value Input' },
    ];

    for (const ioType of ioTypes) {
      const ioData = ioCapabilities[ioType.key] as Record<string, unknown>;
      if (isDefined(ioData)) {
        const count = Object.keys(ioData).length;
        if (count > 0) {
          const details = Object.keys(ioData).join(', ');
          result.push({
            type: ioType.label,
            details: details,
          });
        }
      }
    }

    return result;
  });

  // Add this computed property
  readonly ioTypeItems = computed(() => {
    const accessory = this.accessoryData();
    if (isNullOrUndefined(accessory?.ioCapabilities)) return [];

    const ioCapabilities = accessory.ioCapabilities as Record<string, unknown>;
    const items: IOTypeItem[] = [];

    const ioTypes = [
      { key: 'digital_input', label: 'Digital Input' },
      { key: 'digital_output', label: 'Digital Output' },
      { key: 'analog_input', label: 'Analog Input' },
      { key: 'temperature_input', label: 'Temperature Input' },
      { key: 'can_input', label: 'CAN Input' },
      { key: 'counter_input', label: 'Counter Input' },
      { key: 'camera_input', label: 'Camera Input' },
      { key: 'value_input', label: 'Value Input' },
    ];

    for (const ioType of ioTypes) {
      const ioData = ioCapabilities[ioType.key] as Record<
        string,
        { optional?: boolean }
      >;
      if (isDefined(ioData)) {
        // Convert object to array
        const instancesArray: IOFieldItem[] = [];
        for (const [key, value] of Object.entries(ioData)) {
          instancesArray.push({
            key,
            optional: value?.optional ?? false,
          });
        }
        items.push({
          type: ioType.key,
          label: ioType.label,
          instances: instancesArray,
        });
      }
    }

    return items;
  });

  readonly hasIoCapabilities = computed(() => {
    return this.ioCapabilitiesGridData().length > 0;
  });

  // Settings Definition grid data
  readonly settingsDefinitionGridData = computed(() => {
    const accessory = this.accessoryData();
    if (isNullOrUndefined(accessory?.settingsDefinition)) return [];

    try {
      const settingsJson = JSON.parse(String(accessory.settingsDefinition));
      return Object.entries(settingsJson).map(([key, value]) => ({
        setting: key,
        value:
          typeof value === 'object' && value !== null
            ? (() => {
                try {
                  return JSON.stringify(value);
                } catch {
                  return '[object Object]';
                }
              })()
            : String(value),
      }));
    } catch {
      return [
        {
          setting: 'Raw Definition',
          value: String(accessory.settingsDefinition),
          type: 'string',
        },
      ];
    }
  });

  readonly settingsItems = computed(() => {
    const accessory = this.accessoryData();
    if (isNullOrUndefined(accessory?.settingsDefinition)) return [];

    try {
      const settingsJson = JSON.parse(String(accessory.settingsDefinition));
      const settingsArray: SettingItem[] = [];
      for (const setting of settingsJson['fields']) {
        settingsArray.push(setting);
      }
      return settingsArray;
    } catch {
      return [];
    }
  });

  // Individual field grid data - creates a grid for each field
  readonly settingsFieldsGridData = computed(() => {
    const accessory = this.accessoryData();
    if (isNullOrUndefined(accessory?.settingsDefinition)) return [];

    try {
      const settingsJson = JSON.parse(String(accessory.settingsDefinition));
      const fields = settingsJson.fields;

      if (!isDefined(fields) || typeof fields !== 'object') return [];

      return Object.entries(fields).map(([fieldKey, fieldValue]) => ({
        fieldKey,
        fieldTitle:
          isDefined((fieldValue as Record<string, unknown>)?.['id']) &&
          isDefined((fieldValue as Record<string, unknown>)?.['title'])
            ? `${(fieldValue as Record<string, unknown>)?.['title']}(${(fieldValue as Record<string, unknown>)?.['id']})`
            : fieldKey.charAt(0).toUpperCase() +
              fieldKey.slice(1).replaceAll(/([A-Z])/g, ' $1'),
        gridData:
          typeof fieldValue === 'object' && fieldValue !== null
            ? Object.entries(fieldValue as Record<string, unknown>).map(
                ([key, value]) => ({
                  property: key,
                  value:
                    typeof value === 'object' && value !== null
                      ? JSON.stringify(value)
                      : String(value),
                }),
              )
            : [{ property: 'Value', value: String(fieldValue) }],
      }));
    } catch {
      return [];
    }
  });

  // Columns for field grids
  readonly fieldGridColumns = [
    { field: 'property', header: 'Property' },
    { field: 'value', header: 'Value' },
  ];

  readonly hasSettingsDefinition = computed(() => {
    return this.settingsDefinitionGridData().length > 0;
  });

  // Features grid data
  readonly featuresGridData = computed(() => {
    const accessory = this.accessoryData();
    if (isNullOrUndefined(accessory?.features)) return [];

    const features = accessory.features as Record<string, unknown>;
    return Object.entries(features).map(([key, value]) => ({
      feature: key,
      value:
        typeof value === 'object' && value !== null
          ? JSON.stringify(value, null, 2)
          : String(value),
      type: typeof value,
      enabled: Boolean(value),
    }));
  });

  readonly featureItems = computed(() => {
    const accessory = this.accessoryData();
    if (isNullOrUndefined(accessory?.features)) return [];

    const features = accessory.features as Record<string, unknown>;
    return Object.entries(features).map(([key, value]) => ({
      key,
      value:
        typeof value === 'object' && value !== null
          ? JSON.stringify(value)
          : String(value),
    }));
  });

  readonly hasFeatures = computed(() => {
    return this.featuresGridData().length > 0;
  });

  private getIOCapabilitiesSummary(
    ioCapabilities: Record<string, unknown>,
  ): string {
    if (!isDefined(ioCapabilities)) return '';

    const ioTypes = [
      { key: 'digital_input', label: 'Digital In' },
      { key: 'digital_output', label: 'Digital Out' },
      { key: 'analog_input', label: 'Analog In' },
      { key: 'temperature_input', label: 'Temp In' },
      { key: 'can_input', label: 'CAN In' },
      { key: 'counter_input', label: 'Counter In' },
      { key: 'camera_input', label: 'Camera In' },
      { key: 'value_input', label: 'Value In' },
    ];

    const summary = [];
    for (const ioType of ioTypes) {
      const count = Object.keys(ioCapabilities[ioType.key] ?? {}).length;
      if (count > 0) {
        summary.push(`${count} ${ioType.label}`);
      }
    }

    return summary.join(', ');
  }

  protected override updateFormFields(): void {
    const accessory = this.accessoryData();

    // IO Capability modal configuration (needs to be first since IO Type modal references it)
    this.ioCapabilityModalConfig = {
      modalComponent: ModalListFormComponent,
      modalTitle: 'Manage IO Capability',
      defaultItemProvider: () => ({ key: '', optional: false }),
      listItemFormatter: (ioItem: IOFieldItem) => ({
        id: ioItem.key,
        title: ioItem.key,
        display: `${ioItem.key} (${ioItem.optional ? 'Optional' : 'Required'})`,
        icon: 'pi pi-microchip',
      }),
      modalFields: (ioItem: IOFieldItem | undefined): FormFieldConfig[] => [
        createTextField('key', 'IO Key', ioItem?.key ?? '', {
          placeholder: 'Enter IO key...',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'IO Key is required',
            },
          ],
        }),
        createToggleSwitchField(
          'optional',
          'Optional',
          ioItem?.optional ?? false,
          {
            hint: 'When enabled, this IO is optional. When disabled, this IO is required.',
            props: {
              layout: 'horizontal',
              labelWidth: '1/3',
              onLabel: 'Optional',
              offLabel: 'Required',
            },
          },
        ),
      ],
      onSubmit: async (ioItem: IOFieldItem) => {
        if (!ioItem.key?.trim()) {
          return false;
        }
        return true;
      },
      onDeleteItem: async (ioItem: IOFieldItem) => {
        return new Promise<boolean>((resolve) => {
          this.confirmationService.confirm({
            header: 'Confirmation',
            message: `Are you sure you want to delete IO capability "${ioItem.key}"?`,
            closable: true,
            closeOnEscape: true,
            icon: 'pi pi-exclamation-triangle',
            rejectButtonProps: {
              label: 'Cancel',
              severity: 'secondary',
              outlined: true,
            },
            acceptButtonProps: {
              label: 'Delete',
              severity: 'danger',
            },
            accept: () => resolve(true),
            reject: () => resolve(false),
          });
        });
      },
      subHeader: 'Configure individual IO capabilities for this IO type',
      showEditButton: true,
    };

    // IO Type modal configuration
    this.ioTypeModalConfig = {
      modalComponent: ModalListFormComponent,
      modalTitle: 'IO Capabilities',
      defaultItemProvider: () => ({
        type: 'digital_input',
        label: 'Digital Input',
        instances: [],
      }),
      listItemFormatter: (item: IOTypeItem) => {
        const count = isDefined(item.instances) ? item.instances.length : 0;
        return {
          id: item.type,
          title: item.label,
          display: `${count} ${item.label}(s)`,
          icon: 'pi pi-microchip',
        };
      },
      modalFields: (item: IOTypeItem | undefined): FormFieldConfig[] => {
        return [
          {
            ...createTextField('type', 'Type', item?.type ?? ''),
            hidden: true,
          },
          {
            ...createTextField('label', 'Label', item?.label ?? ''),
            hidden: true,
          },
          createModalListField(
            'instances',
            'IO Capability',
            item?.instances ?? [],
            {
              layout: 'horizontal',
              labelWidth: '1/3',
              props: {
                modalConfig: this
                  .ioCapabilityModalConfig as unknown as ListFieldModalConfig<unknown>,
              },
            },
          ),
        ];
      },
      onSubmit: async (item: IOTypeItem) => {
        if (!item.type?.trim()) return false;
        return true;
      },
      onDeleteItem: async (item: IOTypeItem) => {
        return new Promise<boolean>((resolve) => {
          this.confirmationService.confirm({
            header: 'Confirmation',
            message: `Are you sure you want to delete IO "${item.type}"?`,
            accept: () => resolve(true),
            reject: () => resolve(false),
          });
        });
      },
      subHeader: 'Configure accessory IO capabilities',
      showEditButton: true,
      showAddButton: false,
      showDeleteButton: false,
    };

    // Settings modal configuration
    this.settingsModalConfig = {
      modalComponent: ModalListFormComponent,
      modalTitle: 'Manage Settings',
      defaultItemProvider: () => ({
        id: '',
        title: '',
        type: 'text' as const,
        values: [],
        unit: '',
        min: 0,
        max: 100,
        width: 50,
        defaultValue: '',
      }),
      listItemFormatter: (item: SettingItem) => {
        const unitText = isDefined(item.unit) ? ` - ${item.unit}` : '';
        const valuesText =
          item.values && item.values.length > 0
            ? ` - ${item.values.length} options`
            : '';
        const displayText = `${item.title} (${item.type})${unitText}${valuesText}`;

        return {
          id: item.id,
          title: item.title,
          display: displayText,
          icon: `pi pi-cog`,
        };
      },
      modalFields: (item: SettingItem | undefined): FormFieldConfig[] => [
        createTextField('id', 'Field ID', item?.id ?? '', {
          placeholder: 'Enter field ID...',
          hint: 'Unique identifier for this field',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Field ID is required',
            },
          ],
        }),
        createTextField('title', 'Field Title', item?.title ?? '', {
          placeholder: 'Enter field title...',
          hint: 'Display name for this field',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Field Title is required',
            },
          ],
        }),
        createDropdownField(
          'type',
          'Field Type',
          [
            { label: 'Text', value: 'text' },
            { label: 'Number', value: 'number' },
            { label: 'Combo/Dropdown', value: 'combo' },
            { label: 'Boolean', value: 'boolean' },
          ],
          item?.type ?? 'text',
          {
            hint: 'Select the input type for this field',
            validations: [
              {
                name: 'required',
                validator: Validators.required,
                message: 'Field Type is required',
              },
            ],
          },
        ),
        // Values field (only for combo type)
        ...(item?.type === 'combo'
          ? [
              createModalListField<{ key: string; value: string }>(
                'values',
                'Combo Options',
                item?.values ?? [],
                {
                  layout: 'horizontal',
                  labelWidth: '1/3',
                  name: 'values',
                  hint: 'Define the available options for combo/dropdown fields',
                  props: {
                    modalConfig: {
                      modalComponent: ModalListFormComponent,
                      modalTitle: 'Manage Combo Option',
                      defaultItemProvider: () => ({ key: '', value: '' }),
                      listItemFormatter: (option: {
                        key: string;
                        value: string;
                      }) => ({
                        id: option.key,
                        title: option.key,
                        display: `${option.key}: ${option.value}`,
                        icon: 'pi pi-circle',
                      }),
                      modalFields: [
                        createTextField('key', 'Option Key', '', {
                          placeholder: 'Enter option key...',
                          validations: [
                            {
                              name: 'required',
                              validator: Validators.required,
                              message: 'Option Key is required',
                            },
                          ],
                        }),
                        createTextField('value', 'Option Value', '', {
                          placeholder: 'Enter option value...',
                          validations: [
                            {
                              name: 'required',
                              validator: Validators.required,
                              message: 'Option Value is required',
                            },
                          ],
                        }),
                      ],
                      onSubmit: async (option: {
                        key: string;
                        value: string;
                      }) => {
                        return !!(option.key?.trim() && option.value?.trim());
                      },
                      subHeader: 'Configure individual combo options',
                      showEditButton: true,
                    },
                  },
                },
              ) as FormFieldConfig<unknown>,
            ]
          : []),
        // Unit field (for number type)
        createTextField('unit', 'Unit', item?.unit ?? '', {
          placeholder: 'e.g., millimeters, seconds, etc.',
          hint: 'Unit of measurement for number fields',
        }),
        // Min value field (for number type)
        createNumberField('min', 'Minimum Value', item?.min, {
          placeholder: 'Enter minimum value...',
          hint: 'Minimum allowed value for number fields',
          validations: [
            {
              name: 'min',
              validator: Validators.min(-999999),
              message: 'Minimum value must be -999999 or greater',
            },
          ],
        }),
        // Max value field (for number type)
        createNumberField('max', 'Maximum Value', item?.max, {
          placeholder: 'Enter maximum value...',
          hint: 'Maximum allowed value for number fields',
          validations: [
            {
              name: 'max',
              validator: Validators.max(999999),
              message: 'Maximum value must be 999999 or less',
            },
          ],
        }),
        // Width field (for number type)
        createNumberField('width', 'Field Width', item?.width, {
          placeholder: 'Enter field width...',
          hint: 'Display width for the input field',
          validations: [
            {
              name: 'min',
              validator: Validators.min(1),
              message: 'Field width must be 1 or greater',
            },
            {
              name: 'max',
              validator: Validators.max(1000),
              message: 'Field width must be 1000 or less',
            },
          ],
        }),
        // Default value field
        createTextField(
          'defaultValue',
          'Default Value',
          isDefined(item?.defaultValue) ? String(item?.defaultValue) : '',
          {
            placeholder: 'Enter default value...',
            hint: 'Default value when field is empty',
          },
        ),
      ],
      onSubmit: async (item: SettingItem) => {
        if (!item.id?.trim() || !item.title?.trim()) return false;
        return true;
      },
      onDeleteItem: async (item: SettingItem) => {
        return new Promise<boolean>((resolve) => {
          this.confirmationService.confirm({
            header: 'Confirmation',
            message: `Are you sure you want to delete setting "${item.title}"?`,
            closable: true,
            closeOnEscape: true,
            icon: 'pi pi-exclamation-triangle',
            rejectButtonProps: {
              label: 'Cancel',
              severity: 'secondary',
              outlined: true,
            },
            acceptButtonProps: {
              label: 'Delete',
              severity: 'danger',
            },
            accept: () => resolve(true),
            reject: () => resolve(false),
          });
        });
      },
      subHeader: 'Configure accessory settings with various field types',
      showEditButton: true,
    };

    // Features modal configuration
    this.featuresModalConfig = {
      modalComponent: ModalListFormComponent,
      modalTitle: 'Manage Features',
      defaultItemProvider: () => ({ key: '', value: '' }),
      listItemFormatter: (item: FeatureItem) => ({
        id: item.key,
        title: item.key,
        display: `${item.key}: ${item.value}`,
        icon: 'pi pi-star',
      }),
      modalFields: (item: FeatureItem | undefined): FormFieldConfig[] => [
        createTextField('key', 'Feature Key', item?.key ?? '', {
          placeholder: 'Enter feature key...',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Feature Key is required',
            },
          ],
        }),
        createTextField('value', 'Feature Value', item?.value ?? '', {
          placeholder: 'Enter feature value...',
          validations: [
            {
              name: 'required',
              validator: Validators.required,
              message: 'Feature Value is required',
            },
          ],
        }),
      ],
      onSubmit: async (item: FeatureItem) => {
        if (!item.key?.trim() || !item.value?.trim()) return false;
        return true;
      },
      onDeleteItem: async (item: FeatureItem) => {
        return new Promise<boolean>((resolve) => {
          this.confirmationService.confirm({
            header: 'Confirmation',
            message: `Are you sure you want to delete feature "${item.key}"?`,
            accept: () => resolve(true),
            reject: () => resolve(false),
          });
        });
      },
      subHeader: 'Configure accessory features',
      showEditButton: true,
    };

    // Create form fields with modal lists
    this.formFields = [
      createTextField('name', 'Accessory Name', accessory?.name ?? '', {
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Accessory Name is required',
          },
        ],
      }),
      createModalListField<IOTypeItem>(
        'ioCapabilities',
        'IO Capabilities',
        this.ioTypeItems(),
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          name: 'ioCapabilities',
          props: {
            modalConfig: this.ioTypeModalConfig,
          },
        },
      ) as FormFieldConfig<unknown>,
      createModalListField<SettingItem>(
        'settings',
        'Settings',
        this.settingsItems(),
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          name: 'settings',
          props: {
            modalConfig: this.settingsModalConfig,
          },
        },
      ) as FormFieldConfig<unknown>,
      createModalListField<FeatureItem>(
        'features',
        'Features',
        this.featureItems(),
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          name: 'features',
          props: {
            modalConfig: this.featuresModalConfig,
          },
        },
      ) as FormFieldConfig<unknown>,
    ];
  }

  protected override transformFormData(
    formData: Record<string, unknown>,
  ): Partial<DeviceTypeResponse> {
    const entity = this.entity();
    const key = this.accessoryKey();
    const existingAccessory: DeviceTypeAccessory | undefined =
      isDefined(entity) && isDefined(entity.accessories)
        ? entity.accessories[key]
        : undefined;
    const name = formData['name'] as string;

    // Start with all existing accessories to preserve them
    const accessories = entity?.accessories ? { ...entity.accessories } : {};

    // Transform ioCapabilities from array to object
    const ioCapabilities: Record<string, { optional?: boolean | null }> =
      this.transformIoCapabilities(formData);

    // Transform settings from array to JSON string
    const settingsDefinition: string | null = this.formatSettings(formData);

    // Transform features from array to object (features is Record<string, DeviceTypeFeature>)
    const features: Record<string, { settings?: Record<string, unknown> }> =
      this.formatFeatures(formData);

    // Update only the specific accessory being edited
    accessories[key] = {
      name:
        name === existingAccessory?.name || isNullOrUndefined(name)
          ? existingAccessory?.name
          : name,
      ioCapabilities: isEmpty(ioCapabilities)
        ? (existingAccessory?.ioCapabilities ?? {})
        : ioCapabilities,
      settingsDefinition: isNullOrUndefined(settingsDefinition)
        ? existingAccessory?.settingsDefinition
        : settingsDefinition,
      features: isEmpty(features)
        ? (existingAccessory?.features ?? {})
        : (features as Record<string, DeviceTypeFeature>),
    };

    return {
      accessories: accessories,
    };
  }

  private formatSettings(formData: Record<string, unknown>) {
    let settingsDefinition: string | null = null;
    if (
      isDefined(formData['settings']) &&
      Array.isArray(formData['settings'])
    ) {
      const settingsArray = formData['settings'] as SettingItem[];
      if (settingsArray.length > 0) {
        settingsDefinition = JSON.stringify({ fields: settingsArray });
      }
    }
    return settingsDefinition;
  }

  private formatFeatures(formData: Record<string, unknown>) {
    const features: Record<string, { settings?: Record<string, unknown> }> = {};
    if (
      isDefined(formData['features']) &&
      Array.isArray(formData['features'])
    ) {
      const featuresArray = formData['features'] as FeatureItem[];
      for (const feature of featuresArray) {
        if (isDefined(feature.key)) {
          // Parse the value as it might be a stringified object or primitive
          let parsedValue: unknown;
          try {
            parsedValue = JSON.parse(feature.value);
          } catch {
            parsedValue = feature.value;
          }

          // DeviceTypeFeature has settings and webhook properties
          // If the value is already an object with these properties, use it
          // Otherwise, store it in a generic way
          this.parseFeatures(parsedValue, features, feature);
        }
      }
    }
    return features;
  }

  private parseFeatures(
    parsedValue: unknown,
    features: Record<string, { settings?: Record<string, unknown> }>,
    feature: FeatureItem,
  ) {
    if (typeof parsedValue === 'object' && parsedValue !== null) {
      features[feature.key] = parsedValue as {
        settings?: Record<string, unknown>;
      };
    } else {
      // For primitive values, wrap them in an object structure
      features[feature.key] = { settings: { value: parsedValue } };
    }
  }

  private transformIoCapabilities(formData: Record<string, unknown>) {
    const ioCapabilities: Record<string, { optional?: boolean | null }> = {};
    if (
      isDefined(formData['ioCapabilities']) &&
      Array.isArray(formData['ioCapabilities'])
    ) {
      const ioCapabilitiesArray = formData['ioCapabilities'] as IOTypeItem[];
      for (const ioType of ioCapabilitiesArray) {
        if (isDefined(ioType.type) && isDefined(ioType.instances)) {
          // Convert IOFieldItem array to DeviceTypeIOProperties object
          const instances: Record<string, { optional?: boolean | null }> = {};
          for (const instance of ioType.instances) {
            instances[instance.key] = {
              optional: instance.optional ?? null,
            };
          }
          ioCapabilities[ioType.type] = instances;
        }
      }
    }
    return ioCapabilities;
  }

  protected override getUpdateMethod() {
    return (
      this.operationRegistry.getOperation('device-types', 'update') ??
      ((id, data) =>
        this.deviceTypesService.updateDeviceType(id, data).pipe(
          tap((response) => {
            this.entityChange.emit(response);
            this.broadcastService.broadcast('entity-updated', {
              entityType: 'device-types',
              operation: 'update',
              entityId: response.id,
              data: response,
            });
          }),
        ))
    );
  }

  ngOnInit(): void {
    this.updateFormFields();
  }

  onCancel(): void {
    this.cancelEdit();
  }

  onSave(): void {
    const formGroup = this.getFormGroup();
    if (formGroup) {
      this.onSubmit(formGroup);
    }
  }

  readonly getActiveValues = computed(() => {
    const activeValues: string[] = [];

    if (this.hasIoCapabilities()) {
      activeValues.push('io-capabilities');
    }

    if (this.hasSettingsDefinition()) {
      activeValues.push('settings-definition');
    }

    if (this.hasFeatures()) {
      activeValues.push('features');
    }

    return activeValues;
  });
}
