import {
  Component,
  computed,
  DestroyRef,
  effect,
  inject,
  input,
  OnInit,
  signal,
} from '@angular/core';

import { MessageModule } from 'primeng/message';
import { ProgressSpinnerModule } from 'primeng/progressspinner';
import { firstValueFrom } from 'rxjs';

import {
  DeviceListItem,
  EntitiesService,
  DeviceResponse,
  DeviceTypeAccessory,
  DeviceTypeResponse,
  IoTypeListItem,
  DeviceCreateRequest,
} from '../../../../../api';
import { RequestCacheService } from '../../../../../shared/services/request-cache.service';
import { isDefined, isNullOrUndefined } from '../../../../../shared/utils';
import { EntityDetailsStateService } from '../../../services/entity/entity-details.state.service';
import { EntityOperationRegistryService } from '../../../services/entity/entity-operation-registry.service';
import { DetailsAccordionContainerComponent } from '../../details-accordion-container/details-accordion-container.component';
import { DeviceSectionConfigFactory } from '../../../factories';
import { BroadcastEventMessageService } from '../../../services/broadcast-event-messages.service';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { ToastService } from '../../../../../core/services/toast.service';

interface EntityUpdatedMessage {
  entityType: string;
  data: DeviceResponse;
}
@Component({
  selector: 'app-device-details-container',
  imports: [
    DetailsAccordionContainerComponent,
    ProgressSpinnerModule,
    MessageModule,
  ],
  templateUrl: './device-details-container.component.html',
  styleUrl: './device-details-container.component.scss',
})
export class DeviceDetailsContainerComponent implements OnInit {
  readonly entity = input<DeviceListItem | null>(null);

  private readonly deviceSectionFactory = inject(DeviceSectionConfigFactory);
  readonly entityDetailsState = inject(EntityDetailsStateService);
  private readonly entitiesService = inject(EntitiesService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly cacheService = inject(RequestCacheService);
  private readonly broadcastService = inject(BroadcastEventMessageService);
  private readonly toastService = inject(ToastService);
  private readonly destroyRef = inject(DestroyRef);

  readonly isLoading = computed(() => this.entityDetailsState.isLoading());
  readonly isSoftRefreshing = computed(() =>
    this.entityDetailsState.isSoftRefreshing(),
  );
  readonly isInitialLoading = computed(
    () => this.isLoading() && !this.isSoftRefreshing() && !this.fullDevice(),
  );
  readonly fullDevice = computed(() => {
    const entityDetails = this.entityDetailsState.selectedItem();

    if (!entityDetails) {
      return null;
    }
    return entityDetails as DeviceResponse;
  });
  readonly sections = computed(() => {
    const device = this.fullDevice();
    const deviceType = this.deviceType();
    const ioTypes = this.ioTypes();
    if (!(device || deviceType || isDefined(ioTypes))) return [];
    return this.deviceSectionFactory.createAllDeviceSections(
      device,
      this.deviceTypeAccessories(),
      deviceType,
      ioTypes,
    );
  });
  readonly error = signal<string | null>(null);
  readonly deviceTypeAccessories = signal<
    Record<string, DeviceTypeAccessory> | undefined
  >(undefined);
  readonly deviceType = signal<DeviceTypeResponse | null>(null);
  readonly ioTypes = signal<IoTypeListItem[]>([]);
  private isEntityUpdatedMessage(
    message: unknown,
  ): message is EntityUpdatedMessage {
    return (
      isDefined(message) &&
      message !== null &&
      typeof message === 'object' &&
      'entityType' in message &&
      'data' in message &&
      (message as EntityUpdatedMessage).entityType === 'device'
    );
  }

  constructor() {
    this.operationRegistry.registerEntityOperations('device', {
      update: (id, data) => this.entitiesService.updateDevice(id, data),
      create: (data: DeviceCreateRequest) =>
        this.entitiesService.createDevice(data),
      get: (id) => this.entitiesService.getDevice(id),
    });

    effect(() => {
      const device = this.fullDevice();
      if (device) {
        this.loadDeviceType(device.deviceType?.id ?? '').catch((error) => {
          this.error.set(error.message);
        });
        this.loadIoTypes().catch((error) => {
          this.error.set(error.message);
        });
      }
    });
  }

  ngOnInit(): void {
    this.broadcastService
      .on('entity-updated')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((message) => {
        if (this.isEntityUpdatedMessage(message)) {
          this.toastService.showSuccess(
            'Success',
            'Device updated successfully',
          );
          this.onEntityChange(message.data);
        }
      });
  }

  onEntityChange(entity: unknown): void {
    this.entityDetailsState.updateEntity(entity as DeviceResponse);
    this.error.set(null);
  }

  async loadDeviceType(deviceTypeId: string): Promise<void> {
    const ownerId = this.entity()?.owner?.id;
    if (!deviceTypeId || isNullOrUndefined(ownerId)) return;

    await this.cacheService
      .getOrFetchAsync(
        `deviceType-${deviceTypeId}-${ownerId}`,
        () =>
          firstValueFrom(
            this.entitiesService.getDeviceType(deviceTypeId, ownerId),
          ),
        300000,
      )
      .then((deviceType) => {
        this.deviceType.set(deviceType);
        this.deviceTypeAccessories.set(deviceType.accessories);
      });
  }

  async loadIoTypes(): Promise<void> {
    const ownerId = this.entity()?.owner?.id;
    if (isNullOrUndefined(ownerId)) return;

    await this.cacheService
      .getOrFetchAsync(
        `ioTypes-${ownerId}`,
        () =>
          firstValueFrom(
            this.entitiesService.listIoTypes(ownerId, true, 0, 300),
          ),
        300000,
      )
      .then((response) => {
        this.ioTypes.set(response.items);
      });
  }
}
